import {
  trigger,
  transition,
  style,
  query,
  animateChild,
  group,
  animate,
  animation,
  keyframes,
  AnimationReferenceMetadata,
} from '@angular/animations';

export const sharedStyles = {
  position: 'fixed',
  overflow: 'hidden',
  backfaceVisibility: 'hidden',
  transformStyle: 'preserve-3d',
  left: 0,
  right: 0,
  top: 0,
};
export const routeTransitionAnimations = trigger('slideRouteAnimations', [
  transition(
    '* => *',
    [
      style({ position: 'relative' }),
      query(':enter, :leave', [
        style({
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100%',
        }),
      ], { optional: true }),
      group([
        query(
          ':enter',
          [
            style({
              transform: 'translate({{offsetEnterX}}%, {{offsetEnterY}}%)',
            }),
            animate(
              '0.2s ease-in-out',
              style({ transform: 'translate(0%, 0%)', opacity: 1 })
            ),
          ],
          { optional: true }
        ),
        query(
          ':leave',
          [
            style({ transform: 'translate(0%, 0%)' }),
            animate(
              '0.2s ease-in-out',
              style({
                transform: 'translate({{offsetLeaveX}}%, {{offsetLeaveY}}%)',
                opacity: 0,
              })
            ),
          ],
          { optional: true }
        ),
      ]),
    ],
    {
      params: {
        offsetEnterX: 0,
        offsetEnterY: 0,
        offsetLeaveX: 0,
        offsetLeaveY: 0,
      },
    }
  ),
]);

export const moveFromBottomFade: AnimationReferenceMetadata = animation(
  [
    query(':enter, :leave', style(sharedStyles), { optional: true }),
    group([
      query(
        ':enter',
        [
          animate(
            '{{enterTiming}}s {{enterDelay}}s ease',
            keyframes([
              style({
                transform: 'translateY(4%) scale(0.97)',
                height: '100vh',
                opacity: '0',
                'z-index': '9999',
                overflow: 'hidden',
                offset: 0,
              }),
              style({
                transform: 'translateY(1%) scale(0.99)',
                opacity: '0.5',
                'z-index': '9999',
                offset: 0.9,
              }),
              style({
                transform: 'translateY(0%) scale(1)',
                opacity: '1',
                height: '100vh',
                overflow: 'hidden',
                offset: 1,
              }),
            ])
          ),
        ],
        { optional: true }
      ),

      // query(
      //   ':leave',
      //   [
      //     animate(
      //       '{{leaveTiming}}s {{leaveDelay}}s ease',
      //       keyframes([
      //         style({
      //           opacity: '1',
      //           transform: 'translateY(0%) scale(1)',
      //           'z-index': '9999',
      //           offset: 0,
      //         }),
      //         style({
      //           opacity: '0.3',
      //           transform: 'translateY(30%) scale(0.8)',
      //           offset: 1,
      //         }),
      //       ])
      //     ),
      //   ],
      //   { optional: true }
      // ),
    ]),
  ],
  {
    params: {
      enterTiming: '.2',
      leaveTiming: '.3',
      enterDelay: '0',
      leaveDelay: '0',
    },
  }
);

export const moveFromBottomFadeReverse: AnimationReferenceMetadata = animation(
  [
    query(':leave, :enter', style(sharedStyles), { optional: true }),
    group([
      query(
        ':leave',
        [
          animate(
            '{{enterTiming}}s {{enterDelay}}s ease',
            keyframes([
              style({
                transform: 'translateY(0)',
                offset: 0,
                'z-index': '9999',
              }),
              style({ transform: 'translateY(100%)', offset: 1 }),
            ])
          ),
        ],
        { optional: true }
      ),
      query(
        ':enter,',
        [
          animate(
            '{{leaveTiming}}s {{leaveDelay}}s ease',
            keyframes([
              style({ opacity: '1', offset: 0 }),
              style({ opacity: '0.3', offset: 1 }),
            ])
          ),
        ],
        { optional: true }
      ),
    ]),
  ],
  {
    params: {
      enterTiming: '.2',
      leaveTiming: '.3',
      enterDelay: '0',
      leaveDelay: '0',
    },
  }
);
