import {
  AfterViewInit,
  Component,
  EventEmitter,
  Input,
  Output,
  ViewChild,
} from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';

import { Subscription } from 'rxjs';

import { PasswordInputComponent } from '@shared/input/password-input/password-input.component';

export interface INewPasswordResult {
  newPassword: string;
  oldPassword?: string;
}

@Component({
  selector: 'j-new-password',
  templateUrl: './new-password.component.html',
  styleUrls: ['./new-password.component.scss'],
})
export class NewPasswordComponent implements AfterViewInit {
  @Output() submitted = new EventEmitter<INewPasswordResult>();

  @Input()
  errorText: string;

  @Input()
  isLoading = false;

  @Input()
  isOldShown = false;

  passwordForm: FormGroup;
  confirmError: string;

  isSubmitted = false;

  isOldFocused = false;
  isOldBlurred = false;

  isPasswordFocused = false;
  isPasswordBlurred = false;
  isConfirmBlurred = false;

  requirements = [
    {
      code: 'capital',
      label: 'AUTH.PASSWORD.REQUIREMENTS.CAPITAL',
    },
    {
      code: 'number',
      label: 'AUTH.PASSWORD.REQUIREMENTS.NUMBER',
    },
    {
      code: 'symbol',
      label: 'AUTH.PASSWORD.REQUIREMENTS.SYMBOL',
    },
    {
      code: 'length',
      label: 'AUTH.PASSWORD.REQUIREMENTS.LENGTH',
    },
  ];

  @ViewChild('passwordOldInput')
  passwordOldInput: PasswordInputComponent;

  @ViewChild('passwordInput')
  passwordInput: PasswordInputComponent;

  @ViewChild('passwordConfirmInput')
  passwordConfirmInput: PasswordInputComponent;

  validate = {
    capital: false,
    number: false,
    symbol: false,
    length: false,
  };

  readonly subs = new Subscription();

  constructor() {
    this.passwordForm = new FormGroup({
      passwordOld: new FormControl('', Validators.required),
      password: new FormControl('', Validators.required),
      passwordConfirm: new FormControl('', Validators.required),
    });

    const formSubsciption = this.passwordForm.valueChanges.subscribe(
      (value) => {
        this.isSubmitted = false;
        this.isPasswordBlurred = false;
        this.isConfirmBlurred = false;
        this.confirmError = '';
        this.errorText = '';
        this.validatePassword();
      }
    );
    this.subs.add(formSubsciption);
  }

  public clearForm() {
    this.passwordForm.setValue({
      passwordOld: '',
      password: '',
      passwordConfirm: '',
    });
  }

  ngAfterViewInit() {
    if (this.isOldShown) {
      this.passwordOldInput.focus();
    } else {
      this.passwordInput.focus();
    }
  }

  onOldFocus() {
    this.isOldFocused = true;
  }

  onOldBlur() {
    this.isOldBlurred = true;
    this.isOldFocused = false;
  }

  onOldKeyPress(event: KeyboardEvent) {
    event.preventDefault();
    this.isOldBlurred = true;

    if (this.isOldValid) {
      this.passwordInput.focus();
    }
  }

  onPasswordFocus() {
    this.isPasswordFocused = true;
  }

  onPasswordBlur() {
    this.isPasswordBlurred = true;
    this.isPasswordFocused = false;
  }

  onPasswordKeyPress(event: KeyboardEvent) {
    event.preventDefault();
    this.isPasswordBlurred = true;

    if (this.isPasswordValid) {
      this.passwordConfirmInput.focus();
    }
  }

  onConfirmBlur() {
    this.isConfirmBlurred = true;
  }

  onConfirmKeyPress(event: KeyboardEvent) {
    event.preventDefault();
    this.isConfirmBlurred = true;
    this.onSubmit();
  }

  onSubmit() {
    this.isSubmitted = true;

    if (this.isPasswordValid && !this.isConfirmValid) {
      this.confirmError = 'ERRORS.PASSWORDS_NOT_EQUAL';
      this.passwordConfirmInput.triggerHelperAnimation();
      return;
    }

    if (this.isOldValid && this.isPasswordValid && this.isConfirmValid) {
      this.submitted.emit({
        oldPassword: this.f.passwordOld.value,
        newPassword: this.f.password.value,
      });
    }
  }

  get f() {
    return this.passwordForm.controls;
  }

  get isRequirementsShown() {
    if (this.isPasswordFocused || !this.isPasswordValid) {
      return true;
    }
    if (this.isPasswordValid) {
      return false;
    }
  }

  get isOldValid() {
    if (this.isOldShown && this.isOldEmpty) {
      return false;
    }

    return true;
  }

  get isPasswordValid() {
    if (this.isPasswordEmpty) {
      return false;
    }

    for (const property in this.validate) {
      if (this.validate.hasOwnProperty(property)) {
        if (!this.validate[property]) {
          return false;
        }
      }
    }
    return true;
  }

  get isConfirmValid() {
    const password = this.f.password.value;
    const passwordConfirm = this.f.passwordConfirm.value;

    return password === passwordConfirm && this.isPasswordValid;
  }

  get isOldEmpty() {
    return this.f.passwordOld?.value.length === 0;
  }

  get isPasswordEmpty() {
    return this.f.password.value.length === 0;
  }

  get isConfirmEmpty() {
    return this.f.passwordConfirm.value.length === 0;
  }

  private validatePassword() {
    this.validate.capital = /(?=.*[a-z])(?=.*[A-Z])/g.test(
      this.f.password.value
    );
    this.validate.number = /\d/g.test(this.f.password.value);
    this.validate.symbol = /[.!@#$%^&*)]/g.test(this.f.password.value);
    this.validate.length =
      this.f.password.value.length >= 8 && this.f.password.value.length < 255;
  }
}
