import { Injectable } from '@angular/core';

import { BehaviorSubject, Subject } from 'rxjs';

import { ApiService } from '@api/index';

import { IFile, IImportResult, IUploadingFile } from '@shared/models';
import {
  HttpEvent,
  HttpEventType,
  HttpHeaders,
  HttpParams,
} from '@angular/common/http';
import { scan } from 'rxjs/operators';
import { TranslateService } from '@ngx-translate/core';
import { NotifierService } from 'angular-notifier';

@Injectable({
  providedIn: 'root',
})
export class FilesService {
  imported$ = new Subject<IImportResult[]>();
  public uploading$ = new BehaviorSubject<File[]>([]);

  constructor(
    private apiService: ApiService,
    private translateService: TranslateService,
    private notifer: NotifierService
  ) {}

  async downloadFileById(fileId: string, fileName: string) {
    const url = `files/${fileId}`;

    let headers = new HttpHeaders();
    // headers = headers.set('Accept-File', 'application/pdf');
    headers = headers.set('Content-Type', 'application/octet-stream');

    const params = new HttpParams();
    const fileBlob = await this.apiService.get(url, headers, params, {
      responseType: 'blob',
    });

    return this.downloadFile(fileBlob, fileName);
  }

  downloadFile(fileBlob, fileName: string) {
    const blob = new Blob([fileBlob as any], {
      type: 'application/octet-stream',
    });

    const downloadURL = window.URL.createObjectURL(blob);
    const link = document.createElement('a');

    link.href = downloadURL;
    link.download = fileName;

    link.click();
  }

  onImport() {
    return this.imported$.asObservable();
  }

  async uploadPayments(companyId: string, fileList: FileList) {
    const url = `companies/${companyId}/payments/import`;
    const formData = new FormData();

    for (let index = 0; index < fileList.length; index++) {
      formData.append('files', fileList[index]);
    }

    try {
      const importResult = await this.apiService.post<IImportResult[]>(
        url,
        formData
      );
      this.imported$.next(importResult);

      return importResult;
    } catch (error) {
      throw error;
    }
  }

  uploadFile(file: File) {
    const url = `files`;

    const formData = new FormData();
    formData.append('file', file);

    const extras = {
      reportProgress: true,
      observe: 'events',
    };

    return this.apiService
      .postObservable<HttpEvent<IFile>>(url, formData, null, null, extras)
      .pipe(
        scan(this.scanUploadProgress, {
          progress: 0,
          isLoading: true,
          file: null,
        })
      );
  }

  checkIfCanBeUploadedAndNotify(uploadingFiles: IUploadingFile[]) {
    const { result, reason } = this.canBeUploaded(uploadingFiles);

    if (reason) {
      this.notifer.notify(
        result ? 'info' : 'default',
        this.translateService.instant(reason)
      );
    }

    return result;
  }

  canBeUploaded(uplodingFiles: IUploadingFile[]) {
    if (this.hasLoadingFiles(uplodingFiles)) {
      return {
        result: false,
        reason: this.mapTranslate('PLEASE_WAIT'),
      };
    }

    if (this.isAllFilesErrored(uplodingFiles)) {
      return {
        result: false,
        reason: this.mapTranslate('ALL_FILES_ERRORED'),
      };
    }

    if (this.hasErroredFiles(uplodingFiles)) {
      return {
        result: true,
        reason: this.mapTranslate('HAS_ERRORED_FILES'),
      };
    }

    return {
      result: true,
    };
  }

  private mapTranslate(key: string) {
    return `FILES.${key}`;
  }

  private scanUploadProgress(current, next) {
    if (next.type === HttpEventType.Response) {
      return {
        progress: 100,
        isLoading: false,
        file: next.body,
      };
    }

    if (
      next.type === HttpEventType.DownloadProgress ||
      next.type === HttpEventType.UploadProgress
    ) {
      return {
        progress: next.total
          ? Math.round((100 * next.loaded) / next.total)
          : current.progress,
        isLoading: true,
        file: null,
      };
    }

    return current;
  }

  hasLoadingFiles(uploadingFiles: IUploadingFile[]) {
    return uploadingFiles.some((item) => {
      return item.isLoading;
    });
  }

  hasErroredFiles(uploadingFiles: IUploadingFile[]) {
    return uploadingFiles.some((item) => {
      return !!item.error;
    });
  }

  isAllFilesErrored(uploadingFiles: IUploadingFile[]) {
    let result = true;
    uploadingFiles.forEach((item) => {
      if (!item.error) {
        result = false;
      }
    });
    return result;
  }
}
