import { Directive, HostListener } from '@angular/core';

const keys = {
  8: 'BACKSPACE',
  46: 'DELETE ',
  9: 'TAB',
  37: 'LEFT',
  38: 'UP',
  39: 'RIGHT',
  40: 'DOWN',
  35: 'END',
};

@Directive({
  selector: '[jLockKeyboardInput]',
})
export class LockKeyboardInputDirective {
  @HostListener('keydown', ['$event']) onKeyEvent(event: any) {
    const e = event || window.event; // get event object
    if (e.defaultPrevented) {
      return;
    }
    //  e.key || e.code not a number as usual - just string "Tab" or "Enter"
    const key = e.which || e.key || e.code;
    if (e.key.length <= 1 || !(e.metaKey || e.ctrlKey || e.altKey) || !keys[key]) {
      if (e.preventDefault) {
        e.preventDefault();
      } else {
        e.returnValue = false;
      }
    }
  }
  constructor() {
  }
}
