import { Injectable, OnDestroy } from '@angular/core';
import { BehaviorSubject, Subscription, merge, fromEvent } from 'rxjs';
import {
  startWith,
  mapTo,
  tap,
  scan,
  distinctUntilChanged,
  throttleTime,
} from 'rxjs/operators';
import { NotifierService } from 'angular-notifier';

@Injectable({
  providedIn: 'root',
})
export class AppStateService implements OnDestroy {
  private readonly navigatorOnlineSubject$ = new BehaviorSubject(true);
  readonly navigatorOnline$ = this.navigatorOnlineSubject$.asObservable();
  private readonly subs = new Subscription();

  constructor(
    private window: Window,
    private notifier: NotifierService // private translateService: TranslateService
  ) {
    this.subs.add(
      merge(
        fromEvent(this.window, 'offline').pipe(mapTo(false)),
        fromEvent(this.window, 'online').pipe(mapTo(true))
      )
        .pipe(startWith(window.navigator.onLine))
        .subscribe(this.navigatorOnlineSubject$)
    );
    this.subs.add(
      this.navigatorOnlineSubject$
        .pipe(
          throttleTime(3000, undefined, { leading: true }),
          distinctUntilChanged(),
          scan((acc, v) => {
            if (v && acc) {
              this.notifier.hide(acc);
              return '';
            }
            /**
             * @todo no autohide for concrete toast
             */
            // if (!v && !acc) {
            if (!v) {
              const message =
                localStorage.getItem('lang') == 'kk'
                  ? 'Интернет қолжетімді емес. Байланысты тексеріп, әрекетті қайталаңыз'
                  : 'Интернет недоступен. Проверьте соединение и повторите попытку';

              const id = Date.now().toString();
              this.notifier.show({
                type: 'error',
                message,
                id,
              });
              return id;
            }
            return acc;
          }, '')
        )
        .subscribe()
    );
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  get isOnline() {
    return this.navigatorOnlineSubject$.getValue();
  }

  set isOnline(v: boolean) {
    this.navigatorOnlineSubject$.next(v);
  }
}
