import { Component, EventEmitter, OnInit, Output } from '@angular/core';

@Component({
  selector: 'j-bank-dropdown',
  templateUrl: './bank-dropdown.component.html',
  styleUrls: ['./bank-dropdown.component.scss']
})
export class BankDropdownComponent implements OnInit {
  @Output() selected = new EventEmitter<string>()
  banks = ['DOMESTIC_BANK', 'NATIONAL_BANK'];
  isCollapsed = false;
  selectedBank = 'DOMESTIC_BANK';
  today = new Date();
  constructor() { }

  ngOnInit(): void {
    this.selected.emit(this.selectedBank);
  }

  onClickOutside() {
    this.isCollapsed = false;
  }

  onTriggerClick(event = null) {
    event?.preventDefault();
    this.isCollapsed = !this.isCollapsed;
  }

  isSelected(bank) {
    return bank === this.selectedBank;
  }

  onClick(event: Event, bank) {
    event.preventDefault();

    this.selectedBank = bank;
    this.selected.emit(bank);

    this.isCollapsed = false;
  }
}
