import { LeafRoute, RootRoute, makeChildren } from '../utils';

export class PaymentsRoutes implements LeafRoute {
  root = 'payments';
  tenge: TengePaymentsRoutes;
  tengeChildren
  // state: TengePaymentsRoutes['state'];
  foreignCurrency = 'currency';
  conversion = 'conversion';
  constructor(tenge: TengePaymentsRoutes, state: TengePaymentsRoutes['state']) {
    this.tenge = makeChildren('/' + this.root, tenge);
    // this.state = makeChildren(this.tenge.root, state);
  }
}

export class TengePaymentsRoutes implements LeafRoute {
  root = 'tenge';
  order = 'order';
  state: StatePaymentsRoutes;
  salary = 'salary';
  constructor(state: StatePaymentsRoutes) {
    this.state = makeChildren(this.root, state);
  }
}
export class StatePaymentsRoutes implements LeafRoute {
  root = 'state';
  tax = 'tax';
  social = 'social';
  osms = 'osms';
  pension = 'pension';
}

export const statePaymentsRoutes = new StatePaymentsRoutes();
export const tengePaymentsRoutes = new TengePaymentsRoutes(statePaymentsRoutes);
export const paymentsRoutes = new PaymentsRoutes(
  tengePaymentsRoutes,
  tengePaymentsRoutes.state
);
