import { HttpInterceptor, HttpRequest, HttpResponse, HttpHandler, HttpEvent } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { Observable, Subject, throwError } from 'rxjs';
import { catchError, finalize, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class PreventSendRequestBeforeResponseInterceptor implements HttpInterceptor {
  private cache: Map<string, Subject<HttpEvent<any>>> = new Map();

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const requestKey = req.urlWithParams;
    let subject = this.cache.get(requestKey);

    if (req.method !== "GET") {
      return next.handle(req)
    }

    if (subject) {
      return subject;
    }

    subject = new Subject();
    this.cache.set(requestKey, subject);

    const subs = next.handle(req).pipe(
      catchError(err => {
        subject.error(err);
        return throwError(err);
      }),
      tap(stateEvent => {
        if (stateEvent instanceof HttpResponse) {
          subject.next(stateEvent);
        }
      }),
      finalize(() => {
        subject.complete();
        this.cache.delete(requestKey);
        subs.unsubscribe();
      }),
    ).subscribe();

    return subject;
  }
}