import {
  EventEmitter,
  OnDestroy,
  TemplateRef,
  Component,
  Input,
  OnInit,
  Output,
} from '@angular/core';

import { Subscription } from 'rxjs';

import { generateUniqueId } from '@utils/misc';

@Component({
  selector: 'j-list-item',
  templateUrl: './list-item.component.html',
  styleUrls: ['./list-item.component.scss'],
})
export class ListItemComponent implements OnInit, OnDestroy {
  @Input() item: any;
  @Input() translatePrefix = 'LIST.ITEM';
  @Input() isLoading: boolean;

  @Input() highlightedFn: Function;
  @Input() highlightedTemporarilyFn: Function;

  @Input() isHoverable: boolean = false;
  @Input() isSignable: boolean = false;
  @Input() isSelectable: boolean = false;
  @Input() isDeletable: boolean = false;

  @Input() onSelectAll;

  @Output() selected = new EventEmitter<any>();
  @Output() deleted = new EventEmitter<any>();
  @Output() signed = new EventEmitter<any>();
  @Output() clicked = new EventEmitter<any>();

  @Input() selectableTemplate: TemplateRef<any>;
  @Input() defaultTemplate: TemplateRef<any>;

  @Input() itemClassNames: string;

  id = generateUniqueId();
  isSelected: boolean = false;
  isHighlighted: boolean = false;
  isHighlightedTemporarily: boolean = false;

  subscription = new Subscription();

  constructor() {}

  get listItemStyle() {
    let gridTemplateColumns = '';

    if (this.isSelectable) {
      gridTemplateColumns += '24px ';
    }

    gridTemplateColumns += 'minmax(0, 1fr) ';

    if (this.isDeletable && this.isSignable) {
      gridTemplateColumns += '84px';
    } else if (this.isDeletable || this.isSignable) {
      gridTemplateColumns += '40px';
    }

    return { 'grid-template-columns': gridTemplateColumns };
  }

  ngOnInit() {
    this.initOnSelectSubscription();
    this.initHighlighted();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  mapTranslate(key: string) {
    return `${this.translatePrefix}.${key}`;
  }

  onClick() {
    if (this.isLoading) {
      return;
    }

    this.clicked.emit(this.item);
    this.initHighlighted();
  }

  onSelect() {
    if (this.isLoading) {
      return;
    }

    this.selected.emit(this.item);
  }

  onDelete(event: Event) {
    event.stopPropagation();

    if (this.isLoading) {
      return;
    }

    this.deleted.emit(this.item);
  }

  onSign(event: Event) {
    event.stopPropagation();

    if (this.isLoading) {
      return;
    }

    this.signed.emit(this.item);
  }

  public initHighlighted() {
    if (this.highlightedFn) {
      this.isHighlighted = this.highlightedFn(this.item);
    }

    if (!this.isHighlighted && this.highlightedTemporarilyFn) {
      this.isHighlightedTemporarily = this.highlightedTemporarilyFn(this.item);

      if (this.isHighlightedTemporarily) {
        setTimeout(() => {
          this.isHighlightedTemporarily = false;
        }, 5000);
      }
    }
  }

  private initOnSelectSubscription() {
    if (this.onSelectAll?.subscribe) {
      this.subscription.add(
        this.onSelectAll.subscribe((value) => {
          this.isSelected = value;
        })
      );
    }
  }
}
