import { Injectable } from '@angular/core';
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ApiService } from '@api/api.service';
import { CompanyService } from '@company/company.service';

import { Subject, Observable, BehaviorSubject, combineLatest } from 'rxjs';
import { distinctUntilChanged, filter } from 'rxjs/operators';

export class NavItem {
  id: string;
  label: string;
  isPassed: boolean;
  isActive: boolean;
  isShown: boolean;
  isShownDefault?: boolean;
  hasError?: boolean;
}

export interface ILetterBadges {
  unread: number;
  draft: number;
  onSignForOthers: number;
  onSignForMe: number;
}

export interface IContractBadges {
  onSignForOthers: number;
  onSignForMe: number;
}

@Injectable({
  providedIn: 'root',
})
export class NavService {
  registerList: NavItem[] = [
    {
      id: 'iin',
      label: 'SHARED.IIN',
      isPassed: false,
      isActive: false,
      isShown: true,
    },
    {
      id: 'phone',
      label: 'SHARED.PHONE',
      isPassed: false,
      isActive: false,
      isShown: true,
    },
    {
      id: 'sms',
      label: 'SHARED.SMS.TITLE',
      isPassed: false,
      isActive: false,
      isShown: true,
    },
    {
      id: 'email',
      label: 'AUTH.EMAIL.CONFIRM',
      isPassed: false,
      isActive: false,
      isShown: true,
    },
    {
      id: 'password',
      label: 'AUTH.PASSWORD.TITLE',
      isPassed: false,
      isActive: false,
      isShown: true,
    },
    {
      id: 'result',
      label: 'SHARED.DONE',
      isPassed: false,
      isActive: false,
      isShown: true,
    },
  ];

  private showNoAccount$ = new Subject<boolean>();
  private showNoEmail$ = new Subject<boolean>();
  private showNoIin$ = new Subject<boolean>();
  private showPhoneNotMatch$ = new Subject<boolean>();
  private showUserBlocked$ = new Subject<boolean>();
  private showAlreadyOpened$ = new Subject<boolean>();

  private registerList$ = new Subject<NavItem[]>();

  private letterBadges$ = new BehaviorSubject<ILetterBadges>(null);
  private contractBadges$ = new BehaviorSubject<IContractBadges>(null);

  constructor(
    private router: Router,
    private apiService: ApiService,
    private companyService: CompanyService
  ) {
    this.companyService.refresh$.subscribe((companyRefresh) => {
      // const [routerEvent, companyRefresh] = values;
      const [user, company, isAuthorized] = companyRefresh;

      // if (routerEvent instanceof NavigationEnd) {
      if (isAuthorized) {
        this.retrieveLetterBadges();
        this.retrieveContractBadges();
      } else {
        this.updateNavigationLists();
      }
      //   if (
      //     this.shouldUpdateBadges(routerEvent, 'letters') ||
      //     this.letterBadges$.value === null
      //   ) {
      //     this.retrieveLetterBadges();
      //   }

      //   if (
      //     this.shouldUpdateBadges(routerEvent, 'contracts') ||
      //     this.contractBadges$.value === null
      //   ) {
      //     this.retrieveContractBadges();
      //   }
      // }
      // }
    });
  }

  getLetterBadges() {
    return this.letterBadges$.asObservable().pipe(filter((badges) => !!badges));
  }

  getContractBadges() {
    return this.contractBadges$
      .asObservable()
      .pipe(filter((badges) => !!badges));
  }

  async retrieveLetterBadges() {
    try {
      const url = `companies/${this.companyService.currentId}/letters/badges`;
      const badges = await this.apiService.get<ILetterBadges>(url);
      this.letterBadges$.next(badges);
    } catch (error) {
      this.letterBadges$.next({
        unread: 0,
        draft: 0,
        onSignForOthers: 0,
        onSignForMe: 0,
      });
    }
  }

  async retrieveContractBadges() {
    try {
      const url = `companies/${this.companyService.currentId}/fx-contracts/badges`;
      const badges = await this.apiService.get<IContractBadges>(url);
      this.contractBadges$.next(badges);
    } catch (error) {
      this.contractBadges$.next({
        onSignForOthers: 0,
        onSignForMe: 0,
      });
    }
  }

  getRegisterList(): Observable<NavItem[]> {
    return this.registerList$.asObservable();
  }

  triggerRegisterError(itemId: string, hasError = true) {
    this.registerList = this.triggerListError(
      this.registerList,
      itemId,
      hasError
    );

    if (itemId == 'no-account') {
      this.showNoAccount$.next(hasError);
    }

    if (itemId == 'no-email') {
      this.showNoEmail$.next(hasError);
    }

    if (itemId == 'user-blocked') {
      this.showUserBlocked$.next(hasError);
    }

    if (itemId == 'phone-not-match') {
      this.showPhoneNotMatch$.next(hasError);
    }
  }

  triggerActiveItem(itemId: NavItem['id']) {
    this.registerList = this.triggerListActive(
      this.registerList,
      itemId
    );
  }

  get showNoAccount() {
    return this.showNoAccount$.asObservable();
  }

  get showNoEmail() {
    return this.showNoEmail$.asObservable();
  }

  get showNoIin() {
    return this.showNoIin$.asObservable();
  }

  get showUserBlocked() {
    return this.showUserBlocked$.asObservable();
  }

  get showPhoneNotMatch() {
    return this.showPhoneNotMatch$.asObservable();
  }

  get showAlreadyOpened() {
    return this.showAlreadyOpened$.asObservable();
  }

  public updateBadges() {
    this.retrieveLetterBadges();
    this.retrieveContractBadges();
  }

  // private shouldUpdateBadges(routerEvent: NavigationEnd, targetRoute: string) {
  //   const routes = routerEvent.url.split('/').filter((item) => !!item);
  //   console.log(routes);

  //   return routes[0] == targetRoute && routes.length > 1;
  // }

  private triggerListError(list: NavItem[], itemId: string, hasError: boolean) {
    return list.map((navItem) => {
      if (navItem.id === itemId) {
        navItem.hasError = hasError;
      }
      return navItem;
    });
  }

  private triggerListActive(list: NavItem[], itemId: string) {
    let foundCurrentItem = false;
    return list.map((navItem) => {
      navItem.isActive = !foundCurrentItem;
      if (navItem.id === itemId) {
        foundCurrentItem = true;
        navItem.isActive = true;
      }
      return navItem;
    });
  }

  private updateNavigationLists() {
    this.registerList = this.updateList(this.registerList);
    this.registerList$.next(this.registerList);
  }

  private updateList(navList: NavItem[]) {
    let isShownNextValue = true;

    const result = [];

    navList.map((navItem) => {
      const itemIndex = navList.indexOf(navItem);
      const currentIndex = navList.findIndex((item) => {
        return this.routeLastChuck.includes(item.id);
      });

      navItem.isActive = itemIndex === currentIndex;
      navItem.isPassed = itemIndex < currentIndex;
      navItem.isShown = navItem.isActive || isShownNextValue;

      if (navItem.id == 'result' && navItem.isActive == true) {
        navItem.isPassed = true;
      }

      if (navItem.hasError) {
        isShownNextValue = false;
      }
      result.push(navItem);
    });

    return result;
  }

  private get routeLastChuck() {
    const currentUrlFull = this.router.url.split('/');
    const currentUrl = currentUrlFull[currentUrlFull.length - 1];
    return currentUrl;
  }
}
