import { Directive, Input } from '@angular/core';
import { FormControl, FormControlDirective, NgControl } from '@angular/forms';
import { Observable } from 'rxjs';

@Directive({
  selector: '[jSelectionContainer]',
})
export class SelectionContainerDirective {
  @Input('jSelectionContainer') control: FormControl;
  @Input() rows: { id: string }[];
  constructor() {}

  get value() {
    return this.control.value as string[];
  }

  patchValue(id: string, value: boolean) {
    let selected = this.control.value || [];
    if (value) {
      selected.push(id);
    } else {
      selected = selected.filter((check) => check !== id);
    }
    this.control.patchValue(selected);
  }

  selectAll() {
    this.control.patchValue(this.rows.map(({ id }) => id));
  }

  clearAll() {
    this.control.patchValue([]);
  }

  get isAllSelected() {
    return this.control.value.length === this.rows.length && this.rows.length > 0
  }

  get valueChanges() {
    return this.control.valueChanges;
  }
}
