import { Injectable } from '@angular/core';
import { HttpHeaders, HttpParams } from '@angular/common/http';

import { NgbDate, NgbModal } from '@ng-bootstrap/ng-bootstrap';

import dayjs from 'dayjs';
import utc from 'dayjs/plugin/utc';
import timezone from 'dayjs/plugin/timezone';
import isBetween from 'dayjs/plugin/isBetween';
import customParseFormat from 'dayjs/plugin/customParseFormat';

import { ApiService, HEADERS, STATUS } from '@api/index';

import { resolveURL } from '@utils/index';

import { CompanyService } from '@company/company.service';

import { AuthorityLimitsService } from '@authorities/services';

import { AUTHORITY_TYPE } from '@shared/models/user';
import { CURRENCY, IMoney } from '@shared/currency/currency.model';
import {
  PAYMENT_CATEGORY,
  PAYMENT_TYPE,
  ISmsToken,
  TOKEN_TYPE,
  IFxContract,
} from '@shared/models';

import { MaskedService } from '@shared/masked/masked.service';

import { BasePaymentBody, Payment } from '../models';
import { LimitNotPassedModalComponent } from '../components/limit-not-passed-modal/limit-not-passed-modal.component';
import {
  WorkingTimeModalComponent,
  WORKING_TIME_MODAL,
} from '../components/working-time-modal/working-time-modal.component';
import { BehaviorSubject, Subject } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  filter,
  map,
  switchMap,
} from 'rxjs/operators';

const endpoints = {
  payments: 'companies/::companyId/payments',
  payment: 'companies/::companyId/payments/::paymentId',
  paymentAction: 'companies/::companyId/payments/::paymentId::action',
  commission: 'companies/::companyId/payments/commission',
  signatures: 'companies/::companyId/payments/::paymentId/signatures',
  number: 'companies/::companyId/payments/number',
  prevNotifications: 'companies/::companyId/payments/emails',
};

const TIMEZONE = 'Asia/Almaty';

@Injectable({
  providedIn: 'root',
})
export class PaymentApiService {
  workingHours: {
    isHoliday: boolean;
    start: dayjs.Dayjs;
    end: dayjs.Dayjs;
  };

  fetchedWorkingHours;
  fxContracts$ = new BehaviorSubject<IFxContract[]>(null);

  constructor(
    private company: CompanyService,
    private api: ApiService,
    private modalService: NgbModal,
    private authorityLimitsService: AuthorityLimitsService,
    private maskedService: MaskedService
  ) {
    dayjs.extend(utc);
    dayjs.extend(timezone);
    dayjs.extend(isBetween);
    dayjs.extend(customParseFormat);

    this.retrieveWorkingHours();

    // this.company.refresh$
    //   .pipe(
    //     filter(([, , isAuthorized]) => isAuthorized),
    //     switchMap(([, authority]) => {
    //       return this.api.getObservable<IFxContract[]>(
    //         `companies/${authority.company.id}/fx-contracts`
    //       );
    //     })
    //   )
    //   .subscribe((contracts) => {
    //     this.fxContracts$.next(contracts);
    //   });
  }

  getFxContracts() {
    return this.api.getObservable<IFxContract[]>(
      `companies/${this.company.currentId}/fx-contracts`
    );
  }

  private async retrieveWorkingHours() {
    try {
      const workingHours = await this.getWorkingHours();

      const FORMAT = 'YYYY-MM-DD;HH:mm:ss';

      if (!workingHours) {
        return;
      }
      
      this.workingHours = {
        isHoliday: !workingHours.workingDay,
        start: dayjs(`${workingHours.date};${workingHours.startTime}`, FORMAT),
        end: dayjs(`${workingHours.date};${workingHours.endTime}`, FORMAT),
      };
    } catch (e) {
      console.log(`cannot fetch working hours: ${e}`);
    }
  }

  public isToday(date: NgbDate) {
    if (!date) {
      return false;
    }

    return dayjs().isSame(
      dayjs()
        .year(date.year)
        .month(date.month - 1)
        .date(date.day)
    );
  }

  public async checkAmountLimit(amount: string, paymentType: PAYMENT_TYPE) {
    try {
      const limit = await this.authorityLimitsService.getPaymentLimit(
        paymentType
      );

      if (limit) {
        const numberedAmount = this.maskedService.getNumberedAmount(amount);

        if (limit.limit?.amount < numberedAmount) {
          await this.showLimitNotPassedModal(limit.limit);
          return {
            isPassed: false,
          };
        }
      }

      return {
        isPassed: true,
      };
    } catch (error) {
      return {
        error: error,
        isPassed: true,
      };
    }
  }

  private async showLimitNotPassedModal(amount: IMoney) {
    const modalRef = this.modalService.open(LimitNotPassedModalComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_sm',
      centered: true,
    });

    modalRef.componentInstance.amount = amount;

    try {
      await modalRef.result;
    } catch (error) {
    } finally {
      return;
    }
  }

  public async checkWorkingTime(selectedDate: NgbDate = null) {
    try {
      const currentTime = dayjs().tz(TIMEZONE);

      if (!this.workingHours) {
        await this.retrieveWorkingHours();
      }

      if (!this.workingHours?.start || !this.workingHours?.end) {
        return true;
      }

      const isWorkingPeriod = currentTime.isBetween(
        this.workingHours.start,
        this.workingHours.end,
        'milliseconds',
        '[]'
      );

      const isTodayCutoff = !isWorkingPeriod && this.isToday(selectedDate);
      const isHoliday =
        this.workingHours.isHoliday &&
        (this.isToday(selectedDate) || selectedDate == null);
      const isAutoCutoff = !isWorkingPeriod && selectedDate == null;

      const shouldShowModal = isTodayCutoff || isHoliday || isAutoCutoff;

      if (!shouldShowModal) {
        return true;
      }

      let modalType;

      if (isTodayCutoff) {
        modalType = WORKING_TIME_MODAL.TODAY;
      } else if (isHoliday) {
        modalType = WORKING_TIME_MODAL.HOLIDAY;
      } else if (isAutoCutoff) {
        modalType = WORKING_TIME_MODAL.AUTO;
      } else {
        return true;
      }

      return this.showWorkingTimeModal(modalType);
    } catch (error) {
      return true;
    }
  }

  async showWorkingTimeModal(modalType: WORKING_TIME_MODAL) {
    const modalRef = this.modalService.open(WorkingTimeModalComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_sm',
      centered: true,
    });

    modalRef.componentInstance.type = modalType;
    modalRef.componentInstance.workingHoursEnd =
      this.workingHours.end.format('HH:mm');

    try {
      await modalRef.result;
      return true;
    } catch (error) {
      return false;
    }
  }

  async getWorkingHours() {
    if (!this.fetchedWorkingHours) {
      const url = 'calendar-day';
      this.fetchedWorkingHours = await this.api.get<any>(url);
    }

    return this.fetchedWorkingHours;
  }

  createPayment(
    body: BasePaymentBody,
    { companyId }: { companyId?: string } = {}
  ): Promise<Payment> {
    const url = resolveURL(endpoints.payments, {
      companyId: companyId || this.company.currentCompany.id,
    });

    return this.api.post<Payment>(url, body);
  }
  /**
   * Create payment from existing draft
   * @param body
   * @param paymentId
   * @param companyId
   */
  createFromDraft(
    body: BasePaymentBody,
    { paymentId, companyId }: { paymentId: string; companyId?: string }
  ): Promise<{ id: string; status: { code: STATUS } }> {
    const url = resolveURL(endpoints.paymentAction, {
      companyId: companyId || this.company.currentCompany.id,
      paymentId,
      action: ':to-created',
    });
    return this.api.patch(url, body);
  }
  /**
   * Updates information of draft and created payments. If payment is in created status, then deletes all existing signatures
   * @param body
   * @param param1
   *
   * @todo remove body.paymentRecipient = new RecipientAccount() when api will be consistent. Hack to baypass bug
   */
  updatePayment(
    body: any,
    { paymentId, companyId }: { paymentId: string; companyId?: string }
  ): Promise<Payment> {
    // to remove
    // if (body.status?.code === PAYMENT_STATUS.DRAFT && !body.paymentRecipient) {
    //   body.paymentRecipient = new RecipientAccount();
    // }
    return this.api.put(
      resolveURL(endpoints.payment, {
        companyId: companyId || this.company.currentCompany.id,
        paymentId,
      }),
      body
    );
  }

  signPayment(
    smsToken: ISmsToken,
    paymentId: string,
    companyId: string = this.company.currentCompany.id
  ) {
    return this.api.post(
      resolveURL(endpoints.signatures, {
        paymentId,
        companyId,
      }),
      undefined,
      new HttpHeaders({
        [smsToken.type == TOKEN_TYPE.OTP ? HEADERS.OTP : HEADERS.PHONE]:
          smsToken.signature,
      })
    );
  }

  getCommission(body: {
    companyId: string;
    category: PAYMENT_CATEGORY;
    type: PAYMENT_TYPE;
    details: {
      valueDate: string | null;
      paymentAmount: IMoney;
      urgent: boolean;
      payerIban: string;
    };
  }): Promise<number> {
    return this.api
      .post<{ commissionAmount: number }>(
        resolveURL(endpoints.commission, {
          companyId: body.companyId,
        }),
        body
      )
      .then(({ commissionAmount }) => commissionAmount);
  }

  async getCommissionWithCurrency(body: {
    companyId: string;
    category: PAYMENT_CATEGORY;
    type: PAYMENT_TYPE;
    details: {
      valueDate: string | null;
      paymentAmount: IMoney;
      urgent: boolean;
      payerIban: string;
    };
  }): Promise<IMoney> {
    const commissionWithCurrency = await this.api.post<{
      commissionAmount: number;
      currency: CURRENCY;
    }>(
      resolveURL(endpoints.commission, {
        companyId: body.companyId,
      }),
      body
    );
    return {
      amount: commissionWithCurrency.commissionAmount,
      currency: commissionWithCurrency.currency,
    };
  }

  getPayments() {
    return this.api.get(
      resolveURL(endpoints.payments, {
        companyId: this.company.currentCompany.id,
      })
    );
  }

  getPaymentById(paymentId: string): Promise<Payment> {
    return this.api.get(
      resolveURL(endpoints.payment, {
        companyId: this.company.currentCompany.id,
        paymentId,
      })
    );
  }

  getSignaturesForPaymentId(paymentId: string): Promise<
    {
      authority: AUTHORITY_TYPE;
      created: string;
      id: string;
      paymentId: string;
      userId: string;
    }[]
  > {
    return this.api.get(
      resolveURL(endpoints.payment, {
        companyId: this.company.currentCompany.id,
        paymentId,
      })
    );
  }

  getPaymentNumber(paymentCategory: PAYMENT_CATEGORY) {
    const url = resolveURL(endpoints.number, {
      companyId: this.company.currentCompany.id,
    });

    let params = new HttpParams();
    params = params.append('paymentCategory', paymentCategory);

    return this.api.get<{ number: number }>(url, null, params);
  }
}
