import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IStatus } from '@shared/models';

@Component({
  selector: 'j-statuses',
  templateUrl: './statuses.component.html',
  styleUrls: ['./statuses.component.scss'],
})
export class StatusesComponent implements OnInit {
  @Input() saveSelected = false;
  selectedStatus: IStatus;

  @Input() statuses: IStatus[];
  @Output() selected = new EventEmitter<IStatus>();

  constructor() {}

  ngOnInit() {
    this.selectedStatus =
      this.statuses.find((status) => {
        return status.isSelected === true;
      }) || this.statuses[0];
  }

  onSelect(status: IStatus) {
    this.selectedStatus = status;
    this.selected.emit(status.value);
  }

  // get selectedStatus() {
  //   return this.statuses.find((status) => {
  //     return status.isSelected === true;
  //   });
  // }
}
