import { Injectable } from '@angular/core';
import { ApiService, HEADERS, SMS_HEADERS } from '@api/index';
import { HttpHeaders } from '@angular/common/http';

import { ISmsToken, TOKEN_TYPE } from '../models/token';
import { UserService } from './user.service';
import { resolveURL } from '@utils/index';

const endpoints = {
  requestSMS: 'auth/phone/token',
  verifySMS: 'auth/phone/token/::id',
  requestOTP: 'auth/otp/token/',
  verifyOTP: 'auth/otp/token/::id',
};

@Injectable({
  providedIn: 'root',
})
export class SmsService {
  constructor(private userService: UserService, private api: ApiService) {}

  async requestSms(
    phone: string = this.userService.userPhone,
    params?: {
      actionIds?: string[];
    },
    headerParams?: {
      smsHeader?: string;
    }
  ): Promise<ISmsToken> {
    const url = endpoints.requestSMS;
    const headers = headerParams
      ? new HttpHeaders().set('code-destiny-type', headerParams.smsHeader)
      : null;
    const body = {
      phone,
    };
    params && Object.assign(body, params);

    const unsignedSmsToken = await this.api.post<ISmsToken>(url, body, headers);
    return unsignedSmsToken;
  }

  async submitSms(smsToken: ISmsToken): Promise<ISmsToken> {
    const url = resolveURL(endpoints.verifySMS, { id: smsToken.id });
    const headers = new HttpHeaders().set(HEADERS.SMS, smsToken.signature);

    const signedSmsToken = await this.api.put<ISmsToken>(
      url,
      smsToken,
      headers
    );
    return signedSmsToken;
  }

  async requestOtp(
    phone: string = this.userService.userPhone,
    params?: {
      actionIds?: string[];
    },
    otp = null
  ): Promise<ISmsToken> {
    const url = endpoints.requestOTP;
    let body: any = {
      phone,
    };

    if (otp) {
      body.serial = otp.serial;
    }

    if (params) {
      body = { ...body, ...params };
    }

    const unsignedOtpToken = await this.api.post<ISmsToken>(url, body);
    return unsignedOtpToken;
  }

  async submitOtp(
    smsToken: ISmsToken,
    params?: {
      actionIds?: string[];
    },
    otp = null
  ): Promise<ISmsToken> {
    const url = resolveURL(endpoints.verifyOTP, { id: smsToken.id });
    let body: any = {
      type: TOKEN_TYPE.OTP,
      phone: smsToken.phone,
      code: smsToken.code,
      id: smsToken.id,
    };

    if (otp) {
      body.serial = otp.serial;
    }

    if (params) {
      body = { ...body, ...params };
    }

    if (otp) {
      body.serial = otp.serial;
    }

    const headers = new HttpHeaders().set(HEADERS.OTP, smsToken.signature);
    const signedOtpToken = await this.api.put<ISmsToken>(url, body, headers);

    return signedOtpToken;
  }
}
