import {
  Component,
  Output,
  EventEmitter,
  Input,
  SimpleChanges,
  OnChanges,
} from '@angular/core';
import { Employee } from '@shared/employee/employee.model';
import { PAYMENT_TYPE } from '@shared/models';

@Component({
  selector: 'j-saved-employee',
  templateUrl: './saved-employee.component.html',
  styleUrls: ['./saved-employee.component.scss'],
})
export class SavedEmployeeComponent implements OnChanges {
  @Output() selected = new EventEmitter();
  @Output() edited = new EventEmitter();

  @Output() deleted = new EventEmitter();
  @Output() deleteConfirmed = new EventEmitter();
  @Output() deleteCancelled = new EventEmitter();

  @Input() employee: Employee;
  @Input() ibanRequired?: boolean;
  @Input() birthDateRequired?: boolean;
  @Input() index: number;
  @Input() isDeletingBulk: number;

  @Input() paymentType: PAYMENT_TYPE;

  timer?;
  timerValue?: number;

  constructor() {}

  ngOnChanges(changes: SimpleChanges) {
    if (changes.isDeletingBulk && this.employee.isDeleting) {
      this.startTimer();
    }
  }

  onSelect() {
    this.employee.isSelected = !this.employee.isSelected;
    this.selected.emit(this.employee);
  }

  onEdit(event: Event) {
    event.preventDefault();
    this.edited.emit({
      employee: this.employee,
      index: this.index,
    });
  }

  onDelete(event: Event) {
    event.preventDefault();
    this.employee.isDeleting = true;
    this.deleted.emit({
      employee: this.employee,
      index: this.index,
    });
    this.startTimer();
  }

  onDeleteCancelled(event: Event) {
    event.preventDefault();
    this.stopTimer(true);
  }

  get id() {
    return `employee${this.index}`;
  }

  get fio(): string {
    const fio = [];

    if (this.employee.lastName) {
      fio.push(this.employee.lastName);
    }
    if (this.employee.firstName) {
      fio.push(this.employee.firstName);
    }
    if (this.employee.middleName) {
      fio.push(this.employee.middleName);
    }

    return fio.join(' ');
  }

  private startTimer() {
    this.timerValue = 5;
    this.timer = setInterval(() => {
      this.timerValue--;

      if (this.timerValue === 0) {
        this.stopTimer();
      }
    }, 1000);
  }

  private stopTimer(cancelled = false) {
    clearInterval(this.timer);
    this.employee.isDeleting = false;
    if (cancelled) {
      this.deleteCancelled.emit({
        employee: this.employee,
        index: this.index,
      });
    } else {
      this.deleteConfirmed.emit({
        employee: this.employee,
        index: this.index,
      });
    }
  }
}
