import {
  Component,
  EventEmitter, Input,
  OnDestroy,
  OnInit,
  Output
} from '@angular/core';
import { YaApiLoaderService, YaGeocoderService, YaReadyEvent } from 'angular8-yandex-maps';
import { FormControl, FormGroup } from '@angular/forms';
import { HttpClient, HttpParams } from '@angular/common/http';
import { DEFAULT_COORDS, MAP_CONFIG, SEARCH_API_KEY, SEARCH_BASE_URL } from './config';
import { IAddressByQuery } from './yandex-maps.models';
import { debounceTime, skip } from 'rxjs/operators';
import { Subscription } from 'rxjs';
import { Router } from '@angular/router';




@Component({
  selector: 'j-yandex-maps',
  templateUrl: './yandex-maps.component.html',
  styleUrls: ['./yandex-maps.component.scss'],
})
export class YandexMapsComponent implements OnDestroy, OnInit {
  searchForm: FormGroup = new FormGroup({
    query: new FormControl('')
  });

  @Output() applyClickFn = new EventEmitter();
  @Output() backClickFn = new EventEmitter();
  @Input() addressFromPosAction = 'Алматы';

  center = null;
  zoom = 17;
  coordinates;
  address;
  results = null;
  showResultList = false;
  isPickedAddressFromList = false;
  selectedItem = null;
  searchQuerySub: Subscription;

  placemarkOptions: ymaps.IPlacemarkOptions = {
    iconLayout: 'default#image',
    iconImageHref: './assets/img/yandex-maps/picker.svg',
    iconImageSize: [80, 80],
    draggable: true,
  };

  formCtrlSub: Subscription;


  constructor(
    private yaGeocoderService: YaGeocoderService,
    private yaApiLoaderService: YaApiLoaderService,
    private router: Router,
    private http: HttpClient) {
    this.yaApiLoaderService.load();
  }

  ngOnInit() {
    this.formCtrlSub = this.searchForm.valueChanges.pipe(debounceTime(1000), skip(1)).subscribe(newValue => {
      if (!this.isPickedAddressFromList) {
        this.loadAddressFromSearchForm(`${this.addressFromPosAction} ${newValue.query}`);
      }
      this.isPickedAddressFromList = false;
    });
  }

  // zoomChange(n) {
  //   this.zoom += n;
  // }

  onMapReady(event: YaReadyEvent<ymaps.Map>): void {
    if (this.addressFromPosAction) {
      this.getAddressFromPosActionCoords(this.addressFromPosAction);
    }
  }


  loadAddressFromSearchForm(query: string) {
    const params = new HttpParams()
      .set('text', query)
      .set('lang', MAP_CONFIG.lang)
      .set('apikey', SEARCH_API_KEY);

    this.http.get<IAddressByQuery>(SEARCH_BASE_URL, {
      params
    }).subscribe((response) => {
      this.results = response.features.slice(0, 5);
      this.showResultList = true;
    });
  }

  onDrag(position) {
    this.showResultList = false;
    const coords = position.target.geometry.getCoordinates();
    this.yaGeocoderService.geocode(coords)
      .subscribe((res) => {
        // @ts-ignore
        this.address = res.geoObjects.get(0).properties.get('text');
        this.searchForm.patchValue({ query: this.address });
      });
  }


  onClick(val) {
    this.coordinates = val.event.get('coords');
    this.center = this.coordinates;
    this.yaGeocoderService.geocode(this.coordinates)
      .subscribe(res => {
        // @ts-ignore
        this.address = res.geoObjects.get(0).properties.get('text');
        this.searchForm.patchValue({ query: this.address });
      });
  }

  onChooseFromResultList(result) {
    const address = result.properties.name;
    this.selectedItem = result;
    this.searchForm.patchValue({ query: address });
    this.showResultList = false;
    this.isPickedAddressFromList = true;
    this.yaGeocoderService.geocode(address)
      .subscribe(res => {
        // @ts-ignore
        const firstGeoObject = res.geoObjects.get(0);
        const coords = firstGeoObject.geometry.getCoordinates();
        this.coordinates = coords;
        this.center = coords;
      });
  }

  onApplyClick() {
    this.applyClickFn.emit(this.selectedItem);
  }

  onBackClick() {
    this.backClickFn.emit();
  }

  ngOnDestroy() {
    this.formCtrlSub.unsubscribe();
  }

  getAddressFromPosActionCoords(addressFromPosAction: string) {
    this.yaGeocoderService.geocode(addressFromPosAction)
      .subscribe(res => {
        // @ts-ignore
        const firstGeoObject = res.geoObjects.get(0);
        const coords = firstGeoObject?.geometry?.getCoordinates();
        this.coordinates = coords;
        this.center = coords;
        this.searchForm.patchValue({query: addressFromPosAction});
      });
  }

}
