import { DatePipe } from '@angular/common';
import { HttpClient } from '@angular/common/http';
import { LOCALE_ID, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule } from '@angular/router';

import { NgbDatepickerI18n, NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { NotifierModule } from 'angular-notifier';
import { NgCircleProgressModule } from 'ng-circle-progress';
import { ClipboardModule } from 'ngx-clipboard';
import { LottieModule } from 'ngx-lottie';
import { NgxMaskModule } from 'ngx-mask';
import player from 'lottie-web';

import { CIRCLE_PROGRESS_CONFIG } from '../config/circleProgress.config';
import { ngxMaskConfig } from '../config/ngxMaskConfig';
import { HttpLoaderFactory } from '../config/translate';

import { httpInterceptorProviders } from '../http-interceptors';

import { CustomDatepickerI18n } from './components';

// Note we need a separate function as it's required
// by the AOT compiler.
export function playerFactory() {
  return player;
}

@NgModule({
  declarations: [],
  imports: [
    // Angular
    BrowserAnimationsModule,
    BrowserModule,
    RouterModule,

    // 3rd-party
    NotifierModule.withConfig({
      behaviour: {
        autoHide: 2500,
      },
    }),
    ClipboardModule,
    TranslateModule.forRoot({
      defaultLanguage: 'ru',
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient],
      },
    }),

    NgxMaskModule.forRoot(ngxMaskConfig),
    LottieModule.forRoot({ player: playerFactory }),
    NgCircleProgressModule.forRoot(CIRCLE_PROGRESS_CONFIG),
    NgbModule,
  ],
  exports: [NotifierModule],
  providers: [
    httpInterceptorProviders,
    DatePipe,
    { provide: LOCALE_ID, useValue: 'ru-RU' },
    { provide: LOCALE_ID, useValue: 'kk-KZ' },
    { provide: NgbDatepickerI18n, useClass: CustomDatepickerI18n },
    { provide: Window, useValue: window },
  ],
})
export class CoreModule {}
