import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  ViewChild,
} from '@angular/core';

import { BehaviorSubject } from 'rxjs';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Dictionary } from 'lodash';

import {
  IExchangeRate,
  IHistoryPayment,
  ISmsToken,
  PAYMENT_TYPE,
} from '@shared/models';

import { PaymentsSharedService } from '../../services/payments-shared.service';
import { UserService } from '../../services';
import { SmsComponent } from '../sms/sms.component';
import { OtpComponent } from '../otp/otp.component';
import { CompanyService } from '@company/company.service';

export enum CONFIRM_SIGNATURE_TYPE {
  OTP = 'OTP',
  SMS = 'SMS',
}

const SIGN_ALLOWED = 'signAllowed';
@Component({
  selector: 'j-confirm-sms-modal',
  templateUrl: './confirm-sms-modal.component.html',
  styleUrls: ['./confirm-sms-modal.component.scss'],
})
export class ConfirmSmsModalComponent implements OnInit {
  @Input() smsToken: ISmsToken;
  @Input() isPaymentsSigning = true;
  @Input() additionalParams?: Dictionary<any>;
  @Input() headerParams?: Dictionary<any>;

  otp;
  hasOtp: boolean = false;
  confirmTabs = CONFIRM_SIGNATURE_TYPE;
  currentTab: CONFIRM_SIGNATURE_TYPE = CONFIRM_SIGNATURE_TYPE.SMS;

  public submitted = new EventEmitter<ISmsToken>();
  public closed = new EventEmitter();
  public sentForRevision = new EventEmitter();

  @ViewChild('smsInput') smsInput: SmsComponent;
  @ViewChild('otpInput') otpInput: OtpComponent;

  isConfirmRequired = false;
  isSignAllowed = false;
  isSignBySmsRequested = false;

  isLoading$ = new BehaviorSubject(true);

  confirmData = null;
  signingPayment: IHistoryPayment;
  signingExchangeRate: IExchangeRate;

  constructor(
    public modal: NgbActiveModal,
    public userService: UserService,
    private paymentsSharedService: PaymentsSharedService,
    private companyService: CompanyService
  ) {}

  ngOnInit() {
    this.isLoading$.next(true);

    if (this.isPaymentsSigning) {
      this.checkForConversionRate();
    } else {
      this.allowSign();
    }
  }

  async checkForConversionRate() {
    try {
      await this.checkForBulkSign();
      this.signingPayment = await this.checkForConversion();
      this.signingExchangeRate = await this.checkForRateChange(
        this.signingPayment
      );

      this.showConfirm();
    } catch (error) {
      if (error == SIGN_ALLOWED) {
        this.allowSign();
      }
    }
  }

  async allowSign() {
    this.isSignAllowed = true;
    
    this.otp = await this.userService.retrieveOtpInfo(this.companyService.currentId);
    
    this.hasOtp = !!this.otp;

    if (
      this.hasOtp &&
      localStorage.getItem('preferredSignatureType') ===
        CONFIRM_SIGNATURE_TYPE.OTP
    ) {
      this.showOtp();
    } else {
      this.showSms();
    }
  }

  async checkForRateChange(payment: IHistoryPayment) {
    try {
      const rates = await this.paymentsSharedService.retrieveExhangeRates(
        payment
      );

      if (!rates || rates.original.rate === rates.current.rate) {
        throw SIGN_ALLOWED;
      }

      return rates.current;
    } catch (error) {
      throw SIGN_ALLOWED;
    }
  }

  async checkForConversion() {
    try {
      const payment = await this.paymentsSharedService.getPaymentById(
        this.additionalParams.actionIds[0]
      );
      if (payment.type != PAYMENT_TYPE.CONVERSION) {
        throw SIGN_ALLOWED;
      }
      return payment;
    } catch (error) {
      throw SIGN_ALLOWED;
    }
  }

  checkForBulkSign() {
    if (this.additionalParams.actionIds.length != 1) {
      throw SIGN_ALLOWED;
    }
    return true;
  }

  dismissModal() {
    this.closed.emit();
    this.modal.dismiss();
  }

  showConfirm() {
    const rateAmounts = this.paymentsSharedService.getRateAmounts(
      this.signingExchangeRate
    );
    if (!rateAmounts) {
      throw SIGN_ALLOWED;
    }

    const { sum, calculatedSum, currencyTo, currencyFrom } =
      this.signingExchangeRate;

    const amountFrom = sum;
    const amountTo = {
      amount: calculatedSum,
      currency: sum.currency === currencyFrom ? currencyTo : currencyFrom,
    };

    this.confirmData = {
      rateFrom: rateAmounts.from,
      rateTo: rateAmounts.to,
      amountFrom,
      amountTo,
    };

    this.isConfirmRequired = true;
    this.isLoading$.next(false);
  }

  showOtp(event: Event = null) {
    this.isLoading$.next(false);

    if (event) {
      event.preventDefault();
    }

    this.currentTab = this.confirmTabs.OTP;
    this.otpInput.focus();
  }

  showSms(event: Event = null) {
    if (!this.isSignBySmsRequested) {
      this.isLoading$.next(true);
    }

    this.isSignBySmsRequested = true;

    if (event) {
      event.preventDefault();
    }

    this.currentTab = this.confirmTabs.SMS;

    try {
      this.smsInput.focus();
    } catch (error) {}
  }

  async onConfirmAgreed() {
    this.isConfirmRequired = false;
    this.isLoading$.next(true);

    await this.paymentsSharedService.updatePaymentExchangeRate(
      this.signingPayment,
      this.signingExchangeRate
    );

    this.allowSign();
  }

  async onConfirmSentForRevision() {
    this.isConfirmRequired = false;
    this.isLoading$.next(true);

    const { sum, calculatedSum, currencyFrom, currencyTo } =
      this.signingExchangeRate;

    const updatedPayment = {
      ...this.signingPayment,
      details: {
        ...this.signingPayment.details,
        exchangeRate: this.signingExchangeRate,
        paymentAmount:
          sum.currency === currencyTo
            ? {
                amount: calculatedSum,
                currency: currencyFrom,
              }
            : sum,
      },
    };
    await this.paymentsSharedService.updatePayment(updatedPayment);
    this.sentForRevision.emit();
  }

  onSmsFail() {}

  onOtpSubmit(smsToken: ISmsToken) {
    this.onSubmit(smsToken);
  }

  onSmsSubmit(smsToken: ISmsToken) {
    this.onSubmit(smsToken);
  }

  onSubmit(smsToken: ISmsToken) {
    this.submitted.emit(smsToken);
  }

  onSmsLoaded() {
    this.isLoading$.next(false);
    this.smsInput.focus();
  }

  get phone() {
    return this.userService.userPhone;
  }
}
