import { Component, Input, forwardRef, AfterViewInit, Output, EventEmitter } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';

import { MaskPipe } from 'ngx-mask';

import { JInputComponent } from '../input.component';
import { MaskedService } from '../../masked/masked.service';
import { ICountryCode } from '@shared/models';

@Component({
  selector: 'j-phone-input',
  templateUrl: './phone-input.component.html',
  styleUrls: ['./phone-input.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => PhoneInputComponent),
      multi: true,
    },
  ],
})

export class PhoneInputComponent extends JInputComponent implements AfterViewInit {
  @Input() label = 'SHARED.PHONE';
  @Input() value = '';
  @Input() defaultFocus = true;
  @Input() countryCodes: ICountryCode[];
  @Input() set countryCode(value: ICountryCode | { id: 'phone', code: '', countryCode: '' }) {
    if (value?.code) {
      this.formControl.setValue(value?.code);
    }
    if (value?.countryCode === 'KZ') {
        this.maskType = 'loginPhoneKz';
      } else {
        this.maskType = 'foreignPhone';
      }
    this.phoneMaskConfig = this.maskedService.getConfig(this.maskType);
  }
  @Output() countrySelected = new EventEmitter<ICountryCode>();

  maskType = 'loginPhoneKz';
  phoneMaskConfig = this.maskedService.getConfig('loginPhoneKz');
  phoneInputSpecialCharacters = ['(', ')', '+', ' '];

  constructor(public maskedService: MaskedService, private maskPipe: MaskPipe) {
    super(maskedService);
  }

  ngAfterViewInit() {
    if (this.defaultFocus) {
      this.focus(5);
    }
  }

  inputChange() {
    this.formControl.setValue(this.formControl.value.replace(/\D/g, ''));
    const value = '+' + this.formControl.value;
    let found;
    if (this.countryCodes) {
      found = this.countryCodes.find((code) => code.code === value && code.countryCode);
    }
    if (found || value.length < 2) {
      this.countrySelected.emit(found);
    }
  }

  onPaste(event: ClipboardEvent) {
    event.preventDefault();
    const clipboardData = event.clipboardData || (window as any).clipboardData;
    const pastedText = clipboardData.getData('text');
    const toPasteRaw = this.maskedService.getPastedPhone(pastedText);
    this.formControl.setValue(this.getMaskedPhone(toPasteRaw));
  }

  onBackspace(event) {
    setTimeout(() => {
      if (this.formControl.value.length === 0) {
        this.focus(5);
      }
    }, 0);
  }

  clear() {
    super.clear();
    this.focus(5);
  }

  get isFilled() {
    return true;
  }

  get isShadowHidden() {
    return false;
  }

  get isClearEnabled() {
    const value = this.maskedService.getUnmaskedNumber(this.formControl.value);
    return value.length > 0;
  }

  get shadow() {
    // необходимо добавлять префикс чтобы маска могла учитывать символы префикса для корректной работы
    const valueWithPrefix = `${this.phoneMaskConfig.prefix || ''}${this.getMaskedPhone(this.formControl.value)}`;
    return this.maskedService.getShadow(
      this.maskType,
      valueWithPrefix
    );
  }

  private getMaskedPhone(unmasked: string) {
    const maskedValue = this.maskPipe.transform(
      unmasked,
      this.phoneMaskConfig.mask
    );
    return maskedValue;
  }
}
