import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApiService } from '@api/index';
import { IExchangeRates } from './exchange-rates.model';

const URL = 'currency/rates';

@Injectable({
  providedIn: 'root'
})
export class ExchangeRatesService {

  constructor(private api: ApiService) { }

  async getUserExchangeRates() {
    const exchangeRates = await this.api.get<IExchangeRates[]>(`${URL}/active-and-default`);
    return exchangeRates;
  }

  async getAllExchangeRates() {
    const exchangeRates = await this.api.get<IExchangeRates[]>(`${URL}/all`);
    return exchangeRates;
  }

  async setUserExchangeRates(body) {
    const url = `${URL}/set/status`;
    const headers = new HttpHeaders();
    const statement = await this.api.post<IExchangeRates>(url, body, headers);
    return statement;
  }
}
