import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { PAYMENTS_STEPS } from '@payments/models/steps';
import { HeaderService } from '@shared/services/header.service';
import { Subscription } from 'rxjs';

@Component({
  encapsulation: ViewEncapsulation.None,
  selector: 'j-layout-home',
  templateUrl: './layout-home.component.html',
  styleUrls: ['./layout-home.component.scss'],
})
export class LayoutHomeComponent {
  currentStep = PAYMENTS_STEPS.NONE;
  steps = PAYMENTS_STEPS;

  subscription: Subscription = new Subscription();

  constructor(private headerService: HeaderService) {
    this.headerService.setCurrentStep(this.steps.NONE);

    const headerSubscription = this.headerService
      .getCurrentStep()
      // .pipe(distinctUntilChanged())
      .subscribe((newStep) => {
        this.currentStep = newStep;
      });

    this.subscription.add(headerSubscription);
  }

  setStep(step: number) {
    this.headerService.setCurrentStep(step);
  }
}
