import { Injectable } from '@angular/core';
import { HttpHeaders } from '@angular/common/http';

import {
  NgbModal,
  NgbModalOptions,
  NgbModalRef,
} from '@ng-bootstrap/ng-bootstrap';

import { IBulkOperationResult, ISmsToken, TOKEN_TYPE } from '../models';
import { ConfirmSmsModalComponent, CONFIRM_SIGNATURE_TYPE } from '../components/confirm-sms-modal/confirm-sms-modal.component';

import { ApiService, HEADERS } from '@api/api.service';
import { CompanyService } from '@company/company.service';

@Injectable({
  providedIn: 'root',
})
export class ListService {
  private modalConfigMedium: NgbModalOptions = {
    backdropClass: 'backdrop_light',
    windowClass: 'modal_light modal_md',
    backdrop: 'static',
    centered: true,
  };

  constructor(
    private apiService: ApiService,
    private companyService: CompanyService,
    private modalService: NgbModal
  ) {}

  deleteList(urlPath: string, itemIds: string[]) {
    const url = `companies/${this.companyService.currentId}/${urlPath}`;
    const body = {
      itemIds,
    };
    return this.apiService.deleteWithBody<IBulkOperationResult>(url, body);
  }

  async sign(urlPath: string, ids: string | string[]) {
    const itemIds = Array.isArray(ids) ? (ids as string[]) : [ids as string];

    const modalResult = await this.showSignModal(itemIds);

    if (!modalResult) {
      return;
    }

    const { smsToken, modal } = modalResult;

    return this.signList(urlPath, ids as string, smsToken, modal);
  }

  async signList(
    urlPath: string,
    ids: string | string[],
    smsToken: ISmsToken,
    modal: NgbModalRef
  ) {
    const suffix = Array.isArray(ids) ? '' : `${ids}/`;
    const url = `companies/${this.companyService.currentId}/${urlPath}/${suffix}signatures`;
    const body = {
      itemIds: Array.isArray(ids) ? ids : [ids],
    };

    const headers = new HttpHeaders({
      [smsToken.type == TOKEN_TYPE.OTP ? HEADERS.OTP : HEADERS.PHONE]:
        smsToken.signature,
    });

    const result = await this.apiService.post<IBulkOperationResult>(
      url,
      body,
      headers
    );
    modal.close();

    return result;
  }

  async showSignModal(actionIds: string[]) {
    const modal = this.modalService.open(
      ConfirmSmsModalComponent,
      this.modalConfigMedium
    );

    Object.assign(modal.componentInstance, {
      isPaymentsSigning: false,
      additionalParams: {
        actionIds,
      },
    });

    const modalResult = new Promise<{
      modal: NgbModalRef;
      smsToken: ISmsToken;
    }>((resolve, reject) => {
      (modal.componentInstance as ConfirmSmsModalComponent).submitted.subscribe(
        (smsToken: ISmsToken) => {
          localStorage.setItem(
            'preferredSignatureType',
            smsToken.type === TOKEN_TYPE.OTP
              ? CONFIRM_SIGNATURE_TYPE.OTP
              : CONFIRM_SIGNATURE_TYPE.SMS
          );
          resolve({ smsToken, modal });
        }
      );

      (modal.componentInstance as ConfirmSmsModalComponent).closed.subscribe(
        () => {
          modal.close();
          resolve(null);
        }
      );
    });

    return modalResult;
  }
}
