import { Input } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import {
  BULK_OPERATION_STATUS,
  IBulkOperationItem,
  IBulkOperationResult,
} from '@shared/models';

@Component({
  selector: 'j-list-modal',
  templateUrl: './list-modal.component.html',
  styleUrls: ['./list-modal.component.scss'],
})
export class ListModalComponent implements OnInit {
  @Input() imagePrefix = '/assets/img/history/modals';
  @Input() translatePrefix = 'LIST.MODAL';

  @Input() partialTitleFn: Function;

  @Input() key: string;
  @Input() result: IBulkOperationResult;
  @Input() list: any[];

  BULK_OPERATION_STATUS = BULK_OPERATION_STATUS;

  title: string;
  imgSrc: string;

  constructor(public modal: NgbActiveModal) {}

  ngOnInit() {
    switch (this.result.status) {
      case BULK_OPERATION_STATUS.FAILED:
        this.title = this.mapTranslate(`${this.key}.FAILED.TITLE`);
        this.imgSrc = `${this.imagePrefix}/error.svg`;
        break;

      case BULK_OPERATION_STATUS.PARTLY_SUCCESS:
        this.title = this.mapTranslate(`${this.key}.PARTIAL`);
        this.imgSrc = `${this.imagePrefix}/partial.svg`;
        break;

      case BULK_OPERATION_STATUS.SUCCESS:
        this.title = this.mapTranslate(`${this.key}.SUCCESS.TITLE`);
        this.imgSrc = `${this.imagePrefix}/success.svg`;
        break;
    }
  }

  get successText() {
    switch (this.result?.numberOfSuccess % 10) {
      case 1:
        return this.mapTranslate(`${this.key}.SUCCESS.ONE`);
      case 2:
      case 3:
      case 4:
        return this.mapTranslate(`${this.key}.SUCCESS.FEW`);
      default:
        return this.mapTranslate(`${this.key}.SUCCESS.OTHER`);
    }
  }

  get successParams() {
    return {
      count: this.result?.numberOfSuccess,
    };
  }

  get failedText() {
    switch (this.result?.numberOfFailed % 10) {
      case 1:
        return this.mapTranslate(`${this.key}.FAILED.ONE`);
      case 2:
      case 3:
      case 4:
        return this.mapTranslate(`${this.key}.FAILED.FEW`);
      default:
        return this.mapTranslate(`${this.key}.FAILED.OTHER`);
    }
  }

  get failedParams() {
    return {
      count: this.result?.numberOfFailed,
    };
  }

  get failedItems() {
    return this.result?.results?.filter((item) => {
      return item.status === BULK_OPERATION_STATUS.FAILED;
    });
  }

  getItemTitle(bulkItem: IBulkOperationItem) {
    const foundItem = this.list.find((item) => {
      return item.id === bulkItem.itemId;
    });

    if (foundItem) {
      return this.partialTitleFn(foundItem);
    }

    return '';
  }

  dismissModal() {
    this.modal.dismiss();
  }

  closeModal() {
    this.modal.close();
  }

  retrieveErrors(bulkItem: IBulkOperationItem) {
    if (bulkItem.errorData) {
      const errors = Object.entries(bulkItem.errorData);
      const mappedErrors = errors.map((item) => {
        return item[1];
      });

      return mappedErrors;
    } else if (bulkItem.errorMsg) {
      return [bulkItem.errorMsg];
    }
  }

  mapTranslate(value: string) {
    return `${this.translatePrefix}.${value}`;
  }
}
