import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import * as moment from 'moment';
import { PERIOD_FORMAT_INPUT } from '@filter/filter.model';
import dayjs from 'dayjs';
import customParseFormat from 'dayjs/plugin/customParseFormat';
import isBetween from 'dayjs/plugin/isBetween';

dayjs.extend(customParseFormat);
dayjs.extend(isBetween);

export const UI_DATE_FORMAT = 'DD.MM.YYYY';
export const API_DATE_FORMAT = 'YYYY-MM-DD';

export const ISO_8061 = 'YYYY-MM-DD';
export const edgeTimeLabel = '19:00';


export const convertApiDateToUiFormat = (date) => {
  if (!date) {
    return null;
  }

  return dayjs(date, API_DATE_FORMAT).format(UI_DATE_FORMAT);
};

export function dateToNgbStruct(date: Date): NgbDateStruct {
  return {
    year: date.getFullYear(),
    month: date.getMonth() + 1,
    day: date.getDate(),
  };
}

export function ngbDateToDate({ year, month, day }: NgbDateStruct): Date {
  return new Date(year, month - 1, day);
}
export function ngbDateToUTCStartDay({
  year,
  month,
  day,
}: NgbDateStruct): Date {
  return new Date(Date.UTC(year, month - 1, day, 0, 0, 1));
}
export function toUTCStartDay(date: Date): Date {
  return new Date(
    Date.UTC(date.getFullYear(), date.getMonth(), date.getDate(), 0, 0, 1)
  );
}
export function toUTCStartMonth(date: Date): Date {
  return new Date(Date.UTC(date.getFullYear(), date.getMonth(), 1, 0, 0, 1));
}
export function ngbStructToISO({ year, month, day }: NgbDateStruct): string {
  return `${year}-${month.toString().padStart(2, '0')}-${day
    .toString()
    .padStart(2, '0')}`;
}
export function dateToISO(date: Date): string {
  return `${date.getFullYear()}-${(date.getMonth() + 1)
    .toString()
    .padStart(2, '0')}-${date.getDate().toString().padStart(2, '0')}`;
}

export function dateTimeToISO(date: Date): string {
  return `${dateToISO(date)}T${date
    .getHours()
    .toString()
    .padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}:${date
    .getSeconds()
    .toString()
    .padStart(2, '0')}`;
}
/**
 * @param date local now time
 * @param timeZone to compare ex: Asia/Almaty
 * @returns offset in minutes
 */
export function getTimeZoneOffset(date: Date, timeZone: string): number {
  try {
    // Abuse the Intl API to get a local ISO 8601 string for a given time zone.
    let iso = date
      .toLocaleString('en-ca', { timeZone, hour12: false })
      .replace(', ', 'T');
    // Include the milliseconds from the original timestamp
    iso += '.' + date.getMilliseconds().toString().padStart(3, '0');

    // Lie to the Date object constructor that it's a UTC time.
    // const lie = new Date(iso + 'Z');

    const lie = new Date(iso);

    // Return the difference in timestamps, as minutes
    // Positive values are West of Current Timezone, opposite of ISO 8601
    // this matches the output of `Date.getTimeZoneOffset`
    return -(+lie - +date) / 60 / 1000;
  } catch (e) {
    return 0;
  }
}
