export function formatNgbDate(struct: NgbDateStruct, locale: string): string {
  const lang = Object.keys(month).includes(locale) ? locale : localStorage.getItem('lang');
  return struct.day + ' ' + month[lang][struct.month - 1];
}

export function periodInputDateToISODateSafe(date: string | Date) {
  if (typeof date === 'string') {
    return (
      (date && moment(date, PERIOD_FORMAT_INPUT).format(ISO_8061)) || undefined
    );
  }
  if (date instanceof Date) {
    return moment(date).format(ISO_8061);
  }
  return '';
}

export function isoDateToDateSafe(date: string | Date): Date | undefined {
  if (typeof date === 'string') {
    return (date && moment(date, ISO_8061).toDate()) || undefined;
  }
  if (date instanceof Date) {
    return date;
  }
}

export function isoDateToPeriodInput(date: string): string {
  return moment(date, ISO_8061).format(PERIOD_FORMAT_INPUT);
}

export function isoDateToPeriodInputSafe(date: string | Date): string {
  if (typeof date === 'string') {
    return (date && isoDateToPeriodInput(date)) || '';
  } else if (date) {
    return moment(date).format(PERIOD_FORMAT_INPUT);
  }
}

export function convertDateToString(date: Date) {
  return moment(date).format(PERIOD_FORMAT_INPUT);
}

export function convertDateToApiFormat(date: Date) {
  return moment(date).format(ISO_8061);
}
