import {
  Directive,
  Input,
  ElementRef,
  Output,
  EventEmitter,
  HostListener,
} from '@angular/core';

@Directive({
  selector: '[jCollapsibleTrigger]',
})
export class CollapsibleTriggerDirective {
  @Output() clicked = new EventEmitter();
  constructor(public elem: ElementRef) {}

  @HostListener('click')
  onClick() {
    this.clicked.emit();
  }
}
