import { PAYMENT_TYPE } from './payment';

export enum IMPORT_ERROR_STATUS {
  FAILED = 'FAILED',
  DRAFT = 'DRAFT',
}

export interface IImportError {
  documentNumber: string;
  invalidFieldsNames: string[];
  paymentType: PAYMENT_TYPE;
  status: IMPORT_ERROR_STATUS;

  filename?: string;
}

export enum IMPORT_STATUS {
  ALREADY_IMPORTED = 'ALREADY_IMPORTED',
  INVALID_FORMAT = 'INVALID_FORMAT',
  SUCCESS = 'SUCCESS',
}
export interface IImportResult {
  failed: IImportError[];
  filename: string;
  success: number;
  total: number;
  drafted: number;
  hasInvalidValueDate: boolean;

  status: IMPORT_STATUS;
}
