import { Component, OnInit, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ImportPartialErrorComponent } from '../import-partial-error/import-partial-error.component';

@Component({
  selector: 'j-import-partial-success',
  templateUrl: './import-partial-success.component.html',
  styleUrls: ['./import-partial-success.component.scss'],
})
export class ImportPartialSuccessComponent extends ImportPartialErrorComponent {
  @Input() successOperationsCount: number;

  constructor(public modal: NgbActiveModal) {
    super(modal);
  }

  get successText() {
    switch (this.successOperationsCount % 20) {
      case 1:
        return 'IMPORT.SUCCESS.ONE';
      case 2:
      case 3:
      case 4:
        return 'IMPORT.SUCCESS.FEW';
      default:
        return 'IMPORT.SUCCESS.OTHER';
    }
  }

  get successTranslateParams() {
    return {
      operationsCount: this.successOperationsCount,
    };
  }
}
