import { NgModule } from '@angular/core';
import { registerLocaleData } from '@angular/common';

import localeRu from '@angular/common/locales/ru';
import localeKk from '@angular/common/locales/kk';

import { ApiModule } from '@api/api.module';
import { CoreModule } from '@core/core.module';
import { environment } from '@environments/environment';
import { HeartbeatModule } from '@heartbeat/heartbeat.module';

import { SharedModule } from '@shared/shared.module';
import { MobileComponent } from '@shared/components/mobile/mobile.component';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';

registerLocaleData(localeRu);
registerLocaleData(localeKk);

@NgModule({
  declarations: [AppComponent, MobileComponent],
  imports: [
    CoreModule,
    AppRoutingModule,
    SharedModule,
    HeartbeatModule,
    ApiModule.forRoot({
      baseUrl: environment.baseUrl,
    }),
  ],
  providers: [
    // tmp, need to find the way to share this value between lazy modules
    // cause each lazy module is a separate child of app injector
    // one of a possible ways is to add interceptor to handle requests to jmart and clone them with value from env
    {
      provide: 'JMART_CABINET_BASE_URL',
      useValue: environment.jmartCabinetBaseUrl,
    },
  ],
  bootstrap: [AppComponent],
})
export class AppModule {}
