import {
  Directive,
  ElementRef,
  Optional,
  Self,
  Input,
  HostListener,
} from '@angular/core';
import { NgControl } from '@angular/forms';

@Directive({
  selector: '[jCursorBound]',
  // host: {
  //   '(click)': 'setCursorToEnd',
  //   '(keyup)': 'setCursorToEnd',
  // },
})
export class CursorBoundDirective {
  positionFromEnd: number;
  previousValue: string;

  // @Input() jCursorBound?: string;
  @Input() suffix?: string;
  @HostListener('keyup', ['$event']) onKeyUp = this.setCursorToEnd;
  @HostListener('click', ['$event']) onClick = this.setCursorToEnd;
  constructor(
    private ref: ElementRef,
    @Optional() @Self() private control: NgControl
  ) {
  }

  setCursorToEnd(event: any) {
    const selectionMaxIndex = this.value.replace(this.suffix, '').length;

    if (this.positionFromEnd && event.target.value?.length !== this.previousValue?.length) {
      this.element.setSelectionRange(
        event.target.selectionStart,
        selectionMaxIndex - this.positionFromEnd
      );
    }

    if (event.target.selectionEnd > selectionMaxIndex) {
      this.element.setSelectionRange(
        event.target.selectionStart,
        selectionMaxIndex
      );
    }

    this.positionFromEnd = selectionMaxIndex - event.target.selectionEnd;
    this.previousValue = event.target.value;
  }

  get value() {
    return this.element.value || '';
  }

  get element() {
    return this.ref.nativeElement;
  }
}
