import { BehaviorSubject, Observable, defer, combineLatest } from 'rxjs';
import {
  distinctUntilChanged,
  map,
  share,
  take,
  first,
  shareReplay,
  filter,
  skipWhile,
} from 'rxjs/operators';

import { collectionKeyBy } from './keyBy';

export interface SimpleStoreConfig<T extends { id: string }> {
  ttl?: number;
  onSubscribe?: (store: SimpleStore<T>) => void;
}
const DEFAULT = {
  ttl: 60000,
};
export class SimpleStore<T extends { id: string }> {
  protected readonly itemsSubject$: BehaviorSubject<T[]>;

  public readonly items$: Observable<T[]>;

  /**
   * @deprecated
   */
  public readonly dict$: Observable<{
    [id: string]: T;
  }>;

  protected readonly fetchingSubject$ = new BehaviorSubject<boolean>(false);
  public readonly fetching$ = this.fetchingSubject$.asObservable();

  protected readonly fetchedSubject$ = new BehaviorSubject<boolean>(false);
  public readonly fetched$ = this.fetchedSubject$.asObservable();

  public isExpiredSubject$;
  public isExpired$;

  private readonly initialState: T[];
  protected updated = 0;
  protected config: SimpleStoreConfig<T>;

  constructor(initialState: T[], opts = {}) {
    this.config = Object.assign(opts, DEFAULT);
    this.itemsSubject$ = new BehaviorSubject<T[]>(
      (this.initialState = initialState || [])
    );
    this.dict$ = this.itemsSubject$.pipe(
      map((v) => collectionKeyBy(v, 'id')),
      share()
    );
    if (this.config.onSubscribe) {
      defer(() => (this.config.onSubscribe(this), this.items$));
    } else {
      this.items$ = this.itemsSubject$.asObservable();
    }

    this.isExpiredSubject$ = new BehaviorSubject(this.expired);
    // .pipe(
    // startWith(this.expired),
    // switchMap(v => timer(this.config.ttl).pipe(mapTo(true)))
    // );

    this.isExpired$ = this.isExpiredSubject$
      .pipe(distinctUntilChanged())
      .asObservable();
  }

  get dict(): { [id: string]: T } {
    let r: { [id: string]: T };
    this.dict$.pipe(take(1)).subscribe((v) => (r = v));
    return r;
  }

  get items(): T[] {
    return this.itemsSubject$.getValue();
  }

  set items(items: T[]) {
    this.itemsSubject$.next(items);
  }

  get fetching(): boolean {
    return this.fetchingSubject$.getValue();
  }

  set fetching(state: boolean) {
    this.fetchingSubject$.next(state);
  }

  get fetched(): boolean {
    return this.fetchedSubject$.getValue();
  }

  set fetched(state: boolean) {
    this.fetchedSubject$.next(state);
  }

  addItem(item: T, opts?: { insertBefore?: any; insertAfter?: any }) {
    const newItem = item;
    this.items = [...this.items, newItem];
  }

  get expired(): boolean {
    return Date.now() - this.updated > this.config.ttl;
  }

  getItemById(id: string): T {
    return this.dict[id];
  }

  getItemByKey<K extends keyof T>(v: T[K], key: K): T {
    return this.items.find((item) => item[key] === v);
  }

  setUpdated(updateAt: number = 0) {
    this.updated = updateAt;
  }

  updateItem(toUpdate: Partial<T>) {
    const index = this.items.findIndex((item) => item.id === toUpdate.id);
    if (~index) {
      const updatedItem = { ...this.items[index], ...toUpdate };
      this.items = [...this.items.splice(index, 1, updatedItem)];
    }
  }

  replaceItem(toReplace: T) {
    const index = this.items.findIndex((item) => item.id === toReplace.id);
    if (~index) {
      this.items = [...this.items.splice(index, 1, toReplace)];
    }
  }

  removeItem(id: string) {
    this.items = this.items.filter((item) => item.id !== id);
  }

  setState(items: T[], opts: { updatedAt?: number } = {}) {
    this.items = items;
    this.updated = opts.updatedAt || Date.now();
    this.isExpiredSubject$.next(this.expired);
  }

  resetState() {
    this.items = [...this.initialState];
    this.fetching = false;
    this.fetched = false;
  }

  purge() {
    this.fetching = false;
    this.fetched = false;
    this.items = [];
  }
}

// @TODO use on behaviour subject to store state
// export class EntitiesStore<T extends { id: string }> extends BehaviorSubject<T[]> {
export class EntitiesStore<T extends { id: string }> {
  protected readonly entitiesSubject$: BehaviorSubject<{
    [id: string]: T;
  }>;
  protected readonly idsSubject$: BehaviorSubject<string[]>;

  public readonly items$: Observable<T[]>;
  // public readonly entities$: Observable<{
  //   [id: string]: T;
  // }>;

  public fetchedItems$: Observable<T[]>;

  protected readonly fetchingSubject$ = new BehaviorSubject<boolean>(false);
  public readonly fetching$ = this.fetchingSubject$.asObservable();

  protected readonly fetchedSubject$ = new BehaviorSubject<boolean>(false);
  public readonly fetched$ = this.fetchedSubject$.asObservable();

  public isExpiredSubject$;
  public isExpired$;

  private readonly initialState: T[];
  private readonly suspend$ = new BehaviorSubject(false);
  protected updated = 0;
  protected config: SimpleStoreConfig<T>;

  constructor(initialState: T[] = [], opts = {} as any) {
    // super(initialState);
    this.config = Object.assign(opts, DEFAULT);

    this.entitiesSubject$ = new BehaviorSubject<{ [id: string]: T }>({});

    this.idsSubject$ = new BehaviorSubject<string[]>([]);
    this.isExpiredSubject$ = new BehaviorSubject(this.expired);
    this.isExpired$ = this.isExpiredSubject$
      .pipe(distinctUntilChanged())
      .asObservable();

    this.items$ = combineLatest([
      this.ids$,
      this.entities$,
      this.suspend$,
    ]).pipe(
      skipWhile(() => this.suspend$.getValue()),
      map(([ids, entities]) => (ids as any).map((id) => entities[id])),
      shareReplay(1)
    );

    this.items = initialState;
    this.initialState = initialState;

    this.fetchedItems$ = this.fetchedSubject$.pipe(
      distinctUntilChanged(),
      filter((loaded) => {
        return loaded;
      }),
      map((loaded) => this.items),
      shareReplay(1)
    );
  }

  get entities(): { [id: string]: T } {
    return this.entitiesSubject$.getValue();
  }
  set entities(dict: { [id: string]: T }) {
    this.entitiesSubject$.next(dict);
  }

  get entities$(): Observable<{ [id: string]: T }> {
    return this.entitiesSubject$.pipe(distinctUntilChanged());
  }

  get items(): T[] {
    let r: T[];
    this.items$.pipe(first()).subscribe((v) => (r = v));
    return r;
  }

  get ids$(): Observable<string[]> {
    return this.idsSubject$.pipe(distinctUntilChanged());
  }

  get ids(): string[] {
    return this.idsSubject$.getValue();
  }

  set ids(ids: string[]) {
    this.idsSubject$.next(ids);
  }

  set items(items: T[]) {
    let i = 0;
    let length = items.length;
    const entities = {};
    const ids = [];
    this.suspend$.next(true);
    for (; i < length; i++) {
      entities[items[i].id] = items[i];
      ids.push(items[i].id);
    }
    this.idsSubject$.next(ids);
    this.entitiesSubject$.next(entities);
    this.suspend$.next(false);
  }

  get fetching(): boolean {
    return this.fetchingSubject$.getValue();
  }

  set fetching(state: boolean) {
    this.fetchingSubject$.next(state);
  }

  get fetched(): boolean {
    return this.fetchedSubject$.getValue();
  }

  set fetched(state: boolean) {
    this.fetchedSubject$.next(state);
  }

  // addItem(item: T, opts?: { insertBefore?: any; insertAfter?: any }) {
  //   const newItem = item;
  //   this.items = [...this.items, newItem];
  // }

  get expired(): boolean {
    return Date.now() - this.updated > this.config.ttl;
  }

  getItemsByIds(ids: string[]): T[];
  getItemsByIds(ids: string): T;
  getItemsByIds(ids: any): any {
    if (Array.isArray(ids)) {
      const entities = this.entities;
      return ids.reduce(
        (acc, knpCode) => (
          entities[knpCode] && acc.push(entities[knpCode]), acc
        ),
        []
      );
    } else {
      return this.entities[ids];
    }
  }

  getItemByKey<K extends keyof T>(v: T[K], key: K): T {
    return this.items.find((item) => item[key] === v);
  }

  setUpdated(updateAt: number = 0) {
    this.updated = updateAt;
  }

  // updateItem(toUpdate: Partial<T>) {
  //   const index = this.items.findIndex((item) => item.id === toUpdate.id);
  //   if (~index) {
  //     const updatedItem = { ...this.items[index], ...toUpdate };
  //     this.items = [...this.items.splice(index, 1, updatedItem)];
  //   }
  // }

  // replaceItem(toReplace: T) {
  //   const index = this.items.findIndex((item) => item.id === toReplace.id);
  //   if (~index) {
  //     this.items = [...this.items.splice(index, 1, toReplace)];
  //   }
  // }

  upsertItem(item: T, shift?: boolean) {
    const dict = { ...this.entities };
    let ids = this.ids;

    if (dict[item.id]) {
      dict[item.id] = { ...dict[item.id], ...item };
      this.entities = dict;
    } else {
      dict[item.id] = item;
      ids = shift ? [item.id].concat(ids) : ids.concat([item.id]);
      this.suspend$.next(true);
      this.entities = dict;
      this.ids = ids;
      this.suspend$.next(false);
    }
  }

  removeItems(ids: string | string[]) {
    if (!Array.isArray(ids)) {
      ids = [ids];
    }
    const dict = ids.reduce((acc, id) => {
      acc[id] = true;
      return acc;
    }, {} as Record<string, boolean>);
    this.items = this.items.filter((item) => !(item.id in dict));
  }

  setState(items: T[], opts: { updatedAt?: number } = {}) {
    this.items = items;
    this.updated = opts.updatedAt || Date.now();
    this.isExpiredSubject$.next(this.expired);
  }

  resetState() {
    this.items = [...this.initialState];
    this.fetching = false;
    this.fetched = false;
  }

  purge() {
    this.fetching = false;
    this.fetched = false;
    this.items = [];
  }
}

export class SingleEntityStore<T> {
  protected readonly entitySubject$: BehaviorSubject<T>;
  public readonly entity$: Observable<T>;

  protected readonly fetchingSubject$ = new BehaviorSubject<boolean>(false);
  public readonly fetching$ = this.fetchingSubject$.asObservable();

  protected readonly fetchedSubject$ = new BehaviorSubject<boolean>(false);
  public readonly fetched$ = this.fetchedSubject$.asObservable();

  private readonly initialState: T;

  constructor(initialState?: T) {
    this.entitySubject$ = new BehaviorSubject<T>(
      (this.initialState = initialState)
    );
    this.entity$ = this.entitySubject$.asObservable();
  }

  get entity(): T {
    return this.entitySubject$.getValue();
  }

  set entity(item: T) {
    this.entitySubject$.next(item);
  }

  get fetching(): boolean {
    return this.fetchingSubject$.getValue();
  }

  set fetching(state: boolean) {
    this.fetchingSubject$.next(state);
  }

  get fetched(): boolean {
    return this.fetchedSubject$.getValue();
  }

  set fetched(state: boolean) {
    this.fetchedSubject$.next(state);
  }

  purge() {
    this.fetching = false;
    this.fetched = false;
    this.entity = null;
  }
}

