import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'j-empty-placeholder',
  templateUrl: './empty-placeholder.component.html',
  styleUrls: ['./empty-placeholder.component.scss'],
})
export class EmptyPlaceholderComponent {
  @Input() label: string;
  @Input() minHeight = 250;
  @Input() hasRetry: boolean = false;

  @Output() retried = new EventEmitter();

  constructor() {}

  onRetry() {
    this.retried.emit();
  }
}
