import { Pipe, PipeTransform } from '@angular/core';
import { MaskedService } from '@shared/masked/masked.service';

@Pipe({
  name: 'amountFormat',
  pure: true,
})
export class AmountFormatPipe implements PipeTransform {
  transform(value: string | number, fixedZeroes?: boolean): string {
    if (value) {
      return this.maskService.formatAmount(value, fixedZeroes);
    }
    return `0`;
  }

  constructor(private maskService: MaskedService) {}
}
