import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ViewEncapsulation,
  ElementRef,
} from '@angular/core';
import { FormGroup } from '@angular/forms';

import { combineLatest, Observable, of } from 'rxjs';
import { map, distinctUntilChanged } from 'rxjs/operators';

import { TranslateService } from '@ngx-translate/core';
import { MaskPipe } from 'ngx-mask';

import { CURRENCY } from '@shared/currency/currency.model';
import { FormGroupTypeSafe } from '@utils/index';

import { EmployeeItemModel, EmployeesItem } from '../../employee.model';
import { MaskedService } from '@shared/masked/masked.service';

@Component({
  selector: 'j-employee-edit-item',
  templateUrl: './employee-edit-item.component.html',
  styleUrls: ['./employee-edit-item.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class EmployeeEditItemComponent implements OnInit {
  @Input() birthDateRequired: boolean;
  @Input() additionalPeriodEnabled: boolean;
  @Input() index: number;
  @Input() item: EmployeesItem;
  @Input() masks;
  @Input() group: FormGroup;
  @Input() errors?: any;
  @Input() isSubmitted: boolean;
  @Output() remove = new EventEmitter();
  @Output() cancelRemove = new EventEmitter();
  @Output() autocomplete = new EventEmitter();
  @Output() addPeriod = new EventEmitter();

  @Input() minAmount = 0.01;
  @Input() maxAmount = 212500;

  CURRENCY = CURRENCY;

  minDate = new Date(1900, 0, 0);
  maxDate = new Date();

  /**
   * combine parent removing and own removing stream to avoid
   * case when child can loose parent (additional period)
   */
  hideStream$: Observable<boolean>;
  showCancel$: Observable<boolean>;
  hideControls$: Observable<boolean>;

  constructor(
    public element: ElementRef,
    private maskPipe: MaskPipe,
    private maskedService: MaskedService,
    private translateService: TranslateService
  ) {}

  ngOnInit(): void {
    this.hideStream$ = this.item.linkedWith
      ? combineLatest([
          this.item.removing$,
          this.item.linkedWith.removing$,
        ]).pipe(map(([parent, child]) => parent !== null || child !== null))
      : this.item.removing$.pipe(map((item) => item !== null));

    this.showCancel$ = this.item.removing$.pipe(
      map((item) => item !== null),
      distinctUntilChanged()
    );

    this.hideControls$ =
      this.item.linkedWith &&
      this.item.linkedWith.removing$.pipe(map((item) => item !== null));

    this.group.patchValue({
      lastName: this.form.lastName?.value?.trim() || '',
    });
    this.group.patchValue({
      firstName: this.form.firstName?.value?.trim() || '',
    });
    this.group.patchValue({
      middleName: this.form.middleName?.value?.trim() || '',
    });
    this.group.patchValue({
      amount: this.form.amount?.value
        ? this.maskedService.formatAmount(this.form.amount.value)
        : '',
    });
  }

  get form(): FormGroupTypeSafe<EmployeeItemModel>['controls'] {
    return (this.group as FormGroupTypeSafe<EmployeeItemModel>).controls;
  }

  get amountError$() {
    if (this.form?.amount?.value && this.form?.amount?.errors?.invalidAmount) {
      return this.translateService.get(`PAYMENTS.EMPLOYEES.INVALID_AMOUNT`);
    }
    return of(null);
  }

  get removeLabel(): string {
    return `PAYMENTS.EMPLOYEES.${
      this.item.linkedWith ? 'REMOVE_PERIOD_PAYMENT' : 'REMOVE_FROM_PAYMENT'
    }`;
  }

  onRemove() {
    this.remove.emit(this.item);
  }

  onCancel() {
    this.cancelRemove.emit(this.item);
  }

  onAutocomplete(event?: Event) {
    if (
      this.form.iin.valid &&
      this.item.autocomplete$.getValue() !== this.form.iin.value
    ) {
      this.autocomplete.emit(this.item);
    }
    if (event) {
      event.preventDefault();
    }
  }

  onAddPeriod() {
    this.addPeriod.emit(this.item);
  }

  private getMaskedAmount(amount) {
    const maskedAmount = this.maskPipe.transform(
      amount,
      this.masks.amount.mask
    );
    return `${maskedAmount}${this.masks.amount.suffix}`;
  }
}
