import { Pipe, PipeTransform } from '@angular/core';
import { MaskService, MaskPipe } from 'ngx-mask';
import { MaskedService } from '@shared/masked/masked.service';
import { maskPhone } from '@utils/index';

@Pipe({
  name: 'maskPhone',
  pure: true,
})
export class MaskPhonePipe implements PipeTransform {
  public config;
  constructor(mask: MaskedService, private pipe: MaskPipe) {
    this.config = mask.getConfig('phone');
  }
  transform(value: string): string {
    return maskPhone(
      this.config.prefix + this.pipe.transform(value && value.replace('+7', ''), this.config.mask)
    );
  }
}
