import { Directive, ElementRef, Inject, Input, OnChanges, OnDestroy, SimpleChanges } from '@angular/core';
import { DOCUMENT } from '@angular/common';

const enum BadgeSettings {
  Selector = 'jBadge',
  Element = 'span',
  ClassName = 'j-badge'
}

@Directive({
  selector: '[jBadge]'
})

export class BadgeDirective implements OnChanges, OnDestroy {
  @Input() jBadge: string;

  badgeElement: HTMLElement | null;

  constructor(
    @Inject(DOCUMENT) private document: Document,
    private elRef: ElementRef<HTMLElement>
  ) {}
  ngOnChanges(changes: SimpleChanges): void {
    if (BadgeSettings.Selector in changes) {
      const value = `${changes[BadgeSettings.Selector].currentValue}`.trim();
      if (value?.length) { this.updateBadgeText(value); }
    }
  }

  ngOnDestroy() {
    if (this.badgeElement) { this.badgeElement.remove(); }
  }

  private updateBadgeText(value: string) {
    if (!this.badgeElement) { return this.createBadge(value); }
    this.badgeElement.textContent = value;
  }

  private createBadge(value: string): HTMLElement {
    const badgeElement = this.document.createElement(BadgeSettings.Element);
    badgeElement.textContent = value;
    badgeElement.className = BadgeSettings.ClassName;
    this.elRef.nativeElement.appendChild(badgeElement);
    return badgeElement;
  }
}
