import {
  Component,
  OnInit,
  Input,
  HostBinding,
  AfterViewInit,
  Renderer2,
  ElementRef,
} from '@angular/core';
import { SafeHtml, DomSanitizer } from '@angular/platform-browser';

import { Icons } from './icon';

export enum ICON_SIZE {
  DEFAULT = 'sm',
  MEDIUM = 'md',
}

@Component({
  selector: 'j-icon',
  templateUrl: './icon.component.html',
  styleUrls: ['./icon.component.scss'],
})
export class IconComponent implements OnInit, AfterViewInit {
  @Input() name: string;
  @Input() color = 'default';
  @Input() isStroke: boolean;
  @Input() size: ICON_SIZE = ICON_SIZE.DEFAULT;

  ICON_SIZE = ICON_SIZE;
  svg: SafeHtml;

  constructor(
    private sanitizer: DomSanitizer,
    private renderer: Renderer2,
    private elementRef: ElementRef
  ) {}

  ngOnInit() {
    this.svg = this.sanitizer.bypassSecurityTrustHtml(Icons[this.name] || '');
  }

  ngAfterViewInit() {
    if (this.size == ICON_SIZE.MEDIUM) {
      this.renderer.addClass(this.elementRef.nativeElement, 'size-md');
    }
  }

  get iconClass() {
    let iconClass = `color-${this.color}`;
    if (this.isStroke) {
      iconClass += ' stroke';
    }

    return iconClass;
  }
}
