import {
  Component,
  OnInit,
  Input,
  forwardRef,
  HostListener,
} from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';

import { JInputComponent } from '../input.component';
import { MaskedService } from '../../masked/masked.service';

export const PASSWORD_ALLOWED_REGEX = /[a-zA-Z0-9.!#$%^&*@]/;
export const PASSWORD_RESTRICTED_REGEX = /[^a-zA-Z0-9.!#$%^&*@]/;
@Component({
  selector: 'j-password-input',
  templateUrl: './password-input.component.html',
  styleUrls: ['./password-input.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => PasswordInputComponent),
      multi: true,
    },
  ],
})
export class PasswordInputComponent extends JInputComponent implements OnInit {
  @Input() label = 'SHARED.PASSWORD';
  @Input() isAuth = false;

  @Input() hasCyrillicCheck = false;
  @Input() hasCapsLockCheck = false;

  isPasswordVisible = false;

  @Input() regexStr = PASSWORD_ALLOWED_REGEX;
  @Input() pasteRegexStr = PASSWORD_RESTRICTED_REGEX;

  isCyrillicEntered: boolean = false;
  isCapsLockEnabled: boolean = false;

  constructor(public maskedService: MaskedService) {
    super(maskedService);
  }

  @HostListener('keyup', ['$event']) onKeyUp(event: KeyboardEvent) {
    if (this.hasCyrillicCheck) {
      this.isCyrillicEntered =
        PASSWORD_RESTRICTED_REGEX.test(this.formControl.value) ||
        PASSWORD_RESTRICTED_REGEX.test(event.key);
    }
    if (this.hasCapsLockCheck) {
      try {
        this.isCapsLockEnabled = event.getModifierState('CapsLock');
      } catch (error) {}
    }
  }

  @HostListener('keypress', ['$event']) onKeyPress(event) {
    return this.regexStr.test(event.key);
  }

  @HostListener('paste', ['$event']) blockPaste(event: KeyboardEvent) {
    this.validateFields(event);
  }

  validateFields(event) {
    setTimeout(() => {
      this.input.nativeElement.value = this.input.nativeElement.value
        .replace(this.pasteRegexStr, '')
        .replace(/\s/g, '')
        .replace(/ /g, '');
      event.preventDefault();
    }, 100);
  }

  ngOnInit(): void {
    this.maskConfig = this.maskedService.getConfig(this.maskType);
  }

  togglePasswordVisibility(event: Event): void {
    event.preventDefault();
    this.isPasswordVisible = !this.isPasswordVisible;
    this.focus();
  }

  get passwordInputType(): string {
    return this.isPasswordVisible ? 'text' : 'password';
  }

  get passwordIconName(): string {
    return this.isPasswordVisible ? 'eyeOpened' : 'eyeClosed';
  }
}
