import {
  AbstractControl,
  FormArray,
  FormBuilder,
  FormControl,
  FormGroup,
  NgForm,
  Validators,
} from '@angular/forms';
import {
  BehaviorSubject,
  combineLatest,
  from,
  interval,
  merge,
  Observable,
  of,
  Subscription,
  timer,
} from 'rxjs';
import { BOOTSTRAP_BREAKPOINTS } from '@shared/responsive-toolkit/constants';
import {
  catchError,
  debounceTime,
  delay,
  distinctUntilChanged,
  filter,
  map,
  skip,
  startWith,
  switchMap,
  take,
  takeUntil,
  tap,
  throttleTime,
} from 'rxjs/operators';
import {
  Component,
  ElementRef,
  Input,
  OnDestroy,
  OnInit,
  QueryList,
  ViewChild,
  ViewChildren,
  ViewEncapsulation,
} from '@angular/core';
import { CURRENCY } from '@shared/currency/currency.model';
import {
  Employee,
  EmployeeItemModel,
  EmployeesItem,
} from '../../employee.model';
import { EmployeeEditItemComponent } from '@shared/employee/components/employee-edit-item/employee-edit-item.component';
import { EmployeeService } from '../../employee.service';
import {
  EntitiesStore,
  FormControlTypeSafe,
  FormGroupTypeSafe,
  generateUniqueId,
  getGroupRawValue,
  ibanPrefix,
  isoDateToDateSafe,
  isoDateToPeriodInputSafe,
  safeUnmaskAmount,
} from '@utils/index';
import {
  ErrorStateMatcher,
  ShowOnDirtyErrorStateMatcher,
} from '@shared/form-field/utils';
import { MaskedService } from '@shared/masked/masked.service';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';
import { PAYMENT_TYPE } from '@shared/models';
import { PaymentsService } from '@payments/services/payments.service';
import { SavedEmployeeListComponent } from '../saved-employee-list/saved-employee-list.component';

const autocompleteLock: Array<keyof EmployeeItemModel> = [
  'iin',
  'amount',
  // 'period',
  'fio',
  'birthDate',
  'firstName',
  'lastName',
  'middleName',
];

@Component({
  selector: 'j-employees-modal',
  templateUrl: './employees-modal.component.html',
  styleUrls: ['./employees-modal.component.scss'],
  host: {
    class: 'employees-modal',
  },
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: ErrorStateMatcher,
      useClass: ShowOnDirtyErrorStateMatcher,
    },
  ],
})
export class EmployeesModalComponent implements OnInit, OnDestroy {
  @Input() parentForm?: NgForm;
  @Input() initialEmployees?: EmployeeItemModel[];
  @Input() paymentType: PAYMENT_TYPE = PAYMENT_TYPE.PENSION;
  @Input() ibanRequired: boolean;
  @Input() birthDateRequired = true;
  @Input() additionalPeriodEnabled: boolean;
  @Input() periodRequired;

  @Input() minAmount = 0.01;
  @Input() maxAmount = 212500;

  @ViewChild('scrollContainer') scrollContainer: ElementRef<HTMLElement>;

  currency: CURRENCY = CURRENCY.KZT;

  store = new EntitiesStore<Employee>([]);
  employeesForm = new FormArray([]);
  term: FormControlTypeSafe<string>;
  term$: Observable<string>;

  listSubject$ = new BehaviorSubject<EmployeesItem[]>([]);
  visibleList$: Observable<EmployeesItem[]>;

  currentPage$ = new BehaviorSubject(1);

  collectionSize$ = new BehaviorSubject(0);

  showNotFound$: Observable<boolean>;

  iinEqualsControls = {};

  masks: Record<string, any> = {
    iin: 'iin',
    amount: 'amount',
    date: 'namedDate',
    iban: 'tengeAccount',
    firstName: 'firstName',
    lastName: 'lastName',
    middleName: 'middleName',
  };

  pageSize = 30;

  errors;

  @ViewChild('form') form: NgForm;
  @ViewChildren(EmployeeEditItemComponent)
  editItemList: QueryList<EmployeeEditItemComponent>;
  readonly total$ = new BehaviorSubject(0);
  readonly breakpoint: BOOTSTRAP_BREAKPOINTS;
  private subs = new Subscription();
  private lockOnAutocomplete: Array<keyof EmployeeItemModel>;
  private lockChildFields: Array<keyof EmployeeItemModel> = [
    'iin',
    'fio',
    'birthDate',
    'firstName',
    'middleName',
    'lastName',
  ];
  private lockParentFields: Array<keyof EmployeeItemModel> = [];
  private defaultGroupOpts: Record<string, any> = {};

  constructor(
    public modal: NgbActiveModal,
    private modalService: NgbModal,
    private employeeService: EmployeeService,
    private paymentsService: PaymentsService,
    private maskService: MaskedService,
    private fb: FormBuilder,
    private notify: NotifierService
  ) {}

  ngOnInit(): void {
    this.setInitialConfig();
    this.fetchEmployeeList();

    this.visibleList$ = this.combineVisibleEmployeesList();

    this.subs.add(
      this.employeesForm.valueChanges
        .pipe(throttleTime(200, undefined, { leading: true, trailing: true }))
        .subscribe((arr) => {
          this.initialEmployees = this.initialEmployees.map((employee) => {
            return {
              ...employee,
              ...arr.find((item) => item.id === employee.id),
            };
          });

          const total = arr.reduce((acc, item: EmployeeItemModel) => {
            let itemAmount: number = safeUnmaskAmount(item.amount);
            return acc + +itemAmount;
          }, 0);
          this.total$.next(total);
        })
    );

    if (this.initialEmployees) {
      // TODO check additional period by adding linked with iin
      // order is matter to find additional periods
      this.initialEmployees.reduce((dictionary, employeeModel) => {
        if (employeeModel.linkedWith && this.additionalPeriodEnabled) {
          const newItem = this.addItem(dictionary[employeeModel.linkedWith]);
          newItem.group.patchValue({
            ...employeeModel,
            period: isoDateToDateSafe(employeeModel.period),
          });
        } else {
          const newItem = this.addItem(employeeModel);
          dictionary[employeeModel.id] = newItem;
        }
        return dictionary;
      }, {} as Record<string, EmployeesItem>);
    }

    this.showNotFound$ = combineLatest([this.term$, this.visibleList$]).pipe(
      map(([term, list]) => {
        return term && list.length === 0;
      })
    );
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
    this.list.forEach((item) => {
      item.destroy();
    });
  }

  ngAfterViewInit() {
    // navigate to first invalid row on open for filled array
    if (this.initialEmployees.length) {
      this.subs.add(
        this.editItemList.changes
          .pipe(
            filter((list) => list.length > 1),
            take(1),
            // wait for async validation
            delay(300),
            delay(0)
          )
          .subscribe(() => {
            if (this.employeesForm.invalid) {
              this.searchAndNavigateToInvalidControl();
              this.emulateSubmit();
            }
          })
      );
    }
  }

  get list(): EmployeesItem[] {
    return this.listSubject$.getValue();
  }

  set list(newList: EmployeesItem[]) {
    this.listSubject$.next(newList);
  }

  get currentPage() {
    return this.currentPage$.getValue();
  }

  set currentPage(next: number) {
    this.currentPage$.next(next);
  }

  async openSavedEmployees(event: Event) {
    event.preventDefault();
    const modalRef = this.modalService.open(SavedEmployeeListComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_max-height',
      backdrop: 'static',
    });

    Object.assign(modalRef.componentInstance as SavedEmployeeListComponent, {
      birthDateRequired: this.birthDateRequired,
      ibanRequired: this.ibanRequired,
      paymentType: this.paymentType,
    });
    try {
      let selectedEmployees = await modalRef.result;
      if (selectedEmployees?.length) {
        selectedEmployees
          .map((item) => {
            return {
              ...item,
              amount: item.getBalance(this.paymentType)?.amount || '',
              iin: this.maskService.getUnmaskedAmount(item.iin),
              fio: item.fio?.trim() || '',
              firstName: item.firstName?.trim() || '',
              middleName: item.middleName?.trim() || '',
              lastName: item.lastName?.trim() || '',
              birthDate: isoDateToPeriodInputSafe(item.birthDate),
            };
          })
          .forEach((item) => {
            this.addItem(item);
            const group = this.employeesForm.at(
              this.employeesForm.length - 1
            ) as FormGroup;
            // Object.values(group.controls).forEach((control: AbstractControl) => {
            //   control.markAsDirty();
            // });
          });
      }
    } catch (error) {
      error && this.notify.notify('error', error);
    }
  }

  closeModal() {
    this.modal.close();
  }

  public addItem(origin?: EmployeesItem | EmployeeItemModel) {
    let newItem: EmployeesItem;
    // debugger;

    if (origin) {
      if (origin instanceof EmployeesItem) {
        // additional period for existing item in list
        newItem = new EmployeesItem();
        this.assignChildForAdditionalPeriod(origin, newItem);

        const newItemValue = new EmployeeItemModel(
          {
            ...newItem.linkedWith.group.value,
          },
          { additionalPeriod: newItem.linkedWith.group.value.id }
        );
        delete newItemValue.period;
        newItem.group = this.createEmployeeControl(newItemValue, {
          additionalPeriod: true,
          disabled: this.lockChildFields,
          item: newItem,
        }) as FormGroupTypeSafe<EmployeeItemModel>;
        // link child item with parent
        this.listenParentGroupInAdditionalPeriod(newItem.linkedWith, newItem);
        this.pushToList(newItem, origin);
      } else {
        // copy item from blueprint (add from dictionary)
        newItem = new EmployeesItem();
        newItem.blueprint = origin;
        newItem.group = this.createEmployeeControl(
          Object.assign(
            new EmployeeItemModel({
              ...origin,
              period: isoDateToDateSafe(origin.period),
            }),
            this.defaultGroupOpts
          ),
          {
            disabled: origin.linkedWith
              ? this.lockChildFields
              : this.lockParentFields,
            item: newItem,
          }
        ) as FormGroupTypeSafe<EmployeeItemModel>;

        if ((origin as EmployeeItemModel).linkedWith) {
          const link = this.list.find(
            (employeesItem) =>
              employeesItem.group.value.iin === origin.linkedWith
          );

          if (link) {
            newItem.linkedWith = link;
            link.children
              ? link.children.push(newItem)
              : (link.children = [newItem]);
            this.listenParentGroupInAdditionalPeriod(link, newItem);
          }
        }

        this.pushToList(newItem);
      }
    } else {
      // new item from scratch;
      newItem = new EmployeesItem();
      newItem.group = this.createEmployeeControl(
        new EmployeeItemModel(this.defaultGroupOpts),
        {
          disabled: this.lockParentFields,
          item: newItem,
        }
      ) as FormGroupTypeSafe<EmployeeItemModel>;
      this.pushToList(newItem);
    }

    return newItem;
  }
  /**
   * @param item
   */
  public removeItem(item: EmployeesItem) {
    const lockKeys = item.children
      ? this.lockParentFields
      : this.lockChildFields;
    const toDisable = item.children
      ? [item.group, ...item.children.map((child) => child.group)]
      : [item.group];
    toDisable.forEach((g) => g.disable());
    item.removing$.next(5);
    interval(1000)
      .pipe(
        takeUntil(
          merge(
            ...[
              // enable groups on cancel remove
              item.cancelRemove$.pipe(
                tap((cancel) => {
                  if (cancel) {
                    toDisable.forEach((g) => {
                      g.enable();
                      // preserve controls disabled state
                      lockKeys.forEach((lockKey) =>
                        g.controls[lockKey].disable()
                      );
                    });
                  }
                })
              ),
              item.destroy$,
            ]
          )
        ),
        tap((i) => {
          item.removing$.next(item.removing$.getValue() - 1);
        })
      )
      .subscribe({
        next: (i) => {
          if (i === 4) {
            this.finishItemRemove(item);
          }
        },
        complete: () => {
          !item.destroy$.closed && item.removing$.next(null);
        },
      });
  }

  public cancelRemove(item: EmployeesItem) {
    item.cancelRemove$.next(Date.now());
  }
  /**
   * Listen iin and trigger row autocomplete;
   * @param item
   */
  public onAutocomplete(item: EmployeesItem) {
    if (item.linkedWith) {
      return;
    }

    const group = item.group;
    const iin = group.value.iin;
    item.autocomplete$.next(iin);

    this.switchControlsStateOnAutoComplete(item, false);
    this.store.fetched$
      .pipe(
        filter((loaded) => loaded),
        switchMap(() => {
          const savedEmpl = this.store.getItemByKey(iin, 'iin');
          if (savedEmpl) {
            return of({
              ...savedEmpl,
              amount:
                savedEmpl.getBalance(this.paymentType).amount ||
                group.value.amount,
              birthDate: isoDateToPeriodInputSafe(savedEmpl.birthDate),
            });
          }
          return from(this.employeeService.getNameByIin(iin)).pipe(
            map(({ lastName, firstName, middleName }) => ({
              lastName,
              firstName,
              middleName,
            })),
            catchError(() => of(undefined))
          );
        }),
        takeUntil(merge(item.destroy$, item.autocomplete$.pipe(skip(1))))
      )
      .subscribe((result) => {
        if (result) {
          group.patchValue(result);
        }
        item.autocomplete$.next(null);
        this.lockOnAutocomplete.forEach((k) => {
          if (group.controls[k]) {
            group.controls[k].enable();
          }
        });
        this.switchControlsStateOnAutoComplete(item, true);
      });
  }

  onAddItem() {
    this.addItem();
    this.checkNewPageNavigation();
  }

  onAddPeriod(origin: EmployeesItem) {
    this.addItem(origin);
    this.checkNewPageNavigation();
  }

  onSubmit() {
    if (this.employeesForm.invalid) {
      this.emulateSubmit();
      this.searchAndNavigateToInvalidControl();
      return;
    }
    // pick disabledInputs for additional period
    const rawValue = this.list.reduce((list, item) => {
      if (item.group.enabled) {
        const model = getGroupRawValue(item.group) as EmployeeItemModel;
        // linkedWith iin to allow additional period with the same iin, as existing item (bypass uniq validation)
        if (item.linkedWith) {
          model.linkedWith = item.linkedWith.group.value.iin;
        }
        list.push(model);
      }
      return list;
    }, [] as Array<EmployeeItemModel>);
    this.modal.close(rawValue);
  }

  checkNewPageNavigation() {
    if (this.pageSize * this.currentPage < this.list.length) {
      ++this.currentPage;
    }
  }

  private combineVisibleEmployeesList() {
    let savedPageWithoutSearch = null;
    let searchInProgress = false;
    return combineLatest([
      this.listSubject$,
      this.term$.pipe(throttleTime(100, undefined, { trailing: true })),
      this.currentPage$.pipe(distinctUntilChanged()),
    ]).pipe(
      filter(([list, search, page]) => {
        // preserve current page before search
        if (!search && searchInProgress) {
          this.currentPage = savedPageWithoutSearch;
          searchInProgress = false;
          return true;
        } else if (!search) {
          savedPageWithoutSearch = page;
          searchInProgress = false;
          return true;
        } else if (search && !searchInProgress) {
          searchInProgress = true;
          if (this.currentPage === 1) {
            return true;
          }
          this.currentPage = 1;
          return false;
        }
        return true;
      }),
      map(([list, search, page]) => {
        const filtered = this.filterEmployees(list, search);

        if (!this.parentForm.value.commonPeriodEnabled) {
          filtered.find((item) => {
            const initial = this.initialEmployees.find((employee) => {
              return item.group.value.id === employee.id;
            });

            if (initial) {
              item.group.patchValue(initial);
            }
          });
        }

        this.collectionSize$.next(filtered.length);
        return filtered.slice((page - 1) * this.pageSize, page * this.pageSize);
      })
    );
  }

  private listenParentGroupInAdditionalPeriod(
    parent: EmployeesItem,
    child: EmployeesItem
  ) {
    parent.group.valueChanges
      .pipe(debounceTime(100), takeUntil(child.destroy$))
      .subscribe((v) => {
        const cleared = { ...v };
        delete cleared.amount;
        delete cleared.period;
        child.group.patchValue(cleared);
      });
  }

  private switchControlsStateOnAutoComplete(
    item: EmployeesItem,
    newState: boolean
  ) {
    const method = newState ? 'enable' : 'disable';
    this.lockOnAutocomplete.forEach((k) => {
      if (item.group.controls[k]) {
        item.group.controls[k][method]();
      }
    });
    if (item.children) {
      item.children.forEach((child) => {
        const controls = child.group.controls;
        ['amount', 'period'].forEach(
          (k) => controls[k] && controls[k][method]()
        );
      });
    }
  }

  private async fetchEmployeeList() {
    this.store.fetching = true;
    try {
      const list = await this.employeeService.getEmployees();
      this.store.setState(list);
    } catch (e) {}
    this.store.fetching = false;
    this.store.fetched = true;
  }

  private filterEmployees(list: EmployeesItem[], term = ''): EmployeesItem[] {
    if (!term) {
      return list;
    }
    const normalizedTerm = term.trim().toLocaleLowerCase();

    return list.filter((item) => {
      const { iin, fio } = item.group.controls;
      return [iin.value || '', fio.value || ''].some((controlValue) =>
        controlValue.toLocaleLowerCase().includes(normalizedTerm)
      );
    });
  }

  private createEmployeeControl(
    value: Partial<EmployeeItemModel> = {},
    options: {
      item?: EmployeesItem;
      disabled?: string[];
      additionalPeriod?: boolean;
      ibanRequired?: boolean;
    } = {}
  ) {
    const defaults = Object.assign(
      {
        id: generateUniqueId(),
        iin: '',
        amount: '',
        currency: CURRENCY.KZT,
        fio: '',
        firstName: '',
        middleName: '',
        lastName: '',
        iban: '',
        period: this.defaultGroupOpts.period,
      },
      value
    );

    const form = this.fb.group(
      {
        id: value.id || defaults.id,
        linkedWith: value.linkedWith,
        amount: [
          defaults.amount,
          [
            Validators.required,
            (control: AbstractControl) => {
              if (control.value) {
                const value = safeUnmaskAmount(control.value);
                return value > 0 ? null : { invalidAmount: true };
              }

              //   if (this.minAmount && value < this.minAmount) {
              //     return { minAmount: true };
              //   }
              //   if (this.maxAmount && value > this.maxAmount) {
              //     return { maxAmount: true };
              //   }
              return null;
            },
          ],
        ],
        iin: [
          {
            value: defaults.iin,
            disabled: options.additionalPeriod,
          },
          [Validators.required],
        ],
        currency: [defaults.currency],
        fio: [
          {
            value: defaults.fio,
            disabled: options.additionalPeriod,
          },
        ],
        firstName: [
          {
            value: defaults.firstName,
            disabled: options.additionalPeriod,
          },
          [Validators.required],
        ],
        lastName: [
          {
            value: defaults.lastName,
            disabled: options.additionalPeriod,
          },
          [Validators.required],
        ],
        middleName: [
          {
            value: defaults.middleName,
            disabled: options.additionalPeriod,
          },
        ],
      },
      []
    );

    if (this.birthDateRequired) {
      form.addControl(
        'birthDate',
        new FormControl(
          {
            value: defaults.birthDate,
            disabled: options.additionalPeriod,
          },
          [Validators.required]
        )
      );
    }

    if (this.periodRequired) {
      form.addControl(
        'period',
        new FormControl(defaults.period, [Validators.required])
      );
    }

    if (this.ibanRequired) {
      this.addIbanControl(defaults, form);
    }

    if (options.disabled) {
      options.disabled.forEach((key) => form.get(key).disable());
    }
    const { iin } = form.controls;
    if (iin.enabled) {
      this.setExtendedIinValidation(iin, form);
    }

    return form;
  }

  private addIbanControl(defaults, form) {
    const ibanControl = new FormControl(defaults.iban, [
      Validators.required,
      (control) => {
        if (control.value && control.value.length === 18) {
          const bankExist = this.paymentsService.bankExist(
            ibanPrefix(control.value, this.currency)
          );
          if (bankExist) {
            return { bankExist };
          }
        }
        return null;
      },
    ]);
    form.addControl('iban', ibanControl);
  }

  /**
   * Mutation
   * @param origin
   * @param newItem
   */
  private assignChildForAdditionalPeriod(
    origin: EmployeesItem,
    newItem: EmployeesItem
  ) {
    if (origin.linkedWith) {
      // additional period triggered for item which is additional period itself
      origin.linkedWith.children.push(newItem);
      // link with parent to link with origin for additional period and watch changes
      newItem.linkedWith = origin.linkedWith;
    } else {
      origin.children
        ? origin.children.push(newItem)
        : (origin.children = [newItem]);
      newItem.linkedWith = origin;
    }
    return newItem;
  }

  private finishItemRemove(item: EmployeesItem) {
    const listToUpdate = [...this.list];
    const toDestroy = [item];
    if (item.children) {
      toDestroy.push(...item.children);
    }
    const removeIndexes = toDestroy.reduce((acc, item) => {
      const index = this.list.findIndex((elem) => elem === item);
      acc.push(index);
      return acc;
    }, []);
    removeIndexes.sort((a, b) => b - a);
    removeIndexes.forEach((index) => {
      listToUpdate.splice(index, 1);
    });
    this.list = listToUpdate;
    toDestroy.reverse().forEach((item) => {
      item.destroy();
    });
  }

  private pushToList(newItem: EmployeesItem, relativeTo?: EmployeesItem) {
    if (relativeTo) {
      const formIndex = this.employeesForm.controls.findIndex(
        (group) => group === relativeTo.group
      );
      const listIndex = this.list.findIndex((item) => relativeTo === item);
      if (~formIndex && ~listIndex) {
        this.employeesForm.insert(formIndex + 1, newItem.group);
        const newList = [].concat(this.list);
        newList.splice(listIndex + 1, 0, newItem);
        this.list = newList;
      }
    } else {
      this.employeesForm.push(newItem.group);
      this.listSubject$.next([...this.list, newItem]);
    }
  }

  private searchAndNavigateToInvalidControl() {
    const invalidIndex = this.employeesForm.controls.findIndex(
      (control) => control.invalid
    );
    if (!~invalidIndex) {
      return;
    }
    const page = Math.ceil((invalidIndex + 1) / this.pageSize);
    if (page !== this.currentPage) {
      this.currentPage = page;
    }

    setTimeout(() => {
      const container = this.scrollContainer.nativeElement;
      const containerCoords = container.getBoundingClientRect();
      const containerHeight = container.offsetHeight;
      const currentScroll = container.scrollTop;

      const elemIndexOnPage = invalidIndex % this.pageSize;
      const firstErrorComponent = this.editItemList.toArray()[elemIndexOnPage];
      const elemCoords = (
        firstErrorComponent.element.nativeElement as HTMLElement
      ).getBoundingClientRect();

      let offset = containerCoords.top - elemCoords.top;
      if (offset <= 0 && Math.abs(offset) <= containerHeight - 30) {
        // elem in viewport;
        return;
      } else if (offset < 0) {
        // element lower bottom edge
        container.scrollTop = container.scrollTop + Math.abs(offset) + 30;
      } else {
        // element is higher than upper edge
        container.scrollTop = container.scrollTop - offset - 30;
      }
    }, 0);
  }

  private emulateSubmit() {
    (this.form as any).submitted = true;
    this.form.ngSubmit.emit();
    this.employeesForm.markAllAsTouched();
  }

  private updateUniqIinArrayValidity(forIin: string, skipGroup?: FormGroup) {
    let alreadyExistIins: AbstractControl[] = [];
    this.employeesForm.controls.forEach((groupToCheck) => {
      if (groupToCheck === skipGroup) {
        return false;
      }
      const { iin: iinToCheck, linkedWith } =
        groupToCheck.value as EmployeeItemModel;
      if (!linkedWith && forIin === iinToCheck) {
        alreadyExistIins.push((groupToCheck as FormGroup).controls.iin);
      }
    });

    if (alreadyExistIins.length > 1) {
      alreadyExistIins.forEach((control) => control.setErrors({ uniq: true }));
    } else if (alreadyExistIins.length) {
      const control = alreadyExistIins[0];
      delete this.iinEqualsControls[forIin];
      if (control.errors && control.errors['uniq']) {
        control.updateValueAndValidity();
      }
    }
  }

  private setInitialConfig() {
    this.errors = this.paymentsService.errors;
    const now = new Date();
    let defaultPeriod = new Date(
      now.getFullYear(),
      now.getMonth() - 1,
      0,
      0,
      0,
      1
    );

    const parentFormValue = this.parentForm.value;
    this.lockOnAutocomplete = [...autocompleteLock];
    if (this.periodRequired) {
      if (parentFormValue.commonPeriodEnabled) {
        // don't enable period field on autocomplete end
        defaultPeriod = parentFormValue.period || defaultPeriod;
        this.lockParentFields.push('period');
      } else {
        this.lockOnAutocomplete.push('period');
      }
      this.defaultGroupOpts = {
        period: defaultPeriod,
      };
    }
    if (!this.additionalPeriodEnabled) {
      this.lockChildFields = [];
      this.lockParentFields = [];
    }

    if (this.ibanRequired) {
      this.lockOnAutocomplete.push('iban');
    }

    Object.entries(this.masks).forEach(
      ([k, v]) => (this.masks[k] = this.maskService.getConfig(v))
    );

    this.term = new FormControl('');
    this.term$ = this.term.valueChanges.pipe(startWith(''));
  }

  private setExtendedIinValidation(iin: AbstractControl, form: FormGroup) {
    let prevInvalidIin = '';
    const formArray = this.employeesForm;
    iin.setAsyncValidators([
      (control: AbstractControl) => {
        return timer(300).pipe(
          switchMap(() => {
            const value = control.value;

            if (value && value.length === 12) {
              const alreadyExistIin = formArray.controls.some(
                (groupToCheck) => {
                  // current control from clojure, skip
                  if (groupToCheck === form) {
                    return false;
                  }
                  // avoid linked groups for disabled period
                  const { iin: iinToCheck, linkedWith } =
                    groupToCheck.value as EmployeeItemModel;
                  if (!linkedWith && value === iinToCheck) {
                    prevInvalidIin = value;
                    this.iinEqualsControls[value] = true;
                    return true;
                  }
                }
              );
              if (alreadyExistIin) {
                prevInvalidIin = value;
                this.iinEqualsControls[value];
                return of({ uniq: true });
              }
              if (prevInvalidIin && this.iinEqualsControls[prevInvalidIin]) {
                this.updateUniqIinArrayValidity(prevInvalidIin, form);
              }
              prevInvalidIin = '';

              return of(null);
            }
          }),
          take(1)
        );
      },
    ]);
  }
}
