import { Pipe, PipeTransform } from '@angular/core';
import { MaskedService } from '@shared/masked/masked.service';

@Pipe({
  name: 'formatIban',
  pure: true
})
export class FormatIbanPipe implements PipeTransform {

  transform(value: string, prefix: string = ''): string {
    return this.masService.maskPipe.transform(prefix + value, this.masService.getConfig('account').mask);
  }

  constructor(private masService: MaskedService) {}

}
