import { Injectable } from '@angular/core';
import { ApiService } from '@api/api.service';

import { filter } from 'rxjs/operators';

import { PAYMENT_TYPE } from '@shared/models';

import { IAuthorizedPerson } from '@company/company.model';
import { CompanyService } from '@company/company.service';

import {
  IAuthorityPayment,
  IPermission,
  PERMISSION,
} from '@authorities/models';

const PERMISSIONS_ALL = [
  PERMISSION.READ,
  PERMISSION.EDIT,
  PERMISSION.CREATE,
  PERMISSION.DELETE,
  PERMISSION.EXPORT,
  PERMISSION.SIGN,
  PERMISSION.REVOKE,
];

@Injectable({
  providedIn: 'root',
})
export class AuthorityPermissionsService {
  private cashedPermissions;

  constructor(
    private companyService: CompanyService,
    private apiService: ApiService
  ) {
    this.companyService.refresh$
      .pipe(filter(([, , isAuthorized]) => isAuthorized))
      .subscribe(() => {
        this.updatePermissions();
      });
  }

  grantPermission(person: IAuthorizedPerson, payment: IAuthorityPayment) {
    if (payment.permissionId) {
      return this.updatePermission(
        payment.permissionId,
        person.id,
        PERMISSIONS_ALL
      );
    } else {
      return this.createPermission(person, payment.type, PERMISSIONS_ALL);
    }
  }

  refusePermission(person: IAuthorizedPerson, payment: IAuthorityPayment) {
    if (payment.permissionId) {
      return this.updatePermission(payment.permissionId, person.id, []);
    } else {
      return this.createPermission(person, payment.type, []);
    }
  }

  async createPermission(
    person: IAuthorizedPerson,
    paymentType: PAYMENT_TYPE,
    permissions: PERMISSION[]
  ) {
    const url = this.mapUrl(person.id);
    const body = {
      paymentType,
      iin: person.iin,
      bin: this.companyService.currentCompany.bin,
      permissions,
    };

    const result = await this.apiService.post<IPermission>(url, body);

    if (!this.cashedPermissions) {
      await this.getPermissions();
    }

    this.cashedPermissions.push({
      ...body,
      id: result.id,
    });

    return result;
  }

  async updatePermission(
    permissionId: string,
    authorityId: string,
    permissions: PERMISSION[]
  ) {
    const url = `${this.mapUrl(authorityId)}/${permissionId}`;
    const result = await this.apiService.patch<IPermission>(url, {
      permissions,
    });

    const permissionIndex = this.cashedPermissions.findIndex(
      (permission) => permission.id === permissionId
    );

    if (this.cashedPermissions && permissionIndex > -1) {
      this.cashedPermissions[permissionIndex].permissions = permissions;
    }

    return result;
  }

  async getPermissions(
    authorityId: string = this.companyService.currentAuthority?.id
  ) {
    try {
      const url = this.mapUrl(authorityId);
      const permissions = await this.apiService.get<IPermission[]>(url);

      return permissions || [];
    } catch (error) {
      return [];
    }
  }

  async updatePermissions() {
    try {
      this.cashedPermissions = await this.getPermissions();
    } catch (error) {
      console.log(error);
      this.cashedPermissions = [];
    }
  }

  async isRestricted(paymentType: PAYMENT_TYPE) {
    if (!this.cashedPermissions) {
      this.cashedPermissions = await this.getPermissions();
    }

    const permissions = this.cashedPermissions;
    const paymentPermission = permissions.find(
      (permission) => permission.paymentType === paymentType
    );

    return paymentPermission
      ? !(
          paymentPermission.permissions.includes(PERMISSION.CREATE) ||
          paymentPermission.permissions.includes(PERMISSION.EDIT)
        )
      : false;
  }

  private mapUrl(authorityId: string) {
    return `access-control/companies/${this.companyService.currentId}/authorities/${authorityId}/payment-permissions`;
  }
}
