import {
  AfterViewInit,
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
} from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';

import { Subscription } from 'rxjs';

import { Dictionary } from 'lodash';

import { ERRORS, IError } from '@api/index';

import { SmsInputComponent } from '@shared/input';
import { CONFIRM_SIGNATURE_TYPE } from '../confirm-sms-modal/confirm-sms-modal.component';
import {
  ISmsToken,
  SMS_CODE_LENGTH,
  SMS_ERROR_TYPES,
  SMS_TABS,
  SMS_TIMER_COUNTDOWN,
} from '@shared/models';
import { SmsService } from '@shared/services';
import { IOtp } from '@shared/models/otp';

@Component({
  selector: 'j-otp',
  templateUrl: './otp.component.html',
  styleUrls: ['./otp.component.scss'],
})
export class OtpComponent implements OnInit, OnDestroy, AfterViewInit {
  smsToken: ISmsToken;

  @Input() otp: IOtp;
  @Input() phone: string;
  @Input() params?: Dictionary<any>;
  @Input() isAuthAction = false;

  @Output() submitted = new EventEmitter<ISmsToken>();
  @Output() loaded = new EventEmitter();
  @Output() failed = new EventEmitter();
  @Output() smsAuthRequested = new EventEmitter<string>();

  isLoading: boolean;
  isReady = false;
  isSubmitted: boolean;

  authTabs = SMS_TABS;
  currentTab = SMS_TABS.FORM;

  hasError: boolean;

  otpForm: FormGroup;
  otpCodeLength = SMS_CODE_LENGTH;

  errorText;

  timer: any;
  timerValue: number;
  timerMaxValue = SMS_TIMER_COUNTDOWN;
  isTimerRunning: boolean;

  blockedTimer: any;
  blockedTimerValue: number;
  isBlockedTimerRunning: boolean;

  @ViewChild('smsInput') smsInput: SmsInputComponent;

  errorTypes = SMS_ERROR_TYPES;
  subscription: Subscription = new Subscription();
  errorTranslateParams = {
    triesLeft: 0,
  };

  constructor(private smsService: SmsService) {
    this.otpForm = new FormGroup({
      otpCode: new FormControl('', Validators.required),
    });

    const formSubscription = this.otpForm.valueChanges.subscribe((value) => {
      this.isSubmitted = false;

      if (value.otpCode.length === this.otpCodeLength) {
        this.onSubmit();
      }
    });

    this.subscription.add(formSubscription);
  }

  ngOnInit(): void {
    this.requestOtp();
  }

  ngAfterViewInit() {
    // this.focus();
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  setTab(tab: SMS_TABS) {
    this.currentTab = tab;
  }

  focus() {
    this.smsInput.focus();
  }

  async requestOtp() {
    this.isLoading = true;
    this.errorText = '';

    try {
      this.smsToken = await this.smsService.requestOtp(
        this.phone,
        this.params,
        this.otp
      );
      this.setTab(this.authTabs.FORM);
      this.startTimer();
    } catch (error) {
      console.log(error);
      this.mapRequestError(error);
    } finally {
      if (!this.isReady) {
        this.isReady = true;
      }
      this.isLoading = false;
      this.focus();
      this.loaded.emit();
    }
  }

  async onSubmit() {
    this.isSubmitted = true;
    this.errorText = '';
    this.hasError = false;

    if (this.otpForm.valid && !this.isLoading) {
      this.isLoading = true;
      this.smsToken.code = this.f.otpCode.value;
      try {
        this.smsToken = await this.smsService.submitOtp(
          this.smsToken,
          this.params,
          this.otp
        );
        this.submitted.emit(this.smsToken);
      } catch (error) {
        this.hasError = true;
        this.smsInput.clear();
        this.mapSubmitError(error);
        this.smsInput.triggerHelperAnimation();
        this.isLoading = false;
      }
    }
  }

  onSmsClicked() {
    this.smsAuthRequested.emit(CONFIRM_SIGNATURE_TYPE.SMS);
  }

  get translateParam() {
    return {
      timerValue: this.timerValue,
    };
  }

  get f() {
    return this.otpForm.controls;
  }

  private mapRequestError(error: IError) {
    switch (error.code) {
      case ERRORS.ARGUMENT_MISSING:
        this.failed.emit();
        break;
      case ERRORS.RESOURCE_BLOCKED:
        if (error.data && error.data.millisecondsLeft) {
          this.startBlockedTimer(error.data.millisecondsLeft);
        }
        this.setTab(this.authTabs.FORBIDDEN_BLOCKED);
        break;
      default:
        this.setTab(this.authTabs.ERRORED);
        this.errorText = error.description;
        break;
    }
  }

  private mapSubmitError(error: IError) {
    switch (error.code) {
      case ERRORS.INVALID_CODE:
        let triesLeft;
        if (error.data && error.data.triesLeft) {
          triesLeft = error.data.triesLeft;
          this.errorTranslateParams = {
            triesLeft: triesLeft,
          };
          switch (triesLeft % 20) {
            case 1:
              this.errorText = `ERRORS.INVALID_CODE.ONE`;
              break;
            case 2:
            case 3:
            case 4:
              this.errorText = `ERRORS.INVALID_CODE.FEW`;
              break;
            default:
              this.errorText = `ERRORS.INVALID_CODE.OTHER`;
          }
        } else {
          this.setTab(this.authTabs.FORBIDDEN);
        }
        break;
      case ERRORS.INTERNAL_SERVER_ERROR:
        this.errorText = ERRORS.UNKNOWN;
        break;
      case ERRORS.OTP_NOT_FOUND:
        this.errorText = `ERRORS.OTP_NOT_FOUND_AUTH`;
        break;
      case ERRORS.RESOURCE_BLOCKED:
        if (error.data && error.data.millisecondsLeft) {
          const countdown = Math.ceil(error.data.millisecondsLeft / 1000);
          if (this.isTimerRunning) {
            this.timerValue = countdown;
          } else {
            this.startTimer(countdown);
          }
        }
        this.setTab(this.authTabs.FORBIDDEN);
        break;
      default:
        this.errorText = error.description;
        break;
    }
  }

  private startBlockedTimer(millisecondsLeft: number) {
    this.blockedTimerValue = Math.ceil(millisecondsLeft / 1000);
    this.isBlockedTimerRunning = true;

    this.blockedTimer = setInterval(() => {
      this.blockedTimerValue--;

      if (this.blockedTimerValue === 0) {
        this.stopBlockedTimer();
      }
    }, 1000);
  }

  private stopBlockedTimer() {
    clearInterval(this.blockedTimer);
    this.isBlockedTimerRunning = false;
    this.blockedTimerValue = null;
  }

  private startTimer(timerMaxValue = this.timerMaxValue) {
    this.isTimerRunning = true;
    this.timerValue = timerMaxValue;

    this.timer = setInterval(() => {
      this.timerValue--;

      if (this.timerValue === 0) {
        this.stopTimer();
      }
    }, 1000);
  }

  private stopTimer() {
    clearInterval(this.timer);
    this.setTab(this.authTabs.EXPIRED);
    this.isTimerRunning = false;
  }
}
