import {
  OnInit,
  Component,
  EventEmitter,
  Output,
  ViewChild,
  Input,
  forwardRef,
  OnChanges,
  SimpleChanges,
  OnDestroy,
  ViewEncapsulation,
} from '@angular/core';

import {
  FormControl,
  NG_VALUE_ACCESSOR,
  ControlValueAccessor,
} from '@angular/forms';
import { generateUniqueId, ngbStructToISO } from '@utils/index';
import { Subscription, Subject } from 'rxjs';
import {
  NgbInputDatepicker,
  NgbDateStruct,
  NgbDate,
} from '@ng-bootstrap/ng-bootstrap';
import { coerceElement } from '@shared/form-field/utils';
import { JFormFieldControl } from '@shared/form-field/form-field-control';
@Component({
  selector: 'j-date-picker',
  templateUrl: './date-picker.component.html',
  styleUrls: ['./date-picker.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => JDatePickerComponent),
      multi: true,
    },
    // {provide: NgbDateAdapter, useClass: NgbDateNativeAdapter},
    // { provide: NgbDatepickerI18n, useClass: DatepickerFormatterService },
  ],
  host: {
    class: 'j-form-field j-form-field-datepicker',
    '[class.j-form-field-open]': 'isOpen',
    '[class.j-form-field-should-float]': 'shouldFloat',
    '[class.j-date-picker-invalid]': 'hasError',
    '[class.j-date-picker-auto]': 'allowAuto',
    '[class.j-date-picker-disabled]': 'isDisabled',
    '[class.j-focused]': 'focused',
  },
})
export class JDatePickerComponent
  extends JFormFieldControl<NgbDateStruct>
  implements OnInit, ControlValueAccessor, OnChanges, OnDestroy
{
  @Input() autofocus: boolean;
  @Input() datesEnabled?: string[];
  @Input() id = generateUniqueId();
  @Input() formControl: FormControl;
  @Input() label = '';
  @Input() name = '';
  @Input() errorText = null;

  @Input() errorMessages?: {
    error: string;
    message: string;
  }[];
  @Input() helperText = null;
  @Input() value = null;

  @Input() allowAuto: boolean;

  @Input() minDate;
  @Input() maxDate;

  @Input() formatValue: boolean;
  @Output() changeEvent = new EventEmitter<NgbDateStruct>();

  @ViewChild('inputControl') inputControl: HTMLInputElement;
  @ViewChild(NgbInputDatepicker) datepicker: NgbInputDatepicker;
  controlType = 'date-picker';
  focused = false;
  isDisabled = false;
  helperClasses = '';
  isOpen = false;
  empty = true;
  placeholder = '';
  shouldLabelFloat = false;

  readonly stateChanges = new Subject<void>();

  protected readonly subs = new Subscription();

  constructor() {
    super();
  }

  get isFilled() {
    return !!this.formControl.value;
  }

  get hasError() {
    return (
      this.formControl.invalid && this.formControl.touched && !this.focused
    );
  }

  get shouldFloat() {
    return this.allowAuto || this.isOpen || this.focused;
  }

  get isClearable() {
    return this.formControl.value !== null;
  }

  onClear() {
    this.formControl.setValue(null);
  }

  ngOnInit(): void {
    this.subs.add(
      this.formControl.valueChanges.subscribe((value) => {
        this.changeEvent.emit(value);
      })
    );
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  ngOnChanges(changes: SimpleChanges) {}

  onChange(_: any) {}

  onContainerClick(e?: MouseEvent) {
    // if (this.isOpen && this.focused) {
    //   this.datepicker.close();
    //   this.isOpen = false;
    // }
    // else {
    //   this.datepicker.close();
    //   this.isOpen = false;
    // }
  }

  setDescribedByIds(ids: string[]) {}

  writeValue(value: any) {
    this.value = value;
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

  registerOnTouched() {}

  onFocus() {
    this.focused = true;
    if (!this.isOpen) {
      this.datepicker.open();
      this.isOpen = true;
    }
  }

  onBlur() {
    // this.focused = false;
    // this.blur.emit();
  }

  onToggle(e?: MouseEvent) {
    e && e.stopPropagation();
    // only close event is emitted by datepicker
    if (!this.isOpen) {
      this.datepicker.open();
      this.isOpen = true;
    } else {
      this.datepicker.close();
      this.isOpen = false;
    }
  }

  onClosed() {
    setTimeout(() => {
      this.isOpen = false;
      this.focused = false;
      coerceElement(this.inputControl).blur();
    }, 0);
  }

  onDateSelection(event) {
    this.datepicker.close();
    this.changeEvent.next(event);
  }

  focus() {
    coerceElement(this.inputControl).focus();
  }

  clear() {}

  markDisabled = (
    date: NgbDate,
    current?: { year: number; month: number }
  ): boolean => {
    if (this.datesEnabled) {
      const currentDate = ngbStructToISO(date);
      return !this.datesEnabled.some((day) => currentDate === day);
    }
    return false;
  };
}
