import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { environment } from '@environments/environment';

import { IsNotAuthenticatedGuard } from '@shared/guards/is-not-authenticated.guard';
import { IsAuthenticatedGuard } from '@shared/guards/is-authenticated.guard';

import { homeRoutes } from './home/home-routes';

const routes: Routes = [
  {
    path: 'jmart-cabinet',
    loadChildren: () => import('./jmart-cabinet/jmart-cabinet.module').then((m) => m.JmartCabinetModule),
  },
  {
    path: 'auth',
    canActivate: [IsNotAuthenticatedGuard],
    loadChildren: () => import('./auth/auth.module').then((m) => m.AuthModule),
  },
  {
    canActivate: [IsAuthenticatedGuard],
    path: homeRoutes.root,
    loadChildren: () => import('./home/home.module').then((m) => m.HomeModule),
  },
  {
    path: '**',
    redirectTo: homeRoutes.root,
  },
];

if (!environment.production) {
  routes.unshift({
    path: 'theme',
    loadChildren: () =>
      import('./theme/theme.module').then((m) => m.ThemeModule),
  });
}

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      //  enableTracing: !environment.production
    }),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {}
