import {
  Directive,
  HostListener,
  ElementRef,
  Input,
  AfterContentInit,
  Optional,
  Self,
  SimpleChanges,
  SimpleChange,
  OnInit,
  OnDestroy,
} from '@angular/core';
import { MaskDirective } from 'ngx-mask';
import {
  NgModel,
  NgControl,
  FormControlDirective,
  FormControl,
} from '@angular/forms';
import { first, debounceTime, filter, skip } from 'rxjs/operators';
import { Subscription } from 'rxjs';

/**
 * @description dynamic prefix fo ngx-mask
 * fixed issue with changedAfterChecked error in employee form with dynamic prefix in FormArray
 */
@Directive({
  selector: '[jDynamicPrefix]',
})
export class DynamicPrefixDirective implements OnInit, OnDestroy {
  @Input() jDynamicPrefix: string;
  @Input() with?: string;
  @HostListener('focus', ['$event']) onFocus(event: any) {
    // fixed issue with changedAfterChecked error in employee form with dynamic prefix in FormArray
    if (this.controlDirective && !this.maskDirective.prefix) {
      this.updatePrefix();
    }
  }
  // hide prefix on blur if needed
  // @HostListener('blur', ['$event']) onBlue(event: any) {
  //   if (!this.controlDirective.control.value) {
  //     this.controlDirective.control.markAsUntouched();
  //     // setTimeout(() => {
  //     //   this.maskDirective.ngOnChanges({
  //     //     prefix: {
  //     //       previousValue: this.maskDirective.prefix,
  //     //       currentValue:  this.maskDirective.prefix = '',
  //     //       firstChange: false,
  //     //       isFirstChange: () => false
  //     //     },
  //     //   });
  //     // }, 0);
  //   }
  // }

  private subscription = new Subscription();

  constructor(
    private elem: ElementRef<HTMLInputElement>,
    @Optional() @Self() private controlDirective: FormControlDirective,
    @Optional() @Self() private maskDirective: MaskDirective
  ) {}

  ngOnInit() {
    if (this.controlDirective) {
      if (this.controlDirective.value && !this.maskDirective.prefix) {
        this.updatePrefix();
      }
      this.subscription.add(
        this.controlDirective.valueChanges
          .pipe(
            skip(1),
            debounceTime(300)
            // filter((v) => !!v)
          )
          .subscribe((v: string) => {
            if (
              !this.maskDirective.prefix ||
              v.includes(this.maskDirective.prefix, 0)
            ) {
              this.updatePrefix();
            } else if (!v && this.controlDirective.control.touched) {
              // Handle input clear (direct HTmLInput manipulation)
              this.updatePrefix();
            }
          })
      );
    }
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  updatePrefix() {
    setTimeout(() => {
      this.maskDirective.ngOnChanges({
        prefix: new SimpleChange(
          this.maskDirective.prefix,
          (this.maskDirective.prefix = this.jDynamicPrefix),
          false
        ),
      });
      // patched suffix is making control dirty, which violates rule for error highilght - touched, but not changed
      this.controlDirective.control.markAsPristine();
    }, 0);
  }
}
