import {
  Component,
  forwardRef,
  Input,
  HostListener,
  ChangeDetectorRef,
  OnChanges,
  SimpleChanges,
  ChangeDetectionStrategy,
} from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';

import { MaskPipe } from 'ngx-mask';

import { MaskedService } from '@shared/masked/masked.service';
import { CURRENCY } from '@shared/currency/currency.model';
import { CurSymbolPipe } from '@shared/pipes/cur-symbol.pipe';

import { JInputComponent, InputType } from '../input.component';

@Component({
  selector: 'j-amount-input',
  templateUrl: './amount-input.component.html',
  styleUrls: ['./amount-input.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => AmountInputComponent),
      multi: true,
    },
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AmountInputComponent extends JInputComponent {
  @Input() label = 'SHARED.AMOUNT';
  @Input() minAmount = 0;

  @Input() currency: CURRENCY = CURRENCY.KZT;

  @Input() type = InputType.text;
  maskType = 'amountSeparated';

  regexStr = '^[0-9 ,]*$';

  isMinAmountShown = false;
  currencySymbol: string;

  constructor(
    public maskedService: MaskedService,
    private maskPipe: MaskPipe,
    private cdr: ChangeDetectorRef,
    private curSymbolPipe: CurSymbolPipe
  ) {
    super(maskedService);
  }

  @HostListener('keypress', ['$event']) onKeyPress(event) {
    return new RegExp(this.regexStr).test(event.key);
  }

  onPaste(event: ClipboardEvent) {
    event.preventDefault();

    const clipboardData = event.clipboardData || (window as any).clipboardData;
    const pastedText = clipboardData.getData('text');
    const toPasteRaw = this.maskedService.getMaskedAmount(pastedText);

    this.formControl.setValue(toPasteRaw);
  }

  ngOnInit() {
    if (typeof this.formControl.value != 'string') {
      this.formControl.setValue(`${this.formControl.value}`);
    }

    this.currencySymbol = this.curSymbolPipe.transform(this.currency);
    this.maskConfig = this.maskedService.getConfig(
      this.maskType,
      ` ${this.currencySymbol}`
    );

    this.cdr.detectChanges();

    const formSubscription = this.formControl.valueChanges.subscribe(
      (value) => {
        if (this.unmaskedValue) {
          this.valueChanged.emit(this.unmaskedValue);
        }
      }
    );

    this.subscription.add(formSubscription);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.currency) {
      const prevPristine = this.formControl.pristine;
      this.currencySymbol = this.curSymbolPipe.transform(this.currency);

      if (prevPristine) {
        this.formControl.markAsPristine();
      }

      this.cdr.detectChanges();
    } else {
      this.cdr.detectChanges();
    }
  }

  onBackspaceClick(event) {
    const selectionMaxIndex =
      this.getMaskedAmount(this.formControl.value).length - 2;

    if (
      event.target.selectionEnd >= selectionMaxIndex &&
      event.target.selectionEnd - event.target.selectionStart <= 2
    ) {
      this.formControl.setValue(
        this.formControl.value?.substring(0, this.formControl.value.length - 1)
      );
    }
  }

  onFocus() {
    this.isFocused = true;
    this.isMinAmountShown = true;
    this.focused.emit();
    this.cdr.detectChanges();
  }

  onBlur() {
    this.isFocused = false;
    this.blurred.emit();

    setTimeout(() => {
      if (!this.isFocused) {
        this.isMinAmountShown = false;
      }

      this.cdr.detectChanges();
    }, 200);
  }

  setCursorToEnd(event) {
    // const selectionMaxIndex = this.getMaskedAmount(this.formControl.value)
    //   .length;
    // if (event.target.selectionEnd > selectionMaxIndex) {
    //   this.input.nativeElement.setSelectionRange(
    //     event.target.selectionStart,
    //     selectionMaxIndex
    //   );
    // }
  }

  setMinAmount(event: Event) {
    event.preventDefault();
    event.stopPropagation();
    this.formControl.setValue(`${this.minAmount}`);
    this.cdr.detectChanges();
  }

  get isFilled() {
    return `${this.formControl.value}`?.length > 0;
  }

  get isClearEnabled() {
    return this.unmaskedValue && this.isMinAmountShown;
  }

  get unmaskedValue() {
    return this.maskedService.getNumberedAmount(this.formControl.value);
  }

  get maskedMinAmount() {
    return this.getMaskedAmount(this.minAmount);
  }

  private getMaskedAmount(amount) {
    return `${this.maskedService.formatAmount(amount)} ${this.currencySymbol}`;
  }
}
