import {
  Component,
  forwardRef,
  Input,
  ViewChild,
  SimpleChanges,
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { DatePipe } from '@angular/common';

import * as moment from 'moment';

import {
  NgbDate,
  NgbDatepicker,
  NgbDatepickerI18n,
  NgbDropdown,
} from '@ng-bootstrap/ng-bootstrap';

import { CustomDatepickerI18n } from '@core/components';
import { MaskedService } from '@shared/masked/masked.service';

import { PERIOD_FORMAT_INPUT } from '@filter/filter.model';

import { JInputComponent } from '../input.component';

@Component({
  selector: 'j-date-input',
  templateUrl: './date-input.component.html',
  styleUrls: ['./date-input.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => DateInputComponent),
      multi: true,
    },
    { provide: NgbDatepickerI18n, useClass: CustomDatepickerI18n },
  ],
})
export class DateInputComponent
  extends JInputComponent
  implements ControlValueAccessor
{
  @Input() name = 'date';
  @Input() label = 'SHARED.DATE';
  @Input() maskType = 'date';

  @Input() isErrorsHidden: boolean = false;
  @Input() isCalendarShown: boolean = true;

  @Input() minDate = new Date(1991, 1, 1);
  @Input() maxDate = new Date();

  datepickerModel;

  @ViewChild('datepicker') datepicker: NgbDatepicker;
  @ViewChild('dropdown') dropdown: NgbDropdown;

  constructor(
    public i18n: NgbDatepickerI18n,
    public maskedService: MaskedService,
    private datePipe: DatePipe
  ) {
    super(maskedService);
  }

  ngOnInit() {
    this.maskConfig = this.maskedService.getConfig(this.maskType);
  }

  ngOnChanges(changes: SimpleChanges) {}

  onDateSelect(pickedDate: NgbDate) {
    const newDate = new Date(
      pickedDate.year,
      pickedDate.month - 1,
      pickedDate.day
    );
    const newValue = this.datePipe.transform(newDate, `dd.MM.yyyy`);
    this.formControl.setValue(newValue);
    this.hideDatepicker();
  }

  onKeyUp() {
    this.errorText = '';

    if (this.formControl.errors?.max) {
      this.errorText = 'ERRORS.INVALID_INPUT';
    }

    if (!this.formControl.valid) {
      return;
    }

    const [day, month, year] = this.formControl.value.split('.');

    if (this.isDateValid()) {
      this.datepicker?.navigateTo({
        day: parseInt(day),
        month: parseInt(month),
        year: parseInt(year),
      });

      this.datepickerModel = {
        day: parseInt(day),
        month: parseInt(month),
        year: parseInt(year),
      };
    } else {
      this.errorText = 'ERRORS.INVALID_INPUT';
    }
  }

  showDatepicker() {
    this.dropdown?.open();
  }

  hideDatepicker() {
    this.dropdown?.close();
  }

  onFocus() {
    super.onFocus();
    this.showDatepicker();
  }

  get ngbMinDate() {
    const mapped = {
      day: this.minDate.getDate(),
      month: this.minDate.getMonth() + 1,
      year: this.minDate.getFullYear(),
    };

    return mapped;
  }

  get ngbMaxDate() {
    const mapped = {
      day: this.maxDate.getDate(),
      month: this.maxDate.getMonth() + 1,
      year: this.maxDate.getFullYear(),
    };

    return mapped;
  }

  setDisabledState(state: boolean) {
    this.isDisabled = state;
  }

  private isDateValid() {
    const newDate = moment(this.formControl.value, PERIOD_FORMAT_INPUT);
    const maxDate = moment(this.maxDate);
    const minDate = moment(this.minDate);

    return (
      newDate.isValid() &&
      newDate.isSameOrBefore(maxDate) &&
      newDate.isSameOrAfter(minDate)
    );
  }
}
