import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'j-restricted-placeholder',
  templateUrl: './restricted-placeholder.component.html',
  styleUrls: ['./restricted-placeholder.component.scss'],
})
export class RestrictedPlaceholderComponent {
  @Input() label: string = 'SHARED.RESTRICTED_PLACEHOLDER';
  @Input() isRetryable: boolean = false;
  @Input() isLoading: boolean = false;

  @Output() retried = new EventEmitter();

  constructor() {}

  onRetry() {
    this.retried.emit();
  }
}
