import { RootRoute, makeChildren } from '../utils';
import { PaymentsRoutes, paymentsRoutes } from '../payments/payments-routes';

export class HomeRoutes implements RootRoute {
  root = '';
  dashboard = 'dashboard';
  payments: PaymentsRoutes;
  transfers = 'transfers';
  history = 'history';
  settings = 'settings';
  conversion = 'conversion';
  directory = 'directory';
  contracts = 'contracts';
  letters = 'letters';
  authorities = 'authorities';
  extraSettings = 'extraSettings';
  jmart = 'jmart';

  constructor() {
    this.payments = makeChildren('/' + paymentsRoutes.root, paymentsRoutes);
  }
}
export const homeRoutes = new HomeRoutes();
