import { Injectable } from '@angular/core';
import { EntitiesStore, SingleEntityStore, ISO_8061 } from '@utils/index';
import { ApiService } from '@api/index';
import {
  KNP,
  KBK,
  UGD,
  Bank,
  PAYMENT_TYPE,
  ICountryCode,
  ICountryCodeResponseObj,
  DictionaryResponse,
} from '@shared/models';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import * as moment from 'moment';
import { Dictionary } from 'lodash';
import { TranslateService } from '@ngx-translate/core';
@Injectable({
  providedIn: 'root',
})
export class DictionariesService {
  KNPDictionary: EntitiesStore<KNP>;
  UGDictionary: EntitiesStore<UGD>;
  KBKDictionary: EntitiesStore<KBK>;
  bankDictionary: EntitiesStore<Bank>;
  workingDays: SingleEntityStore<string[]>;
  countryCodes: ICountryCode[];

  constructor(private api: ApiService, private http: HttpClient, private translateService: TranslateService) {
    this.KNPDictionary = new EntitiesStore([]);
    this.KBKDictionary = new EntitiesStore([]);
    this.UGDictionary = new EntitiesStore([]);
    this.bankDictionary = new EntitiesStore([]);
    this.workingDays = new SingleEntityStore([]);

    this.fetchUGD$();
    this.fetchKNP$();
    this.fetchKBK$();
    this.fetchBanks();
    this.fetchWorkingDays();
  }

  fetchUGD$() {
    this.UGDictionary.fetching = true;

    return this.api.get<UGD[]>('/dictionaries/general?pageSize=5000&type=TAX_BENEFICIAR&sortBy=CODE').then(
      (v) => {
        this.UGDictionary.setState(
          (v as any[]).map((item) => ({
            ...item,
            id: item.value.code,
            code: item.value.code,
            bin: item.additional.map.bin,
            name: item.value.name,
            label: item.value.code + ' - ' + item.value.name,
          }))
        );
        this.UGDictionary.fetched = true;
      }
    ).finally(() => {
      this.UGDictionary.fetching = false;
    });
  }

  fetchKNP$() {
    this.KNPDictionary.fetching = true;

    return this.api.get<DictionaryResponse[]>('/dictionaries/general?pageSize=10000&type=KNP&sortBy=CODE').then(
      (v) => {
        this.KNPDictionary.setState(
          (v as any[]).map((item) => ({
            ...item,
            id: item.value.code,
            name: item.value.name,
            type: {
              TAX: item.additional.map.paymentTypes?.includes('TAX'),
              PENSION: item.additional.map.paymentTypes?.includes('PENSION'),
              SOCIAL: item.additional.map.paymentTypes?.includes('SOCIAL'),
              MEDICAL: item.additional.map.paymentTypes?.includes('MEDICAL'),
              REGULAR_PAYMENT_ORDER: item.additional.map.paymentTypes?.includes('REGULAR_PAYMENT_ORDER'),
              FOREIGN: item.additional.map.paymentTypes?.includes('FOREIGN'),
            },
            code: item.value.code,
            favourite: item.additional.map.isFavourite,
            label: `${item.value.code} ${item.value.name}`
          })));

        this.KNPDictionary.fetching = false;
        this.KNPDictionary.fetched = true;
      }
    );
  }

  fetchKBK$() {
    this.KBKDictionary.fetching = true;
    this.KBKDictionary.items = [];
    return this.api.get<DictionaryResponse[]>('/dictionaries/general?pageSize=10000&type=KBK&sortBy=CODE').then(
      (v) => {
        this.KBKDictionary.setState(
          (v as any[]).map((item) => ({
            ...item,
            id: item.value.code,
            name: item.value.name,
            type: { TAX: item.additional.map.paymentType?.includes('TAX') },
            code: item.value.code,
            label: `${item.value.code} ${item.value.name}`
          })));
        this.KBKDictionary.fetching = false;
        this.KBKDictionary.fetched = true;
      }
    );
  }

  fetchCountryCodesDictionary(): Promise<ICountryCodeResponseObj[]> {
    return this.api
      .get<ICountryCodeResponseObj[]>('/dictionaries/general?pageSize=300&type=PHONE_COUNTRY_CODES&sortBy=name');
  }

  async fetchCountryCodes(): Promise<ICountryCode[]> {
    return await this.http
      .get<ICountryCode[]>('/assets/data/country-codes.json').pipe(
        map(res => {
          return res.map(el => ({
            ...el,
            country: this.translateService.instant(`COUNTRIES.${el.country}`),
            id: el.country
          }));
        })
      ).toPromise();
  }

  fetchBanks() {
    this.bankDictionary.fetching = true;
    return this.api
      .get<Bank[]>('/dictionaries/banks')
      .then((banks) => this.bankDictionary.setState(banks))
      .finally(() => {
        this.bankDictionary.fetching = false;
        this.bankDictionary.fetched = true;
      });
  }

  fetchWorkingDays() {
    this.workingDays.fetching = true;
    return this.api
      .get<string[]>('/calendar/working-days')
      .then((days) => (this.workingDays.entity = days))
      .finally(() => {
        this.workingDays.fetching = false;
        this.workingDays.fetched = true;
      });
  }
  /**
   *
   * @param from date in IS0 8061
   * @param to date in IS0 8061
   */
  selectDaysOff(from: string, to: string): string[] {
    const start = moment(from, ISO_8061);
    const end = moment(to, ISO_8061);

    let daysInInterval = start.diff(end, 'days');

    const workingDays = this.workingDays.entity;
    const daysOff: string[] = [];

    const dict = this.selectDaysTimeline(from, to, workingDays).reduce(
      (acc, day) => {
        acc[day] = true;
        return acc;
      },
      {} as Dictionary<boolean>
    );

    const pointer = moment(end);
    let pointerFormat = '';
    while (daysInInterval) {
      pointerFormat = pointer.format(ISO_8061);
      if (!dict[pointerFormat]) {
        daysOff.push(pointerFormat);
      }
      daysInInterval--;
      pointer.add(-1, 'd');
    }
    return daysOff;
  }
  /**
   * slice working days from store
   * if days edge be triggered fallback will be applied (mark day as working from Mon to Fri)
   * @param from date in IS0 8061
   * @param to date in IS0 8061
   * @param workingDays array of working days
   */
  selectDaysTimeline(
    from: string,
    to: string,
    workingDays: string[]
  ): string[] {
    const start = moment(from, ISO_8061);
    const end = moment(to, ISO_8061);
    let startIndex;
    let endIndex;

    if (!workingDays || !workingDays.length) {
      this.fetchWorkingDays();
    }

    workingDays?.some((day, index, arr) => {
      if (
        start.isBetween(
          moment(day, ISO_8061),
          moment(arr[index + 1], ISO_8061),
          undefined,
          '[]'
        )
      ) {
        startIndex = index;
        return true;
      }
      return false;
    });
    if (startIndex === undefined) {
      return this.handleEdgeOfTimeline(
        start.format(ISO_8061),
        end.format(ISO_8061)
      );
    }
    const result = workingDays.slice(startIndex);
    result.some((day, index, arr) => {
      if (
        end.isBetween(
          moment(day, ISO_8061),
          moment(arr[index + 1], ISO_8061),
          undefined,
          '[]'
        )
      ) {
        endIndex = index;
        return true;
      }
      return false;
    });

    if (endIndex === undefined) {
      return result.concat(
        this.handleEdgeOfTimeline(
          result[result.length - 1],
          end.format(ISO_8061)
        )
      );
    }

    return result.slice(0, endIndex + 1);
  }
  /**
   * @param from date in IS0 8061
   * @param to date in IS0 8061
   */
  handleEdgeOfTimeline(from: string, to: string) {
    const start = moment(from, ISO_8061).set('h', 1);
    const end = moment(to, ISO_8061).set('h', 23);

    const result = [];
    const pointer = moment(start);
    while (pointer.isBefore(end)) {
      const day = start.isoWeekday();
      if (day < 6) {
        result.push(pointer.format(ISO_8061));
      }
      pointer.add(1, 'd');
    }
    return result;
  }

  selectByPaymentType<
    T extends {
      type: { [key in PAYMENT_TYPE]: boolean };
      code: string;
      id: string;
      favourite?: { [key in PAYMENT_TYPE]: boolean };
    }
  >(items: T[], opts: { type: PAYMENT_TYPE; sortFavourite?: boolean }) {
    const result = items.filter((item) => {
      return item?.type?.[opts.type];
    });
    if (opts.sortFavourite) {
      result.sort((a, b) => {
        if (a.favourite && !b.favourite) {
          return -1;
        } else if (b.favourite && !a.favourite) {
          return 1;
        }
        return parseInt(a.code, 10) - parseInt(b.code, 10);
      });
    }
    return result;
  }
}
