import { Directive, HostListener, Input, Optional, Self } from '@angular/core';
import { MaskedService } from '@shared/masked/masked.service';
import { MaskPipe } from 'ngx-mask';
import { FormControlDirective } from '@angular/forms';

@Directive({
  selector: '[jReplaceDot]',
})
export class ReplaceDotDirective {
  @Input() applyFormatAmount?: string;

  @HostListener('keydown', ['$event']) onKeyEvent(event: any) {
    const e = event || window.event; // get event object
    if (e.defaultPrevented) {
      return;
    }
    //  e.key || e.code not a number as usual - just string "Tab" or "Enter"
    const key = e.key || e.code;
    if (key === '.') {
      if (e.preventDefault) {
        // get old value
        const start = e.target.selectionStart;
        const end = e.target.selectionEnd;
        const oldValue = e.target.value;

        if (!oldValue.includes(',')) {
          // replace point and change input value
          let newValue = oldValue.slice(0, start) + ',' + oldValue.slice(end);

          if (this.applyFormatAmount) {
            newValue = this.maskedService.getMaskedAmount(
              newValue,
              this.applyFormatAmount
            );
          }

          e.target.value = newValue;

          // replace cursor
          e.target.selectionStart = e.target.selectionEnd = start + 1;
        }
        e.preventDefault();
      } else {
        e.returnValue = false;
      }
    }
  }
  @HostListener('paste', ['$event']) onPaste(event: any) {
    event.preventDefault();
    const clipboardData = event.clipboardData || (window as any).clipboardData;
    const pastedText = clipboardData.getData('text');
    const toPasteRaw = this.maskedService.getMaskedAmount(pastedText);
    this.controlDirective && this.controlDirective.control.setValue(toPasteRaw);
  }
  constructor(
    private maskedService: MaskedService,
    @Optional() @Self() private controlDirective?: FormControlDirective
  ) {}
}
