import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IExchangeRates } from '@shared/exchange-rates/exchange-rates.model';
import { ExchangeRatesService } from '@shared/exchange-rates/exchange-rates.service';

@Component({
  selector: 'j-exchange-rates-list',
  templateUrl: './exchange-rates-list.component.html',
  styleUrls: ['./exchange-rates-list.component.scss']
})
export class ExchangeRatesListComponent implements OnInit {
  @Input() selectedBank: string;
  @Input() exchangeRates: IExchangeRates[];
  @Input() error: string;
  @Output() currencySelectorOpened = new EventEmitter();
  @Output() updateClicked = new EventEmitter();

  constructor() { }

  ngOnInit(): void {
  }

  onCurrencySelectorOpen(event = null) {
    event?.preventDefault();
    this.currencySelectorOpened.emit();
  }

  onUpdate() {
    this.updateClicked.emit();
  }
}
