import { AUTHORITY_TYPE, SIGNATURE_SCHEME, User } from '@shared/models';

export const COMPANY_ID_KEY = 'companyId';

export enum COMPANY_TYPE {
  ENTREPRENEUR = 'ENTREPRENEUR',
  CORPORATE = 'CORPORATE',
}

export class Company {
  id: string;
  colvirId: string;
  shortName: string;
  shortOrLongName: string;
  name: string;
  bin: string;

  kod: string;
  signatureScheme: SIGNATURE_SCHEME;
  legalAddress: string;
  economicSector: string;
  registrationDate: Date;
  locationAddress: string;

  actualAddress: null;

  created: Date;
  departmentCode: string;
  departmentName: string;
  registrationAddress: string;
  resident: boolean;
  type: COMPANY_TYPE;
}

export interface IAuthorizedPerson {
  authority: AUTHORITY_TYPE;
  company: any;
  companyColvirId: string;
  companyId: string;
  firstname: string;
  fromDate: Date;
  id: string;
  lastname: string;
  middlename: string;
  toDate: Date;
  userColvirId: string;
  userId: string;
  position: string;
  iin: string;
  phone: string;
}

export enum OTP_STATUS {
  ASSIGNED = 'ASSIGNED',
  UNASSIGNED = 'UNASSIGNED',
  NOT_ENOUGH_DATA = 'NOT_ENOUGH_DATA',
  BLOCKED = 'BLOCKED',
}

export interface IOtpDevice {
  activated: Date;
  assignedCompany: {
    companyIds: string[];
  };
  created: Date;
  id: string;
  serial: string;
  status: OTP_STATUS;
  user: User;
  userId: string;
}
