import { Injectable } from '@angular/core';
import { HttpHeaders, HttpParams } from '@angular/common/http';

import { TranslateService } from '@ngx-translate/core';
import { NotifierService } from 'angular-notifier';

import { PAYMENT_CATEGORY } from '@shared/models';

import { resolveURL, EntitiesStore } from '@utils/index';
import { ApiService } from '@api/index';
import { pick } from '@utils/pick';

import { CompanyService } from './company.service';
import {
  RecipientAccount,
  Recipient,
  RecipientBody,
  RecipientAccountBody,
} from './recipient/recipient.model';

export interface ICounterParty {
  companyInfo: {
    bin: string;
    fio: string;
    id: string;
    ip: string;
    katoAddress: string;
    katoCode: string;
    name: string;
    okedCode: string;
    okedName: string;
    registerDate: string;
  };
  riskGroups: {
    id: string;
    name: string;
  }[];
}

const endpoints = {
  recipients: 'companies/::companyId/recipients',
  recipient: 'companies/::companyId/recipients/::recipientId',
  recipientsAccounts: 'companies/::companyId/recipients/::recipientId/accounts',
  prevNotifications: 'companies/::companyId/recipients/::iinOrBin/emails',
};

@Injectable({
  providedIn: 'root',
})
export class RecipientsService {
  public readonly store: EntitiesStore<Recipient> = new EntitiesStore([]);
  public readonly foreignStore: EntitiesStore<Recipient> = new EntitiesStore(
    []
  );
  public reqInProgress = {
    foreign: undefined,
    local: undefined,
  };

  constructor(
    private company: CompanyService,
    private notifer: NotifierService,
    private api: ApiService,
    private translateService: TranslateService
  ) {
    this.company.refresh$.subscribe((v) => {
      this.store.fetched && this.store.purge();
      this.getRecipients('', true);
      this.getForeignRecipients('', true);
    });
  }

  async createRecipient(body: RecipientBody): Promise<Recipient> {
    const url = resolveURL(endpoints.recipients, {
      companyId: this.company.currentId,
    });
    return this.api.post(url, body);
  }

  async updateRecipient(body: Recipient): Promise<Recipient> {
    const url = resolveURL(endpoints.recipient, {
      companyId: this.company.currentId,
      recipientId: body.id,
    });
    return this.api.put(url, body);
  }

  async deleteRecipient(recipientId: string | string[]): Promise<void> {
    let url: string;
    if (Array.isArray(recipientId)) {
      url = resolveURL(endpoints.recipients, {
        companyId: this.company.currentId,
      });
      url += `?id=${recipientId.join(',')}`;
    } else {
      url = resolveURL(endpoints.recipient, {
        companyId: this.company.currentId,
        recipientId,
      });
    }
    return this.api.delete(url);
  }

  checkCounterParty(bin: string): Promise<ICounterParty> {
    const url = `counterparty-reports`;

    const headers = new HttpHeaders();
    const params = new HttpParams().set('bin', bin);

    return this.api.get<ICounterParty>(url, headers, params);
  }

  async createRecipientAccount(
    body: RecipientAccountBody,
    params: {
      recipientId: string;
      companyId: string;
    }
  ): Promise<RecipientAccount> {
    const url = resolveURL(endpoints.recipientsAccounts, params);
    return this.api.post(url, body);
  }

  async getRecipients(query: string = '', force?: boolean): Promise<any> {
    let req;
    if (!query && this.reqInProgress.local) {
      return await this.reqInProgress.local;
    } else if (!query) {
      this.reqInProgress.local = req = this.searchInStore(
        this.store,
        query,
        force
      ).finally(() => {
        if (req === this.reqInProgress.local) {
          this.reqInProgress.local = null;
        }
      });
      return await this.reqInProgress.local;
    }
    return await this.searchInStore(this.store, query, force);
  }

  async getForeignRecipients(query: string = '', force?: boolean) {
    return await this.searchInStore(
      this.foreignStore,
      query,
      force,
      PAYMENT_CATEGORY.FOREIGN
    );
  }

  async checkAndInjectRecipientOrAccount(
    recipient: Omit<Recipient, 'id' | 'accounts'>,
    rawAccount?: Omit<RecipientAccount, 'id' | 'recipientId' | 'companyId'>
  ): Promise<any> {
    const processForCompany = recipient.companyId;
    let existingRecipient: Recipient;
    let newAccount: RecipientAccount;
    let newRecipient: Recipient;
    try {
      const account = { ...pick(rawAccount, ['iban', 'bic']) };
      if ((existingRecipient = this.isRecipientExist(recipient))) {
        if (this.isRecipientAccountExist(account.iban, existingRecipient.id)) {
          return null;
        }
        newAccount = await this.createRecipientAccount(account, {
          companyId: existingRecipient.companyId,
          recipientId: existingRecipient.id,
        });
      } else {
        newRecipient = await this.createRecipient(recipient);
        newAccount = await this.createRecipientAccount(account, {
          companyId: newRecipient.companyId,
          recipientId: newRecipient.id,
        });
      }
      if (processForCompany === this.company.currentId) {
        if (newRecipient) {
          newRecipient.accounts = [newAccount];
          this.store.upsertItem(newRecipient);
        } else if (newAccount) {
          this.store.upsertItem({
            ...existingRecipient,
            accounts: existingRecipient.accounts.concat(newAccount),
          });
        }
      }
    } catch (e) {
      this.notifer.notify('error', JSON.stringify(e));
      throw e;
    }
  }

  async createOrUpdateRecipientPipeline(recipient: Recipient) {
    let newAccounts;
    let result: Recipient;
    const processForCompany = this.company.currentCompany.id;
    try {
      if (recipient.id) {
        result = await this.updateRecipient(recipient);
      } else {
        const rawAccounts = recipient.accounts;
        delete recipient.accounts;
        result = await this.createRecipient(recipient);
        newAccounts = await Promise.all(
          rawAccounts.map((rawAccount) =>
            this.createRecipientAccount(rawAccount, {
              companyId: result.companyId,
              recipientId: result.id,
            })
          )
        );
      }

      if (processForCompany === this.company.currentId) {
        if (result && newAccounts) {
          result.accounts = newAccounts;
          this.store.upsertItem(result, true);
        } else if (result) {
          this.store.upsertItem(result);
        }
      }
    } catch (e) {
      this.notifer.notify('error', JSON.stringify(e));
      throw e;
    }
  }

  getPrevNotifications(iinOrBin): Promise<{ email: string }[]> {
    return this.api.get(
      resolveURL(endpoints.prevNotifications, {
        companyId: this.company.currentCompany.id,
        iinOrBin,
      })
    );
  }

  isRecipientExist(
    recipient: Omit<Recipient, 'id' | 'accounts'>
  ): Recipient | undefined {
    const keys: Array<keyof Recipient> = [
      'iinOrBin',
      'kbe',
      'name',
      'companyId',
    ];
    return this.store.items.find((item) =>
      keys.every((k) => item[k] == recipient[k])
    );
  }

  isRecipientAccountExist(
    accountIban: string,
    recipientId?: string
  ): RecipientAccount | null {
    let r: Recipient;
    if (recipientId && (r = this.store.entities[recipientId])) {
      return r.accounts && r.accounts.find((a) => a.iban === accountIban);
    }
    let account: RecipientAccount;
    this.store.items.find((item) => {
      account = item.accounts.find((a) => a.iban === accountIban);
      if (account) {
        return true;
      }
    });
    return account;
  }

  public filterRecipients(recipients: Recipient[], query: string) {
    if (!query) {
      return recipients;
    }

    return recipients?.filter((recipient) => {
      return this.filterRecipient(recipient, query);
    });
  }

  public filterRecipient(recipient: Recipient, query: string) {
    if (!query) {
      return true;
    }

    return (
      this.containsQuery(recipient.iinOrBin, query) ||
      this.containsQuery(recipient.name, query) ||
      this.searchByCountry(recipient, query) ||
      this.getAccountByQuery(recipient.accounts, query).length
    );
  }

  public async fetchRecipients(
    companyId: string,
    paymentCategory: PAYMENT_CATEGORY = PAYMENT_CATEGORY.DOMESTIC
  ): Promise<Recipient[]> {
    const url = resolveURL(endpoints.recipients, {
      companyId,
    });

    const params = new HttpParams().set('paymentCategory', paymentCategory);

    try {
      const retrievedRecipients = await this.api.get<Recipient[]>(
        url,
        null,
        params
      );
      return this.filterEmptyRecipients(retrievedRecipients);
    } catch (error) {
      throw error;
    }
  }

  private async searchInStore(
    store: RecipientsService['store'] = this.store,
    query?: string,
    force?: boolean,
    category?: PAYMENT_CATEGORY
  ) {
    const companyId = this.company.currentId;
    let source;
    if (store.fetched && !force) {
      source = store.items;
    } else {
      try {
        store.fetching = true;
        source = await this.fetchRecipients(companyId, category);
        // prevent override if req resolved after company change
        if (this.company.currentId === companyId) {
          store.items = source;
          store.fetching = false;
          store.fetched = true;
        }
        return source;
      } catch (error) {
        throw error;
      }
    }

    if (query) {
      return this.filterRecipients(source, query);
    } else {
      return source;
    }
  }

  private searchByCountry(recipient: Recipient, query: string) {
    let fieldToSearch;

    switch (this.translateService.currentLang) {
      case 'ru':
      default:
        fieldToSearch = 'nameRu';
        break;
      case 'kk':
        fieldToSearch = 'nameKk';
        break;
      case 'en':
        fieldToSearch = 'nameEn';
        break;
    }

    return (
      this.containsQuery(recipient[fieldToSearch], query) ||
      recipient.countryCode?.toLowerCase().includes(query)
    );
  }

  private filterEmptyRecipients(recipients: Recipient[]) {
    return recipients?.filter((recipient) => {
      return recipient.accounts?.length > 0;
    });
  }

  private getAccountByQuery(accounts: RecipientAccount[], query: string) {
    if (!accounts) {
      return [];
    }

    return accounts.filter((account) => {
      return (
        this.containsQuery(account.iban, query) ||
        this.containsQuery(account.bic, query) ||
        this.containsQuery(account.bankName, query)
      );
    });
  }

  containsQuery(input: string, query: string) {
    if (!query) {
      return true;
    }

    const search = query.toLowerCase();
    return input?.toLowerCase().includes(search);
  }
}
