import { IMoney } from '@shared/currency/currency.model';
import { IDictionaryValue } from './dictionary';
import { ICountry } from './payment';

export interface IContractor {
  address: string;

  bank: IDictionaryValue;
  bankCityName: string;
  bankCountryCode: any;

  colvirId: string;
  country: IDictionaryValue;
  name: string;
  cityName: string;
  defaultCityName: string;
  iinOrBin: string;
}

export interface IFxContractType {
  colvirCode: string;
  description: string;
  id: string;
}

export interface IFileObject {
  base64: string;
  bytes: string;
  created: string;
  extension: string;
  id: string;
  name: string;
  serverFileId: string;
  sizeInBytes: number;
}

export enum FX_CONTRACT_ATTACHMENT_TYPE {
  CONTRACT_TYPE = 'CONTRACT_TYPE',
  UNK = 'UNK',
}

export interface IFxContractAttachment {
  created: string;
  file: IFileObject;
  fileId: string;
  fxContractId: string;
  id: string;
  sent: boolean;
  type: FX_CONTRACT_ATTACHMENT_TYPE;

  isLoading?: boolean;
}

export enum FX_CONTRACT_STATUS {
  PROCESSING = 'PROCESSING',
  REGISTERED = 'REGISTERED',
  REGED = 'REGED',
  REJECTED = 'REJECTED',
  CLOSED = 'CLOSED',
  ON_SIGN = 'ON_SIGN',
}

export interface IFxContractRecipient {
  bankBic: string;
  bankCityName: string;
  bankCountryCode: string;
  bankName: string;
  cityName: string;
  colvirId: string;
  country: ICountry;
  countryCode: string;
  created: Date;
  defaultCityName: string;
  fxContractId: string;
  iban: string;
  id: string;
  kbe: string;
  name: string;
  primary: boolean;
}

export interface IFxContract {
  amount: IMoney;
  colvirId: string;
  companyId: string;
  contractNumber: string;
  contractor: IContractor;
  created: Date;
  documentDate: Date;
  dateFrom: Date;
  dateTo: Date;
  iban: string;
  id: string;
  permittedCurrencies: any;
  registrationNumberContract: string;
  remainder: IMoney;

  status: FX_CONTRACT_STATUS;

  typeId: string;
  type: IFxContractType;

  statusMessage: string;
  attachments: IFxContractAttachment[];

  recipients: IFxContractRecipient[];
  repatriationPeriod: number;
  userId: string;

  isCollapsed: boolean;
}
