import {
  Component,
  OnInit,
  OnChanges,
  Input,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  ViewEncapsulation,
} from '@angular/core';

const specialCharacters = ' ()•';

@Component({
  selector: 'j-input-shadow',
  templateUrl: './input-shadow.component.html',
  styleUrls: ['./input-shadow.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class InputShadowComponent implements OnInit, OnChanges {
  @Input() value: string;
  @Input() shadow: string;
  @Input() hidden: boolean;

  shadowArr = [];

  constructor(public cd: ChangeDetectorRef) {}

  ngOnInit(): void {
    // if (this.shadow) {
    //   this.shadowArr = this.checkShadow(this.value, this.shadow);
    // }
  }

  ngOnChanges() {
    if (this.shadow) {
      this.shadowArr = this.checkShadow(this.value, this.shadow);
      this.cd.markForCheck();
    }
  }

  checkShadow(inputValue = '', shadow: string) {
    const result = [];
    let valueIndex = 0;
    let i = 0;

    for (; i < shadow.length; i++) {
      const maskChar = shadow[i];
      let inputChar;

      if (specialCharacters.includes(maskChar)) {
        inputChar = maskChar;
      } else {
        inputChar = inputValue && inputValue[valueIndex];
        valueIndex++;
      }

      const resultChar = inputChar || maskChar;

      result.push({
        invisible: resultChar === inputChar,
        placeholder: resultChar === '_',
        value: resultChar,
      });
    }
    return result;
  }
}
