import {
  Directive,
  HostBinding,
  ChangeDetectorRef,
  OnDestroy,
  OnInit,
} from '@angular/core';
import { UserService } from '@shared/services/user.service';
import { Subscription } from 'rxjs';
import { MaskPhonePipe } from '@shared/pipes/mask-phone.pipe';
import { distinctUntilChanged } from 'rxjs/operators';

@Directive({
  selector: '[jCurrentUserPhone]',
})
export class CurrentUserPhoneDirective implements OnDestroy, OnInit {
  private readonly s = new Subscription();
  constructor(
    private user: UserService,
    private cd: ChangeDetectorRef,
    private maskPhone: MaskPhonePipe
  ) {}
  @HostBinding('innerText') phone = '';

  ngOnInit() {
    this.s.add(
      this.user.getUser().pipe(distinctUntilChanged()).subscribe((u) => {
        if (u && u.phone) {
          this.phone = this.maskPhone.transform(u.phone);
        } else {
          this.phone = '';
        }
        this.cd.markForCheck();
      })
    );
  }

  ngOnDestroy() {
    this.s.unsubscribe();
  }
}
