import {
  OnInit,
  Component,
  EventEmitter,
  Output,
  ViewChild,
  Input,
  forwardRef,
  OnChanges,
  SimpleChanges,
  OnDestroy,
  ElementRef,
  AfterViewInit,
} from '@angular/core';

import {
  FormControl,
  NG_VALUE_ACCESSOR,
  ControlValueAccessor,
} from '@angular/forms';

import { Subscription } from 'rxjs';

import { generateUniqueId } from '@utils/index';

import { MaskedService } from '../masked/masked.service';
import { MaskedConfig } from '../masked/masked';

export enum InputType {
  textarea = 'textarea',
  text = 'text',
  number = 'number',
}

@Component({
  selector: 'j-input',
  templateUrl: './input.component.html',
  styleUrls: ['./input.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => JInputComponent),
      multi: true,
    },
  ],
})
export class JInputComponent
  implements OnInit, ControlValueAccessor, OnChanges, OnDestroy, AfterViewInit
{
  @Input() id = generateUniqueId();
  @Input() label = '';
  @Input() name = '';
  @Input() type: InputType;
  @Input() maxLength: number;
  @Input() maskType = null;
  @Input() dropSpecialCharacters = true;
  @Input() computeSuffix: (inputValue: any) => string = null;

  @Input() errorText = null;
  @Input() errorParams = null;
  @Input() errorMessages?: {
    error: string;
    message: string;
  }[];
  @Input() helperText = null;
  @Input() helperTextParams = {};

  @Input() hasError = false;
  @Input() isActive = false;
  @Input() isClearable = true;
  @Input() shadowClass = '';
  @Input() inputClass = '';

  @Input() inputExtra;

  @Input() value = '';
  @Output() valueChanged = new EventEmitter<any>();

  @Input() formControl: FormControl = new FormControl(this.value || '');
  @Input() autofocus: boolean;

  @Input() isDisabled: boolean;

  @ViewChild('input') input: ElementRef;
  @ViewChild('link') link: ElementRef;
  @ViewChild('button') button: ElementRef;

  @Output() blurred = new EventEmitter<any>();
  @Output() focused = new EventEmitter<any>();
  @Output() keyup = new EventEmitter<any>();

  helperClasses = '';

  isFocused = false;

  maskConfig: MaskedConfig;

  types = InputType;

  subscription: Subscription = new Subscription();

  constructor(public maskedService: MaskedService) {}

  ngOnInit() {
    this.maskConfig = this.maskedService.getConfig(this.maskType);

    const formSubscription = this.formControl.valueChanges.subscribe(
      (value) => {
        this.valueChanged.emit(value);
      }
    );

    this.subscription.add(formSubscription);
  }

  ngAfterViewInit() {
    // this.detectAutofill();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  ngOnChanges(changes: SimpleChanges) {
    this.triggerHelperAnimation();
  }

  onKeyUp(event) {
    this.keyup.emit(event);
  }

  onChange(_: any) {}
  onTouched(_?: any) {}

  writeValue(value: any) {
    this.value = value;
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

  registerOnTouched(fn) {
    this.onTouched = fn;
  }

  onFocus() {
    this.isFocused = true;
    this.focused.emit();
  }

  onBlur() {
    this.isFocused = false;
    this.onTouched();
    this.blurred.emit();
  }

  blur() {
    this.input.nativeElement.blur();
  }

  clear() {
    if (this.isDisabled) {
      return;
    }
    this.formControl.setValue('');
    this.focus();
  }

  focus(selectionIndex = 5) {
    setTimeout(() => {
      this.input.nativeElement.focus();
      this.input.nativeElement.setSelectionRange(
        selectionIndex,
        selectionIndex
      );
    }, 0);
  }

  // detectAutofill() {
  //   setTimeout(() => {
  //     window
  //       .getComputedStyle(this.input.nativeElement, null)
  //       .getPropertyValue('appearance');
  //   }, 600);
  // }

  public triggerHelperAnimation() {
    this.helperClasses = 'animate__animated animate__shakeX';
    setTimeout(() => {
      this.helperClasses = '';
    }, 1000);
  }

  get hasMask() {
    return this.maskType !== null && this.maskConfig !== null;
  }

  get isFilled() {
    const hasAutoFill =
      this.input?.nativeElement &&
      window
        .getComputedStyle(this.input.nativeElement, null)
        .getPropertyValue('appearance') === 'menulist-button';
    return `${this.formControl.value}`?.length || hasAutoFill ? true : false;
  }

  get isShadowHidden() {
    // if (!this.formControl.value) {
    //   return true;
    // }
    return !this.formControl.value && !this.isFocused;
  }

  get suffix() {
    if (this.maskConfig.suffix) return this.maskConfig.suffix;
    if (this.computeSuffix) {
      return this.computeSuffix(this.formControl.value);
    }
    return '';
  }

  get shadow() {
    return this.maskedService.getShadow(this.maskType, this.formControl.value);
  }
}
