import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'j-error-placeholder',
  templateUrl: './error-placeholder.component.html',
  styleUrls: ['./error-placeholder.component.scss'],
})
export class ErrorPlaceholderComponent implements OnInit {
  @Input() isRetryable: boolean = true;
  @Input() errorText: string;
  @Input() errorBadge: string;
  @Input() imgLink = '/assets/img/shared/server-error.svg';
  @Output() retried = new EventEmitter();

  constructor() {}

  ngOnInit() {}

  onRetry() {
    this.retried.emit();
  }
}
