import {
  Directive,
  Self,
  Input,
  OnInit,
  OnDestroy,
  ViewContainerRef,
  OnChanges,
  ComponentRef,
  ComponentFactoryResolver,
  ChangeDetectorRef,
  HostListener,
  Optional,
  ElementRef,
} from '@angular/core';
import { NgControl } from '@angular/forms';
import { InputShadowComponent } from '@shared/input-shadow/input-shadow.component';
import {
  Subscription,
  combineLatest,
  BehaviorSubject,
  Observable,
  fromEvent,
} from 'rxjs';
import { startWith, map } from 'rxjs/operators';

@Directive({
  selector: '[jShadow]',
})
export class ShadowControlDirective implements OnInit, OnDestroy, OnChanges {
  @Input() jShadow: string;
  @Input() jDynamicPrefix: string;
  focused$ = new BehaviorSubject(false);
  vChange$: Observable<string>;
  ref: ComponentRef<InputShadowComponent>;
  readonly subs = new Subscription();

  @HostListener('blur') onBlur() {
    this.focused$.next(false);
  }

  @HostListener('focus') onFocus() {
    this.focused$.next(true);
  }

  constructor(
    @Optional() @Self() public ngControl: NgControl,
    private element: ElementRef,
    private vcr: ViewContainerRef,
    private resolver: ComponentFactoryResolver,
    public cd: ChangeDetectorRef
  ) {}

  ngOnInit() {
    if (this.jShadow) {
      const factory = this.resolver.resolveComponentFactory(
        InputShadowComponent
      );
      if (this.ngControl) {
        this.vChange$ = this.ngControl.valueChanges.pipe(
          startWith(this.ngControl.value)
        );
      } else {
        this.vChange$ = fromEvent(this.element.nativeElement, 'input').pipe(
          startWith(this.element.nativeElement.value),
          map((e) => this.element.nativeElement.value)
        );
      }
      this.ref = this.vcr.createComponent(factory);
      const instance = this.ref.instance;
      instance.shadow = this.jShadow;
      this.subs.add(
        combineLatest(this.vChange$, this.focused$).subscribe(([v, f]) => {
          const nextVisibility = (f) || (f && this.jDynamicPrefix);
          if (nextVisibility || !!nextVisibility !== !instance.hidden) {
            instance.hidden = !nextVisibility;
            instance.value = this.jDynamicPrefix ? this.jDynamicPrefix + v : v;
            instance.ngOnChanges();
          }
        })
      );
    }
  }

  ngOnChanges() {}

  ngOnDestroy() {
    this.subs.unsubscribe();
    this.vcr.clear();
  }
}
