import {
  Directive,
  AfterViewInit,
  ContentChild,
  Input,
  OnDestroy,
  Renderer2,
  ElementRef,
  OnChanges,
} from '@angular/core';

import { Subscription } from 'rxjs';

import { CollapsibleBodyDirective } from './collapsible-body.directive';
import { CollapsibleTriggerDirective } from './collapsible-trigger.directive';

@Directive({
  selector: '[jCollapsible]',
})
export class CollapsibleDirective implements AfterViewInit, OnDestroy {
  @ContentChild(CollapsibleBodyDirective) contentBody: CollapsibleBodyDirective;
  @ContentChild(CollapsibleTriggerDirective)
  contentTrigger;

  @Input() jCollapsible;

  maxHeight: number;
  bodyStyleRef;
  isCollapsed: boolean;

  subscription = new Subscription();

  constructor(private renderer: Renderer2, private elementRef: ElementRef) {}

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  ngAfterViewInit() {
    this.init();
  }

  public close() {
    this.isCollapsed = false;
    this.onCollapse();
  }

  private init() {
    this.isCollapsed = this.jCollapsible;
    this.maxHeight = this.contentBody.elem.nativeElement.scrollHeight;
    this.bodyStyleRef = this.contentBody.elem.nativeElement.style;

    this.appendTransitions();
    this.onCollapse();

    const clickTriggerSubscription = this.contentTrigger.clicked.subscribe(
      (event) => {
        this.isCollapsed = !this.isCollapsed;
        this.onCollapse();
      }
    );
    this.subscription.add(clickTriggerSubscription);
  }

  private onCollapse() {
    this.onCollapseHost(this.isCollapsed);
    this.onCollapseBody(this.isCollapsed);
  }

  private appendTransitions() {
    this.bodyStyleRef.transition = 'all 0.3s ease-in-out';
  }

  private onCollapseHost(isCollapsed: boolean) {
    if (isCollapsed) {
      this.renderer.addClass(this.elementRef.nativeElement, 'j-collapsed');
    } else {
      this.renderer.removeClass(this.elementRef.nativeElement, 'j-collapsed');
    }
  }

  private onCollapseBody(isCollapsed: boolean) {
    if (isCollapsed) {
      this.setBodyStyles('unset', 1, this.maxHeight);
    } else {
      this.setBodyStyles('hidden', 0, 0);
    }
  }

  private setBodyStyles(overflow: string, opacity: number, maxHeight: number) {
    this.bodyStyleRef.overflowY = overflow;
    this.bodyStyleRef.opacity = opacity;
    this.bodyStyleRef.maxHeight = `${maxHeight}px`;
  }
}
