import { Component, EventEmitter, forwardRef, Input, Output } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { MaskedService } from '@shared/masked/masked.service';
import { ICountryCode } from '@shared/models';
import { CountryCodesComponent } from '../country-codes/country-codes.component';
import { JInputComponent } from '../input.component';

@Component({
  selector: 'j-phone-prefix-input',
  templateUrl: './phone-prefix-input.component.html',
  styleUrls: ['./phone-prefix-input.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => PhonePrefixInputComponent),
      multi: true,
    },
  ],
})
export class PhonePrefixInputComponent extends JInputComponent {
  countryCodeValue: ICountryCode;
  imageUrl = '/assets/countryflags/$blank_flag.png';
  @Input() countryCodes: ICountryCode[];

  @Input() label = 'COUNTRIES.CODE';

  @Input() set countryCode(value: ICountryCode) {
    this.countryCodeValue = value;
    const imageName = this.countryCodeValue?.countryCode ? this.countryCodeValue.countryCode.toLowerCase() : 'blank_flag';
    this.imageUrl = `/assets/countryflags/$${imageName}.png`;
  }
  @Output() countrySelected = new EventEmitter<ICountryCode>();

  constructor(
    public maskedService: MaskedService,
    private modalService: NgbModal,
  ) {
    super(maskedService);
  }

  clear() {
  }

  get isFilled() {
    return true;
  }

  async onFocusCountryCode() {
    const modalRef = this.modalService.open(CountryCodesComponent, {
      backdropClass: 'backdrop_light',
      windowClass:
        'modal_light modal-adaptive_sm modal_max-height modal-adaptive modal_md',
      backdrop: 'static',
    });
    modalRef.componentInstance.countryCodes = this.countryCodes;
    try {
      this.countryCode = await modalRef.result;
      const imageName = this.countryCodeValue?.countryCode ? this.countryCodeValue.countryCode.toLowerCase() : 'blank_flag';
      this.imageUrl = `/assets/countryflags/$${imageName}.png`;
      this.countrySelected.emit(this.countryCodeValue);
    } catch (error) {
    }
  }
}
