import { Injectable } from '@angular/core';
import { PAYMENTS_STEPS } from '@payments/models/steps';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class HeaderService {
  currentStep$ = new BehaviorSubject<number>(PAYMENTS_STEPS.NONE);

  constructor() {}

  getCurrentStep() {
    return this.currentStep$.asObservable();
  }

  setCurrentStep(newStep: number) {
    this.currentStep$.next(newStep);
  }
}
