import {
  animate,
  state,
  style,
  transition,
  trigger,
  AnimationTriggerMetadata,
} from '@angular/animations';

/**
 * @docs-private
 */
export const jFormFieldAnimations: {
  readonly transitionMessages: AnimationTriggerMetadata
} = {
  /** Animation that transitions the form field's error and hint messages. */
  transitionMessages: trigger('transitionMessages', [
    state('enter', style({ opacity: 1, transform: 'translateY(0%)', height: '*' })),
    transition('void => enter', [
      style({ opacity: 0, transform: 'translateY(-100%)', height: '0' }),
      animate('300ms 100ms cubic-bezier(0.55, 0, 0.55, 0.2)'),
    ]),
  ])
};
