import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import {
  Component,
  ContentChild,
  EventEmitter,
  HostListener,
  Input,
  Output,
  TemplateRef,
  ViewEncapsulation,
} from '@angular/core';

export enum KEYBOARD_KEY {
  ARROW_DOWN = 'ArrowDown',
  ARROW_UP = 'ArrowUp',
  ENTER = 'Enter',
  ESC = 'Escape',
  SPACE = 'Space',
}

@Component({
  selector: 'j-dropdown-input',
  templateUrl: './dropdown-input.component.html',
  styleUrls: ['./dropdown-input.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: [
    trigger('smoothCollapse', [
      state(
        'initial',
        style({
          height: '0',
          overflow: 'hidden',
          opacity: '0.5',
        })
      ),
      state(
        'final',
        style({
          overflow: 'auto',
          opacity: '1',
          maxHeight: '300px',
        })
      ),
      transition('initial=>final', animate('300ms ease-in-out')),
      transition('final=>initial', animate('300ms ease-in-out')),
    ]),
  ],
})
export class DropdownInputComponent {
  @Input() label: string;
  @Input() inputClass: string;
  @Input() isLoading: boolean = false;
  @Input() hasError: boolean = false;

  @Input() isDisabled: boolean = false;
  @Input() isLabelHidden: boolean = false;
  @Input() labelShowAnyWay: boolean = false;
  @Input() options: any[] = [];

  @Input() emptyLabel: string = 'ACCOUNTS.EMPTY';

  @Input() blockedFn: Function;

  @Input() nonClosingAfterSelection = false;

  @Input() selectedItem: any;
  @Output() selected = new EventEmitter();

  hoveredItem = null;
  isCollapsed = false;

  @ContentChild('selectedTemplate', { static: false })
  selectedTemplate: TemplateRef<any>;
  @ContentChild('optionTemplate', { static: false })
  optionTemplate: TemplateRef<any>;

  constructor() {}

  public clearSelected() {
    this.selectedItem = null;
  }

  public selectItem(item: any) {
    if (!item) {
      return;
    }

    this.selectedItem = item;

    this.isCollapsed = this.isCollapsed = !!this.nonClosingAfterSelection;;
  }

  isBlocked(item: any) {
    if (!this.blockedFn) {
      return false;
    }

    return this.blockedFn(item);
  }

  @HostListener('document:keydown', ['$event'])
  handleKeyDown(event: KeyboardEvent) {
    if (!this.isCollapsed) {
      return;
    }

    switch (event.key) {
      case KEYBOARD_KEY.ARROW_DOWN:
        event.preventDefault();
        this.handleArrowDown();
        break;

      case KEYBOARD_KEY.ARROW_UP:
        event.preventDefault();
        this.handleArrowUp();
        break;

      case KEYBOARD_KEY.ENTER:
        event.preventDefault();
        this.handleEnter();
        break;

      default:
        this.close();
        break;
    }
  }

  close() {
    this.isCollapsed = false;
  }

  toggleCollapse() {
    if (this.isDisabled) {
      return;
    }
    this.isCollapsed = !this.isCollapsed;
  }

  onClickOutside() {
    this.isCollapsed = false;
  }

  onSelect(item) {
    if (this.isBlocked(item)) {
      return;
    }

    this.selectItem(item);
    this.selected.emit(item);
  }

  get selectableOptions() {
    return this.options.filter((option) => !this.isBlocked(option));
  }

  private handleArrowDown() {
    if (!this.hoveredItem) {
      this.hoveredItem = this.selectableOptions[0];
    } else {
      let index = this.selectableOptions.indexOf(this.hoveredItem);
      if (index < this.selectableOptions.length - 1) {
        this.hoveredItem = this.selectableOptions[++index];
      } else {
        this.hoveredItem = this.selectableOptions[0];
      }
    }
  }

  private handleArrowUp() {
    if (!this.hoveredItem) {
      this.hoveredItem = this.selectableOptions[0];
    } else {
      let index = this.selectableOptions.indexOf(this.hoveredItem);
      if (index > 0) {
        this.hoveredItem = this.selectableOptions[--index];
      } else {
        this.hoveredItem = this.selectableOptions[this.options.length - 1];
      }
    }
  }

  private handleEnter() {
    if (!this.hoveredItem) {
      return;
    }
    this.onSelect(this.hoveredItem);
  }
}
