import { Component, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ICountryCode } from '@shared/models';

@Component({
  selector: 'j-country-codes',
  templateUrl: './country-codes.component.html',
  styleUrls: ['./country-codes.component.scss']
})
export class CountryCodesComponent {
  @Input() countryCodes: ICountryCode[];
  searchText = '';

  constructor(
    public modal: NgbActiveModal,
  ) { }

  onQueryChange(event) {
    this.searchText = event;
  }

  onCodeSelected(el) {
    this.modal.close(el);
  }

  get filteredCountryCodes() {
    return this.countryCodes?.filter(el => el.country.toLowerCase().includes(this.searchText.toLowerCase()) || el.code.includes(this.searchText));
  }
}
