import { Injectable } from '@angular/core';
import {
  HttpParams,
  HttpHeaders,
  HttpParameterCodec,
} from '@angular/common/http';
import { Router } from '@angular/router';

import { BehaviorSubject } from 'rxjs';

import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { ApiService, HEADERS, ERRORS, SMS_HEADERS } from '@api/index';

import { TokenService } from '@shared/token/token.service';
import { UserService } from '@shared/services/user.service';
import { ISmsToken, IEmailToken, TOKEN_TYPE } from '@shared/models';

import {
  IDeviceToken,
  IUserToken,
  ICheckPhoneStatus,
  ICheckIinStatus,
  ICheckIinAndPhoneStatus,
} from './auth.model';

export class HttpUrlEncodingCodec implements HttpParameterCodec {
  encodeKey(k: string): string {
    return standardEncoding(k);
  }
  encodeValue(v: string): string {
    return standardEncoding(v);
  }
  decodeKey(k: string): string {
    return decodeURIComponent(k);
  }
  decodeValue(v: string) {
    return decodeURIComponent(v);
  }
}
function standardEncoding(v: string): string {
  return encodeURIComponent(v);
}

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  private smsToken: ISmsToken;
  private emailToken: IEmailToken;
  private deviceToken: IDeviceToken;

  currentAuthState: boolean;
  authEvents$ = new BehaviorSubject<boolean>(false);

  constructor(
    private api: ApiService,
    private userService: UserService,
    private tokenService: TokenService,
    private router: Router,
    private modalService: NgbModal
  ) {
    if (this.tokenService.getUserToken()) {
      this.changeAuthState(true);
    } else {
      this.changeAuthState(false);
    }
  }

  // Auth
  authEvents() {
    return this.authEvents$.asObservable();
  }

  changeAuthState(value: boolean) {
    if (value !== this.currentAuthState) {
      this.currentAuthState = value;
      this.authEvents$.next(this.currentAuthState);
    }
  }

  setSmsToken(smsToken: ISmsToken) {
    this.smsToken = smsToken;
  }

  get isAuthenticated() {
    return this.authEvents$.getValue() || false;
  }

  // Login
  async login(phone: string, password: string) {
    const url = `oauth/token`;
    const body: FormData = new FormData();
    
    body.append('username', phone);
    body.append('password', password);

    let headers = this.tokenService.getBasicHeaders();
    headers = this.tokenService.applyDeviceHeaders(headers, password);

    const params = new HttpParams().set('grant_type', 'password');
    const result = await this.api.post<IUserToken>(url, body, headers, params);

    this.tokenService.setUserToken(result.access_token);
    this.userService.setUser(result.user);
    this.changeAuthState(true);

    return result;
  }

  // Logout
  logout() {
    this.changeAuthState(false);
    this.userService.setUser(null);
    this.tokenService.clearUserToken();
    this.router.navigateByUrl('auth/login');
    this.modalService.dismissAll();
  }

  // Server-side logout
  async revokeSession() {
    const url = `oauth/revoke`;
    const headers = this.tokenService.getBasicHeaders();
    await this.api.post(url, null, headers);
  }

  // Device
  async requestDevice(phone: string, authType: TOKEN_TYPE) {
    const url = `auth/device/token`;
    const body = {
      phone,
    };
    const headers = new HttpHeaders().set(
      HEADERS[authType],
      this.smsToken.signature
    );

    this.deviceToken = await this.api.post<IDeviceToken>(url, body, headers);
    return this.deviceToken;
  }

  // Sms
  async requestSms(phone: string): Promise<ISmsToken> {
    const url = `auth/phone/token`;
    const headers = new HttpHeaders()
      .set('code', SMS_HEADERS.AUTHORIZATION)
      .set('Content-Type', 'application/x-www-form-urlencoded; charset=UTF-8');
    const body = new HttpParams().set('phone', encodeURIComponent(phone));
    this.smsToken = await this.api.post<ISmsToken>(url, body, headers);
    return this.smsToken;
  }

  async submitSms(smsCode: string): Promise<ISmsToken> {
    if (!this.smsToken || !this.smsToken.signature) {
      Promise.reject({
        code: ERRORS.ARGUMENT_MISSING,
        description: `Has no token saved`,
      });
    }

    this.smsToken.code = smsCode;

    const url = `auth/phone/token/${this.smsToken.id}`;
    const headers = new HttpHeaders().set(HEADERS.SMS, this.smsToken.signature);

    this.smsToken = await this.api.put<ISmsToken>(url, this.smsToken, headers);
    return this.smsToken;
  }

  // Email
  async requestEmail(phone: string, iin: string): Promise<IEmailToken> {
    if (!this.smsToken || !this.smsToken.signature) {
      return Promise.reject({
        code: ERRORS.ARGUMENT_MISSING,
      });
    }

    const url = `auth/email/token`;
    const body = {
      phone: phone,
      iin: iin,
    };

    const headers = new HttpHeaders()
      .set(HEADERS.PHONE, this.smsToken.signature)
      .set(HEADERS.SMS, this.smsToken.signature);

    this.emailToken = await this.api.post<IEmailToken>(url, body, headers);
    return this.emailToken;
  }

  async submitEmail(
    emailCode: string,
    phone: string,
    iin: string
  ): Promise<IEmailToken> {
    this.emailToken.phone = phone;
    this.emailToken.iin = iin;
    this.emailToken.code = emailCode;

    const url = `auth/email/token/${this.emailToken.id}`;

    const headers = new HttpHeaders()
      .set(HEADERS.PHONE, this.smsToken.signature)
      .set(HEADERS.EMAIL, this.emailToken.signature);

    this.emailToken = await this.api.put<IEmailToken>(
      url,
      this.emailToken,
      headers
    );
    return this.emailToken;
  }

  // Register
  async register(phone: string, password: string, iin: string) {
    const url = `users`;

    const body = {
      phone: phone,
      password: password,
      iin: iin,
    };

    const headers = new HttpHeaders()
      .set(HEADERS.PHONE, this.smsToken.signature)
      .set(HEADERS.EMAIL, this.emailToken.signature);

    const registerResult = await this.api.post(url, body, headers);
    return registerResult;
  }

  // Restore
  async restore(phone: string, password: string, iin: string) {
    const url = `users/password`;

    const body = {
      phone: phone,
      password: password,
      iin: iin,
    };

    const headers = new HttpHeaders()
      .set(HEADERS.PHONE, this.smsToken.signature)
      .set(HEADERS.EMAIL, this.emailToken.signature);

    const restoreResult = await this.api.put(url, body, headers);
    return restoreResult;
  }

  // // Iin inline
  // async searchIin(iin: string): Promise<string> {
  //   return new Promise((resolve, reject) => {
  //     setTimeout(() => {
  //       resolve('ИП Курманов Манар');
  //     }, 1000);
  //   });
  // }

  // Check phone
  async checkPhone(phone: string): Promise<ICheckPhoneStatus> {
    const url = `auth/check`;
    let params = new HttpParams({ encoder: new HttpUrlEncodingCodec() });

    params = params.append('phone', encodeURI(phone));
    return this.api.get<ICheckPhoneStatus>(url, null, params);
  }

  // // Iin step
  // async checkIin(iin: string): Promise<ICheckIinStatus> {
  //   const url = `auth/check`;
  //   const params = new HttpParams().set('iin', iin);

  //   return this.api.get(url, null, params);
  // }

  // Iin step
  async checkIinAndPhone(
    iin: string,
    phone: string
  ): Promise<ICheckIinAndPhoneStatus> {
    const url = `auth/check`;

    let params = new HttpParams();
    params = params.append('iin', iin);
    params = params.append('phone', phone);

    const headers = new HttpHeaders().set(
      HEADERS.PHONE,
      this.smsToken.signature
    );

    return this.api.get(url, headers, params);
  }
}
