import { Injectable } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { MaintenanceModalComponent } from '@shared/modals';

@Injectable({
  providedIn: 'root',
})
export class JModalService {
  modalSmParams = {
    backdropClass: 'backdrop_light',
    windowClass: 'modal_light modal_sm',
    centered: true,
  };

  constructor(private modalService: NgbModal) {}

  showMaintenanceModal() {
    this.modalService.open(MaintenanceModalComponent, this.modalSmParams);
  }
}
