import {
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';

import { Subscription, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { NotifierService } from 'angular-notifier';
import { TranslateService } from '@ngx-translate/core';

import { STATUSES } from '@api/errors';

import { IUploadingFile } from '@shared/models';
import { FilesService } from '@shared/services';

@Component({
  selector: 'j-uploading-files',
  templateUrl: './uploading-files.component.html',
  styleUrls: ['./uploading-files.component.scss'],
})
export class UploadingFilesComponent implements OnInit, OnDestroy {
  @Input() isLoading: boolean = false;
  @Input() acceptedFormats: string;
  @Input() uploadingFiles: IUploadingFile[] = [];
  @Input() maxSize: number;

  @Output() updated = new EventEmitter<IUploadingFile[]>();

  subscription = new Subscription();

  constructor(
    private filesService: FilesService,
    private translateService: TranslateService,
    private notifierService: NotifierService
  ) {}

  ngOnInit() {
    this.uploadingFiles = [];
    
    this.subscription.add(
      this.filesService.uploading$.subscribe((files) => {
        files.forEach((file) => {
          if (!this.isSupportedFormat(file.name)) {
            this.notifierService.notify(
              'warning',
              this.translateService.instant(
                'FILES.IS_NOT_SUPPORTED_FORMAT_ERROR',
                {
                  acceptedFormats: this.acceptedFormats,
                }
              )
            );
            return;
          }

          if (!this.isSupportedSize(file.size)) {
            this.notifierService.notify(
              'warning',
              this.translateService.instant('FILES.MAX_SIZE_ERROR')
            );
            return;
          }

          this.prepareUpload(file);
        });
      })
    );
  }

  ngOnDestroy() {
    this.updated.emit([]);
    this.filesService.uploading$.next([]);
    this.uploadingFiles = [];
    this.subscription.unsubscribe();
  }

  onFileDelete(file: IUploadingFile) {
    this.uploadingFiles = this.uploadingFiles.filter((item) => {
      return item != file;
    });
    this.updated.emit(this.uploadingFiles);
  }

  mapTranslate(key: string) {
    return `FILES.${key}`;
  }

  private async prepareUpload(file: File) {
    const textContent = await (file as any).text();

    if (
      this.uploadingFiles
        .map((uploading) => uploading.textContent)
        .includes(textContent)
    ) {
      this.notifierService.notify(
        'default',
        this.translateService.instant('FILES.ALREADY_UPLOADING', {
          fileName: file.name,
        })
      );
      return;
    }

    this.uploadFile(file, textContent);
  }

  private uploadFile(file: File, textContent: string) {
    this.uploadingFiles.push({
      textContent,
      isLoading: true,
    });

    const lastUploadedIndex = this.uploadingFiles.length - 1;

    this.subscription.add(
      this.filesService
        .uploadFile(file)
        .pipe(
          catchError((error, caught) => {
            const errorMessage =
              error.status === STATUSES.ENTITY_TOO_LARGE
                ? 'MAX_SIZE_ERROR'
                : 'DEFAULT_ERROR';

            this.uploadingFiles[lastUploadedIndex] = {
              textContent: textContent,
              isLoading: false,
              progress: 100,
              error: this.mapTranslate(errorMessage),
              file: { name: file.name },
            };
            return throwError(error);
          })
        )
        .subscribe((uploadingFile: IUploadingFile) => {
          this.mapFile(lastUploadedIndex, uploadingFile);
          this.updated.emit(this.uploadingFiles);
        })
    );
  }

  private mapFile(index: number, newFile: IUploadingFile) {
    if (!this.uploadingFiles[index]) {
      return;
    }

    if (newFile.error) {
      this.uploadingFiles[index].error = newFile.error;
    }

    if (newFile.isLoading != this.uploadingFiles[index].isLoading) {
      this.uploadingFiles[index].isLoading = newFile.isLoading;
    }

    if (newFile.file && !this.uploadingFiles[index].file) {
      this.uploadingFiles[index].file = newFile.file;
    }

    if (newFile.progress) {
      this.uploadingFiles[index].progress = newFile.progress;
    }
  }

  private isSupportedSize(size: number) {
    if (!this.maxSize) {
      return true;
    }

    return size <= this.maxSize;
  }

  private isSupportedFormat(filename: string) {
    if (!this.acceptedFormats) {
      return true;
    }

    const re = /(?:\.([^.]+))?$/;
    const extension = re.exec(filename)[1] ? `.${re.exec(filename)[1]}` : '';

    return this.acceptedFormats.split(', ').includes(extension);
  }
}
