import {
  Directive,
  ElementRef,
  HostListener,
  SkipSelf,
  forwardRef,
  Inject,
  ContentChildren,
  QueryList,
  Input,
  Component,
  Optional,
  Self,
  OnInit,
  EventEmitter,
  OnDestroy,
} from '@angular/core';
import { coerceElement } from '@shared/form-field/utils';
import { JFormFieldControl } from '@shared/form-field/form-field-control';
import { Subscription } from 'rxjs';
import { debounceTime } from 'rxjs/operators';

/**
 * @todo check possible ways to manipulate with focus on custom elements
 * tabindex or focusable iteration
 * inject focusable container marker and iterate there?
 */
@Directive({
  selector: '[jFocusOnReturn]',
})
export class FocusOnReturnDirective implements OnInit, OnDestroy {
  readonly subs = new Subscription();
  @Input() jFocusOnReturn?: JFormFieldControl<any>;
  @HostListener('keydown', ['$event']) onKeyDown(e) {
    if (e.which === 13 || e.keyCode === 13) {
      if (this.focusNext()) {
        e.preventDefault();
        e.stopPropagation();
      }
    }
  }
  @HostListener('focus', ['$event']) onFocus(e) {
    this.container.currentTarget = this;
  }
  constructor(
    public el: ElementRef,
    @Optional()
    @Self()
    public hostComponent: JFormFieldControl<any>,
    @Inject(forwardRef(() => FocusableContainerDirective))
    private container: FocusableContainerDirective
  ) {}

  ngOnInit() {
    const host = this.jFocusOnReturn || this.hostComponent;
    if (host) {
      // handle select next for select component
      if (host.changeEvent) {
        this.subs.add(
          (host.changeEvent as EventEmitter<any>)
            .pipe(debounceTime(100))
            .subscribe((v) => {
              if (v) {
                this.focusNext();
              }
            })
        );
      }
    }
  }
  ngOnDestroy() {
    this.subs.unsubscribe();
    if (this.container.currentTarget === this) {
      this.container.currentTarget = null;
    }
  }

  focusNext(): boolean {
    const focusable = this.container.targets.toArray();
    const currentIndex = focusable.indexOf(this);
    const nextIndex = currentIndex + 1;
    if (nextIndex >= 0 && nextIndex < focusable.length) {
      const next = focusable[nextIndex];
      let targetTrigger;
      if (next.jFocusOnReturn) {
        targetTrigger = coerceElement(next.jFocusOnReturn);
      } else {
        targetTrigger = coerceElement(next.el);
      }
      targetTrigger.focus();

      return true;
    }
    return false;
  }
}
/**
 * @todo expensive checking descendants
 */
@Directive({
  selector: '[jFocusableContainer]',
})
export class FocusableContainerDirective {
  @ContentChildren(FocusOnReturnDirective, {
    descendants: true,
    // read: true
  })
  targets: QueryList<FocusOnReturnDirective>;
  currentTarget: FocusOnReturnDirective;
  constructor(public elem: ElementRef) {}

  focusNext() {
    this.currentTarget && this.currentTarget.focusNext();
  }
}
