import { Pipe, PipeTransform } from '@angular/core';

const symbols = {
  KZT: '₸',
  USD: '$',
  EUR: '€',
  RUB: '₽',
  AUD: '$',
  AZN: '₼',
  BYN: 'р.',
  DKK: 'kr.',
  SAR: '.ر.س',
  SGD: '$',
  XAU: 'XAU',
  KGS: 'С̲',
  GBP: '£',
  CNY: '¥',
  CHF: '₣',
  HKD: '$',
  GEL: 'ლ.', // Грузинский лари,
  AED: 'د.إ', // Дирхам ОАЭ,
  INR: '₹', // Индийская рупия,
  CAD: '$', // Канадский доллар,
  MYR: 'RM', // Малайзийский ринггит,
  PLN: 'zł', // Польский злотый,
  THB: '฿', // Тайский бат,
  TRY: '₺', // Турецкая лира,
  UZS: "so'm", // узбекских сумов,
  UAH: '₴', // Украинская гривна,
  CZK: 'Kč', // Чешская крона,
  SEK: 'kr', // Шведская крона,
  KRW: '₩', // южнокорейских вон,
  JPY: '¥', // Японская иен,
  KZT_RU: 'тг'
};
@Pipe({
  name: 'curSymbol',
  pure: true,
})
export class CurSymbolPipe implements PipeTransform {
  transform(value: string): string {
    return symbols[value];
  }
}
