import {
  Component,
  Output,
  EventEmitter,
  Input,
  ViewChild,
  OnDestroy,
} from '@angular/core';
import {
  FormGroup,
  FormControl,
  AbstractControl,
  Validators,
} from '@angular/forms';

import { Subscription } from 'rxjs';

import { NgbDate, NgbDatepicker } from '@ng-bootstrap/ng-bootstrap';

import dayjs from 'dayjs';
import customParseFormat from 'dayjs/plugin/customParseFormat';
import isBetween from 'dayjs/plugin/isBetween';

import { MaskedService } from '@shared/masked/masked.service';

import { JInputComponent } from '../input.component';

const TODAY = new Date();

const DATE_VALIDATOR = (control: AbstractControl) => {
  if (!control.value) {
    return null;
  }

  if (!dayjs(control.value, 'DD.MM.YYYY', true).isValid()) {
    return { dateInvalid: true };
  }

  return null;
};

@Component({
  selector: 'j-range-input',
  templateUrl: './range-input.component.html',
  styleUrls: ['./range-input.component.scss'],
})
export class RangeInputComponent implements OnDestroy {
  maskConfig;

  @Input() submitLabel: string = 'SHARED.APPLY';
  @Input() hideBorders: boolean = false;

  @Input() minDate = {
    year: 1970,
    month: 1,
    day: 1,
  };

  @Input() maxDate = {
    year: TODAY.getFullYear(),
    month: TODAY.getMonth() + 1,
    day: TODAY.getDate(),
  };

  @Input() formGroup: FormGroup;

  @Output() closed = new EventEmitter();
  @Output() submitted = new EventEmitter();

  hoveredDate: NgbDate | null = null;
  fromDate: NgbDate;
  toDate: NgbDate | null = null;
  datepickerModel;

  isFromActive = true;
  isToActive = false;

  @ViewChild('datepicker') datepicker: NgbDatepicker;
  @ViewChild('fromInput') fromInput: JInputComponent;
  @ViewChild('toInput') toInput: JInputComponent;

  subscription = new Subscription();

  constructor(private maskedService: MaskedService) {
    dayjs.extend(isBetween);
    dayjs.extend(customParseFormat);

    this.maskConfig = this.maskedService.getConfig('date');

    this.formGroup = new FormGroup(
      {
        from: new FormControl('', [
          Validators.required,
          (control) => {
            return DATE_VALIDATOR(control);
          },
        ]),
        to: new FormControl('', [Validators.required, DATE_VALIDATOR]),
      },
      (formGroup: FormGroup) => {
        return this.isRangeValid(formGroup);
      }
    );

    const formSubscription =
      this.formGroup.controls.from.valueChanges.subscribe((fromValue) => {
        if (fromValue.length == 0) {
          this.fromDate = null;

          if (this.toDate) {
            this.datepickerModel = this.toDate;
          }
        }
      });

    this.subscription.add(formSubscription);
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  onFromFocus() {
    this.isFromActive = true;
  }

  onToFocus() {
    this.isToActive = true;
  }

  onFromBlur() {
    this.isFromActive = false;
  }

  onToBlur() {
    this.isToActive = false;
  }

  onKeyUpFrom() {
    if (this.formGroup.controls.from.valid) {
      this.navigateDatepicker(this.formGroup.controls.from.value);
      this.fromInput.blur();
      this.toInput.focus();
    }
  }

  onKeyUpTo() {
    if (this.formGroup.controls.to.valid) {
      this.navigateDatepicker(this.formGroup.controls.to.value);
      this.toInput.blur();
    }
  }

  onDateSelection(date: NgbDate, updateInputs: boolean = true) {
    if (!this.fromDate && !this.toDate) {
      this.fromDate = date;
      updateInputs &&
        this.setDateInput(this.fromDate, this.formGroup.controls.from);

      this.isFromActive = false;
      this.isToActive = true;
    } else if (this.fromDate && !this.toDate && date.equals(this.fromDate)) {
      this.toDate = this.fromDate;
      updateInputs &&
        this.setDateInput(this.toDate, this.formGroup.controls.to);
    } else if (this.fromDate && !this.toDate && date.after(this.fromDate)) {
      this.toDate = date;
      updateInputs &&
        this.setDateInput(this.toDate, this.formGroup.controls.to);

      this.isFromActive = false;
      this.isToActive = false;
    } else {
      this.toDate = null;
      this.fromDate = date;

      updateInputs &&
        this.setDateInput(this.toDate, this.formGroup.controls.to);
      updateInputs &&
        this.setDateInput(this.fromDate, this.formGroup.controls.from);

      this.isFromActive = false;
      this.isToActive = true;
    }
  }

  isHovered(date: NgbDate) {
    return (
      this.fromDate &&
      !this.toDate &&
      this.hoveredDate &&
      date.after(this.fromDate) &&
      date.before(this.hoveredDate)
    );
  }

  isInside(date: NgbDate) {
    return this.toDate && date.after(this.fromDate) && date.before(this.toDate);
  }

  isRange(date: NgbDate) {
    return (
      date.equals(this.fromDate) ||
      (this.toDate && date.equals(this.toDate)) ||
      this.isInside(date) ||
      this.isHovered(date)
    );
  }

  isRangeStart(date: NgbDate) {
    return date.equals(this.fromDate) && this.fromDate && this.toDate;
  }

  isRangeEnd(date: NgbDate) {
    return date.equals(this.toDate) && this.fromDate && this.toDate;
  }

  close() {
    this.fromDate = null;
    this.toDate = null;
    this.formGroup.controls.from.setValue('');
    this.formGroup.controls.to.setValue('');
    this.closed.emit();
  }

  submit() {
    let result: any = {};

    if (this.fromDate) {
      result.from = this.convertNgbToDate(this.fromDate);
    }
    if (this.toDate) {
      result.to = this.convertNgbToDate(this.toDate);
    }

    this.submitted.emit(result);
  }

  private setDateInput(date: NgbDate, control: AbstractControl) {
    const newValue = date
      ? dayjs(new Date(date.year, date.month - 1, date.day)).format(
          'DD.MM.YYYY'
        )
      : '';

    control.setValue(newValue);
  }

  private isValidDate(dateString: string) {
    return dayjs(dateString, 'DD.MM.YYYY', true).isValid();
  }

  private isRangeValid(formGroup: FormGroup) {
    if (!formGroup.value) {
      return null;
    }

    const from = formGroup.get('from').value;
    const to = formGroup.get('to').value;

    const fromDate = dayjs(from, 'DD.MM.YYYY', true);
    const toDate = dayjs(to, 'DD.MM.YYYY', true);

    const minDate = dayjs(
      new Date(this.minDate.year, this.minDate.month - 1, this.minDate.day)
    );
    const maxDate = dayjs(
      new Date(this.maxDate.year, this.maxDate.month - 1, this.maxDate.day)
    );

    if (
      !(
        (fromDate.isSame(toDate) ||
          fromDate.isBetween(minDate, toDate) ||
          fromDate.isSame(minDate)) &&
        (toDate.isSame(fromDate) ||
          toDate.isBetween(fromDate, maxDate) ||
          toDate.isSame(maxDate))
      )
    ) {
      return { dateRange: true };
    }

    return null;
  }

  private navigateDatepicker(dateString: string) {
    if (this.isValidDate(dateString)) {
      const [day, month, year] = dateString.split('.');
      this.datepicker.navigateTo({
        day: parseInt(day),
        month: parseInt(month),
        year: parseInt(year),
      });

      const ngbDate = new NgbDate(
        parseInt(year),
        parseInt(month),
        parseInt(day)
      );

      this.datepickerModel = ngbDate;
      this.onDateSelection(ngbDate, false);
    }
  }

  private convertNgbToDate(ngbDate: NgbDate) {
    return dayjs(
      new Date(ngbDate.year, ngbDate.month - 1, ngbDate.day)
    ).toDate();
  }
}
