import {
  Component,
  ContentChild,
  EventEmitter,
  Input,
  Output,
  TemplateRef,
} from '@angular/core';

import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { ListModalComponent } from '../../modals';
import { BULK_OPERATION_STATUS, IBulkOperationResult } from '../../models';
import { ListService } from '../../services/list.service';

@Component({
  selector: 'j-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
})
export class ListComponent {
  @Input() title: string;
  @Input() list: any[];
  @Input() translatePrefix: string = 'LIST';
  @Input() urlPath: string;

  @Input() partialTitleFn: Function;
  @Input() highlightedFn: Function;
  @Input() highlightedTemporarilyFn: Function;

  @Input() isHoverable: boolean = false;
  @Input() isDeletable: boolean = false;
  @Input() isSelectable: boolean = false;
  @Input() isSignable: boolean = false;

  @Input() itemClassNames: string;

  @Output() clicked = new EventEmitter<any>();
  @Output() scrolled = new EventEmitter();
  @Output() signed = new EventEmitter();
  @Output() cleared = new EventEmitter();

  @ContentChild('selectableTemplate', { static: false })
  selectableTemplate: TemplateRef<any>;
  @ContentChild('defaultTemplate', { static: false })
  defaultTemplate: TemplateRef<any>;

  selected: any[] = [];
  loadingIds: string[] = [];

  allSelected = new EventEmitter<boolean>();
  isAllSelected: boolean = false;

  isSigningSelected = false;
  isDeletingSelected = false;

  constructor(
    private modalService: NgbModal,
    private listService: ListService
  ) {}

  onClick(clickedItem: any) {
    this.clicked.emit(clickedItem);
  }

  onSelect(selectedItem: any) {
    if (this.selected.includes(selectedItem)) {
      this.selected = this.selected.filter((item) => item != selectedItem);
    } else {
      this.selected.push(selectedItem);
    }

    this.isAllSelected = this.selected.length === this.list.length;
  }

  onScroll() {
    this.scrolled.emit();
  }

  onSelectAll() {
    if (this.isAllSelected) {
      this.selected = [...this.list];
    } else {
      this.selected = [];
    }

    this.allSelected.emit(this.isAllSelected);
  }

  onDelete(deletedItem: any) {
    this.onDeleteBulk([deletedItem.id]);
  }

  async onDeleteSelected() {
    if (this.isDeletingSelected || this.isSigningSelected) {
      return;
    }
    this.isDeletingSelected = true;

    try {
      await this.onDeleteBulk(this.getSelectedIds());
    } catch (error) {
    } finally {
      this.isDeletingSelected = false;
    }
  }

  async onDeleteBulk(ids: string[]) {
    try {
      const idsToDelete = ids.filter((id) => !this.loadingIds.includes(id));

      if (idsToDelete.length === 0) {
        return;
      }

      this.includeIdsToLoading(ids);

      let result = await this.listService.deleteList(this.urlPath, idsToDelete);

      const deleteResult = await this.showModal(
        'DELETE',
        result,
        this.onDeleteBulk,
        ids
      );

      this.clearListByIds(ids);

      return deleteResult;
    } catch (error) {
      this.showModal(
        'DELETE',
        {
          numberOfFailed: ids.length,
          numberOfSuccess: 0,
          status: BULK_OPERATION_STATUS.FAILED,
        },
        this.onDeleteBulk,
        ids
      );
    } finally {
      this.excludeIdsFromLoading(ids);
    }
  }

  async onSign(letter: any) {
    try {
      let result = await this.listService.sign(this.urlPath, letter.id);

      if (result) {
        this.showModal(
          'SIGN',
          {
            numberOfFailed: 0,
            numberOfSuccess: 1,
            status: BULK_OPERATION_STATUS.SUCCESS,
            results: [],
          },
          this.onSign,
          letter
        );

        this.clearListByIds([letter.id]);
      }
    } catch (error) {
      this.showModal(
        'SIGN',
        {
          numberOfFailed: 1,
          numberOfSuccess: 0,
          status: BULK_OPERATION_STATUS.FAILED,
          results: [],
        },
        this.onSign,
        letter
      );
    }
  }

  async onSignBulk(ids: string[]) {
    try {
      const idsToSign = ids.filter((id) => !this.loadingIds.includes(id));
      if (idsToSign.length === 0) {
        return;
      }

      this.includeIdsToLoading(ids);

      let result = await this.listService.sign(this.urlPath, idsToSign);

      if (result) {
        this.signed.emit();
        await this.showModal('SIGN', result, this.onSignBulk, ids);
        this.clearListByIds(ids);
      }

      return result;
    } catch (error) {
      this.showModal(
        'SIGN',
        {
          numberOfFailed: ids.length,
          numberOfSuccess: 0,
          status: BULK_OPERATION_STATUS.FAILED,
        },
        this.onSignBulk,
        ids
      );
    } finally {
      this.excludeIdsFromLoading(ids);
    }
  }

  onSignAll() {
    this.onSignBulk(this.getAllIds());
  }

  async onSignSelected() {
    if (this.isDeletingSelected || this.isSigningSelected) {
      return;
    }
    this.isSigningSelected = true;

    try {
      await this.onSignBulk(this.getSelectedIds());
    } catch (error) {
    } finally {
      this.isSigningSelected = false;
    }
  }

  mapTranslate(key: string) {
    return `${this.translatePrefix}.${key}`;
  }

  private async showModal(
    key: string,
    result: IBulkOperationResult = null,
    callback: Function,
    args: any
  ) {
    const modalRef = this.modalService.open(ListModalComponent, {
      backdropClass: 'backdrop_light',
      windowClass: `modal_light ${
        result.results?.length === 0 ||
        result.status === BULK_OPERATION_STATUS.SUCCESS
          ? 'modal_sm'
          : ''
      }`,
      backdrop: 'static',
      centered: true,
    });

    (modalRef.componentInstance as ListModalComponent).translatePrefix =
      this.mapTranslate('MODAL');
    (modalRef.componentInstance as ListModalComponent).partialTitleFn =
      this.partialTitleFn;
    (modalRef.componentInstance as ListModalComponent).key = key;
    (modalRef.componentInstance as ListModalComponent).result = result;
    (modalRef.componentInstance as ListModalComponent).list = this.list;

    try {
      await modalRef.result;
      return callback.apply(this, [args]);
    } catch (error) {
      return error;
    }
  }

  private includeIdsToLoading(ids: string[]) {
    ids.forEach((id) => {
      if (!this.loadingIds.includes(id)) {
        this.loadingIds.push(id);
      }
    });
  }

  private excludeIdsFromLoading(ids: string[]) {
    ids.forEach((id) => {
      const index = this.loadingIds.indexOf(id);

      if (index >= 0) {
        this.loadingIds.splice(index, 1);
      }
    });
  }

  private clearListByIds(ids: string[]) {
    ids.forEach((id) => {
      const itemIndex = this.list.findIndex((item) => item.id === id);
      this.list.splice(itemIndex, 1);

      const selectedIndex = this.selected.findIndex((item) => item.id === id);
      this.selected.splice(selectedIndex, 1);
    });

    this.cleared.emit();
  }

  private getSelectedIds() {
    return this.selected.map((item) => {
      return item.id;
    });
  }

  private getAllIds() {
    return this.list.map((item) => {
      return item.id;
    });
  }
}
