import { Component, Input } from '@angular/core';

import { AmountFormatPipe } from '@shared/pipes/amount-format.pipe';
import { IMoney } from '@shared/currency/currency.model';

const DECIMALS_MAX_LENGTH = 10;
@Component({
  selector: 'j-money',
  templateUrl: './money.component.html',
  styleUrls: ['./money.component.scss'],
})
export class MoneyComponent {
  @Input() money: IMoney;

  @Input() showSign: boolean;
  @Input() isDebit: boolean;
  @Input() isCredit: boolean;

  @Input() showAllDecimals = false;

  constructor(private amountFormatPipe: AmountFormatPipe) {}

  get sign() {
    if (this.amount == 0) {
      return '';
    }

    if ((this.showSign && this.isDebit) || this.amount < 0) {
      return `- `;
    }

    if (
      (this.showSign && this.amount > 0) ||
      (this.showSign && this.isCredit)
    ) {
      return `+ `;
    }
  }

  get splittedAmount() {
    if (this.amount) {
      let amountToDivide;

      if (this.showAllDecimals) {
        amountToDivide = this.addZeroes(this.amount);
      } else {
        amountToDivide = this.amountFormatPipe.transform(this.amount);
      }

      const [integers, decimals] = amountToDivide.split(',');

      return this.generateSplitted(integers, decimals);
    } else {
      return this.generateSplitted();
    }
  }

  get amount() {
    return this.money && this.money.amount;
  }

  get currency() {
    return this.money && this.money.currency;
  }

  private addZeroes(num) {
    if (!num) {
      return num;
    }

    num = +num.toFixed(DECIMALS_MAX_LENGTH);

    const dec = `${num}`.split('.')[1];
    const len = dec && dec.length > 2 ? dec.length : 2;

    return `${+num.toFixed(len)}`.replace('.', ',');
  }

  private addFixedZeroes(num) {
    if (!num) {
      return num;
    }
    const dec = `${num}`.split('.')[1];

    if (dec?.length > 0) {
      return `${Number(num).toFixed(2)}`.replace('.', ',');
    } else {
      return `${Number(num)}`.replace('.', ',');
    }
  }

  private hasDecimals(decimals: string) {
    return parseInt(decimals) > 0;
  }

  private generateSplitted(integers = null, decimals = null) {
    return {
      integers: integers || '0',
      decimals: this.hasDecimals(decimals)
        ? decimals.length < 2
          ? `${decimals}0`
          : decimals
        : null,
    };
  }
}
