import { Injectable } from '@angular/core';
import { Employee, EmployeeName } from './employee.model';
import { ApiService, ERRORS } from '@api/index';
import { CompanyService } from '@company/company.service';
import { HttpParams, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root',
})
export class EmployeeService {
  constructor(private api: ApiService, private company: CompanyService) {}

  async createEmployee(employee: Employee) {
    const url = `companies/${this.company.currentId}/employees`;
    const newEmployee = await this.api.post<Employee>(
      url,
      employee,
      new HttpHeaders()
    );

    return new Employee(newEmployee);
  }

  async getNameByIin(iin: string): Promise<EmployeeName> {
    const url = `companies/${this.company.currentId}/employees/fullname`;
    const headers = new HttpHeaders();
    const params = new HttpParams().set('iin', iin);

    try {
      const retrievedData = await this.api.get<EmployeeName>(
        url,
        headers,
        params
      );
      if (retrievedData?.lastName) {
        return {
          lastName: retrievedData.lastName,
          firstName: retrievedData.firstName,
          middleName: retrievedData.middleName
        };
      } else {
        throw ERRORS.RESOURCE_NOT_FOUND;
      }
    } catch (error) {
      throw error;
    }
  }

  async getEmployees(): Promise<Employee[]> {
    const url = `companies/${this.company.currentId}/employees`;
    const retrievedEmployees = await this.api.get<Employee[]>(url);

    return retrievedEmployees.map((employee) => {
      const mappedEmployee = new Employee(employee);
      return mappedEmployee;
    });
  }
  async deleteEmployee(iins: string[] | string) {
    let toDelete: string[]
    if (Array.isArray(iins)) {
      toDelete = iins;
    } else {
      toDelete = [iins];
    }
    const url = `companies/${this.company.currentId}/employees`;
    let params = new HttpParams();

    toDelete.forEach((iin) => {
      params = params.append('iins', iin);
    });

    const removeResult = await this.api.delete<Employee>(
      url,
      new HttpHeaders(),
      params
    );

    return removeResult;
  }

  async editEmployee(employee: Employee) {
    const url = `companies/${this.company.currentId}/employees`;

    const employeeBody = {
      birthDate: employee.birthDate,
      fio: employee.fio,
      firstName: employee.firstName,
      middleName: employee.middleName,
      lastName: employee.lastName,
      iban: employee.iban,
      iin: employee.iin,
      ocmcSum: employee.ocmcSum,
      pensionSum: employee.pensionSum,
      salarySum: employee.salarySum,
      socialSum: employee.socialSum,
    };

    const editResult = await this.api.put<Employee>(
      url,
      employeeBody,
      new HttpHeaders()
    );

    return new Employee(editResult);
  }

  public filterEmployee(employee: Employee, term: string) {
    return ['iban', 'fio', 'firstName', 'middleName', 'lastName', 'iin'].some((key: keyof Employee) =>
      (employee[key]) && (employee[key] as string).toLocaleLowerCase().includes(term)
    );
  }
}
