import { AbstractControl, ValidationErrors } from '@angular/forms';

import { isEmptyInputValue } from './utils';

export class AccountValidators {
  static isTengeAcc(control: AbstractControl): ValidationErrors | null {
    if (
      isEmptyInputValue(control.value) ||
      typeof control.value !== 'string' ||
      control.value.length < 2
    ) {
      return null;
    }

    if (control.value.substring(0, 2) !== 'KZ') {
      return { isTengeAcc: true };
    }
    return null;
  }
}
