import {
  NgModule, ModuleWithProviders,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { IsBreakpointPipe } from './is-breakpoint.pipe';
import { ResponsiveDetector } from './responsive-detector.component';
import { breakpointProvider } from './constants';

@NgModule({
  declarations: [IsBreakpointPipe, ResponsiveDetector],
  exports: [IsBreakpointPipe, ResponsiveDetector],
  providers: [breakpointProvider],
  imports: [CommonModule],
})
export class ResponsiveToolkitModule {
  static forRoot(config?): ModuleWithProviders<ResponsiveToolkitModule> {
    return {
      ngModule: ResponsiveToolkitModule,
      providers: [breakpointProvider],
    };
  }
}
