import { Component, forwardRef, Input, OnInit } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { MaskedService } from '@shared/masked/masked.service';
import { BankFromAccountPipe } from '@shared/pipes';
import { MaskPipe } from 'ngx-mask';
import { JInputComponent } from '../input.component';

@Component({
  selector: 'j-iban-input',
  templateUrl: './iban-input.component.html',
  styleUrls: ['./iban-input.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => IbanInputComponent),
      multi: true,
    },
  ],
})
export class IbanInputComponent extends JInputComponent {
  @Input() label = 'IBAN';
  @Input() isLoading = false;
  @Input()
  maskType = 'iban';

  constructor(
    public maskedService: MaskedService,
    private maskPipe: MaskPipe,
    private bankFromAccountPipe: BankFromAccountPipe
  ) {
    super(maskedService);
  }

  ngOnInit() {
    super.ngOnInit();
  }

  onPaste(event: ClipboardEvent) {
    event.preventDefault();
    const clipboardData = event.clipboardData || (window as any).clipboardData;
    const pastedText = clipboardData.getData('text');
    const toPasteRaw = this.maskedService.getPastedIban(pastedText);
    this.formControl.setValue(this.getMaskedIban(toPasteRaw));
  }

  onBackspaceClick(event) {
    const selectionMinIndex = this.maskConfig.prefix.length;
    if (event.target.selectionStart < selectionMinIndex) {
      this.input.nativeElement.setSelectionRange(
        selectionMinIndex,
        event.target.selectionEnd
      );
    }
  }

  onKeyUp() {
    if (this.hasInternalError) {
      this.triggerHelperAnimation();
    }
  }

  get internalError() {
    if (this.hasWrongBicError) {
      return 'ERRORS.WRONG_BIC';
    } else if (this.hasMaxLengthError) {
      return 'ERRORS.IBAN_LENGTH';
    }
    return null;
  }

  get hasInternalError() {
    return this.hasWrongBicError || this.hasMaxLengthError;
  }

  get hasWrongBicError() {
    return !this.formControl.errors?.mask && this.formControl.errors?.bankExist;
  }

  get hasMaxLengthError() {
    return this.formControl.errors?.mask && this.hasError;
  }

  get shadow() {
    const maskedValue = this.maskPipe.transform(
      this.formControl.value,
      this.maskConfig.mask
    );
    return this.maskedService.getShadow(this.maskType, maskedValue);
  }

  get hasBankLogo() {
    return this.bic ? true : false;
  }

  get isShadowHidden() {
    return !this.isFilled && !this.isFocused;
  }

  get isPrefixHidden() {
    return !this.isFilled && !this.isFocused;
  }

  get bic() {
    const fullIban = this.getFullIban(this.formControl.value);
    return this.bankFromAccountPipe.transform(fullIban, 'bic');
  }

  clear() {
    if (this.isDisabled) {
      return;
    }
    this.formControl.setValue('');

    const selectionIndex =
      this.formControl.value.length + this.maskConfig.prefix.length;
    this.focus(selectionIndex);
  }

  private getFullIban(ibanWithoutPrefix: string) {
    return `${this.maskConfig.prefix}${ibanWithoutPrefix}`;
  }

  private getMaskedIban(unmaskedIban: string) {
    const maskedValue = this.maskPipe.transform(
      unmaskedIban,
      this.maskConfig.mask
    );
    return maskedValue;
  }
}
