import { Injectable, OnDestroy } from '@angular/core';

import { BehaviorSubject, Observable, Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';

import { ApiService } from '@api/api.service';

import { IOtpDevice } from '@company/company.model';
import { CompanyService } from '@company/company.service';

@Injectable({
  providedIn: 'root',
})
export class AuthoritiesService implements OnDestroy {
  private otpDevices: IOtpDevice[];
  private otpDevices$ = new BehaviorSubject<IOtpDevice[]>([]);

  subscription = new Subscription();

  constructor(
    private companyService: CompanyService,
    private apiService: ApiService
  ) {
    this.initCompanySubscription();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  getOtpDevices(): Observable<IOtpDevice[]> {
    return this.otpDevices$.asObservable();
  }

  async fetchOtpDevices() {
    try {
      const url = `companies/${this.companyService.currentId}/otp-devices`;
      this.otpDevices = await this.apiService.get<IOtpDevice[]>(url);
      this.otpDevices$.next(this.otpDevices);
    } catch (error) {
      this.otpDevices$.next(null);
    }
  }

  fetchRestrictions() {
    const url = `companies/${this.companyService.currentId}/otp-devices`;
    return this.apiService.get<IOtpDevice[]>(url);
  }

  private initCompanySubscription() {
    this.subscription.add(
      this.companyService.refresh$
        .pipe(filter(([, , isAuthorized]) => isAuthorized))
        .subscribe(() => {
          this.fetchOtpDevices();
        })
    );
  }
}
