import { CURRENCY } from '@shared/currency/currency.model';
import { Observable, of } from 'rxjs';

export type currencyCode =  keyof typeof CURRENCY;

export type Currency = {
    code: currencyCode,
    isPossible: boolean,
    possible?: boolean,
};

export type CurrencyWithName = Currency & { name: string };

export abstract class CurrenciesListServiceAbstract {

    public abstract loadCurrencyList(): Observable<Currency[]>;

}
