import { Injectable } from '@angular/core';
import { ApiService } from '@api/api.service';
import { CompanyService } from '@company/company.service';
import { Observable, of } from 'rxjs';
import { CurrenciesListServiceAbstract, Currency } from './currencies-list.service.abstract';

@Injectable()
export class CurrenciesListService extends CurrenciesListServiceAbstract {

    constructor(
        private apiService: ApiService,
        private companyService: CompanyService
    ) {
        super();
    }

    loadCurrencyList(): Observable<Currency[]> {
        const url = `/companies/${this.companyService.currentId}/additional-accounts/currencies`;
        return this.apiService.getObservable(url);
    }

}
