import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';

import { BehaviorSubject } from 'rxjs';

import { ApiService, HEADERS } from '@api/index';
import { CompanyService } from '@company/company.service';

import { FEATURE_FLAG, IFeatureFlag } from './feature-flags.model';
import { filter, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class FeatureFlagsService {
  private isPending = false;
  private featureFlags: IFeatureFlag[];

  public featureFlags$ = new BehaviorSubject<IFeatureFlag[]>([]);

  constructor(
    private apiService: ApiService,
    private router: Router,
    private companyService: CompanyService
  ) {
    this.loadFeatureFlags();

    this.companyService.refresh$.subscribe((companyRefresh) => {
      const [user, company, isAuthorized] = companyRefresh;

      if (isAuthorized) {
        this.loadFeatureFlags();
      }
    });
  }

  getFeatureFlags() {
    return this.featureFlags$
      .asObservable()
      .pipe(filter((flags) => flags.length > 0));
  }

  async checkFeatureRoute(key: FEATURE_FLAG) {
    if (this.isDisabled(key)) {
      this.router.navigateByUrl('/');
    }
  }

  private async loadFeatureFlags() {
    if (this.isPending) {
      return;
    }

    this.isPending = true;

    const url = `feature-flags`;

    try {
      this.featureFlags = await this.apiService.get<IFeatureFlag[]>(
        url,
        new HttpHeaders({ [HEADERS.SKIP_OFFLINE_INTERCEPTOR]: 'true' })
      );
    } catch (error) {
      this.featureFlags = [];
    } finally {
      this.isPending = false;
    }

    this.featureFlags$.next(this.featureFlags);
    // this.featureFlags$.complete();

    return this.featureFlags;
  }

  isDisabled(key: FEATURE_FLAG) {
    return (
      this.featureFlags?.find((featureFlag) => featureFlag.key === key)
        ?.active === false || false
    );
  }

  isDisabledStream(key: FEATURE_FLAG) {
    return this.getFeatureFlags().pipe(map(() => this.isDisabled(key)));
  }

  async isDisabledPromise(key: FEATURE_FLAG) {
    if (!this.featureFlags) {
      await this.loadFeatureFlags();
    }

    return this.isDisabled(key);
  }
}
