import {
  ValidatorFn,
  AbstractControl,
  ValidationErrors,
  Validators,
} from '@angular/forms';
import { isEmptyInputValue } from './utils';
import { countWords, safeUnmaskAmount } from '@utils/index';

export enum CompareOperation {
  EQUAL = 'EQUAL',
}
const operators: Record<CompareOperation, (a, b) => boolean> = {
  [CompareOperation.EQUAL]: (a, b) => a === b,
};

export class MiscValidators {
  static kbe(control: AbstractControl): ValidationErrors | null {
    if (isEmptyInputValue(control.value)) {
      return null;
    }
    const parsed = parseFloat(control.value);
    if (
      typeof parsed !== 'number' ||
      parsed > 29 ||
      parsed < 11 ||
      parsed === 20 ||
      parsed === 10
    ) {
      return { kbe: true };
    }
    return null;
  }
  static isGreaterThan(thanControl: AbstractControl): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      if (thanControl.value && control.value) {
        if (thanControl.value >= control.value) {
          return { isGreaterThan: true };
        }
      }
      return null;
    };
  }

  static isSmallerThan(thanControl: AbstractControl): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      if (thanControl.value && control.value) {
        if (thanControl.value <= control.value) {
          return { isSmallerThan: true };
        }
      }
      return null;
    };
  }

  static wordsCount(
    count: number,
    operator: keyof typeof operators = CompareOperation.EQUAL
  ) {
    return (control: AbstractControl): ValidationErrors | null => {
      if (control.value && typeof control.value === 'string') {
        const isValid = operators[operator](count, countWords(control.value));
        if (!isValid) {
          return { countWords: true };
        }
      }
      return null;
    };
  }

  static transformBeforeValidator(transformFn, validatorFn: ValidatorFn) {
    return (control: AbstractControl) => {
      return validatorFn({
        value: transformFn(control.value),
      } as AbstractControl);
    };
  }

  static SafeMinAmount(min: number) {
    return MiscValidators.transformBeforeValidator(
      safeUnmaskAmount,
      Validators.min(min)
    );
  }

  static SafeMaxAmount(max: number) {
    return MiscValidators.transformBeforeValidator(
      safeUnmaskAmount,
      Validators.max(max)
    );
  }

  static revalidateLinkControlIf(predicate, link: AbstractControl) {
    return (control: AbstractControl) => (
      predicate() && link.updateValueAndValidity(), null
    );
  }
}
