import { Injectable } from '@angular/core';
import { ApiService } from '@api/api.service';
import { CompanyService } from '@company/company.service';
import { IExchangeRate, IHistoryPayment, PAYMENT_TYPE } from '@shared/models';

@Injectable({
  providedIn: 'root',
})
export class PaymentsSharedService {
  constructor(
    private apiService: ApiService,
    private companyService: CompanyService
  ) {}

  getPaymentById(paymentId: string) {
    const url = `companies/${this.companyService.currentId}/payments/${paymentId}`;

    return this.apiService.get<IHistoryPayment>(url);
  }

  getExchangeRate(body: IExchangeRate) {
    const url = `companies/${this.companyService.currentId}/payments/exchange-rate`;
    return this.apiService.post<IExchangeRate>(url, body);
  }

  async retrieveExhangeRates(payment: IHistoryPayment) {
    const exchangeRate = payment.details.exchangeRate;

    const { currencyFrom, currencyTo, sum } = exchangeRate;

    const current = await this.getExchangeRate({
      currencyFrom,
      currencyTo,
      sum,
    });

    return {
      original: exchangeRate,
      current,
    };
  }

  getRateAmounts(exchangeRate: IExchangeRate) {
    if (!exchangeRate) {
      return;
    }

    if (exchangeRate.equation) {
      return {
        from: exchangeRate.equation.from,
        to: exchangeRate.equation.to,
      };
    }

    if (
      exchangeRate.currencyFrom &&
      exchangeRate.currencyTo &&
      exchangeRate.rate
    ) {
      return {
        from: {
          amount: exchangeRate.rate,
          currency: exchangeRate.currencyFrom,
        },
        to: {
          amount: 1,
          currency: exchangeRate.currencyTo,
        },
      };
    }
  }

  updatePaymentExchangeRate(
    payment: IHistoryPayment,
    exchangeRate: IExchangeRate
  ) {
    const url = `companies/${this.companyService.currentId}/payments/${payment.id}/exchange-rate`;
    const body = {
      ...exchangeRate,
      commissionWithdrawalIban:
        payment.details.exchangeRate.commissionWithdrawalIban,
    };

    return this.apiService.patch(url, body);
  }

  updatePayment(payment: IHistoryPayment) {
    const url = `companies/${this.companyService.currentId}/payments/${payment.id}`;
    return this.apiService.put(url, payment);
  }
}
