export enum TOKEN_TYPE {
  SMS = 'PHONE',
  OTP = 'OTP',
  EMAIL = 'EMAIL',
}

export interface IAuthToken {
  id?: string;

  timestamp?: Date;
  type?: TOKEN_TYPE;

  signature?: string;

  phone: string;
  code: string;
  text?: string;
}

export interface ISmsToken extends IAuthToken {
  actionIds?: string[];
}

export interface IEmailToken extends IAuthToken {
  type: TOKEN_TYPE.EMAIL;
  email: string;
  iin: string;
}
