import { Component, OnInit, Input, OnDestroy } from '@angular/core';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

import { Employee } from '@shared/employee/employee.model';
import { EmployeeService } from '@shared/employee/employee.service';
import { MaskPipe } from 'ngx-mask';
import { MaskedService } from '@shared/masked/masked.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { PAYMENT_TYPE } from '@shared/models';
import { CompanyService } from '@company/company.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'j-saved-employee-list',
  templateUrl: './saved-employee-list.component.html',
  styleUrls: ['./saved-employee-list.component.scss'],
})
export class SavedEmployeeListComponent implements OnInit, OnDestroy {
  list: Employee[];
  search = '';
  isAllSelected = false;
  isDeletingBulk = false;
  isLoading = true;
  isEditing = false;
  editEmployee: Employee;
  editEmployeeIndex: Employee;

  deletingEmployees = [];

  iinMaskConfig = this.maskedService.getConfig('iin');

  @Input() paymentType: PAYMENT_TYPE;
  @Input() birthDateRequired?: boolean;
  @Input() ibanRequired?: boolean;

  subscription = new Subscription();

  constructor(
    private maskedService: MaskedService,
    private employeeService: EmployeeService,
    private modal: NgbActiveModal,
    private maskPipe: MaskPipe,
    private spinner: NgxSpinnerService,
    private companyService: CompanyService
  ) {
    const companySubscription = this.companyService.refresh$.subscribe(
      (refreshResult) => {
        this.init();
      }
    );
    this.subscription.add(companySubscription);
  }

  ngOnDestroy() {
    this.removeDeletingEmployees();
    this.subscription.unsubscribe();
  }

  ngOnInit(): void {
    this.init();
  }

  closeModal(event: Event) {
    event.preventDefault();
    this.modal.close(this.selectedItems);
  }

  selectAll() {
    this.isAllSelected = !this.isAllSelected;
    this.filteredList.forEach((item) => {
      item.isSelected = this.isAllSelected;
    });
  }

  deleteBulk(event: Event) {
    event.preventDefault();
    this.selectedItems.forEach((item, index) => {
      if (index === 0) {
        item.isDeleting = true;
      } else {
        item.isHidden = true;
      }
      this.deletingEmployees.push(item);
      return item;
    });
    this.isDeletingBulk = true;
  }

  onSelect(employee: Employee) {
    this.isAllSelected = this.selectedItems.length == this.filteredList.length;
  }

  onEdit(editResult) {
    this.editEmployee = editResult.employee;
    this.editEmployeeIndex = editResult.index;
    this.isEditing = true;
  }

  onEditClose() {
    this.isEditing = false;
  }

  onEditSave(editResult) {
    this.list[editResult.index] = editResult.employee;
    this.isEditing = false;
  }

  onDelete(deleteResult) {
    const { employee, index } = deleteResult;
    this.deletingEmployees.push(employee);
  }

  onDeleteCancelled(deleteResult) {
    if (this.isDeletingBulk) {
      this.list = this.list.map((item) => {
        item.isHidden = false;
        item.isDeleting = false;
        return item;
      });

      this.deletingEmployees = [];
      this.isDeletingBulk = false;
    } else {
      const { employee, index } = deleteResult;
      this.deletingEmployees.filter((item) => item.id != employee.id);
    }
  }

  async onDeleteConfirmed(deleteResult) {
    let toRemove;
    if (this.isDeletingBulk) {
      toRemove = this.selectedItems;
      this.list = this.list.filter((item) => item.isSelected == false);
      this.isDeletingBulk = false;

      toRemove.forEach((employee) => {
        this.deletingEmployees.filter((item) => item.id != employee.id);
      });
    } else {
      const { employee, index } = deleteResult;
      this.deletingEmployees.filter((item) => item.id != employee.id);
      toRemove = [employee];
      this.list.splice(index, 1);
    }

    const removeResult = await this.employeeService.deleteEmployee(
      this.retrieveIinsFromEmployees(toRemove)
    );
    return removeResult;
  }

  async init() {
    this.isLoading = true;
    this.spinner.show();
    this.list = await this.employeeService.getEmployees();
    this.spinner.hide();
    this.isLoading = false;
  }

  get filteredList() {
    if (!this.search) {
      return this.list;
    }
    return this.list.filter(
      (item) =>
        item.name.toLowerCase().indexOf(this.search.toLowerCase()) > -1 ||
        item.iin.toLowerCase().indexOf(this.search.toLowerCase()) > -1
    );
  }

  get selectedItems() {
    if (!this.filteredList) {
      return [];
    }
    return this.filteredList
      .filter((item) => item.isSelected == true)
      .map((item) => {
        item.iin = this.maskPipe.transform(item.iin, this.iinMaskConfig.mask);
        return item;
      });
  }

  private retrieveIinsFromEmployees(employees: Employee[]) {
    return employees.map((employee) => {
      const unmaskedIin = this.maskedService.getUnmaskedNumber(employee.iin);
      return unmaskedIin;
    });
  }

  private async removeDeletingEmployees() {
    if (this.deletingEmployees.length > 0) {
      try {
        await this.employeeService.deleteEmployee(
          this.retrieveIinsFromEmployees(this.deletingEmployees)
        );
      } catch (error) {}
    }
  }
}
