import { Injectable } from '@angular/core';

import { BehaviorSubject, Observable, combineLatest } from 'rxjs';
import { distinctUntilChanged, map, filter, switchMap } from 'rxjs/operators';

import { User, AUTHORITY_TYPE, IUserAuthority } from '@shared/models/user';
import { UserService, AUTHORITY_ID } from '@shared/services/user.service';
import {
  ISignature,
  LETTER_STATUS,
  PAYMENT_STATUS,
  SIGNATURE_SCHEME,
} from '@shared/models';

import { ApiService } from '@api/api.service';

import { AuthService } from '@auth/auth.service';

import { Company, COMPANY_ID_KEY, IAuthorizedPerson } from './company.model';
import { TokenService } from '@shared/token/token.service';

const FROM_ATF_QUERY_PARAM = 'fromAtf';

@Injectable({
  providedIn: 'root',
})
export class CompanyService {
  public currentAuthoritySubject$: BehaviorSubject<IUserAuthority> =
    new BehaviorSubject<IUserAuthority>(undefined);
  public currentAuthority$ = this.currentAuthoritySubject$.pipe(
    distinctUntilChanged()
  );
  public readonly refresh$: Observable<
    [User | null, IUserAuthority | null, boolean | null] | []
  >;

  private authorizedPersons: IAuthorizedPerson[];
  private authorizedPersons$ = new BehaviorSubject<IAuthorizedPerson[]>([]);

  constructor(
    private userService: UserService,
    private api: ApiService,
    private authService: AuthService,
    private tokenService: TokenService
  ) {
    this.userService
      .getUser()
      .pipe(filter((u) => !!u))
      .subscribe((user) => this.initializeAuthority());

    this.refresh$ = combineLatest([
      this.userService.getUser().pipe(distinctUntilChanged()),
      this.currentAuthoritySubject$.pipe(distinctUntilChanged()),
      this.authService.authEvents$,
    ]);

    this.refresh$
      .pipe(filter(([user, company, isAuthorized]) => isAuthorized))
      .subscribe(() => {
        this.fetchAuthorizedPersons();
      });

    // combineLatest([this.userService.getUser(), this.currentCompanySubjet$])
    //   .pipe(
    //     filter(([u, c]) => !!(u && u.userAuthorities && c && c.id)),
    //     map(
    //       ([u, c]) =>
    //         u.userAuthorities.find((authority) => authority.companyId === c.id)
    //           .authority
    //     )
    //   )
    //   .subscribe(this.authorityLevelSubject$);
  }

  get currentAuthority(): IUserAuthority {
    return this.currentAuthoritySubject$.getValue();
  }

  set currentAuthority(authority: IUserAuthority) {
    if (authority) {
      localStorage.setItem(AUTHORITY_ID, authority.id);
      localStorage.setItem(COMPANY_ID_KEY, authority.companyId);
      this.currentAuthoritySubject$.next(authority);
    }
  }

  get currentCompany$(): Observable<Company> {
    return this.currentAuthority$.pipe(map((a) => a && a.company));
  }

  get currentCompany(): Company {
    return this.currentAuthority && this.currentAuthority.company;
  }

  get authorityLevel$(): Observable<AUTHORITY_TYPE> {
    return this.currentAuthority$.pipe(map((a) => a && a.authority));
  }

  get authorityLevel(): AUTHORITY_TYPE {
    return this.currentAuthority && this.currentAuthority.authority;
  }

  get currentId() {
    return (
      (this.currentCompany && this.currentCompany.id) ||
      localStorage.getItem(COMPANY_ID_KEY)
    );
  }

  isFromAtf(queryParams = null) {
    return (
      queryParams &&
      queryParams[FROM_ATF_QUERY_PARAM] &&
      JSON.parse(queryParams[FROM_ATF_QUERY_PARAM]) === true
    );
  }

  hasOnlyPrimaryAuthorities(
    persons: IAuthorizedPerson[] = this.authorizedPersons
  ) {
    return persons.every(
      (person) => person.authority === AUTHORITY_TYPE.PRIMARY_SIGNATURE
    );
  }

  isNoSignAuthority(
    authority: AUTHORITY_TYPE = this.currentAuthority?.authority
  ) {
    return authority === AUTHORITY_TYPE.NO_SIGNATURE;
  }

  isPrimaryAuthority(
    authority: AUTHORITY_TYPE = this.currentAuthority?.authority
  ) {
    return authority === AUTHORITY_TYPE.PRIMARY_SIGNATURE;
  }

  isSecondaryAuthority(
    authority: AUTHORITY_TYPE = this.currentAuthority?.authority
  ) {
    return authority === AUTHORITY_TYPE.SECONDARY_SIGNATURE;
  }

  async fetchAuthorizedPersons() {
    if (!this.authService.isAuthenticated) {
      return;
    }

    try {
      const url = `companies/${this.currentId}/authorized-persons`;
      this.authorizedPersons = await this.api.get<IAuthorizedPerson[]>(url);

      if (this.authorizedPersons$.thrownError) {
        this.authorizedPersons$ = new BehaviorSubject<IAuthorizedPerson[]>(
          this.authorizedPersons
        );
      } else {
        this.authorizedPersons$.next(this.authorizedPersons);
      }
    } catch (error) {
      this.authorizedPersons$.error(error);
    }
  }

  getAuthorizedPersons(): Observable<IAuthorizedPerson[]> {
    return this.authorizedPersons$.asObservable();
  }

  isSignable(
    status: PAYMENT_STATUS | LETTER_STATUS = null,
    signatureScheme: SIGNATURE_SCHEME = this.currentAuthority.company
      .signatureScheme,
    authority: AUTHORITY_TYPE = this.currentAuthority.authority,
    signaturesCount: number = 0
  ) {
    if (!signatureScheme) {
      signatureScheme = this.currentAuthority.company.signatureScheme;
    }

    if (
      status &&
      !(
        status === PAYMENT_STATUS.CREATED ||
        status === PAYMENT_STATUS.DRAFT ||
        status === LETTER_STATUS.DRAFT ||
        status === LETTER_STATUS.ON_SIGN
      )
    ) {
      return false;
    }

    if (this.isNoSignAuthority(authority)) {
      return false;
    }

    if (
      this.isSingleSignScheme(signatureScheme) &&
      this.isPrimaryAuthority(authority)
    ) {
      return true;
    }
    if (
      this.isMultipleSignScheme(signatureScheme) &&
      this.isPrimaryAuthority(authority)
    ) {
      return signaturesCount > 0;
    }
    if (
      this.isMultipleSignScheme(signatureScheme) &&
      this.isSecondaryAuthority(authority)
    ) {
      return signaturesCount === 0;
    }

    return false;
  }

  isSendableForSign(
    paymentStatus: PAYMENT_STATUS,
    signatureScheme: SIGNATURE_SCHEME,
    authority: AUTHORITY_TYPE = this.currentAuthority.authority
  ) {
    if (!signatureScheme) {
      signatureScheme = this.currentAuthority.company.signatureScheme;
    }

    if (
      !(
        paymentStatus === PAYMENT_STATUS.CREATED ||
        paymentStatus === PAYMENT_STATUS.DRAFT
      )
    ) {
      return false;
    }

    if (this.isNoSignAuthority(authority)) {
      return true;
    }

    if (
      this.isMultipleSignScheme(signatureScheme) &&
      this.isPrimaryAuthority(authority)
    ) {
      return true;
    }

    return false;
  }

  isSignableByOthers(
    paymentStatus: PAYMENT_STATUS,
    signatureScheme: SIGNATURE_SCHEME,
    authority: AUTHORITY_TYPE = this.currentAuthority.authority,
    signaturesCount: number = 0
  ) {
    return (
      paymentStatus === PAYMENT_STATUS.CREATED &&
      this.isSignable(
        paymentStatus,
        signatureScheme,
        authority,
        signaturesCount
      ) === false
    );
  }

  isSingleSignScheme(
    signatureScheme: SIGNATURE_SCHEME = this.currentCompany.signatureScheme
  ) {
    return signatureScheme === SIGNATURE_SCHEME.SINGLE_SIGN;
  }

  isMultipleSignScheme(
    signatureScheme: SIGNATURE_SCHEME = this.currentCompany.signatureScheme
  ) {
    return signatureScheme === SIGNATURE_SCHEME.MULTIPLE_SIGN;
  }

  retrieveShortName(person: IAuthorizedPerson) {
    const firstName = person.firstname ? `${person.firstname[0]}.` : '';
    const middlename = person.middlename ? `${person.middlename[0]}.` : '';

    return `${person.lastname} ${firstName}${middlename}`;
  }

  public generateSignersList(
    status: PAYMENT_STATUS | LETTER_STATUS = null,
    signatureScheme: SIGNATURE_SCHEME,
    signatures: ISignature[]
  ) {
    const signers = [];

    this.authorizedPersons?.forEach((person: IAuthorizedPerson) => {
      const canSign = this.isSignable(
        status,
        signatureScheme,
        person.authority,
        signatures?.length
      );

      const signed = !!signatures?.find((signature) => {
        return signature.userId !== null && signature.userId === person.userId;
      });

      const name = this.retrieveShortName(person);

      if (signed) {
        signers.push({
          name,
          label: 'SHARED.SIGNED',
          classList: 'text-success',
        });
      } else if (canSign) {
        signers.push({
          name,
          label: 'SHARED.FOR_SIGN',
          classList: 'text-muted',
        });
      }
    });

    return signers;
  }

  private initializeAuthority() {
    const authorityId = localStorage.getItem(AUTHORITY_ID);
    let foundAuthority;

    if (authorityId) {
      foundAuthority = this.userService.user.userAuthorities.find(
        (authority) => authority.id === authorityId
      );
    }
    this.currentAuthority =
      foundAuthority || this.userService.user.userAuthorities[0];
  }

  getCompany(companyId: string): Promise<Company> {
    const url = `/companies/${companyId}`;
    return this.api.get<Company>(url);
  }
}
