import { Component, OnInit, forwardRef, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';

import { JInputComponent } from '@shared/input/input.component';
import { MaskedService } from '@shared/masked/masked.service';

@Component({
  selector: 'j-sms-input',
  templateUrl: './sms-input.component.html',
  styleUrls: ['./sms-input.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SmsInputComponent),
      multi: true,
    },
  ],
})
export class SmsInputComponent extends JInputComponent {
  @Input() errorTranslateParams;
  @Input() maskType = 'sms';

  constructor(public maskedService: MaskedService) {
    super(maskedService);
  }

  get smsSelectionIndex() {
    return this.formControl?.value?.length || 0;
  }

  focus(selectionIndex = this.smsSelectionIndex) {
    // setTimeout(() => {
    this.input.nativeElement.addEventListener('onload', () => {
      this.input.nativeElement.focus();
      this.input.nativeElement.setSelectionRange(
        selectionIndex,
        selectionIndex
      );
    });
    // }, 0);
  }
}
