import { isPlatformBrowser } from '@angular/common';
import {
  AfterViewInit,
  Component,
  ElementRef,
  HostListener,
  Inject,
  PLATFORM_ID,
  ViewEncapsulation,
} from '@angular/core';
import { ResponsiveToolkitService } from './responsive-toolkit.service';

@Component({
  selector: 'responsive-detector',
  template: '',
  styleUrls: ['./responsive-detector.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    class: 'responsive-detector'
  }
})
export class ResponsiveDetector implements AfterViewInit {
  running = false;
  constructor(
    private el: ElementRef,
    private s: ResponsiveToolkitService,
    @Inject(PLATFORM_ID) private platformId
  ) {}

  @HostListener('window:resize', [])
  private onResize() {
    this.triggerBreakpointCheck();
  }

  ngAfterViewInit() {
    this.triggerBreakpointCheck();
  }

  triggerBreakpointCheck() {
    if (this.running) {
      return;
    }
    this.running = true;
    requestAnimationFrame(() => {
      this.running = false;
      this.s.currentBreakpoint = this.getCurrentBreakpointName();
    });
  }

  private getCurrentBreakpointName() {
    if (isPlatformBrowser(this.platformId)) {
      return window
        .getComputedStyle(this.el.nativeElement, ':before')
        .getPropertyValue('content');
    } else {
      return '';
    }
  }
}
