import { Injectable } from '@angular/core';
import {
  FormArray,
  Validators,
  AbstractControl,
  ValidatorFn,
  ValidationErrors,
  Validator,
} from '@angular/forms';

import { combineLatest, of, Observable, from, BehaviorSubject } from 'rxjs';
import { debounceTime, filter, map } from 'rxjs/operators';

import { NgbModal, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';
import pick from 'lodash/pick';

import { AccountsService } from '@accounts/index';
import { ACCOUNT_TYPE, RegularAccount } from '@accounts/account.model';

import { RecipientsService } from '@company/recipients.service';
import { CompanyService } from '@company/company.service';

import {
  dateToNgbStruct,
  SingleEntityStore,
  ngbDateToUTCStartDay,
} from '@utils/index';

import { errors } from '@shared/validators';
import { AmountFormatPipe } from '@shared/pipes/amount-format.pipe';
import { UGD, Bank } from '@shared/models/dictionary';
import { CURRENCY } from '@shared/currency/currency.model';
import { BankFromAccountPipe } from '@shared/pipes/bank-from-account.pipe';

import { PaymentApiService } from './payment-api.service';
import {
  PAYMENT_TYPE,
  PAYMENT_CATEGORY,
  AUTHORITY_TYPE,
  SIGNATURE_SCHEME,
} from '@shared/models';
import { paymentsRoutes } from '@payments/payments-routes';
import { NgxSpinnerService } from 'ngx-spinner';
import { EmployeeItemModel } from '@shared/employee';

export enum PaymentRootTitle {
  NEW = 'NEW_PAYMENT',
  EDIT_DRAFT = 'EDIT_PAYMENT_DRAFT',
  EDIT_PAYMENT = 'EDIT_PAYMENT',
}
export enum MessageType {
  info = 'info',
}
// TODO: проверить используются ли где-нибудь эти переменные
export const messages = {
  noAccounts: `У вас нет счетов в национальной валюте.
  Вам необходимо открыть счет в тенге для проведения платежа.`,
  allLocked: `Ваш Текуший счет заблокирован
  Недоступно проведение платежей`,
};
@Injectable({
  providedIn: 'root',
})
export class PaymentsService {
  rules = {
    purposeLength: 482,
    vat: 0.12,
    kbkCodeVinReq: ['104401', '104402'],
    maxAmount: 999_999_999_999.99,
    minAmount: 0.0,
  };

  errors = errors;

  infoMessage = new SingleEntityStore<
    | {
        message: string;
        type: MessageType;
      }
    | undefined
  >();

  private loadingPaymentSubject$ = new BehaviorSubject<{
    id: string;
    request$: Observable<any>;
  } | null>(null);

  loadingPayment$ = this.loadingPaymentSubject$.asObservable();

  rootTitle$ = new BehaviorSubject<PaymentRootTitle>(PaymentRootTitle.NEW);

  paymentNumber = new SingleEntityStore<string | null>(null);
  foreignPaymentNumber = new SingleEntityStore<string | null>(null);
  conversionPaymentNumber = new SingleEntityStore<string | null>(null);

  constructor(
    public amountPipe: AmountFormatPipe,
    public bankFromAccount: BankFromAccountPipe,
    public notify: NotifierService,
    public spinner: NgxSpinnerService,
    private api: PaymentApiService,
    private company: CompanyService
  ) {}

  set loadingPayment(
    v: {
      id: string;
      request$: Observable<any>;
    } | null
  ) {
    this.loadingPaymentSubject$.next(v);
  }

  set rootTitle(title: PaymentRootTitle) {
    this.rootTitle$.next(title);
  }

  get stdDateRangeNgb() {
    const now = new Date();
    // 10 days from tomorrow
    now.setDate(now.getDate() + 10);
    return {
      minDate: dateToNgbStruct(new Date()),
      maxDate: dateToNgbStruct(now),
    };
  }

  get paymentNumber$(): PaymentsService['paymentNumber']['entity$'] {
    const numStore = this.paymentNumber;
    if (numStore.entity === null && !numStore.fetching) {
      this.getCurrentAutoNumber(numStore, PAYMENT_CATEGORY.DOMESTIC);
    }
    return numStore.entity$.pipe(
      filter((n) => {
        return n !== null;
      })
    );
  }

  get foreignPaymentNumber$(): PaymentsService['foreignPaymentNumber']['entity$'] {
    const numStore = this.foreignPaymentNumber;
    if (numStore.entity === null && !numStore.fetching) {
      this.getCurrentAutoNumber(numStore, PAYMENT_CATEGORY.FOREIGN);
    }
    return numStore.entity$.pipe(
      filter((n) => {
        return n !== null;
      })
    );
  }

  get conversionPaymentNumber$(): PaymentsService['conversionPaymentNumber']['entity$'] {
    const numStore = this.conversionPaymentNumber;
    if (numStore.entity === null && !numStore.fetching) {
      this.getCurrentAutoNumber(numStore, PAYMENT_CATEGORY.CONVERSION);
    }
    return numStore.entity$.pipe(
      filter((n) => {
        return n !== null;
      })
    );
  }

  private getCurrentAutoNumber(numStore, paymentCategory: PAYMENT_CATEGORY) {
    numStore.fetching = true;
    return this.api
      .getPaymentNumber(paymentCategory)
      .then((v) => (numStore.entity = v.number.toString()))
      .catch((e) => (numStore.entity = null))
      .finally(() => (numStore.fetching = false));
  }

  bankExist(iban: string): ValidationErrors | null {
    let bic = '';
    if (
      !iban ||
      iban.length < 20 ||
      (bic = this.bankFromAccount.transform(iban, 'bic'))
    ) {
      return null;
    }

    return { bankExist: true };
  }

  checkPaymentTypeFromIban(
    recipientIban = '',
    payerIban = ''
  ): PAYMENT_TYPE.INTERNAL | PAYMENT_TYPE.EXTERNAL | undefined {
    const jisnaBic = 'TSESKZKA';
    const recipientBic = this.bankFromAccount.transform(recipientIban);
    const payerIbanBic = this.bankFromAccount.transform(payerIban);
    let result = undefined;
    const comp = [];
    payerIbanBic && comp.push(payerIbanBic);
    recipientBic && comp.push(recipientBic);
    if (comp.every((bic) => bic === jisnaBic)) {
      result = PAYMENT_TYPE.INTERNAL;
    } else if (comp.length) {
      result = PAYMENT_TYPE.EXTERNAL;
    }
    return result;
  }

  getPaymentRouteByType(type: PAYMENT_TYPE | undefined): string {
    switch (type) {
      case PAYMENT_TYPE.MEDICAL:
        return paymentsRoutes.tenge.state.osms;
      case PAYMENT_TYPE.PENSION:
        return paymentsRoutes.tenge.state.pension;
      case PAYMENT_TYPE.SOCIAL:
        return paymentsRoutes.tenge.state.social;
      case PAYMENT_TYPE.TAX:
        return paymentsRoutes.tenge.state.tax;
      case PAYMENT_TYPE.SALARY:
        return paymentsRoutes.tenge.salary;
      default:
        return paymentsRoutes.tenge.order;
    }
  }

  calculateCommission$(data: {
    amount: number;
    currency: string;
    companyId: string;
    category: PAYMENT_CATEGORY;
    urgent: boolean;
    date: NgbDateStruct | null;
    type: PAYMENT_TYPE;
    kbk?: string;
    knp?: string;
    senderAccount?: RegularAccount;
  }): Observable<number> {
    const body = {
      type: data.type,
      companyId: data.companyId,
      category: data.category,
      details: {
        valueDate: data.date
          ? ngbDateToUTCStartDay(data.date).toISOString()
          : (data.date as null),
        urgent: data.urgent,
        payerIban: data.senderAccount?.iban,
        paymentAmount: {
          currency: data.currency as CURRENCY,
          amount: data.amount,
        },
        kbk: undefined,
        knp: undefined,
      },
    };

    if (data.kbk) {
      body.details.kbk = {
        code: data.kbk,
      };
    }

    if (data.knp) {
      body.details.knp = {
        code: data.knp,
      };
    }

    return from(this.api.getCommission(body));
  }

  combineAcceptableAccounts(accountStore: AccountsService['store']) {
    return combineLatest([
      accountStore.items$.pipe(
        map((items) =>
          items.filter((item) => item.accountType === ACCOUNT_TYPE.ACCOUNT)
        )
      ),
      accountStore.fetching$,
      accountStore.fetched$,
    ]).pipe(
      debounceTime(0),
      filter(([a, fetching, fetched]) => !fetching || fetched),
      map(([arr]) =>
        arr.filter(
          (acc) =>
            acc.balance.currency === CURRENCY.KZT &&
            !acc.isClosed &&
            !acc.isLocked
        )
      )
    );
  }

  genericSearchTerm(
    term: string,
    item: { code?: string; name?: string; label?: string }
  ) {
    if (term) {
      const normalized = term.toLowerCase();

      return Object.values(pick(item, 'name', 'code', 'label')).some(
        (v) => v && v.toLowerCase().includes(normalized)
      );
    }
    return true;
  }

  ugdSearchFn(term: string, item: UGD): boolean {
    if (term) {
      const normalized = term.toLowerCase();
      return Object.values(pick(item, 'name', 'code', 'bin')).some((v) =>
        v.toLowerCase().includes(normalized)
      );
    }
    return true;
  }

  bankReceiverSearchFn(term: string, item: Bank): boolean {
    if (term) {
      const normalized = term.toLowerCase();
      return Object.values(
        pick(item, 'name', 'salaryTransitIban', 'bic', 'bin')
      ).some((v) => (v || '').toLowerCase().includes(normalized));
    }
    return true;
  }

  employeeValidationMap = (
    options: {
      ibanDefined?: boolean;
      periodRequired?: boolean;
      birthDateRequired?: boolean;
    } = {}
  ) => {
    const map: Record<string, ValidatorFn[]> = {
      iin: [Validators.required, Validators.minLength(12)],
      firstName: [Validators.required],
      lastName: [Validators.required],

      amount: [
        Validators.required,
        Validators.min(this.rules.minAmount),
        Validators.max(this.rules.maxAmount),
      ],
      currency: [],
    };

    if (options.birthDateRequired) {
      map.birthDate = [Validators.required];
    }

    if (options.periodRequired) {
      map.period = [Validators.required];
    }

    if (options.ibanDefined) {
      map.iban = [Validators.required, Validators.minLength(16)];
    }
    return map as Record<keyof EmployeeItemModel, Array<ValidatorFn>>;
  }

  employeeGroupControlValidator =
    (paymentType: PAYMENT_TYPE) => (control: AbstractControl) => {
      const value: EmployeeItemModel[] = control.value;
      if (!value || value.length < 1) {
        return null;
      }
      const modelKeys = Object.keys(value[0]);
      const ibanDefined = paymentType === PAYMENT_TYPE.SALARY;
      const periodRequired = paymentType !== PAYMENT_TYPE.SALARY;
      const birthDateRequired = paymentType !== PAYMENT_TYPE.SALARY;

      const map = this.employeeValidationMap({
        ibanDefined,
        periodRequired,
        birthDateRequired,
      });

      let firstError;

      const firstErrorGroup = value.find((employeeModel) => {
        return modelKeys.find((modelKey) => {
          return (
            map[modelKey] &&
            map[modelKey].find((validator) => {
              if (
                (firstError = validator({ value: employeeModel[modelKey] }))
              ) {
                console.log(firstError);
                return firstError;
              }
            })
          );
        });
      });

      if (firstErrorGroup) {
        return { groupError: true };
      }
      // check uniq iin
      const dictionary: Record<string, string> = {};
      const alreadyExistEmployee = value.some((employeeModel) => {
        // additional period, ignore
        if (employeeModel.linkedWith) {
          return false;
        }
        if (dictionary[employeeModel.iin]) {
          return true;
        }
        dictionary[employeeModel.iin];
      });

      if (alreadyExistEmployee) {
        return { alreadyExistEmployee };
      }
    }

  get isSignatureRequired(): boolean {
    const { authorityLevel, currentCompany } = this.company;
    if (authorityLevel === AUTHORITY_TYPE.NO_SIGNATURE) {
      return false;
    } else if (
      authorityLevel === AUTHORITY_TYPE.PRIMARY_SIGNATURE &&
      currentCompany.signatureScheme !== SIGNATURE_SCHEME.SINGLE_SIGN
    ) {
      return false;
    }
    return true;
  }
}
