
import { AnalyticsInstance } from '../../interfaces/instance.interface';
import { availableAnalytics } from '../instances';

export abstract class AnalyticsManagerAbstract {
    /**
     * Log event to all analytic instances(firebase, amplitude and others)
     * @returns void
     */
    public abstract logEvent(eventName: string, params: { [key: string]: unknown }, instanceKey?: availableAnalytics): void;

    /**
     * field to store analytics instances, each instance must implelemnt common AnalyticInstance interface
     */
    instances: Map<string, AnalyticsInstance> = new Map();

    /**
     * Returns the analytic instance for a specific key
     * Useful to extract specific instance and do something with instance, for example set specific user properties
     * @param  {string} key
     * @returns AnalyticsInstance
     */
    abstract getInstance(key: availableAnalytics): AnalyticsInstance;

    /**
     * @param  {{[key:string]:string}} props
     * Set user properties
     */
    abstract setUserProperties(props: { [key: string]: string }): void;

    abstract setUserId(id: string): void;
} ;
