import {
  AfterViewInit,
  ChangeDetectorRef,
  Directive,
  HostBinding,
  HostListener,
  Input,
  OnDestroy,
  OnInit,
} from '@angular/core';
import { Subscription } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { SelectionContainerDirective } from './selection-container.directive';

@Directive({
  selector: '[jSelectionRow]',
})
export class SelectionRowDirective implements OnDestroy, OnInit, AfterViewInit {
  // checked: boolean;
  @Input('jSelectionRow') id: string;
  @Input() masterSelect: boolean;
  private readonly subs = new Subscription();
  constructor(
    private container: SelectionContainerDirective,
    private cd: ChangeDetectorRef
  ) {}

  @HostBinding() checked: boolean;
  @HostListener('change', ['$event.target.checked'])
  change(checked) {
    if (!this.masterSelect) {
      this.container.patchValue(this.id, checked);
    } else {
      if (this.container.isAllSelected) {
        this.container.clearAll();
      } else {
        this.container.selectAll();
      }
    }

    // this.cd.detectChanges();
  }

  ngOnInit() {
    this.subs.add(
      this.container.valueChanges
        .pipe(debounceTime(0))
        .subscribe((v: string[]) => {
          if (!this.masterSelect) {
            const newValue = !!v.find((id) => id === this.id);
            if (newValue !== this.checked) {
              this.checked = newValue;
              this.cd.detectChanges();
            }
          } else {
            this.checked = this.container.isAllSelected;
            this.cd.detectChanges();
          }
        })
    );
  }

  ngAfterViewInit() {
    // this.cd.detach();
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }
}
