import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { Router } from '@angular/router';
import dayjs from 'dayjs';
import { Observer, Subject } from 'rxjs';

export type jmartMerchantCredentials = {
    login: string,
    password: string,
};

export type jmartAuthResponseToken = {
    token: string,
    issued: string,
    expired: string,
};

export type jmartAuthResponseData = {
    tokens: {
        auth: jmartAuthResponseToken,
        refresh: jmartAuthResponseToken,
    }
};

export type jmartAuthResponse = {
    success: boolean,
    data: jmartAuthResponseData
};

export type JmartCabinetAuthObserver = Partial<Observer<jmartAuthResponse>>;

@Injectable({
    providedIn: 'root'
})
export class JmartAuthService {

    jmartCabinetAuthData: { companyId: number } & jmartAuthResponseData = {
        tokens: null, 
        companyId: null,
    };

    refreshSubject$ = new Subject();

    private localStorageAuthKey = "jmart-cabinet-auth";

    constructor(
        @Inject('JMART_CABINET_BASE_URL') private baseUrl: string,
        private http: HttpClient,
        private router: Router,
    ) {
        this.synchronizeAuthData();
    }

    auth(credentials: jmartMerchantCredentials, observer: JmartCabinetAuthObserver = null) {

        return this.http.post(
         `${this.baseUrl}/gw/user/v1/auth/sign-in`,
         credentials
        ).subscribe({
            ...observer,
            next: (res: jmartAuthResponse) => {
                if (res.success) {
     
                    if (observer.next) {
                        observer.next(res); 
                    }

                     // tmp, token parsing will move to api request
                     function parseJwt (token) {
                         const base64Url = token.split('.')[1];
                         const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
                         const jsonPayload = decodeURIComponent(atob(base64).split('').map(function(c) {
                            return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
                         }).join(''));
                     
                         return JSON.parse(jsonPayload);
                     };
                     this.jmartCabinetAuthData.companyId = parseJwt(res.data.tokens.auth.token).company_id;
                     this.setAuthTokens(res.data);
                     
                     // should move navigate data to router to make it more declarative
                     this.router.navigate(['jmart-cabinet']);
                } else {
                 //    handle auth error here
                //  throw new Error()
                }
            },
        });

    }

    refresh() {
        if (this.getRemainigTokenTime() < -1800000) {
          this.refreshSubject$.next();
          return;
        }
        this.http.post(
          `${this.baseUrl}/gw/user/v1/auth/refresh`,
          {
              token: this.jmartCabinetAuthData.tokens.refresh.token,
          }
        ).subscribe((res: jmartAuthResponse) => {
          if (res.success) {
              this.setAuthTokens(res.data);
              this.refreshSubject$.next();
          } else {
          //    handle auth error here
          }
        });
    }

    private setAuthTokens(res: jmartAuthResponseData) {
        this.jmartCabinetAuthData.tokens = res.tokens;
        this.storeAuthData();
    }

    private storeAuthData() {
        localStorage.setItem(this.localStorageAuthKey, JSON.stringify(this.jmartCabinetAuthData));
    }

    public synchronizeAuthData() {
        const authData = localStorage.getItem(this.localStorageAuthKey);
        if (authData) {
            this.jmartCabinetAuthData = JSON.parse(authData);
        }
    }

    public checkLastUserActivityTime() {
        const remainingTime = this.getRemainigTokenTime();
        const remainFiveMinutes = -300000;
        const isTokenExpired = this.isTokenExpired();
        const hasLess5Minutes = !isTokenExpired && remainingTime >= remainFiveMinutes;
        if (hasLess5Minutes) {
            this.refresh();
        }
        if (isTokenExpired) {
           this.router.navigate(['jmart-cabinet', 'auth']);
        }
    }

    private getRemainigTokenTime() {
        return dayjs().diff(dayjs(this.jmartCabinetAuthData.tokens.auth.expired));
    }

    public isTokenExpired(): boolean {
        return this.getRemainigTokenTime() > 0;
    }

    public isUserLogged() {
        return this.jmartCabinetAuthData.tokens && !this.isTokenExpired();
    }

    public logout() {
        localStorage.removeItem(this.localStorageAuthKey);
        this.jmartCabinetAuthData = {
            tokens: null,
            companyId: null,
        };
    }

}
