import { Injectable } from '@angular/core';
import { DatePipe } from '@angular/common';
import { HttpHeaders, HttpParams } from '@angular/common/http';

import { filter, map, switchMap } from 'rxjs/operators';
import { of, from, Subject, BehaviorSubject } from 'rxjs';

import { TranslateService } from '@ngx-translate/core';

import { SimpleStore, SingleEntityStore } from '@utils/index';

import { ApiService } from '@api/api.service';

import { RegularAccount, Deposit, IUserGrant } from '@accounts/account.model';
import { IStatement } from '@statement/index';

import { IFilterScheme } from '@filter/filter.model';

import { CompanyService } from '@company/company.service';
import { StatementService } from '@statement/statement.service';
import { AUTHORITY_TYPE } from '@shared/models';

import { ACCOUNT_TYPE } from '../account.model';

@Injectable({
  providedIn: 'root',
})
export class AccountsService {
  store: SimpleStore<RegularAccount | Deposit> = new SimpleStore([]);
  storeForTotal: SingleEntityStore<number> = new SingleEntityStore(0);

  private accountsList$ = new BehaviorSubject<(RegularAccount | Deposit)[]>([]);
  private accountsList: (RegularAccount | Deposit)[];

  private cashedStatement: IStatement;
  private cashedFilter: IFilterScheme;
  private cashedPageNumber: number;
  private cashedPageSize: number;

  private mergeDate$ = new Subject<string>();

  constructor(
    private company: CompanyService,
    private api: ApiService,
    private datePipe: DatePipe,
    private translateService: TranslateService,
    private statementService: StatementService
  ) {
    this.company.refresh$
      .pipe(
        switchMap(([user, userAuthority, isAuthorized]) => {
          if (
            user &&
            userAuthority &&
            userAuthority.companyId &&
            isAuthorized
          ) {
            return this.getAccountsAsObservable(userAuthority.companyId);
            //from(this.getAccounts(userAuthority.companyId));
          }
          return of(null);
        })
      )
      .subscribe((accountsList) => {
        if (accountsList) {
          this.store.setState(accountsList);
        } else {
          this.store.purge();
        }
      });
  }

  async retrieveAccounts() {
    const url = `companies/${this.company.currentId}/accounts`;
    const accountsList = await this.api.get<RegularAccount[]>(url);

    return accountsList.map((account) => {
      switch (account.accountType) {
        case ACCOUNT_TYPE.DEPOSIT:
          return new Deposit(account);

        case ACCOUNT_TYPE.ACCOUNT:
        default:
          return new RegularAccount(account);
      }
    });
  }

  async getAccounts(companyId: string = this.company.currentId) {
    this.store.fetching = true;

    const url = `companies/${companyId}/accounts`;
    const accountsList = await this.api.get<(RegularAccount | Deposit)[]>(url);

    this.accountsList = accountsList.map((account) => {
      switch (account.accountType) {
        case ACCOUNT_TYPE.DEPOSIT:
          return new Deposit(account);

        case ACCOUNT_TYPE.ACCOUNT:
        default:
          return new RegularAccount(account);
      }
    });

    this.store.fetching = false;
    this.store.fetched = true;
    this.store.setState(this.accountsList);

    this.accountsList$.next(this.accountsList);

    return this.accountsList;
  }

  getAccountsList() {
    return this.accountsList$.asObservable();
  }

  getDepositName(deposit: Deposit) {
    return (
      deposit.depositType?.descriptionTranslations?.translations[
        this.translateService.currentLang
      ] || this.translateService.instant('DEPOSITS.DEFAULT_NAME')
    );
  }

  getAccountsAsObservable(companyId: string = this.company.currentId) {
    this.store.fetching = true;

    const url = `companies/${companyId}/accounts`;

    return this.api.getObservable<(RegularAccount | Deposit)[]>(url).pipe(
      map((retrievedList) => {
        this.accountsList = retrievedList.map((account) => {
          switch (account.accountType) {
            case ACCOUNT_TYPE.DEPOSIT:
              return new Deposit(account);

            case ACCOUNT_TYPE.ACCOUNT:
            default:
              return new RegularAccount(account);
          }
        });

        this.store.fetching = false;
        this.store.fetched = true;
        this.store.setState(this.accountsList);

        this.accountsList$.next(this.accountsList);

        return this.accountsList;
      })
    );
  }

  async getAccountByIban(companyId: string, iban: string) {
    if (this.accountsList) {
      return this.findByIban(iban, this.accountsList);
    } else {
      const list = await this.getAccounts(companyId);
      return this.findByIban(iban, list);
    }
  }

  async getDeposits(companyId: string = this.company.currentId) {
    const url = `companies/${companyId}/deposits`;

    const depositsList = await this.api.get<Deposit[]>(url);
    return depositsList.map((deposit) => {
      return new Deposit(deposit);
    });
  }

  async fetchStatement(companyId: string, iban: string, filter: IFilterScheme) {
    const url = `companies/${companyId}/accounts/${iban}/statements`;

    const headers = new HttpHeaders();

    const body = {
      account: iban,
      from: this.datePipe.transform(filter.periodFrom, 'yyyy-MM-dd'),
      to: this.datePipe.transform(filter.periodTo, 'yyyy-MM-dd'),
    };

    const statement = await this.api.post<IStatement>(url, body, headers);

    return statement;
  }

  async getStatement(
    iban: string,
    filter: IFilterScheme,
    pageNumber: number = 1,
    pageSize: number = 50,
    exportType: string = null,
    companyId: string = this.company.currentId
  ): Promise<IStatement> {
    if (
      this.cashedStatement &&
      this.cashedFilter === filter &&
      this.cashedPageNumber === pageNumber &&
      this.cashedPageSize === pageSize &&
      !exportType
    ) {
      return this.cashedStatement;
    }

    const url = `companies/${companyId}/accounts/${iban}/statements`;
    let isNextPage;

    if (exportType) {
      isNextPage = false;
    } else {
      isNextPage =
        this.cashedFilter === filter && pageNumber > this.cashedPageNumber;
    }

    try {
      const accountStatement = await this.statementService.getStatement(
        url,
        filter,
        this.cashedStatement,
        isNextPage,
        pageNumber,
        pageSize,
        false,
        exportType
      );

      if (exportType) {
        return accountStatement;
      }

      this.cashedFilter = filter;
      this.cashedPageNumber = pageNumber;
      this.cashedPageSize = pageSize;

      this.cashedStatement = accountStatement;

      return this.cashedStatement;
    } catch (error) {
      throw error;
    }
  }

  async getQrPrint(
    companyId: string,
    companyiban: string,
    companykbe: string,
    companyknp: string
  ) {
    const url = `companies/${this.company.currentId}/account/qr/print`;

    let headers = new HttpHeaders();

    headers = headers.set('Content-Type', 'application/octet-stream');

    let params = new HttpParams();

    params = params.append('iban', companyiban);
    params = params.append('knp', companyknp);
    params = params.append('kbe', companykbe);

    const extras = {
      responseType: 'blob',
    };

    return this.api.get(url, headers, params, extras);
  }

  async getPdf(companyId: string, iban: string) {
    const url = `companies/${companyId}/accounts/${iban}/requisites`;

    let headers = new HttpHeaders();
    headers = headers.set('Accept-File', 'application/pdf');
    headers = headers.set('Content-Type', 'application/octet-stream');

    const params = new HttpParams();
    const extras = {
      responseType: 'blob',
    };

    return this.api.get(url, headers, params, extras);
  }

  async getPdfWithMultipleIbans(companyId: string, ibans: string[]) {
    const url = `companies/${companyId}/accounts/ibans/requisites`;

    let headers = new HttpHeaders();
    headers = headers.set('Accept-File', 'application/pdf');
    headers = headers.set('Content-Type', 'application/octet-stream');

    let params = new HttpParams();
    ibans.forEach((iban) => {
      params = params.append('iban', iban);
    });
    const extras = {
      responseType: 'blob',
    };

    return this.api.get(url, headers, params, extras);
  }

  getMergeDate() {
    return this.mergeDate$.asObservable();
  }

  async retrieveMergeDate() {
    const url = `properties/merge-day`;
    const mergeDate = await this.api.get<string>(url);

    this.mergeDate$.next(mergeDate);
  }

  private findByIban(iban: string, list: (RegularAccount | Deposit)[]) {
    const found = list.find((account) => {
      return account.iban === iban;
    });
    return found;
  }

  async retrieveTotalLimitations(
    companyId: string = this.company.currentId,
    accountId: string
  ) {
    this.storeForTotal.fetching = true;

    const url = `companies/${companyId}/accounts/${accountId}/grants/blocked-users/count`;
    const total = await this.api.get<number>(url);
    this.storeForTotal.fetching = false;
    this.storeForTotal.fetched = true;
    this.storeForTotal.entity = total;

    return total;
  }

  get storeForTotal$(): AccountsService['storeForTotal']['entity$'] {
    return this.storeForTotal.entity$.pipe(
      filter((n) => {
        return n !== null;
      })
    );
  }

  async getAccountsGrants(
    companyId: string = this.company.currentId,
    accountId: string
  ) {
    const url = `companies/${companyId}/accounts/${accountId}/grants`;
    return (await this.api.get<IUserGrant[]>(url)).map((user) => ({
      ...user,
      initials: this.retrieveInitials(user),
      role: this.mapRole(user),
      sign: this.mapSignText(user),
      color: this.mapInitialsColor(user),
    }));
  }

  async setAccountGrant(
    companyId: string = this.company.currentId,
    accountId: string,
    body
  ) {
    const url = `companies/${companyId}/accounts/${accountId}/grants`;
    return await this.api.put(url, body);
  }

  private mapRole(person: IUserGrant) {
    switch (person.authority) {
      case AUTHORITY_TYPE.PRIMARY_SIGNATURE:
        return this.mapAuthoritiesPrefix('HEAD');
      case AUTHORITY_TYPE.SECONDARY_SIGNATURE:
        return this.mapAuthoritiesPrefix('MAIN_ACCOUNTANT');
      case AUTHORITY_TYPE.NO_SIGNATURE:
      default:
        return this.mapAuthoritiesPrefix('ACCOUNTANT');
    }
  }

  private mapAuthoritiesPrefix(key: string) {
    return this.mapTranslatePrefix('AUTHORITIES', key);
  }

  private mapTranslatePrefix(domain: string, key: string) {
    return `SETTINGS.${domain}.${key}`;
  }

  private mapSignText(person: IUserGrant) {
    switch (person.authority) {
      case AUTHORITY_TYPE.PRIMARY_SIGNATURE:
        return this.mapAuthoritiesPrefix('FIRST_SIGN');
      case AUTHORITY_TYPE.SECONDARY_SIGNATURE:
        return this.mapAuthoritiesPrefix('SECOND_SIGN');
      case AUTHORITY_TYPE.NO_SIGNATURE:
      default:
        return this.mapAuthoritiesPrefix('NO_SIGN');
    }
  }

  private mapInitialsColor(person: IUserGrant) {
    switch (person.authority) {
      case AUTHORITY_TYPE.PRIMARY_SIGNATURE:
        return 'first-sign';
      case AUTHORITY_TYPE.SECONDARY_SIGNATURE:
        return 'second-sign';
      case AUTHORITY_TYPE.NO_SIGNATURE:
      default:
        return 'no-sign';
    }
  }

  private retrieveInitials(person: IUserGrant) {
    return `${person.lastname[0]}${person.firstname[0]}`;
  }
}
