import { Pipe, PipeTransform } from '@angular/core';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { TranslateService } from '@ngx-translate/core';
import { ngbDateToDate, formatNgbDate } from '@utils/index';
import { Observable, of } from 'rxjs';

@Pipe({
  name: 'jDateFormat',
  // pure: true,
})
export class DateFormatPipe implements PipeTransform {
  constructor(private translateService: TranslateService) {}

  transform(value: NgbDateStruct): Observable<string> {
    if (!value) {
      return this.translateService.get('PAYMENTS.DATE.AUTO');
    }
    const dateValue = ngbDateToDate(value);

    const now = new Date();
    let tomorrow = new Date();

    tomorrow.setDate(tomorrow.getDate() + 1);
    tomorrow.setHours(23, 59);

    if (value.day === now.getDate()) {
      return this.translateService.get('PAYMENTS.DATE.TODAY', {
        date: formatNgbDate(value, this.translateService.currentLang),
      });
    } else if (tomorrow >= dateValue) {
      return this.translateService.get('PAYMENTS.DATE.TOMORROW', {
        date: formatNgbDate(value, this.translateService.currentLang),
      });
    }

    return of(formatNgbDate(value, this.translateService.currentLang));
  }
}
