import { Component } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import {
  FeatureFlagsService,
  FEATURE_FLAG,
} from '@feature-flags/index';

import { Subscription } from 'rxjs';

import { NavService } from '../nav.service';

const NAV_LIST = [
  {
    id: 'home',
    title: 'HOME.TITLE',
    url: '/accounts',
    disabled: false,
    badge: 0,
  },
  {
    id: 'history',
    title: 'HISTORY.TITLE',
    url: '/history',
    disabled: false,
    badge: 0,
  },
  // {
  //   id: 'history2',
  //   title: 'HISTORY.TITLE',
  //   url: '/history2',
  //   disabled: false,
  //   badge: 0,
  // },
  {
    id: 'contracts',
    title: 'CONTRACTS.TITLE',
    url: '/contracts',
    disabled: false,
    badge: 0,
  },
  {
    id: 'letters',
    title: 'LETTERS.TITLE',
    url: '/letters',
    disabled: false,
    badge: 0,
  },
  {
    id: 'directory',
    title: 'DIRECTORY.TITLE',
    url: '/directory',
    disabled: false,
    badge: 0,
  },
  {
    id: 'settings',
    title: 'SETTINGS.TITLE',
    url: '/settings',
    disabled: false,
    badge: 0,
  },
  {
    id: 'landing',
    title: 'JMART.NAV.TITLE',
    url: '/jmart/landing',
    disabled: false,
    badge: 'new',
  },
  {
    id: 'extraSettings',
    title: 'EXTRA_SETTINGS.TITLE',
    url: '/extraSettings',
    disabled: false,
    badge: 0,
    isCollapsed: false,
    children: [{
      id: 'faq',
      title: 'SETTINGS.FAQ.TITLE',
      url: '/extraSettings/faq',
      disabled: false,
      badge: 0
    },
    {
      id: 'feature-request',
      title: 'SETTINGS.FEATURE_REQUEST.TITLE',
      url: '/extraSettings/feature-request',
      disabled: false,
      badge: 0
    }]
  },
];

@Component({
  selector: 'j-nav-main',
  templateUrl: './nav-main.component.html',
  styleUrls: ['./nav-main.component.scss'],
})
export class NavMainComponent {
  navList = NAV_LIST;

  subscription = new Subscription();

  constructor(
    private navService: NavService,
    private featureFlagsService: FeatureFlagsService,
    private router: ActivatedRoute
  ) {

    this.checkAndClearIfDisabled([
      [ FEATURE_FLAG.LETTER, 'letters' ],
      [ FEATURE_FLAG.JUSAN_MARKET, 'landing' ],
    ]);

    this.navService.getLetterBadges().subscribe((badges) => {
      this.updateNavItem('letters', {
        badge: badges ? badges.unread + badges.onSignForMe : null,
      });
    });

    this.navService.getContractBadges().subscribe((badges) => {
      this.updateNavItem('contracts', {
        badge: badges ? badges.onSignForMe : null,
      });
    });
  }

  private checkAndClearIfDisabled(flags: Array<[ FEATURE_FLAG, typeof NAV_LIST[number]['id']]>) {
    if (this.featureFlagsService.featureFlags$.isStopped) {
      flags.forEach(([ flag, navItemId ]) => {
        const isDisabled = this.featureFlagsService.isDisabled(flag);
        if (isDisabled) {
          this.navList = this.navList.filter((navItem) => navItem.id != navItemId);
        }
      });
    } else {
      this.featureFlagsService.featureFlags$.subscribe({
        complete: () => {
          this.checkAndClearIfDisabled(flags);
        }
      });
    }
  }

  private updateNavItem(id: string, values: any) {
    if (this.featureFlagsService.isDisabled(FEATURE_FLAG.LETTER)) {
      return;
    }

    Object.assign(
      this.navList.find((navItem) => navItem.id === id),
      { ...values }
    );
  }
}
