import { Directive, HostListener, ElementRef } from '@angular/core';
import { MaskedService } from '@shared/masked/masked.service';

@Directive({
  selector: '[jNormalizeAmount]',
})
export class NormalizeAmountDirective {
  @HostListener('paste', ['$event']) onPasteEvent(event: any) {
    try {
      event.preventDefault();
      const clipboardData =
        event.clipboardData || (window as any).clipboardData;
      const pastedText = clipboardData.getData('text');
      const toPasteRaw = this.maskedService.getMaskedAmount(pastedText);
      this.elem.nativeElement.value = toPasteRaw;
    } catch (error) {}
  }
  @HostListener('input', ['$event']) normalize() {}
  constructor(
    private elem: ElementRef<HTMLInputElement>,
    private maskedService: MaskedService
  ) {}
}
