import { Injectable } from '@angular/core';
import { AnalyticsInstance } from '../../interfaces/instance.interface';
import { analyticsInstances, availableAnalytics } from '../instances';
import { AnalyticsManagerAbstract } from './manager.service.abstract';

@Injectable()
export class AnalyticsManager extends AnalyticsManagerAbstract {
    constructor() {
        super();
        this.instances = new Map(Object.entries(analyticsInstances));
    }

    public logEvent(eventName: string, params, instanceKey?: availableAnalytics): void {
        if (!instanceKey) {
            this.instances.forEach((instance) => {
                instance.logEvent(eventName, params);
            });
        } else {
            const instance = this.getInstance(instanceKey);
            instance.logEvent(eventName, params);
        }
    }
    
    getInstance(key: availableAnalytics): AnalyticsInstance {
        return this.instances.get(key);
    }

    setUserProperties(props: { [key: string]: string; }): void {
        this.instances.forEach(instance => instance.setUserProperties(props));
    }

    setUserId(id: string): void {
        this.instances.forEach(instance => instance.setUserId(id));
    }

};
