import { Injectable } from '@angular/core';
import { HttpHeaders } from '@angular/common/http';

import { HEADERS } from '@api/index';

import * as CryptoJS from 'crypto-js';
import { UAParser } from 'ua-parser-js';

import { environment } from '@environments/environment';

export interface IToken {
  access_token: string;
  token_type: string;
  expires_in: string;
  scope: string;
}

@Injectable({
  providedIn: 'root',
})
export class TokenService {
  private BASIC_TOKEN = btoa('webapp:rjZI9MfQOf');
  private CLIENT_TOKEN: string;
  private DEVICE_TOKEN: string;

  private CLIENT_TOKEN_KEY = 'client_token';
  private USER_TOKEN_KEY = 'user_token';
  private DEVICE_TOKEN_KEY = 'device_token';

  private userToken: string;
  private uaParser = new UAParser();

  constructor() {}

  getBasicHeaders(headers: HttpHeaders = new HttpHeaders()) {
    headers = this.applyDeviceInfoHeaders(headers);
    return headers.set(HEADERS.AUTHORIZATION, `Basic ${this.BASIC_TOKEN}`);
  }

  applyClientHeaders(headers: HttpHeaders = new HttpHeaders()) {
    return headers.set(HEADERS.AUTHORIZATION, `Bearer ${this.clientToken}`);
  }

  applyUserHeaders(headers: HttpHeaders = new HttpHeaders()) {
    return headers.set(HEADERS.AUTHORIZATION, `Bearer ${this.getUserToken()}`);
  }

  applyDeviceHeaders(
    headers: HttpHeaders = new HttpHeaders(),
    password: string
  ) {
    headers = this.applyDeviceInfoHeaders(headers);

    this.getDeviceToken(password);

    if (this.DEVICE_TOKEN != undefined) {
      return headers.set(HEADERS.DEVICE, this.DEVICE_TOKEN);
    }

    return headers;
  }

  applyAuthHeaders(headers: HttpHeaders = new HttpHeaders()) {
    headers = this.applyDeviceInfoHeaders(headers);

    if (this.userToken) {
      headers = this.applyUserHeaders(headers);
    } else if (this.clientToken) {
      headers = this.applyClientHeaders(headers);
    }

    return headers;
  }

  get clientToken() {
    if (!this.CLIENT_TOKEN) {
      this.clientToken = localStorage.getItem(this.CLIENT_TOKEN_KEY);
    }
    return this.CLIENT_TOKEN;
  }

  set clientToken(clientToken: string) {
    if (clientToken != null) {
      this.CLIENT_TOKEN = clientToken;
      localStorage.setItem(this.CLIENT_TOKEN_KEY, this.CLIENT_TOKEN);
    }
  }

  getUserToken() {
    if (!this.userToken) {
      this.userToken = localStorage.getItem(this.USER_TOKEN_KEY);
    }

    return this.userToken;
  }

  setUserToken(userToken: string) {
    if (!userToken) {
      return;
    }

    this.userToken = userToken;
    localStorage.setItem(this.USER_TOKEN_KEY, this.userToken);
  }

  getDeviceToken(password: string) {
    if (!this.DEVICE_TOKEN && localStorage.getItem(this.DEVICE_TOKEN_KEY)) {
      try {
        const decryptedDeviceToken = this.decryptData(
          localStorage.getItem(this.DEVICE_TOKEN_KEY),
          password
        );
        this.DEVICE_TOKEN = decryptedDeviceToken;
      } catch (error) {
        throw error;
      }
    }
    return this.DEVICE_TOKEN;
  }

  setDeviceToken(deviceToken: string, password: string) {
    if (deviceToken != null) {
      this.DEVICE_TOKEN = deviceToken;
      try {
        const encryptedDeviceToken = this.encryptData(deviceToken, password);
        localStorage.setItem(this.DEVICE_TOKEN_KEY, encryptedDeviceToken);
      } catch (error) {}
    }
  }

  clearClientToken() {
    this.CLIENT_TOKEN = null;
    localStorage.removeItem(this.CLIENT_TOKEN_KEY);
  }

  clearUserToken() {
    this.userToken = null;
    localStorage.removeItem(this.USER_TOKEN_KEY);
  }

  clearDeviceToken() {
    this.DEVICE_TOKEN = null;
    localStorage.removeItem(this.DEVICE_TOKEN_KEY);
  }

  private applyDeviceInfoHeaders(headers: HttpHeaders = new HttpHeaders()) {
    this.uaParser.setUA(window.navigator.userAgent);

    const lang = localStorage.getItem('lang');
    const uaInfo = this.uaParser.getResult();
    const os = `${uaInfo.os.name} ${uaInfo.os.version}`;
    const browser = `${uaInfo.browser.name} ${uaInfo.browser.version}`;

    return headers
      .set('Client-Type', 'web')
      .set('X-Language', lang)
      .set('x-app-version', environment.appVersion)
      .set('x-device-info', `${os} ${browser}`);
  }

  private encryptData(data, encryptSecretKey) {
    return CryptoJS.AES.encrypt(
      JSON.stringify(data),
      encryptSecretKey
    ).toString();
  }

  private decryptData(data, encryptSecretKey) {
    const bytes = CryptoJS.AES.decrypt(data, encryptSecretKey);

    if (bytes.toString()) {
      return JSON.parse(bytes.toString(CryptoJS.enc.Utf8));
    }
    return data;
  }
}
