import { Component, forwardRef, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';

import { MaskedService } from '@shared/masked/masked.service';

import {
  BsDatepickerConfig,
  BsDatepickerViewMode,
  BsLocaleService,
} from 'ngx-bootstrap/datepicker';

import { defineLocale } from 'ngx-bootstrap/chronos';
import { ruLocale } from 'ngx-bootstrap/locale';
import { kkLocale } from 'ngx-bootstrap/locale';

import { JInputComponent } from '../input.component';
import { TranslateService } from '@ngx-translate/core';

(ruLocale.months = {
  format:
    'Января_Февраля_Марта_Апреля_Мая_Июня_Июля_Августа_Сентября_Октября_Ноября_Декабря'.split(
      '_'
    ),
  standalone:
    'Январь_Февраль_Март_Апрель_Май_Июнь_Июль_Август_Сентябрь_Октябрь_Ноябрь_Декабрь'.split(
      '_'
    ),
}),
  defineLocale('ru', ruLocale);

(ruLocale.months = {
  format:
    'Қаңтар_Ақпан_Наурыз_Сәуір_Мамыр_Маусым_Шілде_Тамыз_Қыркүйек_Қазан_Қараша_Желтоқсан'.split(
      '_'
    ),
  standalone:
    'Қаңтар_Ақпан_Наурыз_Сәуір_Мамыр_Маусым_Шілде_Тамыз_Қыркүйек_Қазан_Қараша_Желтоқсан'.split(
      '_'
    ),
}),
  defineLocale('kk', kkLocale);

@Component({
  selector: 'j-period-input',
  templateUrl: './period-input.component.html',
  styleUrls: ['./period-input.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => PeriodInputComponent),
      multi: true,
    },
  ],
})
export class PeriodInputComponent extends JInputComponent {
  @Input() label = 'SHARED.PERIOD.TITLE';
  @Input() formControl;

  locale = this.translateService.currentLang;

  colorTheme = 'theme-jysan';
  minMode: BsDatepickerViewMode = 'month';
  bsValue: Date;
  bsConfig: Partial<BsDatepickerConfig>;

  minDate = new Date('1991-01-01');
  maxDate = new Date();

  constructor(
    public maskedService: MaskedService,
    private localeService: BsLocaleService,
    private translateService: TranslateService,
    private bsDatepickerConfig: BsDatepickerConfig
  ) {
    super(maskedService);

    this.maxDate.setFullYear(this.maxDate.getFullYear() + 1);
    this.maxDate.setMonth(11);

    this.bsConfig = Object.assign(
      {},
      {
        minDate: this.minDate,
        maxDate: this.maxDate,
        isAnimated: true,
        dateInputFormat: 'MMMM YYYY',
        minMode: this.minMode,
        containerClass: this.colorTheme,
      }
    );
    this.bsDatepickerConfig.dateInputFormat = 'MMMM YYYY';
    this.localeService.use(this.locale);

    this.subscription.add(
      this.translateService.onLangChange.subscribe((locale) => {
        this.localeService.use(locale.lang);
      })
    );
  }

  ngOnInit(): void {
    if (!this.formControl.value && this.value) {
      this.formControl.setValue(this.value);
    }
    const formSubscription = this.formControl.valueChanges.subscribe(
      (value) => {
        this.valueChanged.emit(value);
      }
    );

    this.subscription.add(formSubscription);
  }

  onBsChange(selectedDate: Date) {
    if (selectedDate) {
      // this.formControl.setValue(selectedDate);
      setTimeout(() => {
        this.onChange(selectedDate);
      });
    }
  }

  get isFilled() {
    return this.formControl.value ? true : false;
  }
}
