import {
  Component,
  EventEmitter,
  Output,
  HostListener,
  ViewEncapsulation,
  OnDestroy,
} from '@angular/core';
import { trigger, transition, useAnimation } from '@angular/animations';
import { RouterOutlet } from '@angular/router';

import { combineLatest, Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';

import { TranslateService } from '@ngx-translate/core';
import { AnimationOptions } from 'ngx-lottie';

import {
  moveFromBottomFade,
  moveFromBottomFadeReverse,
} from '@shared/animations';
import { UserService } from '@shared/services';

import { HeartbeatService } from '@heartbeat/heartbeat.service';
import { ApiService } from '@api/api.service';
import { AuthorityPermissionsService } from '@authorities/services';
import { LangService } from '@shared/services/lang.service';
import { CompanyService } from './company';
import { AnalyticsManagerAbstract } from '@analytics/services/manager';
import { TokenService } from '@shared/token/token.service';

// import { FeatureFlagsService } from './feature-flags/feature-flags.service';
// import { CurrencyPaymentService } from '@payments/services';
// import { CompanyService } from '@company/company.service';

@Component({
  selector: 'j-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: [
    trigger('routeAnimations', [
      transition('* => routerFadeFromBottom', useAnimation(moveFromBottomFade)),
      transition('* => void', useAnimation(moveFromBottomFadeReverse)),
    ]),
  ],
})
export class AppComponent implements OnDestroy {
  title = 'Jusan';
  isReady = false;

  lottieOptions: AnimationOptions = {
    path: '/assets/lottie/primary.json',
  };

  @Output() eventHandler: EventEmitter<Event> = new EventEmitter<Event>();

  @HostListener('document:keydown', ['$event'])
  @HostListener('document:keyup', ['$event'])
  @HostListener('document:keypress', ['$event'])
  @HostListener('click', ['$event'])
  @HostListener('mousemove', ['$event'])
  @HostListener('mouseup', ['$event'])
  @HostListener('mousedown', ['$event'])
  @HostListener('document:scroll', ['$event'])
  onEvent(event) {
    this.eventHandler.emit(event);
  }

  subscription = new Subscription();

  constructor(
    private tokenService: TokenService,
    private apiService: ApiService,
    private heartbeatService: HeartbeatService,
    private translateService: TranslateService,
    private userService: UserService,
    private authorityPermissionsService: AuthorityPermissionsService,
    private langService: LangService,
    private companyService: CompanyService,
    private analytics: AnalyticsManagerAbstract
  ) {
    this.updateBaseUrlIfNeeded();
    this.langService.init();
    this.appInit();
    this.setUserId();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  updateBaseUrlIfNeeded() {
    const baseUrl = localStorage.getItem('baseUrl');
    if (baseUrl) {
      this.apiService.baseUrl = baseUrl;
    }
  }

  async appInit() {
    try {
      await this.heartbeatService.beatRequest();
    } catch (error) {
      console.log(error);
      // this.router.navigate(['auth', 'login'], {
      //   queryParamsHandling: 'preserve',
      // });
    } finally {
      this.isReady = true;
    }
  }

  prepareRoute(outlet: RouterOutlet) {
    return (
      outlet &&
      outlet.activatedRouteData &&
      outlet.activatedRouteData['animation']
    );
  }

  setUserId() {
    const authorizedPersons$ = this.companyService
      .getAuthorizedPersons()
      .pipe(filter((arr) => arr?.length > 0));
    const currentUser$ = this.userService.getUser();
    const userIdSubscribtion = combineLatest([
      authorizedPersons$,
      currentUser$,
    ]).subscribe(
      ([
        authorizedPersons,
        { firstname: userFirstname, lastname: userLastname },
      ]) => {
        const { userColvirId } = authorizedPersons.find((person) => {
          return (
            person?.firstname === userFirstname &&
            person?.lastname === userLastname
          );
        }) || { userColvirId: null };

        if (userColvirId) {
          this.analytics.setUserId(userColvirId);
          userIdSubscribtion.unsubscribe();
        }
      }
    );
  }
}
