  HCSKKZKA: true,
  HLALKZKZ: true,
  HSBKKZKX: true,
  ICBKKZKX: true,
  INEARUMM: true,
  INLMKZKA: true,
  IRTYKZKA: true,
  KCJBKZKX: true,
  KICEKZKX: true,
  KINCKZKA: true,
  KISCKZKX: true,
  KKMFKZ2A: true,
  KPSTKZKA: true,
  KSNVKZKA: true,
  KZIBKZKA: true,
  LARIKZKA: true,
  NBPAKZKA: true,
  NBPFKZKX: true,
  NURSKZKX: true,
