import { HTTP_INTERCEPTORS } from '@angular/common/http';

import { PreventSendRequestBeforeResponseInterceptor } from './prevent-send-request-before-response-interceptor.service';
import { JmartCabinetInterceptor } from './jmart-cabinet.interceptor';
import { AuthInterceptor } from './auth.interceptor';
// import { OfflineInterceptor } from './offline.interceptor';

export const httpInterceptorProviders = [
  // Angular applies interceptors in the order that you provide them.
  // If you provide interceptors A, then B, then C,
  // requests will flow in A->B->C and responses will flow out C->B->A.
  // {
  //   provide: HTTP_INTERCEPTORS,
  //   useClass: OfflineInterceptor,
  //   multi: true,
  // },
  {
    provide: HTTP_INTERCEPTORS,
    useClass: PreventSendRequestBeforeResponseInterceptor,
    multi: true,
  },
  {
    provide: HTTP_INTERCEPTORS,
    useClass: AuthInterceptor,
    multi: true,
  },
  {
    provide: HTTP_INTERCEPTORS,
    useClass: JmartCabinetInterceptor,
    multi: true,
  },
];
