import { Component, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'j-import-error',
  templateUrl: './import-error.component.html',
  styleUrls: ['./import-error.component.scss'],
})
export class ImportErrorComponent {
  @Input() filesCount: number;
  @Input() errorText: string;
  @Input() descriptionKey: string = 'FAILED';

  constructor(public modal: NgbActiveModal) {}

  dismissModal() {
    this.modal.dismiss();
  }

  closeModal() {
    this.modal.close();
  }

  get title() {
    return `IMPORT.${this.descriptionKey}.TITLE`;
  }

  get errorDescription() {
    if (this.filesCount === 1) {
      return `IMPORT.${this.descriptionKey}.ONE`;
    } else {
      return `IMPORT.${this.descriptionKey}.OTHER`;
    }
  }
}
