import { Company } from '@company/company.model';

import { ACTION_TYPE, ACTION_SUBJECT_TYPE } from '../../../../projects/admin/src/app/actions/actions.model';

export class User {
  id: string;
  colvirId: string;

  // Status
  accountNonExpired: boolean;
  accountNonLocked: boolean;
  status: USER_STATUS;
  enabled: boolean;
  incorrectTries: number;

  // Data
  iin: string;
  phone: string;
  email: string;
  password: string;
  firstname: string;
  middlename: string;
  lastname: string;

  // Authorities
  authorities: IUserAuthority[];
  userAuthorities: IUserAuthority[];

  username: string;

  // Ignorable
  roles: any;
}

export interface IUserAuthority {
  id: string;
  authority: AUTHORITY_TYPE;

  company: Company;
  companyColvirId: string;
  companyId: string;

  fromDate: Date;
  toDate: Date;

  userId: string;
  userColvirId: null;

  firstname: string;
  lastname: string;
  middlename: string;
  position: string;
}

export enum USER_STATUS {
  ACTIVE = 'ACTIVE',
  INACTIVE = 'INACTIVE',
}

export enum AUTHORITY_TYPE {
  PRIMARY_SIGNATURE = 'PRIMARY_SIGNATURE',
  SECONDARY_SIGNATURE = 'SECONDARY_SIGNATURE',
  NO_SIGNATURE = 'NO_SIGNATURE',
}

export enum SIGNATURE_SCHEME {
  MULTIPLE_SIGN = 'MULTIPLE_SIGN',
  SINGLE_SIGN = 'SINGLE_SIGN',
}


export interface IUserLastSession {
  ipAddress: string,
  timestamp: string,
  id: string,
  deviceInfo: string,
  actionType: ACTION_TYPE.LOGIN | ACTION_TYPE,
  subjectType: ACTION_SUBJECT_TYPE.USER | ACTION_SUBJECT_TYPE,
}