import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IMoney } from '@shared/currency/currency.model';

@Component({
  selector: 'j-conversion-confirm',
  templateUrl: './conversion-confirm.component.html',
  styleUrls: ['./conversion-confirm.component.scss'],
})
export class ConversionConfirmComponent implements OnInit {
  @Input() confirmData: {
    amountFrom: IMoney;
    amountTo: IMoney;

    rateFrom: IMoney;
    rateTo: IMoney;
  };

  @Output() agreed = new EventEmitter();
  @Output() sentForRevision = new EventEmitter();

  constructor() {}

  ngOnInit() {}

  async onAgreed() {
    this.agreed.emit();
  }

  async onSendForRevision() {
    this.sentForRevision.emit() ;
  }
}
