import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  forwardRef,
  HostListener,
  Input,
  Output,
  Provider,
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';

import { KEYBOARD_KEY } from '../dropdown-input/dropdown-input.component';

const TO_SHOW: number = 2;

export interface IMultiSelectDropdownOption {
  value: unknown;
  label: string;
}

const MSDI_VALUE_ACCESSOR: Provider = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => MultiSelectDropdownInputComponent),
  multi: true,
};

const SMOOTH_COLLAPSE = trigger('smoothCollapse', [
  state(
    'initial',
    style({
      height: '0',
      overflow: 'hidden',
      opacity: '0.5',
    })
  ),
  state(
    'final',
    style({
      overflow: 'auto',
      opacity: '1',
      maxHeight: '300px',
    })
  ),
  transition('initial=>final', animate('300ms ease-in-out')),
  transition('final=>initial', animate('300ms ease-in-out')),
]);

@Component({
  selector: 'j-multi-select-dropdown-input',
  templateUrl: './multi-select-dropdown-input.component.html',
  styleUrls: ['./multi-select-dropdown-input.component.scss'],
  providers: [MSDI_VALUE_ACCESSOR],
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [SMOOTH_COLLAPSE],
})
export class MultiSelectDropdownInputComponent implements ControlValueAccessor {
  @Input()
  options: IMultiSelectDropdownOption[] = [];

  @Input()
  focusIndex: number = 0;

  @Input()
  placeholder: string;

  @Input()
  isCollapsed: boolean = false;

  @Input()
  loading: boolean = false;

  @Input()
  openUnder: boolean = true;

  @Input()
  disabled: boolean = false;

  @Output()
  changed: EventEmitter<unknown[]> = new EventEmitter();

  selectedItems: IMultiSelectDropdownOption[] = [];

  touched: boolean = false;

  @HostListener('document:click', ['$event'])
  clickOutside(event: MouseEvent) {
    if (!this.eRef.nativeElement.contains(event.target)) {
      this.isCollapsed = false;
      this.cdr.detectChanges();
    }
  }

  @HostListener('document:keydown', ['$event'])
  keydown(event: KeyboardEvent) {
    const allowedKeys: string[] = Object.values(KEYBOARD_KEY);

    if (!this.isCollapsed || !allowedKeys.includes(event.code)) {
      return;
    } else {
      event.preventDefault();
    }

    switch (event.code) {
      case KEYBOARD_KEY.ARROW_DOWN:
        this.arrowDown();
        break;
      case KEYBOARD_KEY.ARROW_UP:
        this.arrowUp();
        break;
      case KEYBOARD_KEY.ESC:
        this.close();
        break;
      case KEYBOARD_KEY.ENTER:
        this.finish();
        break;
      case KEYBOARD_KEY.SPACE:
        this.toggleSelect(this.options[this.focusIndex]);
        break;
    }
  }

  // TODO: It's just a draft will be optimized & refactored
  @Input()
  displaySelectedFn(selectedItems: IMultiSelectDropdownOption[]): string {
    let result: string = this.translateService.instant(this.placeholder);

    const stringify: string = selectedItems
      ?.slice(0, TO_SHOW)
      .map((item) => this.translateService.instant(item.label))
      .join(', ');

    if (selectedItems?.length > TO_SHOW) {
      result =
        stringify +
        ' ' +
        this.translateService.instant('SHARED.AND') +
        ' ' +
        (selectedItems.length - TO_SHOW);
    } else if (selectedItems?.length > 0) {
      result = stringify;
    }

    return result;
  }

  @Input()
  displayItemFn(item: IMultiSelectDropdownOption): string {
    const toString = this.translateService.instant(item.label);

    return toString;
  }

  constructor(
    private cdr: ChangeDetectorRef,
    private eRef: ElementRef,
    private translateService: TranslateService
  ) {}

  private findItemIndex(option: IMultiSelectDropdownOption): number {
    return this.selectedItems.findIndex((item) => item.value == option.value);
  }

  private arrowDown(): void {
    if (this.focusIndex < this.options.length - 1) {
      this.focusIndex++;
    } else {
      this.focusIndex = 0;
    }

    this.cdr.markForCheck();
  }

  private arrowUp(): void {
    if (this.focusIndex > 0) {
      this.focusIndex--;
    } else {
      this.focusIndex = this.options.length - 1;
    }

    this.cdr.markForCheck();
  }

  writeValue(items: unknown[]): void {
    const rawByValue = this.options.filter((item) =>
      items?.includes(item.value)
    );
    this.selectedItems = rawByValue ?? [];

    this.cdr.markForCheck();
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;

    this.cdr.markForCheck();
  }

  onChange(_: any) {}

  onTouched = () => {};

  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }

    this.cdr.markForCheck();
  }

  isItemActive(option: IMultiSelectDropdownOption): boolean {
    return this.findItemIndex(option) !== -1;
  }

  toggleSelect(option: IMultiSelectDropdownOption): void {
    if (this.isItemActive(option)) {
      const index = this.findItemIndex(option);
      this.selectedItems.splice(index, 1);
    } else {
      this.selectedItems.push(option);
    }

    const values = this.selectedItems.map((item) => item.value);

    this.onChange(values);
    this.changed.emit(values);
    this.cdr.markForCheck();
  }

  finish(): void {
    this.close();
  }

  toggleCollapse(): void {
    if (this.disabled) {
      return;
    }

    this.isCollapsed = !this.isCollapsed;
  }

  close(): void {
    this.isCollapsed = false;
  }

  trackByLabel(
    index: number,
    obj: IMultiSelectDropdownOption
  ): string | number {
    return obj.label;
  }
}
