import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { NgxSpinnerService } from 'ngx-spinner';
import { MaskPipe } from 'ngx-mask';

import * as moment from 'moment';

import { convertDateToString, ISO_8061, ibanPrefix } from '@utils/index';

import { ERRORS } from '@api/index';

import { PERIOD_FORMAT_INPUT } from '@filter/filter.model';

import { PaymentsService } from '@payments/services/payments.service';

import { MaskedService } from '@shared/masked/masked.service';
import { errors } from '@shared/validators';
import { PAYMENT_TYPE } from '@shared/models';
import { CURRENCY } from '@shared/currency/currency.model';

import { EmployeeService } from '../../../employee/employee.service';
import { Employee } from '../../../employee/employee.model';
import { Masks } from '@shared/masked/masked';

@Component({
  selector: 'j-edit-saved-employee',
  templateUrl: './edit-saved-employee.component.html',
  styleUrls: ['./edit-saved-employee.component.scss'],
})
export class EditSavedEmployeeComponent implements OnInit {
  @Input() employee: Employee;
  @Input() index: number;
  @Input() birthDateRequired?: boolean;
  @Input() ibanRequired?: boolean;
  @Input() paymentType: PAYMENT_TYPE;

  @Output() deleted = new EventEmitter();
  @Output() closed = new EventEmitter();
  @Output() saved = new EventEmitter();

  employeeForm: FormGroup;

  isSubmitted = false;

  minAmount = 4250;
  iinMaskConfig = this.maskedService.getConfig('iin');
  ibanMaskConfig = this.maskedService.getConfig('tengeAccount');

  errors = errors;
  errorText: string;

  constructor(
    private maskedService: MaskedService,
    private maskPipe: MaskPipe,
    private employeeService: EmployeeService,
    private spinner: NgxSpinnerService,
    private paymentsService: PaymentsService
  ) {}

  ngOnInit(): void {
    let iin = this.employee?.iin || '';
    const lastName = this.employee?.lastName || '';
    const firstName = this.employee?.firstName || '';
    const middleName = this.employee?.middleName || '';
    let amount = this.employee?.getBalance(this.paymentType)?.amount || '';

    if (amount) {
      amount = this.maskedService.formatAmount(amount);
    }

    iin = this.maskPipe.transform(iin, this.iinMaskConfig.mask);

    this.employeeForm = new FormGroup({
      iin: new FormControl({ value: iin, disabled: true }),
      lastName: new FormControl(lastName, Validators.required),
      firstName: new FormControl(firstName, Validators.required),
      middleName: new FormControl(middleName),
      amount: new FormControl(amount, [
        Validators.required,
        (control) => {
          if (parseFloat(control.value) > 0) {
            return null;
          } else {
            return {
              minAmount: true,
            };
          }
        },
      ]),
    });

    if (this.birthDateRequired) {
      const mappedBirthDate = this.employee?.birthDate
        ? this.mapBirthDateToInput(this.employee?.birthDate)
        : '';
      this.employeeForm.addControl(
        'birthDate',
        new FormControl(mappedBirthDate, Validators.required)
      );
    }

    if (this.ibanRequired) {
      this.employeeForm.addControl(
        'iban',
        new FormControl(this.employee.iban ? this.employee.iban.slice(2) : '', [
          Validators.required,
          (control) =>
            this.paymentsService.bankExist(
              ibanPrefix(control.value, CURRENCY.KZT)
            ),
        ])
      );
    }

    this.employeeForm.valueChanges.subscribe((value) => {
      this.isSubmitted = false;
    });
  }

  get minDate() {
    return new Date(1900, 0, 0);
  }

  get maxDate() {
    return new Date();
  }

  get currency() {
    return (
      this.employee?.getBalance(this.paymentType)?.currency || CURRENCY.KZT
    );
  }

  async deleteEmployee(event: Event) {
    event.preventDefault();

    this.spinner.show();

    const deleted = await this.employeeService.deleteEmployee([
      this.employee.iin,
    ]);

    this.deleted.emit({
      employee: deleted,
      index: this.index,
    });

    this.spinner.hide();

    this.close();
  }

  close() {
    this.closed.emit();
  }

  async save() {
    this.isSubmitted = true;

    if (this.employeeForm.invalid) {
      return;
    }

    this.spinner.show();

    const newEmployee = new Employee(this.employee);
    newEmployee.iin = newEmployee.iin.replace(/ /g, '');

    const formValue = this.employeeForm.getRawValue();
    let amount = formValue.amount;
    if (typeof amount == 'string') {
      amount = parseFloat(amount.replace(',', '.').replace(/ /g, ''));
    }

    switch (this.paymentType) {
      case PAYMENT_TYPE.PENSION:
        newEmployee.pensionSum = {
          amount: amount,
          currency: CURRENCY.KZT,
        };
        break;
      case PAYMENT_TYPE.MEDICAL:
        newEmployee.ocmcSum = {
          amount: amount,
          currency: CURRENCY.KZT,
        };
        break;
      case PAYMENT_TYPE.SOCIAL:
        newEmployee.socialSum = {
          amount: amount,
          currency: CURRENCY.KZT,
        };
        break;
      case PAYMENT_TYPE.SALARY:
        newEmployee.salarySum = {
          amount: amount,
          currency: CURRENCY.KZT,
        };
        break;
    }

    newEmployee.fio = formValue.name;

    if (this.ibanRequired) {
      const unmaskedIban = this.maskedService.getPastedIban(
        formValue.iban,
        Masks.iban.prefix
      );
      newEmployee.iban = unmaskedIban;
    }

    if (this.birthDateRequired) {
      const eBirthDate = moment(formValue.birthDate, PERIOD_FORMAT_INPUT);
      newEmployee.birthDate = eBirthDate.format(ISO_8061);
    }

    try {
      const saved = await this.employeeService.editEmployee(newEmployee);
      this.saved.emit({
        employee: saved,
        index: this.index,
      });
      this.close();
    } catch (error) {
      if (error.code === ERRORS.BAD_REQUEST) {
        this.errorText = 'ERRORS.INVALID_INPUT';
      } else {
        this.errorText = error.description || error.code;
      }
    } finally {
      this.spinner.hide();
    }
  }

  get f() {
    return (this.employeeForm.controls as { [key: string]: FormControl; });
  }

  get hasLastNameError(): boolean {
    return this.isSubmitted && !!this.f.lastName.errors;
  }

  get hasFirstNameError(): boolean {
    return this.isSubmitted && !!this.f.firstName.errors;
  }

  get hasMiddleNameError(): boolean {
    return this.isSubmitted && !!this.f.middleName.errors;
  }

  get hasBirthdayError() {
    return this.isSubmitted && this.f.birthDate.errors;
  }

  get hasAmountError() {
    return this.isSubmitted && this.f.amount.errors;
  }

  get hasIbanError() {
    return this.isSubmitted && this.f.iban.errors;
  }

  private mapBirthDateToInput(isoDate: string) {
    const date = new Date(isoDate);
    return convertDateToString(date);
  }
}
