import { Component, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { IMoney } from '@shared/currency/currency.model';
import { MoneyPipe } from '@shared/pipes';

@Component({
  selector: 'j-limit-not-passed-modal',
  templateUrl: './limit-not-passed-modal.component.html',
  styleUrls: ['./limit-not-passed-modal.component.scss'],
})
export class LimitNotPassedModalComponent {
  @Input() amount: IMoney;
  translateParams;

  constructor(public modal: NgbActiveModal, private moneyPipe: MoneyPipe) {}

  ngOnInit() {
    this.translateParams = {
      amount: this.moneyPipe.transform(this.amount),
    };
  }

  closeModal() {
    this.modal.close();
  }
}
