import { Component, Output, EventEmitter, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';

import { combineLatest, Subscription } from 'rxjs';

import { CURRENCY } from '@shared/currency/currency.model';

import { ACCOUNT_TYPE, RegularAccount } from '@accounts/account.model';
import { AccountsService } from '@accounts/services/accounts.service';

import {
  PAYMENTS_STEPS,
  PAYMENT_SELECT_TYPE,
  PAYMENT_TYPES,
} from '@payments/models/steps';

import {
  FeatureFlagsService,
  FEATURE_FLAG,
} from '@feature-flags/index';
import { CompanyService } from '@company/company.service';

@Component({
  selector: 'j-payment-select',
  templateUrl: './payment-select.component.html',
  styleUrls: ['./payment-select.component.scss'],
})
export class PaymentSelectComponent implements OnDestroy {
  @Output() selected = new EventEmitter();
  types = PAYMENT_TYPES;

  subscription = new Subscription();

  constructor(
    private companyService: CompanyService,
    private router: Router,
    private accountsService: AccountsService,
    private featureFlagsService: FeatureFlagsService
  ) {
    this.subscription.add(
      combineLatest([
        this.featureFlagsService.featureFlags$,
        this.companyService.refresh$,
      ]).subscribe((values) => {
        const [flags, companyRefresh] = values;
        const [user, company, isAuthorized] = companyRefresh;

        if (isAuthorized) {
          this.retrieveAccounts();
        }
      })
    );
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  close() {
    this.selected.emit(PAYMENTS_STEPS.NONE);
  }

  select(event: Event, type) {
    event && event.preventDefault();
    if (type.isDisabled) {
      return;
    }

    this.selected.emit(type.step);
    if (type.url) {
      this.router.navigateByUrl(type.url);
    }
  }

  private async retrieveAccounts() {
    const accounts = await this.accountsService.retrieveAccounts();
    this.applyAccountsRules(accounts);
  }

  private async applyAccountsRules(accounts: RegularAccount[]) {
    const filteredAccounts = accounts.filter(
      (account) =>
        account.accountType == ACCOUNT_TYPE.ACCOUNT && account.balance?.currency
    );

    this.applyForeignRules(filteredAccounts);
    this.applyConversionRules(filteredAccounts);
  }

  private async applyForeignRules(accounts: RegularAccount[]) {
    // const currencyAccounts = accounts.filter(
    //   (account) => account.balance?.currency != CURRENCY.KZT
    // );

    const isFeatureDisabled = this.featureFlagsService.isDisabled(
      FEATURE_FLAG.FOREIGN_PAYMENT
    );

    this.types.find(
      (type) => type.id === PAYMENT_SELECT_TYPE.FOREIGN
    ).isDisabled = isFeatureDisabled;
  }

  private async applyConversionRules(accounts: RegularAccount[]) {
    // const uniqueCurrencies = this.uniqueBy(
    //   accounts,
    //   (o1, o2) => o1.balance?.currency === o2.balance?.currency
    // );

    const isFeatureDisabled = this.featureFlagsService.isDisabled(
      FEATURE_FLAG.CONVERSION
    );

    this.types.find(
      (type) => type.id === PAYMENT_SELECT_TYPE.CONVERSION
    ).isDisabled = isFeatureDisabled;
  }

  // private uniqueBy(a, cond) {
  //   return a.filter((e, i) => a.findIndex((e2) => cond(e, e2)) === i);
  // }
}
