import { Component, OnInit, Output, EventEmitter, Input, ViewEncapsulation, ContentChild, TemplateRef } from '@angular/core';

export enum CLOSE_POSITION{
  INSIDE = 'inside',
  OUTSIDE = 'outside'
}

@Component({
  selector: 'f-page',
  templateUrl: './f-page.component.html',
  styleUrls: ['./f-page.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class FPageComponent implements OnInit {
  @Output() closed = new EventEmitter();

  @Input() containerClassList = `container`;
  @Input() colsClassList = 'offset-md-1 col-md-10 offset-lg-2 col-lg-8 offset-xl-3 col-xl-6';
  @Input() headerClassList = '';
  @Input() bodyClassList = '';

  @Input() closePosition = CLOSE_POSITION.INSIDE;
  closePositions = CLOSE_POSITION;

  @ContentChild('contentTemplate', { static: false })
  contentTemplate: TemplateRef<any>;

  constructor() {}

  ngOnInit(): void {}

  close() {
    this.closed.emit();
  }

  get pageClasses() {
    return {
      container: `${this.containerClassList} f-page-container`,
      header: `${this.headerClassList} f-page-header`,
      row: 'row',
      cols: this.colsClassList,
      body: `${this.bodyClassList} f-page-body`
    };
  }
}
