import { formatDate } from '@angular/common';
import { IKbe } from '@payments/models';

import { CURRENCY, IMoney } from '@shared/currency/currency.model';
import {
  PAYMENT_STATUS,
  PAYMENT_TYPE,
  IKNP,
  IKBK,
  PAYMENT_CATEGORY,
  IPaymentEmployee,
  SIGNATURE_SCHEME,
  ISignature,
  IHistoryPaymentStatus,
  HISTORY_PAYMENT_STATE,
  IRefundDetails,
} from '@shared/models';

import { RecipientBody } from '@company/recipient/recipient.model';
import { ACCOUNT_TYPE } from '@accounts/account.model';
import { ICardTransaction } from '@cards/models';
import dayjs from 'dayjs';
import { TranslateService } from '@ngx-translate/core';
import { of } from 'rxjs';

export interface ITransaction {
  id: string;
  iban: string;
  reference: string;
  documentNumber: string;
  correspondentBankBic: string;
  correspondentAccount: string;
  correspondentName: string;
  kbe: IKbe;
  correspondentBin: string;
  debit: IMoney;
  credit: IMoney;
  description: string;
  documentDate: string | Date;
  valueDate: string | Date;
  knp: IKNP;
  kbk: IKBK;
  signHead: string;
  signBuch: string;

  emailOnSuccess?: string;

  amount: number;
  amountLCY: number;
  amountVat: number;
  commissionFlag: string;
  referenceKisc: string;
  referenceRelatedDocument: string;

  isExportable?: boolean;
  isEditable?: boolean;
  isSelected?: boolean;
  isSignable?: boolean;
  isSignableByOthers?: boolean;
  isLoading?: boolean;
  isPayment?: boolean;

  signsCountText?: string;

  signatureScheme?: SIGNATURE_SCHEME;
  signatures?: ISignature[];
}

export interface ITransactionDetails {
  category: PAYMENT_CATEGORY;
  colvirId: string;
  colvirResultCode: string;
  colvirResultDescription: string;
  companyId: string;
  details: {
    associatedField: any;
    commission: IMoney;
    createDate: Date;
    description: string;
    documentId: string;
    issueDate: Date;
    kbk: IKBK;
    knp: IKNP;
    payerIban: string;
    paymentAmount: IMoney;
    urgent: boolean;
    valueDate: Date;
    vat: boolean;
  };
  id: string;
  imported: boolean;
  paymentEmployees: IPaymentEmployee[];
  paymentRecipient: RecipientBody;
  signatureScheme: SIGNATURE_SCHEME;
  signatures: ISignature[];
  status: PAYMENT_STATUS;
  type: PAYMENT_TYPE;
}

export interface ITransactionGroup {
  title?: string;
  hasLargeTransactions?: boolean;
  groupDate?: string;
  debitSum: IMoney;
  creditSum: IMoney;
  transactionsCount: number;
  transactions: Transaction[];
}

export enum TRANSACTION_GROUP_TYPE {
  DAY = 'DAY',
  MONTH = 'MONTH',
  NONE = 'NONE',
}

export enum TRANSACTION_TYPE {
  DEBIT = 'DEBIT',
  CREDIT = 'CREDIT',
  RETURN = 'RETURN',
}

export enum TRANSACTION_CATEGORY {
  TRANSFER = 'TRANSFER',
  REFUND = 'REFUND',
  COMMISSION = 'COMMISSION',
}

export class Transaction implements ITransaction {
  id: string;
  iban: string;

  category: TRANSACTION_CATEGORY;
  reference: string;
  documentNumber: string;
  correspondentBankBic: string;
  correspondentAccount: string;
  correspondentName: string;
  kbe: IKbe;
  correspondentBin: string;
  debit: IMoney;
  credit: IMoney;
  description: string;
  documentDate: string | Date;
  valueDate: string | Date;
  knp: IKNP;
  kbk: IKBK;
  signHead: string;
  signBuch: string;

  emailOnSuccess: string;

  amount: number;
  amountLCY: number;
  amountVat: number;
  commissionFlag: string;
  referenceKisc: string;
  referenceRelatedDocument: string;

  type?: TRANSACTION_TYPE;

  isRepeatable?: boolean;
  isRevokable?: boolean;
  isExportable?: boolean;
  isEditable?: boolean;
  isRemovable?: boolean;
  isSelected?: boolean;
  isPayment?: boolean;
  isSignable?: boolean;
  isSignableByOthers?: boolean;
  isSendableForSign?: boolean;
  isLoading?: boolean;
  signsCountText?: string;

  paymentEmployees: IPaymentEmployee[];

  paymentStatus?: IHistoryPaymentStatus;
  refundDetails?: IRefundDetails[];
  paymentType?: PAYMENT_TYPE;
  createDate?: Date;
  signatureScheme?: SIGNATURE_SCHEME;
  signatures?: ISignature[];
  foreignPaymentDetails: any;
  foreignRecipient: any;

  paymentColvirId: string;
  paymentId: string;
  parentReference: string;

  accountType: string;

  associatedField: any;

  constructor(
    transaction,
    private translateService: TranslateService,
    accountType = ACCOUNT_TYPE.ACCOUNT
  ) {
    this.accountType = accountType;
    this.id = transaction.id;
    this.category = transaction.category;
    this.iban = transaction.iban;
    this.reference = transaction.reference;
    this.documentNumber = this.getDocumentNumber(transaction);
    this.correspondentBankBic = transaction.correspondentBankBic;
    this.correspondentAccount = transaction.correspondentAccount;
    this.correspondentName = this.getCorrespondentName(transaction);
    this.kbe = transaction.kbe;
    this.correspondentBin = transaction.correspondentBin;
    this.debit = this.getDebit(transaction);
    this.credit = this.getCredit(transaction);
    this.description = this.getDescription(transaction);
    this.documentDate = transaction.documentDate;
    this.valueDate = transaction.valueDate;
    this.knp = transaction.knp;
    this.kbk = transaction.kbk;
    this.signHead = transaction.signHead;
    this.signBuch = transaction.signBuch;

    this.emailOnSuccess = transaction.emailOnSuccess;

    this.amount = transaction.amount;
    this.amountLCY = transaction.amountLCY;
    this.amountVat = transaction.amountVat;
    this.commissionFlag = transaction.commissionFlag;
    this.referenceKisc = transaction.referenceKisc;
    this.referenceRelatedDocument = transaction.referenceRelatedDocument;

    this.type = this.determineTransactionType();
    this.isPayment = transaction.isPayment;
    this.paymentType = transaction.paymentType;
    this.paymentStatus = transaction.paymentStatus;
    this.refundDetails = transaction.refundDetails;
    this.paymentEmployees = transaction.paymentEmployees;
    this.createDate = this.getCreateDate(transaction);

    this.paymentColvirId = transaction.paymentColvirId;
    this.paymentId = transaction.paymentId;
    this.parentReference = transaction.parentReference;

    this.signatures = transaction.signatures;
    this.signatureScheme = transaction.signatureScheme;
    this.isSignable = transaction.isSignable;
    this.isSignableByOthers = transaction.isSignableByOthers;
    this.signsCountText = transaction.signsCountText;

    this.isEditable = transaction.isEditable;
    this.isRemovable = transaction.isRemovable;
    this.isExportable = transaction.isExportable;
    this.isSendableForSign = transaction.isSendableForSign;
    this.isRepeatable = transaction.isRepeatable;
    this.isRevokable = transaction.isRevokable;

    this.foreignPaymentDetails = transaction.foreignPaymentDetails;
    this.foreignRecipient = transaction.foreignRecipient;
  }

  get isDebit() {
    return this.debit && this.debit.amount > 0;
  }

  get isCredit() {
    return this.credit && this.credit.amount > 0;
  }

  get balance() {
    if (this.isCredit) {
      return this.credit;
    } else {
      return this.debit;
    }
  }

  get sign() {
    if (this.isCredit) {
      return `+`;
    } else {
      return `-`;
    }
  }

  get dateStatusText() {
    const statusDate = this.paymentStatus?.timestamp || this.date;
    return this.mapDateTime(statusDate);
  }

  get paymentMessageTranslateParams() {
    if (
      this.paymentStatus?.state === HISTORY_PAYMENT_STATE.WAITING_FOR_VALUE_DATE
    ) {
      return {
        valueDate: this.convertToDayFormat(this.valueDate as Date),
      };
    }
    return null;
  }

  get paymentMessage() {
    if (this.paymentStatus?.message) {
      return this.paymentStatus?.message;
    } else if (
      this.paymentStatus?.state === HISTORY_PAYMENT_STATE.WAITING_FOR_VALUE_DATE
    ) {
      if (this.valueDate) {
        return `STATEMENT.TRANSACTION.STATE.${HISTORY_PAYMENT_STATE.WAITING_FOR_VALUE_DATE}`;
      }
      return null;
    } else if (this.paymentStatus?.state) {
      return `STATEMENT.TRANSACTION.STATE.${this.paymentStatus?.state}`;
    }
    return null;
  }

  private determineTransactionType() {
    if (this.category == TRANSACTION_CATEGORY.REFUND) {
      return TRANSACTION_TYPE.RETURN;
    } else if (this.isCredit) {
      return TRANSACTION_TYPE.CREDIT;
    } else if (this.isDebit) {
      return TRANSACTION_TYPE.DEBIT;
    }
  }

  private get date() {
    if (this.isPayment) {
      return new Date(this.createDate);
    } else {
      return new Date(this.documentDate);
    }
  }

  private mapDateTime(date: Date) {
    const isToday = dayjs().isSame(dayjs(date), 'day');
    const isYesterday = dayjs().subtract(1, 'date').isSame(dayjs(date), 'day');

    const time = dayjs(date).format('HH:mm');

    if (isToday) {
      return this.isPayment
        ? this.translateService.get('STATEMENT.TRANSACTION.DATE.TODAY_TIME', {
            time,
          })
        : this.translateService.get('STATEMENT.TRANSACTION.DATE.TODAY');
    } else if (isYesterday) {
      return this.isPayment
        ? this.translateService.get(
            'STATEMENT.TRANSACTION.DATE.YESTERDAY_TIME',
            {
              time,
            }
          )
        : this.translateService.get('STATEMENT.TRANSACTION.DATE.YESTERDAY');
    } else {
      return of(this.convertToDayHourFormat(this.date));
    }
  }

  // private isToday(compareDate = this.date) {
  //   const today = new Date();
  //   return (
  //     this.convertToDayFormat(compareDate) == this.convertToDayFormat(today)
  //   );
  // }

  // private isYesterday(compareDate = this.date) {
  //   const today = new Date();
  //   const yesterday = new Date(today.setDate(today.getDate() - 1));

  //   return (
  //     this.convertToDayFormat(compareDate) == this.convertToDayFormat(yesterday)
  //   );
  // }

  private convertToDayFormat(date: Date) {
    return formatDate(date, 'd MMMM yyyy', this.translateService.currentLang);
  }

  private convertToDayHourFormat(date: Date) {
    const format = dayjs().isSame(this.date, 'year')
      ? 'd MMMM HH:mm'
      : 'd MMMM yyyy HH:mm';
    return formatDate(this.date, format, this.translateService.currentLang);
  }

  private getCreateDate(transaction) {
    if (this.accountType === ACCOUNT_TYPE.CARD) {
      return transaction.documentDate;
    }
    return transaction.createDate;
  }

  private getDescription(transaction) {
    if (this.accountType === ACCOUNT_TYPE.CARD) {
      return transaction.details;
    }
    return transaction.description?.replace(/^\s+|\s+$/g, '') || '';
  }

  private getDebit(transaction) {
    if (this.accountType === ACCOUNT_TYPE.CARD) {
      return transaction.transactionType === TRANSACTION_TYPE.DEBIT
        ? this.getCardTransactionAmount(transaction)
        : null;
    }
    return transaction.debit;
  }

  private getCredit(transaction) {
    if (this.accountType === ACCOUNT_TYPE.CARD) {
      return transaction.transactionType === TRANSACTION_TYPE.CREDIT
        ? this.getCardTransactionAmount(transaction)
        : null;
    }
    return transaction.credit;
  }

  private getCardTransactionAmount(cardTransaction: ICardTransaction) {
    if (cardTransaction.amount?.amount && cardTransaction.amount?.currency) {
      return cardTransaction.amount;
    } else if (cardTransaction.feeAmount?.amount) {
      return {
        amount: Math.abs(cardTransaction.feeAmount?.amount),
        currency: CURRENCY.KZT,
      };
    }
    return null;
  }

  private getCorrespondentName(transaction) {
    if (this.accountType === ACCOUNT_TYPE.CARD) {
      return transaction.description;
    }
    return transaction.correspondentName || transaction.description;
  }

  private getDocumentNumber(transaction) {
    if (this.accountType === ACCOUNT_TYPE.CARD) {
      return transaction.finDocId;
    }
    return transaction.documentNumber || transaction.documentId;
  }
}
