import { Pipe, PipeTransform } from '@angular/core';
import { ibanPrefix } from '@utils/index';

export const UNKNOWN = 'unknown';

@Pipe({
  pure: true,
  name: 'bankFromAccount',
})
export class BankFromAccountPipe implements PipeTransform {
  transform(
    value: string = '',
    key?: keyof BankDictEntity,
    safePrefix?: string
  ): string {
    if (!value || (value && value.length < 7)) {
      return '';
    } else if (value) {
      const bank =
        dict[
          ((safePrefix && ibanPrefix(value, safePrefix)) || value)
            .replace(' ', '')
            .slice(4, 7)
        ];

      if (bank) {
        return key ? bank[key] : bank.bic;
      }
    }

    return '';
  }
}

export interface BankDictEntity {
  bic: string;
  name: string;
}

