import { HttpParams } from '@angular/common/http';

export function mapParams(data: any) {
  let params = new HttpParams();

  for (const key in data) {
    if (!data.hasOwnProperty(key)) {
      continue;
    }

    const value = data[key];
    if (!value) {
      continue;
    }

    params = params.append(key, value);
  }

  return params;
}
