import { Injectable } from '@angular/core';

import { BehaviorSubject } from 'rxjs';

import { IUserLastSession, User } from '@shared/models/user';
import { ApiService } from '@api/index';
import { CompanyService } from '@company/company.service';
import { HttpParams } from '@angular/common/http';

export const AUTHORITY_ID = 'authority_id';

@Injectable({
  providedIn: 'root',
})
export class UserService {
  private user$ = new BehaviorSubject<User>(
    JSON.parse(localStorage.getItem('user'))
  );

  lang: string;

  constructor(private apiService: ApiService) { }

  get user() {
    return this.user$.getValue();
  }

  get userPhone() {
    return this.user && this.user.phone;
  }

  getUser() {
    return this.user$.asObservable();
  }

  setUser(user: User) {
    if (user == null) {
      this.lang = null;
      localStorage.removeItem('user');
    } else {
      localStorage.setItem('user', JSON.stringify(user));
    }

    this.user$.next(user);
  }

  changePassword(oldPassword: string, newPassword: string) {
    const url = 'users/password:change';
    const body = {
      oldPassword,
      newPassword,
    };
    return this.apiService.put(url, body);
  }

  async retrieveUserLastSession(): Promise<IUserLastSession> {
    const url = `users/latest-session`;
    return this.apiService.get<IUserLastSession>(url);
  }

  async retrieveOtpInfo(companyId: string = null) {
    try {
      const url = `users/${this.user.id}/otp-devices`;

      let params = new HttpParams();
      params = params.append('companyId', companyId);

      const otpList = await this.apiService.get<any>(url, null, params);

      return otpList?.length > 0 ? otpList[0] : false;
    } catch (error) {
      return false;
    }
  }
}
