import { Injectable } from '@angular/core';
import {
  HttpInterceptor,
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpErrorResponse,
  HttpParams,
  HttpClient,
} from '@angular/common/http';

import { Observable, throwError, Subject } from 'rxjs';
import { catchError, switchMap } from 'rxjs/operators';

import { environment } from '@environments/environment';

import { TokenService } from '@shared/token/token.service';
import { AuthService } from '@auth/auth.service';

@Injectable()
export class AuthInterceptor implements HttpInterceptor {
  private refreshSubject: Subject<any> = new Subject<any>();

  constructor(
    private tokenService: TokenService,
    private authService: AuthService,
    private http: HttpClient
  ) {}

  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    if (req.url.endsWith('/oauth/token')) {
      return next.handle(req);
    } else {
      return next.handle(this.updateHeader(req)).pipe(
        catchError((error, caught) => {
          if (error instanceof HttpErrorResponse) {
            if (
              req.url.includes('authorized-persons') ||
              req.url.includes('access-control')
            ) {
              return throwError(error);
            } else if (this.isAccessDenied(error)) {
              this.authService.logout();
              return throwError(error);
            } else if (this.isTokenExpired(error)) {
              this.tokenService.clearUserToken();
              return this.checkTokenExpired().pipe(
                switchMap(() => {
                  return next.handle(this.updateHeader(req));
                })
              );
            } else {
              return throwError(error);
            }
          }
          return caught;
        })
      );
    }
  }

  private isTokenExpired(errorResponse: HttpErrorResponse): boolean {
    return errorResponse.status && errorResponse.status === 401;
  }

  private isAccessDenied(errorResponse: HttpErrorResponse): boolean {
    return (
      errorResponse.status &&
      errorResponse.status === 403 &&
      errorResponse.error.error === 'access_denied'
    );
  }

  private checkTokenExpired() {
    this.refreshSubject.subscribe({
      next: (data) => {
        this.tokenService.clientToken = data.access_token;
      },
      complete: () => {
        this.refreshSubject = new Subject<any>();
      },
    });

    if (this.refreshSubject.observers.length === 1) {
      this.refreshToken().subscribe(this.refreshSubject);
    }

    return this.refreshSubject;
  }

  private updateHeader(req) {
    const authHeaders = this.tokenService.applyAuthHeaders(req.headers);
    req = req.clone({
      headers: authHeaders,
    });
    return req;
  }

  private refreshToken(): Observable<any> {
    const baseUrl = localStorage.getItem('baseUrl') || environment.baseUrl;

    const url = `${baseUrl}/oauth/token`;
    const headers = this.tokenService.getBasicHeaders();

    const params = new HttpParams().set('grant_type', 'client_credentials');

    return this.http.post<any>(
      url,
      {},
      {
        headers: headers,
        params: params,
      }
    );
  }
}
