import { Component, Input, HostBinding, OnChanges } from '@angular/core';

@Component({
  selector: 'j-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss'],
})
export class MainComponent implements OnChanges{
  @Input() isCentered: boolean = false;
  @HostBinding('class.is-centered') isCenteredActive: boolean = false;

  constructor() {}

  ngOnChanges(){
    this.isCenteredActive = this.isCentered;
  }
}
