import { Injectable } from '@angular/core';
import { ApiService } from '@api/api.service';

import { TranslateService } from '@ngx-translate/core';

import { CompanyService } from '@company/company.service';
import dayjs from 'dayjs';

import ru from 'dayjs/locale/ru';
import kk from 'dayjs/locale/kk';
import en from 'dayjs/locale/en';

const APP_LANG_KEY = 'lang';

@Injectable({
  providedIn: 'root',
})
export class LangService {
  isAuthorized = false;

  constructor(
    private apiService: ApiService,
    private translateService: TranslateService,
    private companyService: CompanyService
  ) {}

  init() {
    const savedLang =
      localStorage.getItem(APP_LANG_KEY) || this.translateService.defaultLang;

    this.translateService.use(savedLang);
    this.updateDayJsLocale(savedLang);
    localStorage.setItem(APP_LANG_KEY, savedLang);

    this.companyService.refresh$.subscribe(([user, company, isAuthorized]) => {
      this.isAuthorized = isAuthorized;

      if (isAuthorized) {
        this.loadServerLang();
      }
    });
  }

  setLang(lang: string, shouldUpdateServer = this.isAuthorized) {
    localStorage.setItem(APP_LANG_KEY, lang);
    this.translateService.use(lang);

    this.updateDayJsLocale(lang);

    if (shouldUpdateServer) {
      this.setLanguage(lang);
    }
  }

  async loadServerLang() {
    try {
      const serverLang = await this.retrieveLanguage();

      if (serverLang && serverLang !== this.translateService.currentLang) {
        this.setLang(this.translateService.currentLang, false);
      }
    } catch (error) {
      console.log(error);
    }
  }

  async setLanguage(language: string) {
    const url = `users/language`;
    return this.apiService.put<any>(url, { language });
  }

  private async retrieveLanguage() {
    const url = `users/language`;
    const serverProps = await this.apiService.get<any>(url);

    return serverProps?.language;
  }

  private updateDayJsLocale(lang: string) {
    switch (lang) {
      case 'ru':
        dayjs.locale(ru);
        break;

      case 'kk':
        dayjs.locale(kk);
        break;

      case 'en':
        dayjs.locale(en);
        break;
    }
  }
}
