import { CURRENCY } from '@shared/currency/currency.model';
import { FormGroup } from '@angular/forms';

export function getLang() {
  if (navigator.languages !== undefined) {
    return navigator.languages[0];
  } else {
    return navigator.language;
  }
}
/**
 *
 * @param number
 * @param precision
 * ceil number to selected precision, add zeros to float part
 */
export function toFixedTrunc(number: string | number, precision: number) {
  const v = (typeof number === 'string' ? number : number.toString()).split(
    '.'
  );
  if (precision <= 0) return v[0];
  let f = v[1] || '';
  if (f.length > precision) {
    return `${v[0]}.${f.substr(0, precision)}`;
  }
  while (f.length < precision) f += '0';
  return `${v[0]}.${f}`;
}
/**
 *
 * @param number
 * @param precision
 * ceil number to selected precision, no adding zeros to float part
 */
export function toFixedTrim(number: string | number, precision: number) {
  return parseFloat(toFixedTrunc(number, precision)).toString();
}

export function generateUniqueId(): string {
  const array = new Uint32Array(8);
  window.crypto.getRandomValues(array);
  let str = '';
  for (let i = 0; i < array.length; i++) {
    str += (i < 2 || i > 5 ? '' : '-') + array[i].toString(16).slice(-4);
  }
  // start HTML id attr with string only
  return 'a' + str.slice(1);
}

export function maskPhone(phone: string): string {
  return `${phone.slice(0, 11)} ••• ${phone.slice(16)}`;
}

export function maskIban(iban: string): string {
  return `${iban.slice(0, 2)} •• ${iban.slice(16)}`;
}

const currencyToPrefix = {
  [CURRENCY.KZT]: 'KZ',
};

export function ibanPrefix(iban: string = '', currency: string) {
  const prefix = currencyToPrefix[currency];
  return (prefix + iban).replace(prefix + prefix, '');
}

export function safeUnmaskAmount(value: string | number): number {
  return typeof value == 'string'
    ? parseFloat((value && value.replace(/ /g, '').replace(/,/g, '.')) || '0')
    : value;
}

export function countWords(str: string): number {
  return str
    .trim()
    .split(' ')
    .filter(function (n) {
      return n !== '';
    }).length;
}
/**
 * Pick values for all controls (disabled too)
 * @param group
 * @param pick
 */
export function getGroupRawValue<T>(group: FormGroup, pick?: string[]): T {
  return (pick ? pick : Object.keys(group.controls)).reduce(
    (rawModel, controlName) => {
      rawModel[controlName] = group.controls[controlName].value;
      return rawModel;
    },
    {} as {[K in keyof T]: T[K]}
  );
}
