import { IMoney } from '@shared/currency/currency.model';

import { ITransactionGroup, TRANSACTION_GROUP_TYPE } from './transaction.model';

export const STATEMENT_LARGE_AMOUNT = 1000000000;

export enum STATEMENT_TYPE {
  INCOME = 'INCOME',
  OUTCOME = 'OUTCOME',
}

export interface IStatement {
  statementDate?: Date;
  iban?: string;

  dateFrom?: Date;
  dateTo?: Date;

  balanceIn?: IMoney;
  balanceInLCY?: IMoney;
  balanceOut?: IMoney;
  balanceOutLCY?: IMoney;

  creditTotalSum?: IMoney;
  debitTotalSum?: IMoney;

  transactionGroups: ITransactionGroup[];
  groupType?: TRANSACTION_GROUP_TYPE;

  totalTransactionsCount: number;

  // client properties
  hasLargeBalances?: boolean;
  hasLargeTransactions?: boolean;
}

export enum STATEMENT_MODE {
  CARDS = 'CARDS',
  ACCOUNTS = 'ACCOUNTS',
  PAYMENTS = 'PAYMENTS',
}

