import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { JInputComponent } from '../input.component';
import { MaskedService } from '@shared/masked/masked.service';

@Component({
  selector: 'j-search-input',
  templateUrl: './search-input.component.html',
  styleUrls: ['./search-input.component.scss'],
})
export class SearchInputComponent extends JInputComponent implements OnInit, OnDestroy {
  @Input() placeholder: string = '';
  public isClearEnabled = false;
  constructor(public maskedService: MaskedService) {
    super(maskedService);
  }

  ngOnInit(): void {
    this.isClearEnabled = this.value.length > 0;
    const formSubscription = this.formControl.valueChanges.subscribe((value) => {
      this.valueChanged.emit(value);
      this.isClearEnabled = value.length > 0;
    });

    this.subscription.add(formSubscription);
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  clear() {
    super.clear();
    this.focus(5);
  }
}
