import { Injectable } from '@angular/core';
import { AuthService } from '@auth/auth.service';

import { NotifierService } from 'angular-notifier';

import { ApiService } from '@api/index';

import { HEARTBEAT_CONFIG } from '../config/heartbeat.config';
import { TranslateService } from '@ngx-translate/core';
import { HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root',
})
export class HeartbeatService {
  /**
   * User active flag
   */
  isActive = true;

  /**
   * User inactive minutes count
   */
  inactiveCount = 0;

  /**
   * Interval countdown
   */
  countdown = HEARTBEAT_CONFIG.countdown;

  /**
   * Interval object
   */
  interval: any;

  /**
   * Interval notification count.
   * Index to show notification after given amount of inactive loops.
   */
  notificationIndex = HEARTBEAT_CONFIG.notificationIndex;

  /**
   * Interval expiration count
   * Index to logout after given amount of inactive loops.
   */
  expirationIndex = HEARTBEAT_CONFIG.expirationIndex;

  subscribed = false;

  constructor(
    private api: ApiService,
    private authService: AuthService,
    private notifierService: NotifierService,
    private translateService: TranslateService
  ) {}

  /**
   * Hearbeat initializer constructor
   * Listens authService.authorized event.
   */
  initSubscribe() {
    this.authService.authEvents().subscribe((isAuthorized) => {
      if (isAuthorized) {
        this.startTimer();
      } else {
        this.stopTimer();
      }
    });
    this.subscribed = true;
  }

  /**
   * Used to update heartbeat active flag
   */
  eventTrigger() {
    this.isActive = true;
    this.inactiveCount = 0;
    if (!this.subscribed) {
      this.initSubscribe();
    }
  }

  /**
   * Starts heartbeat timer
   */
  startTimer() {
    this.interval = setInterval(() => {
      if (this.isActive) {
        this.beat();
      } else {
        this.inactiveCount++;

        if (this.inactiveCount === this.notificationIndex) {
          this.notifierService.notify(
            'warning',
            this.translateService.instant('SESSION.ABOUT_TO_EXPIRE')
          );
        } else if (this.inactiveCount >= this.expirationIndex) {
          this.handleExpiration();
        }
      }

      this.isActive = false;
    }, this.countdown);
  }

  /**
   * Stops heartbeat timer
   */
  stopTimer() {
    clearInterval(this.interval);
  }

  /**
   * Heartbeat update request
   */
  async beat() {
    try {
      await this.beatRequest();
      this.inactiveCount = 0;
      this.notificationIndex = HEARTBEAT_CONFIG.notificationIndex;
      this.expirationIndex = HEARTBEAT_CONFIG.expirationIndex;
    } catch (error) {
      this.handleExpiration();
    }
  }

  beatRequest() {
    const url = `heartbeat`;
    const headers = new HttpHeaders({
      'X-Language': this.translateService.currentLang,
    });
    return this.api.put(url, {}, headers);
  }

  /**
   * Heartbeat expire handler
   */
  private handleExpiration() {
    this.notifierService.hideAll();
    this.notifierService.notify(
      'error',
      this.translateService.instant('SESSION.EXPIRED')
    );
    this.authService.logout();
  }
}
