import { forwardRef, Inject, Injectable, Injector } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpResponse,
  HttpErrorResponse,
} from '@angular/common/http';
import { Observable, of, throwError } from 'rxjs';
import { JmartAuthService } from '@jmart-cabinet/services/jmart-auth.service';
import { AuthService } from '@auth/auth.service';
import { catchError, map } from 'rxjs/operators';
import { Router } from '@angular/router';

@Injectable()
export class JmartCabinetInterceptor implements HttpInterceptor {
  constructor(
    private jmartAuthService: JmartAuthService,
    private JBAuthService: AuthService,
    private router: Router,
    @Inject('JMART_CABINET_BASE_URL') private baseUrl: string,
    private injector: Injector
  ) {}

  hasHeartBeatStopped = false;

  intercept(
    request: HttpRequest<unknown>,
    next: HttpHandler
  ): Observable<HttpEvent<unknown>> {
    const authToken =
      this.jmartAuthService.jmartCabinetAuthData.tokens?.auth.token;
    const isJmartRequest = request.url.includes(this.baseUrl);

    // way to handle preflight hearbeat request and fake it, cause this request will cause redirect to jb login
    // location.pathname is not the angular way to solve this kind of issues, but heartbeat request started at
    // constructor so we don't have an ability to determine the route, also injecting the Location service here
    // will cause cyclic dependency issue, because Location service uses http service under the hood
    if (
      request.url.includes('heartbeat') &&
      location.pathname.includes('jmart-cabinet') &&
      !this.hasHeartBeatStopped
    ) {
      this.hasHeartBeatStopped = true;
      return of(
        new HttpResponse({
          status: 200,
          body: {},
        })
      );
    }

    if (authToken && !this.JBAuthService.isAuthenticated && isJmartRequest) {
      request = request.clone({
        setHeaders: {
          'Request-Name-Strategy': 'true',
          'Response-Name-Strategy': 'true',
          'Accept-Language': 'ru',
          'User-Id': '85',
          'Current-Company-Id': String(
            this.jmartAuthService.jmartCabinetAuthData.companyId
          ),
          'Company-Id': String(
            this.jmartAuthService.jmartCabinetAuthData.companyId
          ),
          'Company-Ids': String(
            this.jmartAuthService.jmartCabinetAuthData.companyId
          ),
          Authorization: `Bearer ${authToken}`,
        },
      });
      return next.handle(request).pipe(
        catchError((err) => {
          if (
            err instanceof HttpErrorResponse &&
            [401, 403].includes(err.status)
          ) {
            this.router.navigate(['jmart-cabinet', 'auth']);
          }
          console.log(err);
          return throwError(err);
        })
      );

    }
    return next.handle(request);
  }
}
