import { Component } from '@angular/core';

import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { TranslateService } from '@ngx-translate/core';

import { CompanyService } from '@company/company.service';

import {
  ImportPartialSuccessComponent,
  ImportPartialErrorComponent,
  ImportSuccessComponent,
  ImportErrorComponent,
} from '@shared/modals';
import {
  IImportResult,
  IMPORT_ERROR_STATUS,
  IMPORT_STATUS,
} from '@shared/models';
import { FilesService } from '@shared/services/files.service';

@Component({
  selector: 'j-import',
  templateUrl: './import.component.html',
  styleUrls: ['./import.component.scss'],
})
export class ImportComponent {
  isImporting = false;
  importModel: any;
  restrictedFormats = [
    'wav', 'aiff', 'ape', 'flac', 'mp3', 'ogg', 'wma', 'midi', 'mid', 'aac', 'aa', 'au', 'ac3', 'aud', 'm4a', 'avi', 'asf',
    'mp4', 'm4v', 'mov', 'mpg', 'mpeg', '3gp', 'wmv', 'mkv', 'zip', 'rar', '7z', 'ace', 'arj', 'cab', 'tar', 'lzh',
    'gz', 'gzip', 'pkg', 'pak', 'exe', 'com', 'apk', 'jar', 'bin', 'cgi', 'cmd', 'msi', 'js', 'jse', 'scr',
    'vbe', 'vbs', 'cpp', 'bat', 'sql', 'docm', 'dot', 'dotx', 'dotm', 'xlsm', 'xlsb', 'xlt', 'xltx', 'ppt', 'pptx', 'svg',
  ];

  constructor(
    private companyService: CompanyService,
    private filesService: FilesService,
    private modalService: NgbModal,
    private translateService: TranslateService
  ) {}

  onImportClick(event) {
    const rawFiles = event.target.files || event.target.file;
    const importingFiles = [];

    for (let i = 0; i < rawFiles.length; i++) {
      importingFiles[i] = rawFiles[i];
    }

    this.importPayments(importingFiles);
    this.importModel = null;
  }

  private async importPayments(importingFiles) {
    let restricted;
    let fileSizeAllowed = true;
    importingFiles.forEach(file => {
      if (file.size > 5120000) {
        fileSizeAllowed = false;
      }
      restricted = this.restrictedFormats.find(format => format === file.name.split('.').pop());
    });
    if (!fileSizeAllowed) {
      this.showImportError(importingFiles, 'IMPORT.TOO_BIG_FILE', 0);
    }
    if (restricted) {
      this.showImportError(importingFiles, 'IMPORT.RESTRICTED_FILES', 0);
    }
    if (!importingFiles || restricted || !fileSizeAllowed) {
      return;
    }

    this.isImporting = true;

    try {
      const importResultList = await this.filesService.uploadPayments(
        this.companyService.currentId,
        importingFiles
      );

      const totalOperations = this.countTotalOperations(importResultList);
      const succeededOperations = this.countSuccessOperations(importResultList);
      const invalidFiles = this.countInvalidFiles(importResultList);
      const alreadyImportedFiles =
        this.countAlreadyImportedFiles(importResultList);
      const draftedOperations = this.countDraftedOperations(importResultList);
      const erroredOperations =
        totalOperations - succeededOperations - draftedOperations;

      if (totalOperations === 0 || invalidFiles === importResultList.length) {
        this.showImportError(importingFiles, null, alreadyImportedFiles);
      } else if (succeededOperations === 0) {
        this.showImportPartialError(
          importResultList,
          invalidFiles,
          draftedOperations,
          erroredOperations
        );
      } else if (totalOperations === succeededOperations) {
        this.showImportSuccess(succeededOperations);
      } else {
        this.showImportPartialSuccess(
          importResultList,
          invalidFiles,
          draftedOperations,
          succeededOperations,
          erroredOperations
        );
      }
    } catch (error) {
      let description = error.description;

      if (error.code?.toUpperCase() === 'DISABLE_SEND_DEPOSIT_ACCOUNT') {
        description = this.translateService.instant(
          `IMPORT.DISABLE_SEND_DEPOSIT_ACCOUNT.ONE`
        );
      }

      this.showImportError(importingFiles, description);
    } finally {
      this.isImporting = false;
    }
  }

  private async showImportPartialSuccess(
    importResultList: IImportResult[],
    invalidFilesCount: number,
    draftedOperationsCount: number,
    successOperationsCount: number,
    erroredOperationsCount: number
  ) {
    const modal = this.modalService.open(ImportPartialSuccessComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_md',
      backdrop: 'static',
      centered: true,
    });
    modal.componentInstance.invalidFilesCount = invalidFilesCount;
    modal.componentInstance.draftedOperationsCount = draftedOperationsCount;
    modal.componentInstance.erroredOperationsCount = erroredOperationsCount;
    modal.componentInstance.successOperationsCount = successOperationsCount;
    modal.componentInstance.importResultList = importResultList;
  }

  private async showImportPartialError(
    importResultList: IImportResult[],
    invalidFilesCount: number,
    draftedOperationsCount: number,
    erroredOperationsCount: number
  ) {
    const modal = this.modalService.open(ImportPartialErrorComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_md',
      backdrop: 'static',
      centered: true,
    });
    modal.componentInstance.invalidFilesCount = invalidFilesCount;
    modal.componentInstance.draftedOperationsCount = draftedOperationsCount;
    modal.componentInstance.importResultList = importResultList;
    modal.componentInstance.erroredOperationsCount = erroredOperationsCount;
  }

  private async showImportSuccess(operationsCount: number) {
    const modal = this.modalService.open(ImportSuccessComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_sm',
      backdrop: 'static',
      centered: true,
    });

    modal.componentInstance.operationsCount = operationsCount;
  }

  private async showImportError(
    importingFiles = null,
    errorText: string = null,
    alreadyImportedCount: number = 0
  ) {
    const modal = this.modalService.open(ImportErrorComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_sm',
      backdrop: 'static',
      centered: true,
    });

    modal.componentInstance.filesCount = importingFiles?.length;
    modal.componentInstance.errorText = errorText;

    if (alreadyImportedCount > 0) {
      modal.componentInstance.descriptionKey = 'ALREADY_IMPORTED';
    }

    try {
      await modal.result;
      this.importPayments(importingFiles);
    } catch (error) {}
  }

  private countDraftedOperations(importResultList: IImportResult[]) {
    let result = 0;
    importResultList.forEach((importResult) => {
      result += importResult.failed?.reduce((prev, current) => {
        return prev + (current.status === IMPORT_ERROR_STATUS.FAILED ? 0 : 1);
      }, 0);
    });
    return result;
  }

  private countSuccessOperations(importResultList: IImportResult[]) {
    let result = 0;
    importResultList.forEach((importResult) => {
      result += importResult.success;
    });
    return result;
  }

  private countTotalOperations(importResultList: IImportResult[]) {
    let result = 0;
    importResultList.forEach((importResult) => {
      result += importResult.total;
    });
    return result;
  }

  private countInvalidFiles(importResultList: IImportResult[]) {
    let result = 0;
    importResultList.forEach((importResult) => {
      if (importResult.status === IMPORT_STATUS.INVALID_FORMAT) {
        result++;
      }
    });
    return result;
  }

  private countAlreadyImportedFiles(importResultList: IImportResult[]) {
    let result = 0;
    importResultList.forEach((importResult) => {
      if (importResult.status === IMPORT_STATUS.ALREADY_IMPORTED) {
        result++;
      }
    });
    return result;
  }
}
