
import { environment } from '@environments/environment';
import amplitude from 'amplitude-js';
import { AnalyticsInstance } from '../../interfaces/instance.interface';

export class AmplitudeAnalytics implements AnalyticsInstance {

    private instance;

    constructor(
    ) {
        this.instance = amplitude.getInstance();
        const apiKey = window && window['env'] && window['env']['AmplitudeApiKey'] || environment.amplitudeApiKey;
        this.instance.init(apiKey);
    }

    logEvent(eventName: string, params) {
        this.instance.logEvent(
            eventName,
            params,
        );
    }

    setUserProperties(props) {
        const identify = new amplitude.Identify();
        Object.entries(props).forEach((key, value) => {
            identify.set(key, value)
        });
        this.instance.indentify(identify);
    }

    setUserId(id: string) {
        this.instance.setUserId(id);
    }
}
