import { IMoney } from '@shared/currency/currency.model';

export enum BULK_OPERATION_STATUS {
  FAILED = 'FAILED',
  SUCCESS = 'SUCCESS',
  PARTLY_SUCCESS = 'PARTLY_SUCCESS',
}

export interface IBulkOperationItem {
  errorData: { [s: string]: unknown } | ArrayLike<unknown>;
  errorMsg: string;
  itemId: string;
  result: any;
  status: BULK_OPERATION_STATUS;

  documentNumber?: string;
}

export interface IBulkOperationResult {
  numberOfFailed: number;
  numberOfSuccess: number;

  results?: IBulkOperationItem[];
  status?: BULK_OPERATION_STATUS;

  totalBalance?: IMoney;
}
