import { AnalyticsInstance } from '../../interfaces/instance.interface';

export class GtagAnalytics implements AnalyticsInstance {

    private gtag;

    constructor() {
        this.gtag = window && window['gtag'] || (() => {});
        this.gtag('config', window && window['env'] && window['env']['gaTrackingId']);
    }

    public logEvent(eventName: string, params = {}) {
        this.gtag('event', eventName, params);
    }

    public setUserProperties(props) {
        this.gtag('set', 'user_properties', props);
    }

    public setUserId(user_id: string) {
        this.gtag('config',  window && window['env'] && window['env']['gaTrackingId'], {
            user_id
        });
    }

}
