import { Component, OnDestroy, OnInit } from '@angular/core';

import { Subscription } from 'rxjs';

import { NavService, NavItem } from '../nav.service';

@Component({
  selector: 'j-nav-auth',
  templateUrl: './nav-auth.component.html',
  styleUrls: ['./nav-auth.component.scss'],
})
export class NavAuthComponent implements OnDestroy, OnInit {
  navList: NavItem[] = this.navService.registerList;

  showNoAccount: boolean;
  showNoEmail: boolean;
  showUserBlocked: boolean;
  showPhoneNotMatch: boolean;

  readonly subs = new Subscription();

  constructor(private navService: NavService) {}

  ngOnInit() {
    const navSubscription = this.navService
      .getRegisterList()
      .subscribe((navList) => {
        this.navList = navList;
      });
    this.subs.add(navSubscription);

    const showNoAccountSubscription = this.navService.showNoAccount.subscribe(
      (data) => {
        this.showNoAccount = data;
      }
    );
    this.subs.add(showNoAccountSubscription);

    const showNoEmailSubscription = this.navService.showNoEmail.subscribe(
      (data) => {
        this.showNoEmail = data;
      }
    );
    this.subs.add(showNoEmailSubscription);

    const showUserBlockedSubscription =
      this.navService.showUserBlocked.subscribe((data) => {
        this.showUserBlocked = data;
      });
    this.subs.add(showUserBlockedSubscription);

    const showPhoneNotMatchSubscription =
      this.navService.showPhoneNotMatch.subscribe((data) => {
        this.showPhoneNotMatch = data;
      });
    this.subs.add(showPhoneNotMatchSubscription);
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  navItemClick(event: Event): void {
    event.preventDefault();
  }
}
