import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ViewChild,
  OnDestroy,
} from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { Subscription } from 'rxjs';

import { Dictionary } from 'lodash';
import { MaskPipe } from 'ngx-mask';

import { MaskedConfig } from '@shared/masked/masked';
import {
  SMS_CODE_LENGTH,
  SMS_TABS,
  SMS_TIMER_COUNTDOWN,
  SMS_ERROR_TYPES,
  ISmsToken,
  ICountryCode,
} from '@shared/models';
import { MaskedService } from '@shared/masked/masked.service';
import { SmsService } from '@shared/services/sms.service';

import { IError, ERRORS } from '@api/index';
import { DEFAULT_COUNTRY_CODE } from '@auth/auth.model';

import { CONFIRM_SIGNATURE_TYPE } from '../../components/confirm-sms-modal/confirm-sms-modal.component';
import { SmsInputComponent } from '../../input/sms-input/sms-input.component';
import { environment } from '@environments/environment';

@Component({
  selector: 'j-sms',
  templateUrl: './sms.component.html',
  styleUrls: ['./sms.component.scss'],
})
export class SmsComponent implements OnInit, OnDestroy {
  smsToken: ISmsToken;

  isDevelopment = !environment.production;

  @Input() phone: string;
  @Input() params?: Dictionary<any>;
  @Input() headers?: Dictionary<any>;
  @Input() isAuthAction = false;
  @Input() countryCode?: ICountryCode;

  @Output() submitted = new EventEmitter<ISmsToken>();
  @Output() failed = new EventEmitter();
  @Output() loaded = new EventEmitter();
  @Output() otpAuthRequested = new EventEmitter<string>();

  isLoading: boolean;
  isReady = false;
  isSubmitted: boolean;

  authTabs = SMS_TABS;
  currentTab = SMS_TABS.FORM;

  hasError: boolean;

  authSmsForm: FormGroup;
  smsCodeLength = SMS_CODE_LENGTH;
  phoneMaskConfig: MaskedConfig;

  errorText;

  timer: any;
  timerValue: number;
  timerMaxValue = SMS_TIMER_COUNTDOWN;
  isTimerRunning: boolean;

  blockedTimer: any;
  blockedTimerValue: number;
  isBlockedTimerRunning: boolean;

  @ViewChild('smsInput') smsInput: SmsInputComponent;

  errorTypes = SMS_ERROR_TYPES;

  subscription: Subscription = new Subscription();

  errorTranslateParams = {
    triesLeft: 0,
  };

  constructor(
    private smsService: SmsService,
    private maskedService: MaskedService,
    private maskPipe: MaskPipe
  ) {
    this.countryCode = this.countryCode
      ? this.countryCode
      : localStorage.getItem('countryCode') != 'undefined'
      ? JSON.parse(localStorage.getItem('countryCode'))
      : null;

    this.authSmsForm = new FormGroup({
      smsCode: new FormControl('', Validators.required),
    });

    const formSubscription = this.authSmsForm.valueChanges.subscribe(
      (value) => {
        this.isSubmitted = false;

        if (value.smsCode?.length === this.smsCodeLength) {
          this.onSubmit();
        }
      }
    );

    this.subscription.add(formSubscription);
  }

  ngOnInit(): void {
    this.requestSms();
    const maskType =
      this.countryCode?.countryCode === DEFAULT_COUNTRY_CODE.KAZAKHSTAN
        ? 'loginPhoneKz'
        : 'foreignPhone';
    this.phoneMaskConfig = this.maskedService.getConfig(maskType);
  }

  ngOnDestroy(): void {
    this.stopTimer();
    this.stopBlockedTimer();
    this.subscription.unsubscribe();
  }

  setTab(tab: SMS_TABS) {
    this.currentTab = tab;
  }

  focus() {
    this.smsInput.focus();
  }

  async requestSms() {
    this.isLoading = true;

    this.errorText = '';

    try {
      this.smsToken = await this.smsService.requestSms(
        this.phone,
        this.params,
        this.headers
      );
      this.setTab(this.authTabs.FORM);
      this.f.smsCode.setValue(this.isDevelopment ? this.smsToken.code : '');
      this.startTimer();
    } catch (error) {
      this.mapRequestError(error);
    } finally {
      if (!this.isReady) {
        this.isReady = true;
      }
      this.isLoading = false;

      this.focus();
      this.loaded.emit();
    }
  }

  async onSubmit() {
    this.isSubmitted = true;
    this.errorText = '';
    this.hasError = false;

    if (this.authSmsForm.valid && !this.isLoading) {
      this.isLoading = true;
      this.smsToken.code = this.f.smsCode.value;

      try {
        this.smsToken = await this.smsService.submitSms(this.smsToken);
        localStorage.setItem('countryCode', JSON.stringify(this.countryCode));
        this.submitted.emit(this.smsToken);
      } catch (error) {
        this.hasError = true;
        this.smsInput.clear();
        this.authSmsForm.patchValue({
          smsCode: '',
        });
        this.mapSubmitError(error);
        this.smsInput.triggerHelperAnimation();
        this.isLoading = false;
      }
    }
  }

  onOtpClicked() {
    this.otpAuthRequested.emit(CONFIRM_SIGNATURE_TYPE.OTP);
  }

  get translateParam() {
    return {
      timerValue: this.timerValue,
    };
  }

  get f() {
    return this.authSmsForm.controls;
  }

  get maskedPhone() {
    if (this.phone) {
      const phone = this.maskedService.getPastedPhone(this.phone);
      let maskedPhone = '';
      if (this.phoneMaskConfig.shadow && this.countryCode) {
        maskedPhone = this.maskPipe.transform(phone, this.phoneMaskConfig.mask);
        maskedPhone = `${maskedPhone.slice(0, 7)}•••${maskedPhone.slice(
          12,
          18
        )}`;
      } else {
        maskedPhone = `${phone.slice(0, 7)}•••${phone.slice(-3, phone.length)}`;
      }
      return `+${maskedPhone}`;
    }
  }

  private mapRequestError(error: IError) {
    switch (error.code) {
      case ERRORS.ARGUMENT_MISSING:
        this.failed.emit();
        break;
      case ERRORS.RESOURCE_BLOCKED:
        if (error.data && error.data.millisecondsLeft) {
          this.startBlockedTimer(error.data.millisecondsLeft);
        }
        this.setTab(this.authTabs.FORBIDDEN_BLOCKED);
        break;
      default:
        this.setTab(this.authTabs.ERRORED);
        this.errorText = error.description;
        break;
    }
  }

  private mapSubmitError(error: IError) {
    switch (error.code) {
      case ERRORS.ARGUMENT_MISSING:
        this.failed.emit();
        break;
      case ERRORS.INVALID_CODE:
        let triesLeft;
        if (error.data && error.data.triesLeft) {
          triesLeft = error.data.triesLeft;
          this.errorTranslateParams = {
            triesLeft,
          };
          switch (triesLeft % 20) {
            case 1:
              this.errorText = `ERRORS.INVALID_CODE.ONE`;
              break;
            case 2:
            case 3:
            case 4:
              this.errorText = `ERRORS.INVALID_CODE.FEW`;
              break;
            default:
              this.errorText = `ERRORS.INVALID_CODE.OTHER`;
          }
        } else {
          this.setTab(this.authTabs.FORBIDDEN);
        }
        break;
      case ERRORS.RESOURCE_BLOCKED:
        if (error.data && error.data.millisecondsLeft) {
          const countdown = Math.ceil(error.data.millisecondsLeft / 1000);
          if (this.isTimerRunning) {
            this.timerValue = countdown;
          } else {
            this.startTimer(countdown);
          }
        }
        this.setTab(this.authTabs.FORBIDDEN);
        break;
      default:
        this.errorText = error.description;
        break;
    }
  }

  private startBlockedTimer(millisecondsLeft: number) {
    this.blockedTimerValue = Math.ceil(millisecondsLeft / 1000);
    this.isBlockedTimerRunning = true;

    this.blockedTimer = setInterval(() => {
      this.blockedTimerValue--;

      if (this.blockedTimerValue === 0) {
        this.stopBlockedTimer();
      }
    }, 1000);
  }

  private stopBlockedTimer() {
    clearInterval(this.blockedTimer);
    this.isBlockedTimerRunning = false;
    this.blockedTimerValue = null;
  }

  private startTimer(timerMaxValue = this.timerMaxValue) {
    this.isTimerRunning = true;
    this.timerValue = timerMaxValue;

    this.timer = setInterval(() => {
      this.timerValue--;

      if (this.timerValue === 0) {
        this.stopTimer();
      }
    }, 1000);
  }

  private stopTimer() {
    clearInterval(this.timer);
    this.setTab(this.authTabs.EXPIRED);
    this.isTimerRunning = false;
  }
}
