import { Component, HostListener, Inject, OnInit } from '@angular/core';
import { DOCUMENT } from '@angular/common';

enum OS {
  WINDOWS_MOBILE = 'WindowsPhone',
  ANDROID = 'Android',
  IOS = 'iOS',
  UNKNOWN = 'Unknown',
}

const links = {
  ios: 'https://apps.apple.com/ru/app/jusan-business/id1536089771',
  android:
    'https://play.google.com/store/apps/details?id=kz.jysan.business&hl=ru&gl=US',
};

const MOBILE_CLOSED_KEY = 'isMobileClosed';
const MOBILE_CLASSNAME = 'mobile';

@Component({
  selector: 'j-mobile',
  templateUrl: './mobile.component.html',
  styleUrls: ['./mobile.component.scss'],
})
export class MobileComponent implements OnInit {
  isClosed = JSON.parse(localStorage.getItem(MOBILE_CLOSED_KEY)) || false;
  mobileAppLink = null;

  constructor(@Inject(DOCUMENT) private document: Document) {
    this.updateMobileClassName();
  }

  ngOnInit() {}

  onClose() {
    this.isClosed = true;
    localStorage.setItem(MOBILE_CLOSED_KEY, JSON.stringify(true));

    this.hideMobilePanel();
  }

  @HostListener('window:resize', ['$event'])
  private updateMobileClassName() {
    if (this.isClosed) {
      this.document.body.classList.remove(MOBILE_CLASSNAME);
    }

    const os = this.getMobileOperatingSystem();

    switch (os) {
      case OS.IOS:
        this.mobileAppLink = links.ios;
        this.showMobilePanel();
        break;

      case OS.ANDROID:
        this.mobileAppLink = links.android;
        this.showMobilePanel();
        break;

      case OS.WINDOWS_MOBILE:
      default:
        this.hideMobilePanel();
        break;
    }
  }

  private showMobilePanel() {
    this.document.body.classList.add(MOBILE_CLASSNAME);
  }

  private hideMobilePanel() {
    this.document.body.classList.remove(MOBILE_CLASSNAME);
  }

  private getMobileOperatingSystem() {
    var userAgent = navigator.userAgent || navigator.vendor || window['opera'];

    // Windows Phone must come first because its UA also contains "Android"
    if (/windows phone/i.test(userAgent)) {
      return OS.WINDOWS_MOBILE;
    }

    if (/android/i.test(userAgent)) {
      return OS.ANDROID;
    }

    // iOS detection from: http://stackoverflow.com/a/9039885/177710
    if (/iPad|iPhone|iPod/.test(userAgent) && !window['MSStream']) {
      return OS.IOS;
    }

    return OS.UNKNOWN;
  }
}
