import { Component, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { TranslateService } from '@ngx-translate/core';

export enum WORKING_TIME_MODAL {
  TODAY = 'TODAY',
  AUTO = 'AUTO',
  HOLIDAY = 'HOLIDAY',
}

const TRANSLATE_PREFIX = `PAYMENTS.TIME_MODALS`;

@Component({
  selector: 'j-working-time-modal',
  templateUrl: './working-time-modal.component.html',
  styleUrls: ['./working-time-modal.component.scss'],
})
export class WorkingTimeModalComponent {
  @Input()
  type: WORKING_TIME_MODAL;

  @Input()
  workingHoursEnd: string;

  constructor(
    public modal: NgbActiveModal,
    private translateService: TranslateService
  ) {}

  get title() {
    switch (this.type) {
      case WORKING_TIME_MODAL.TODAY:
      case WORKING_TIME_MODAL.AUTO:
        return this.translateService.instant(
          `${TRANSLATE_PREFIX}.${this.type}.TITLE`,
          { endTime: this.workingHoursEnd }
        );

      case WORKING_TIME_MODAL.HOLIDAY:
        return this.translateService.instant(
          `${TRANSLATE_PREFIX}.${this.type}.TITLE`
        );

      default:
        return ``;
    }
  }

  get description() {
    return this.translateService.instant(
      `${TRANSLATE_PREFIX}.${this.type}.DESCRIPTION`
    );
  }

  get closeText() {
    switch (this.type) {
      case WORKING_TIME_MODAL.TODAY:
        return this.translateService.instant(`SHARED.OK`).toUpperCase();
      case WORKING_TIME_MODAL.AUTO:
      case WORKING_TIME_MODAL.HOLIDAY:
      default:
        return this.translateService.instant(`SHARED.CANCEL`);
    }
  }

  get canBeSubmitted() {
    return (
      this.type === WORKING_TIME_MODAL.AUTO ||
      this.type === WORKING_TIME_MODAL.HOLIDAY
    );
  }

  onSubmit() {
    this.modal.close();
  }

  onClose() {
    this.modal.dismiss();
  }
}
