import { IMoney } from '@shared/currency/currency.model';

import { Card } from '@cards/models';
import { GeneralDictionary } from '@core/models';

export type IAccountDropdownOption =
  | RegularAccount
  | Deposit
  | Card
  | Partial<RegularAccount>
  | Partial<Deposit>
  | Partial<Card>;

export enum ACCOUNT_TYPE {
  ACCOUNT = 'ACCOUNT',
  DEPOSIT = 'DEPOSIT',
  CARD = 'CARD',
  ALL = 'ALL',
}

export enum ACCOUNT_STATE {
  OPENED = 'OPENED',
  CLOSED = 'CLOSED',
}

export enum ACCOUNT_BLOCK_TYPE {
  LOCK = 'LOCK', // активный
  NOLOCK = 'NOLOCK', // активный
  LIMLOCK = 'LIMLOCK', // сумма в аресте
  PARTLOCK = 'PARTLOCK', // заблокирован
}

export enum ACCOUNT_STATUS {
  OPENED = 'OPENED',
  CLOSED = 'CLOSED',
  MIGRATED = 'MIGRATED',
}

export enum GRANTS {
  AVAILABLE = 'AVAILABLE',
  UNAVAILABLE = 'UNAVAILABLE',
}

// TODO: Переделать на Account и заменить в платежах
export class RegularAccount {
  id: string;
  alterCode: string;
  accountType: ACCOUNT_TYPE;
  balance: IMoney;
  iban: string;
  state: ACCOUNT_STATE;
  blockType: ACCOUNT_BLOCK_TYPE;
  isSelected?: boolean;

  companyId: string;
  created: Date;
  hasFailedMigrations: boolean;
  irreducibleBalance: number;
  openDate: string;
  status: ACCOUNT_STATUS;

  isFirstAtfInTheList?: boolean = false;

  constructor(account) {
    Object.assign(this, account);
  }

  get hasAtfIban() {
    if (!this.alterCode) {
      return false;
    }

    return (
      this.alterCode.length === 20 &&
      this.alterCode.substring(0, 2) === 'KZ' &&
      this.alterCode.substring(4, 7) === '826'
    );
  }

  get maskedIban() {
    return this.maskIban(this.iban);
  }

  get maskedAlterCode() {
    return this.maskIban(this.alterCode);
  }

  get hasLockedState() {
    return this.blockType !== ACCOUNT_BLOCK_TYPE.NOLOCK;
  }

  get isMigrated() {
    return this.status === ACCOUNT_STATUS.MIGRATED;
  }

  get isLocked() {
    return this.blockType === ACCOUNT_BLOCK_TYPE.LOCK;
  }

  get isClosed() {
    return this.state === ACCOUNT_STATE.CLOSED;
  }

  private maskIban(iban: string) {
    if (!iban) {
      return '';
    }

    return `${iban.substring(0, 4)} •••• ${iban.substring(
      iban.length - 4,
      iban.length
    )}`;
  }
}

export class Deposit extends RegularAccount {
  accountType = ACCOUNT_TYPE.DEPOSIT;
  depositType: GeneralDictionary;

  additionalDetails: {
    addPay: boolean;
    clientAccount: string;
    maxSum: number;
    payOut: boolean;
  };

  constructor(account) {
    super(account);
  }
}

export class IUserGrant {
  userId: string;
  firstname: string;
  middlename: string;
  lastname: string;
  userAuthorityId: string;
  position: string;
  authority: string;
  accountGrantType: string;
  initials: string;
  role: string;
  sign: string;
  color: string;
}
