import { DOCUMENT } from '@angular/common';
import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { IExchangeRates } from './exchange-rates.model';
import { ExchangeRatesService } from './exchange-rates.service';

@Component({
  selector: 'j-exchange-rates',
  templateUrl: './exchange-rates.component.html',
  styleUrls: ['./exchange-rates.component.scss']
})
export class ExchangeRatesComponent implements OnInit, OnDestroy {
  selectedBank: string;
  isAnimating: boolean = false;
  wrapperClasses = '';
  selectorClasses = '';
  isSelectorShown: boolean = false;
  exchangeRates: IExchangeRates[];
  error: string;

  constructor(
    private exchangeRatesService: ExchangeRatesService,
    @Inject(DOCUMENT) private document: Document
  ) { }

  ngOnInit(): void {
    this.loadUserExchangeRates();
  }
  
  ngOnDestroy(): void {
    this.document.body.classList.remove('overflow-hidden');
  }

  showCurrencySelector() {
    if (!this.isAnimating) {
      this.isAnimating = true;
      this.isSelectorShown = true;
      this.wrapperClasses = 'animate__animated zoomInJ';

      setTimeout(() => {
        this.document.body.classList.add('overflow-hidden');
        this.selectorClasses = 'is-shown';
      }, 250);

      setTimeout(() => {
        this.wrapperClasses = '';
        this.isAnimating = false;
      }, 300);
    }
  }

  async loadUserExchangeRates() {
    this.error = null;
    try {
      this.exchangeRates = await this.exchangeRatesService.getUserExchangeRates();
    } catch (error) {
      this.error = error.status || 'error';
    }
  }

  hideSelector() {
    this.loadUserExchangeRates();
    if (!this.isAnimating) {
      this.isAnimating = true;
      this.selectorClasses = '';

      setTimeout(() => {
        this.document.body.classList.remove('overflow-hidden');
        this.wrapperClasses = 'animate__animated zoomOutJ';
      }, 250);

      setTimeout(() => {
        this.wrapperClasses = '';
        this.isSelectorShown = false;
        this.isAnimating = false;
      }, 300);
    }
  }
}
