import { Pipe, PipeTransform } from '@angular/core';
import { BankFromAccountPipe } from './bank-from-account.pipe';

@Pipe({
  name: 'bankAccountName',
  pure: true,
})
export class BankAccountNamePipe implements PipeTransform {
  transform(value: string, ...args: any[]): any {
    return value ? this.biq.transform(value, 'name') : '';
  }
  constructor(private biq: BankFromAccountPipe) {}
}
