import { FormGroup, FormArray } from '@angular/forms';

import { Subject, BehaviorSubject } from 'rxjs';

import { IMoney, CURRENCY } from '@shared/currency/currency.model';
import { PAYMENT_TYPE } from '@shared/models';

import { FormGroupTypeSafe, generateUniqueId } from '@utils/index';

export interface EmployeeName {
  lastName?: string;
  firstName?: string;
  middleName?: string;
}

export class Employee implements EmployeeName {
  id: string;
  companyId: string;
  fio: string;
  lastName?: string;
  firstName?: string;
  middleName?: string;

  ocmcSum: IMoney;
  pensionSum: IMoney;
  salarySum: IMoney;
  socialSum: IMoney;

  iin: string; // 12 symbols numeric
  period: Date; // mm/yyyy
  amount: number; // макс = 212 500 Т, мин = 0,01 Т
  currency: CURRENCY;
  birthDate?: string;
  name?: string;
  iban?: string;

  isDeleting?: boolean;
  isSelected?: boolean;
  isHidden?: boolean;
  index?: number;

  formGroup?: FormGroup;

  constructor(employee?: Employee) {
    Object.assign(this, employee);

    this.name = `${employee.lastName} ${employee.firstName} ${employee.middleName}`;
    // const body = {
    //   ...this,
    //   ...employee,
    //   iin: '',
    //   period: this.getDefaultPeriod(),
    //   amount: null,
    //   name: '',
    // } as Employee;

    this.isDeleting = false;
    this.isSelected = false;
    this.isHidden = false;
    this.index = null;

    // this.formGroup = this.generateFormGroup(body);
  }

  public getShortName?() {
    const [surname, name, middlename] = this.name.split(' ');
    if (middlename) {
      return `${surname || ''} ${name[0] || ''}. ${middlename[0] || ''}.`;
    } else if (name) {
      return `${surname || ''} ${name[0] || ''}.`;
    } else {
      return `${surname || ''}`;
    }
  }

  public getBalance(paymentType: PAYMENT_TYPE) {
    switch (paymentType) {
      case PAYMENT_TYPE.PENSION:
        return this.pensionSum;
      case PAYMENT_TYPE.MEDICAL:
        return this.ocmcSum;
      case PAYMENT_TYPE.SOCIAL:
        return this.socialSum;
      case PAYMENT_TYPE.SALARY:
        return this.salarySum;
    }
  }

  // private getDefaultPeriod() {
  //   return moment().subtract(1, 'month').date(1).toDate();
  // }
}

export class EmployeeItemModel {
  id: string; // internal id to link additional period
  iin?: string; // 12 symbols numeric
  period?: Date; // mm/yyyy
  amount?: number; // макс = 212 500 Т, мин = 0,01 Т
  currency: CURRENCY;
  birthDate?: string;
  fio?: string;
  iban?: string;
  linkedWith?: string; //  additional period for linked item with iin
  firstName?: string;
  middleName?: string;
  lastName?: string;

  constructor(
    blueprint: Partial<EmployeeItemModel> = {},
    opts: { additionalPeriod?: string } = {}
  ) {
    Object.assign(
      this,
      {
        currency: CURRENCY.KZT,
      },
      blueprint
    );
    this.linkedWith = undefined;
    if (opts.additionalPeriod) {
      this.linkedWith = opts.additionalPeriod;
      delete this.id;
    }
    if (!this.id) {
      this.id = generateUniqueId();
    }
  }
}

export class EmployeesItem {
  group: FormGroupTypeSafe<EmployeeItemModel>;
  // index: number;
  cancelRemove$ = new Subject();
  removing$ = new BehaviorSubject(null);
  updating$ = new BehaviorSubject(false);
  blueprint?: Employee | EmployeeItemModel;
  children?: EmployeesItem[];
  linkedWith?: EmployeesItem;
  destroy$ = new Subject();
  autocomplete$ = new BehaviorSubject<string>(null);
  constructor(opts?: Partial<EmployeesItem>) {
    opts && Object.assign(this, opts);
  }

  destroy() {
    this.destroy$.next(Date.now());
    this.destroy$.complete();
    this.removing$.complete();
    this.autocomplete$.complete();
    this.cancelRemove$.complete();

    let parentFormArray = this.group.parent as FormArray;
    parentFormArray.removeAt(
      parentFormArray.controls.findIndex((group) => group === this.group)
    );
    parentFormArray = undefined;
    this.group = undefined;
    if (this.linkedWith) {
      this.linkedWith.children = this.linkedWith.children.filter(
        (item) => item !== this
      );
      this.linkedWith = undefined;
    }
  }
}
