import { Component, Input, Output, EventEmitter } from '@angular/core';
import { SMS_ERROR_TYPES } from '@shared/models';

@Component({
  selector: 'j-sms-error',
  templateUrl: './sms-error.component.html',
  styleUrls: ['./sms-error.component.scss'],
})
export class SmsErrorComponent {
  @Input() destinationLabel: string;
  @Input() destinationValue: string;

  @Input() errorType: string;
  errorTypes = SMS_ERROR_TYPES;

  @Input() isLoading: boolean;
  @Input() isTimerRunning: boolean;
  @Input() timerValue: number;
  @Input() backLabel: string;

  @Output() codeRequested = new EventEmitter();
  @Output() smsErrorSendSms = new EventEmitter();

  constructor() {}

  onClick() {
    this.codeRequested.emit();
  }

  onClickSms() {
    this.smsErrorSendSms.emit();
  }

  get translateParams() {
    return {
      timerValue: this.timerValue,
    };
  }
}
