import { Pipe, PipeTransform } from '@angular/core';
import { IMoney } from '@shared/currency/currency.model';

import { AmountFormatPipe } from './amount-format.pipe';
import { CurSymbolPipe } from './cur-symbol.pipe';

@Pipe({
  name: 'money',
})
export class MoneyPipe implements PipeTransform {
  constructor(
    private amountFormatPipe: AmountFormatPipe,
    private curSymbolPipe: CurSymbolPipe
  ) {}

  transform(value: IMoney, args?: any): any {
    if (!value) {
      return;
    }

    const amount = this.amountFormatPipe.transform(value.amount);
    const currency = this.curSymbolPipe.transform(value.currency);

    return `${amount} ${currency}`;
  }
}
