import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { IExchangeRates } from '@shared/exchange-rates/exchange-rates.model';
import { ExchangeRatesService } from '@shared/exchange-rates/exchange-rates.service';
import { NotifierService } from 'angular-notifier';

@Component({
  selector: 'j-currency-selector',
  templateUrl: './currency-selector.component.html',
  styleUrls: ['./currency-selector.component.scss']
})
export class CurrencySelectorComponent implements OnInit {
  @Input() exchangeRates: IExchangeRates[];
  @Output() closed = new EventEmitter();
  currencies: IExchangeRates[];
  searchText = '';
  error: string;

  constructor(
    private exchangeRatesService: ExchangeRatesService,
    private notifierService: NotifierService,
    private translateService: TranslateService
  ) { }

  ngOnInit(): void {
    this.loadAllCurrencies();
  }

  async loadAllCurrencies() {
    try {
      this.error = null;
      const currencies = await this.exchangeRatesService.getAllExchangeRates();
      this.currencies = this.mapIsActiveCurrencies(currencies);
    } catch (error) {
      this.error = error.code || 'error';
    }
  }

  mapIsActiveCurrencies(currencies) {
    return currencies.map(currency => {
      currency.isActive = this.exchangeRates.some(el => el.code === currency.code);
      return currency;
    }).filter(currency => {
      currency.isDefault = this.exchangeRates.find(el => el.code === currency.code)?.isDefault || false;
      return currency;
    })
  }

  onClose(event: Event) {
    event.preventDefault();
    this.closed.emit();
  }

  async onChange(event, currency) {
    try {
      const params = {
        code: currency.code,
        status: event.target.value
      }
      await this.exchangeRatesService.setUserExchangeRates(params);
      this.showNotification(params);
    } catch (error) {
      this.notifierService.notify('error', error.description || error.code || this.translateService.instant('EXCHANGE_RATES.SET_ERROR'));
    }
  }

  showNotification(params) {
    const currencyName = this.translateService.instant(`SHARED.CURRENCY_DESCRIPTION.${params.code}`);
    this.notifierService.notify(
      'default',
      this.translateService.instant(params.status === 'true' ? 'EXCHANGE_RATES.CURRENCY_ADDED' : 'EXCHANGE_RATES.CURRENCY_DELETED', 
          {currency: currencyName})
    );
  }

  onSearch(event){
    this.searchText = event.target.value;
  }
}
