import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'j-password-requirement',
  templateUrl: './password-requirement.component.html',
  styleUrls: ['./password-requirement.component.scss'],
})
export class PasswordRequirementComponent implements OnInit {
  @Input() label: string;
  @Input() isValid: boolean;
  @Input() isChecked: boolean;

  constructor() {}

  ngOnInit(): void {}

  get isFailed() {
    return !this.isValid && this.isChecked;
  }

  get color() {
    if (this.isValid) {
      return 'success';
    } else if (this.isChecked) {
      return 'danger';
    }
    return 'disabled';
  }

  get textClass() {
    if (this.isValid) {
      return 'text-dark';
    }
    if (!this.isValid && this.isChecked) {
      return 'text-danger';
    }
    return 'text-muted';
  }
}
