import { Inject, Injectable } from '@angular/core';
import { Router } from '@angular/router';
import {
  HttpClient,
  HttpHeaders,
  HttpParams,
  HttpErrorResponse,
} from '@angular/common/http';

import { Observable, of, throwError } from 'rxjs';
import { catchError, take } from 'rxjs/operators';

import { ERRORS } from './errors';
import { IApiConfig } from './api.module';

export interface EndPoints {}

export enum HEADERS {
  AUTHORIZATION = 'Authorization',
  SMS = 'SMSAuthorization',
  PHONE = 'PhoneAuthorization',
  EMAIL = 'EmailAuthorization',
  DEVICE = 'DeviceAuthorization',
  OTP = 'OTPAuthorization',
  SKIP_OFFLINE_INTERCEPTOR = 'X-Skip-Interceptor',
}

export enum SMS_HEADERS {
  REGISTER = 'REGISTRATION_JB',
  RESTORE = 'RECOVERY_ACCESS_JB',
  SIGNING_PAYMENT = 'SIGNING_PAYMENT',
  AUTHORIZATION = 'AUTHORIZATION_JB',
  SIGNING_PACKAGE = 'SIGNING_PACKAGE',
  OPENING_ACCOUNT = 'OPENING_ACCOUNT',
}

export interface IError {
  code: string;
  status: number;
  data?: any;
  description?: string;
}

@Injectable({
  providedIn: 'root',
})
export class ApiService {
  endPoints;
  public baseUrl: string;

  constructor(
    @Inject('config') private config: IApiConfig,
    private http: HttpClient,
    private router: Router
  ) {
    this.baseUrl = config.baseUrl;
  }

  // resolve(url: string, params?) {
  //   if (!params) {
  //     return url;
  //   }
  //   const resolved = Object.keys(params).reduce((acc, param) => {
  //     return acc.replace(`::${param}`, params[param]);
  //   }, url);
  //   return resolved;
  // }

  /**
   * Performs GET request via http client
   *
   * @param url request url. Empty by default.
   */
  async get<T>(
    urlPath: string = '',
    headers: HttpHeaders = null,
    params: HttpParams = null,
    extras: any = null
  ) {
    const url = `${this.baseUrl}/${urlPath}`;
    try {
      const getResult = await this.http
        .get<T>(url, this.getOptions(headers, params, extras))
        .toPromise();
      return getResult;
    } catch (response) {
      throw this.mapError(response);
    }
  }

  /**
   * Performs GET request via http client with body
   *
   * @param url request url. Empty by default.
   * @param body request body
   */
  async getWithBody<T>(
    urlPath: string = '',
    body: any = {},
    headers: HttpHeaders = null,
    params: HttpParams = null
  ) {
    const url = `${this.baseUrl}/${urlPath}`;

    try {
      return await this.http
        .request<T>('get', url, {
          body,
          ...this.getOptions(headers, params),
        })
        .toPromise();
    } catch (response) {
      throw this.mapError(response);
    }
  }

  getV2Observable<T>(
    urlPath: string = '',
    headers: HttpHeaders = null,
    params: HttpParams = null,
    extras: any = null
  ) {
    const url = `${this.baseUrl.replace('/v1', '')}/${urlPath}`;
    return this.http.get<T>(url, this.getOptions(headers, params, extras)).pipe(
      catchError((response, caught) => {
        return throwError(this.mapError(response));
      })
    );
  }

  postV2Observable<T>(
    urlPath: string = '',
    body: any = {},
    headers: HttpHeaders = null,
    params: HttpParams = null,
    extras: any = null
  ) {
    const url = `${this.baseUrl.replace('/v1', '')}/${urlPath}`;
    return this.http
      .post<T>(url, body, this.getOptions(headers, params, extras))
      .pipe(
        catchError((response, caught) => {
          return throwError(this.mapError(response));
        })
      );
  }

  getObservable<T>(
    urlPath: string = '',
    headers: HttpHeaders = null,
    params: HttpParams = null,
    extras: any = null
  ) {
    const url = `${this.baseUrl}/${urlPath}`;
    return this.http.get<T>(url, this.getOptions(headers, params, extras)).pipe(
      catchError((response, caught) => {
        return throwError(this.mapError(response));
      })
    );
  }

  /**
   * Performs POST request via http client
   *
   * @param url request url. Empty by default.
   * @param body request body
   */
  async post<T>(
    urlPath: string = '',
    body: any = {},
    headers: HttpHeaders = null,
    params: HttpParams = null,
    extras: any = null
  ) {
    const url = `${this.baseUrl}/${urlPath}`;
    try {
      return await this.http
        .post<T>(
          url,
          body,
          this.getOptions(
            headers,
            params,
            extras,
            body instanceof FormData ? true : false
          )
        )
        .toPromise();
    } catch (response) {
      throw this.mapError(response, extras);
    }
  }

  postObservable<T>(
    urlPath: string = '',
    body: any = {},
    headers: HttpHeaders = null,
    params: HttpParams = null,
    extras: any = null
  ) {
    const url = `${this.baseUrl}/${urlPath}`;
    return this.http
      .post<T>(
        url,
        body,
        this.getOptions(
          headers,
          params,
          extras,
          body instanceof FormData ? true : false
        )
      )
      .pipe(
        catchError((response, caught) => {
          return throwError(this.mapError(response));
        })
      );
  }

  /**
   * Performs PATCH request via http client
   *
   * @param url request url. Empty by default.
   * @param body request body
   */
  async patch<T>(
    urlPath: string = '',
    body: any = {},
    headers: HttpHeaders = null,
    params: HttpParams = null
  ) {
    const url = `${this.baseUrl}/${urlPath}`;
    try {
      return await this.http
        .patch<T>(url, body, this.getOptions(headers, params))
        .toPromise();
    } catch (response) {
      throw this.mapError(response);
    }
  }

  /**
   * Performs PUT request via http client
   *
   * @param url request url. Empty by default.
   * @param body request body
   */
  async put<T>(
    urlPath: string = '',
    body: any = {},
    headers: HttpHeaders = null,
    params: HttpParams = null
  ) {
    const url = `${this.baseUrl}/${urlPath}`;
    try {
      return await this.http
        .put<T>(url, body, this.getOptions(headers, params))
        .toPromise();
    } catch (response) {
      throw this.mapError(response);
    }
  }

  /**
   * Performs PUT request via http client
   *
   * @param url request url. Empty by default.
   * @param body request body
   */
  async deleteWithBody<T>(
    urlPath: string = '',
    body: any = {},
    headers: HttpHeaders = null,
    params: HttpParams = null
  ) {
    const url = `${this.baseUrl}/${urlPath}`;

    try {
      return await this.http
        .request<T>('delete', url, {
          body,
          ...this.getOptions(headers, params),
        })
        .toPromise();
    } catch (response) {
      throw this.mapError(response);
    }
  }

  /**
   * Performs PUT request via http client
   *
   * @param url request url. Empty by default.
   * @param body request body
   */
  async delete<T>(
    urlPath: string = '',
    headers: HttpHeaders = null,
    params: HttpParams = null
  ) {
    const url = `${this.baseUrl}/${urlPath}`;

    try {
      return await this.http
        .delete<T>(url, this.getOptions(headers, params))
        .toPromise();
    } catch (response) {
      throw this.mapError(response);
    }
  }

  /**
   * Extracts data from Error response and return as IError object
   *
   * @param errorResponse HttpErrorResponse
   */
  mapError(errorResponse: HttpErrorResponse, extras = null): IError {
    if (extras?.responseType == 'blob') {
      return errorResponse as any;
    } else {
      const error = errorResponse.error;
      let code = ERRORS.UNKNOWN;

      code =
        ERRORS[error?.code?.toUpperCase()] ||
        error?.code ||
        error?.error ||
        null;

      if (code == ERRORS.INVALID_TOKEN) {
        this.router.navigateByUrl('auth');
      }

      const data = error?.data || null;
      const description =
        error?.description ||
        error?.error_description ||
        error?.message ||
        null;

      return {
        status: errorResponse.status,
        code,
        data,
        description,
      };
    }
  }

  /**
   * Updates http options for matching type
   *
   * @param headers http headers
   * @param params http params
   */
  private getOptions(
    headers: HttpHeaders,
    params: HttpParams,
    extras: any = null,
    disableContentType: boolean = false
  ): object {
    let options: any = {};
    if (params) {
      options.params = params;
    }
    if (headers) {
      if (!headers.has('Content-Type') && !disableContentType) {
        headers = headers.append('Content-Type', 'application/json');
      }
      options.headers = headers;
    } else if (!disableContentType) {
      headers = new HttpHeaders();
      headers = headers.append('Content-Type', 'application/json');
      options.headers = headers;
    }

    if (extras) {
      for (const key in extras) {
        if (Object.prototype.hasOwnProperty.call(extras, key)) {
          options[key] = extras[key];
        }
      }
    }
    return options;
  }
}
