import { Component, OnInit, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';

@Component({
  selector: 'j-import-success',
  templateUrl: './import-success.component.html',
  styleUrls: ['./import-success.component.scss'],
})
export class ImportSuccessComponent {
  @Input() operationsCount: number = 0;

  constructor(public modal: NgbActiveModal, private router: Router) {}

  dismissModal() {
    this.modal.dismiss();
  }

  closeModal() {
    this.modal.close();
  }

  get successDescription() {
    switch (this.operationsCount % 20) {
      case 1:
        return 'IMPORT.SUCCESS.ONE';
      case 2:
      case 3:
      case 4:
        return 'IMPORT.SUCCESS.FEW';
      default:
        return 'IMPORT.SUCCESS.OTHER';
    }
  }

  get translateParams() {
    return {
      operationsCount: this.operationsCount,
    };
  }
  
  navigateToSigned(){
    this.router.navigate(['/history/tenge',{}])
  }
}
