import { Input, Component } from '@angular/core';
import { AbstractControl } from '@angular/forms';

let nextUniqueId = 0;

@Component({
  selector: 'j-error',
  templateUrl: './error.component.html',
  host: {
    class: 'j-error',
    role: 'alert',
    '[attr.id]': 'id',
  },
})
export class JErrorComponent {
  @Input() id = `j-error-${nextUniqueId++}`;
  @Input() params;
  @Input() errorMessages?: {
    error: string;
    message: string;
  }[];

  /**
   * Parent formGroup or form required to get abstract control if provided only control name
   */
  @Input() control: AbstractControl | string;

  constructor() {}
}
