import {
  Component,
  forwardRef,
  Input,
  OnDestroy,
  OnInit,
  Optional,
  Self,
  ViewEncapsulation,
} from '@angular/core';
import {
  AbstractControl,
  FormGroupDirective,
  NgControl,
  NG_VALUE_ACCESSOR,
  ControlValueAccessor,
  Validator,
} from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { MaskedService } from '@shared/masked/masked.service';
import { PAYMENT_TYPE } from '@shared/models';
import { Subscription } from 'rxjs';
import {
  distinctUntilChanged,
  filter,
  startWith,
  throttleTime,
} from 'rxjs/operators';
import { EmployeesModalComponent } from '../../modals/employees-modal/employees-modal.component';
import { BOOTSTRAP_BREAKPOINTS } from '@shared/responsive-toolkit/constants';
import { errors } from '@shared/validators';
import { EmployeeFormModel } from '@payments/services/form-model';
import { EmployeeItemModel } from '@shared/employee/employee.model';
import { ErrorsConfiguration } from '@ngspot/ngx-errors';
import {
  isoDateToPeriodInputSafe,
  isoDateToDateSafe,
  periodInputDateToISODateSafe,
  ibanPrefix,
} from '@utils/index';
import { CURRENCY } from '@shared/currency/currency.model';

@Component({
  selector: 'j-employee-group-control',
  templateUrl: './employee-group-control.component.html',
  styleUrls: ['./employee-group-control.component.scss'],
  host: {
    class: 'employee-group',
  },
  providers: [
    // {
    //   provide: NG_VALUE_ACCESSOR,
    //   useExisting: forwardRef(() => EmployeeGroupControlComponent),
    //   multi: true,
    // },
    {
      provide: ErrorsConfiguration,
      useValue: {
        showErrorsOnlyIfInputDirty: true,
        showErrorsWhenFormSubmitted: true,
      },
    },
  ],
  encapsulation: ViewEncapsulation.None,
})
export class EmployeeGroupControlComponent
  implements OnInit, OnDestroy, ControlValueAccessor
{
  @Input() paymentType: PAYMENT_TYPE;

  @Input() isSubmitted: boolean;
  @Input() ibanRequired?: boolean;
  @Input() birthDateRequired?: boolean = true;
  @Input() periodRequired?: boolean;
  @Input() additionalPeriodEnabled?: boolean;
  // @Input() disablePeriod?: boolean;

  // В зависимости от типа платежа могут меняться, поэтому надо передавать
  @Input() minAmount = 0.01;
  @Input() maxAmount = 212500;
  @Input() currency: CURRENCY = CURRENCY.KZT;

  errors = errors.employees;
  value = undefined;
  disabled = false;
  readonly subs = new Subscription();
  constructor(
    private modalService: NgbModal,
    private maskedService: MaskedService,
    @Optional() @Self() public ngControl: NgControl,
    @Optional() private parentGroup: FormGroupDirective
  ) {
    if (this.ngControl !== null) {
      this.ngControl.valueAccessor = this;
    }
  }

  ngOnInit(): void {
    if (this.periodRequired) {
      this.listenCommonPeriod();
    }
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  get commonPeriodEnabled(): AbstractControl {
    return this.parentGroup?.form.controls.commonPeriodEnabled;
  }

  get commonPeriod(): AbstractControl {
    return this.parentGroup?.form.controls.period;
  }

  get selectedCount(): number {
    return (this.value && this.value.length) || 0;
  }

  listenCommonPeriod() {
    this.subs.add(
      this.commonPeriodEnabled.valueChanges
        .pipe(
          startWith(this.commonPeriodEnabled.value),
          throttleTime(100, undefined, { leading: true, trailing: true }),
          distinctUntilChanged()
        )
        .subscribe((v) => {
          if (v) {
            this.commonPeriod.enable();
            // this.formArray.controls.forEach((group) =>
            //   (group as FormGroup).controls.period.disable()
            // );
          } else {
            this.commonPeriod.disable();
            this.commonPeriod.patchValue(null);
            // this.formArray.controls.forEach((group) =>
            //   (group as FormGroup).controls.period.enable()
            // );
          }
        })
    );
    this.subs.add(
      this.commonPeriod.valueChanges
        .pipe(
          filter((v) => !!v),
          throttleTime(100, undefined, { leading: true, trailing: true }),
          distinctUntilChanged()
        )
        .subscribe((v) => {
          // this.formArray.controls.forEach((group) =>
          //   (group as FormGroup).controls.period.patchValue(v)
          // );
        })
    );
  }

  showPicker() {
    let modal = this.modalService.open(EmployeesModalComponent, {
      backdropClass: 'backdrop_light',
      backdrop: 'static',
      windowClass: 'modal_light modal-adaptive',
      size: 'lg',
      centered: false,
    });

    this.ngControl && this.ngControl.control.markAsTouched();
    Object.assign(modal.componentInstance as EmployeesModalComponent, {
      additionalPeriodEnabled: this.additionalPeriodEnabled,
      initialEmployees: this.normalizeForInnerModel(this.value || []),
      parentForm: this.parentGroup,
      birthDateRequired: this.birthDateRequired,
      ibanRequired: this.ibanRequired,
      periodRequired: this.periodRequired,
      paymentType: this.paymentType,
      minAmount: this.minAmount,
      maxAmount: this.maxAmount,
    });

    modal.result
      .then((result) => {
        this.value = this.normalizeModel(result);
        this.ngControl.control.setValue(this.value);
        this.ngControl.control.markAsDirty();
      })
      .catch(() => {
        /*  no selection or dismiss */
      })
      .finally(() => (modal = undefined));
  }

  writeValue(value: any): void {
    this.value = value;
  }

  registerOnChange(fn: (value: any) => void): void {
    this._onChange = fn;
  }

  registerOnTouched(fn: () => {}): void {
    this._onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
    // this._changeDetectorRef.markForCheck()
  }
  /** `View -> model callback called when value changes` */
  _onChange: (value: any) => void = (c) => {};

  /** `View -> model callback called when select has been touched` */
  _onTouched = () => {};

  private normalizeModel(employees: EmployeeItemModel[]) {
    const result = employees.map((rawItem) => {
      const item = {
        ...rawItem,
        amount: this.maskedService.getNumberedAmount(rawItem.amount || '0'),
        period: periodInputDateToISODateSafe(rawItem.period),
        birthDate: periodInputDateToISODateSafe(rawItem.birthDate),
      };
      if (this.ibanRequired) {
        item.iban = ibanPrefix(item.iban, this.currency);
      }
      delete item.id;
      delete item.linkedWith;
      return item;
    });
    return result;
  }

  normalizeForInnerModel(list) {
    const result = list.reduce(
      (acc, rawItem) => {
        let linkWith;
        let newItem: EmployeeItemModel;
        if (rawItem.iin && (linkWith = acc.equalIin[rawItem.iin])) {
          newItem = new EmployeeItemModel(rawItem, {
            additionalPeriod: linkWith.id,
          });
        } else {
          newItem = new EmployeeItemModel(rawItem);
          this.additionalPeriodEnabled && (acc.equalIin[newItem.iin] = newItem);
        }
        newItem.period = isoDateToDateSafe(newItem.period);
        newItem.birthDate = isoDateToPeriodInputSafe(newItem.birthDate);
        if (this.ibanRequired) {
          // remove iban prefix
          newItem.iban = newItem.iban && newItem.iban.slice(2);
        }
        acc.list.push(newItem);
        return acc;
      },
      {
        list: [],
        equalIin: {},
      } as {
        list: Array<EmployeeItemModel>;
        equalIin: Record<string, EmployeeItemModel>;
      }
    );

    return result.list;
  }
}
