import {
  Component,
  forwardRef,
  Input,
  SimpleChanges,
  ViewChild,
} from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';

import { MaskPipe } from 'ngx-mask';

import { MaskedService } from '@shared/masked/masked.service';

import { JInputComponent } from '../input.component';

@Component({
  selector: 'j-iin-input',
  templateUrl: './iin-input.component.html',
  styleUrls: ['./iin-input.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => IinInputComponent),
      multi: true,
    },
  ],
})
export class IinInputComponent extends JInputComponent {
  @Input() label = 'SHARED.IIN';
  @Input() isLoading = false;
  @Input()
  maskType = 'iin';

  @Input() iinName: string;

  @ViewChild('nameSuffix', { static: true }) nameSuffix;

  iinMaskConfig = this.maskedService.getConfig('iin');

  constructor(public maskedService: MaskedService, private maskPipe: MaskPipe) {
    super(maskedService);
  }

  onPaste(event: ClipboardEvent) {
    const clipboardData = event.clipboardData || (window as any).clipboardData;
    const pastedText = clipboardData.getData('text');
    const maskedText = this.maskPipe.transform(
      pastedText,
      this.iinMaskConfig.mask
    );

    setTimeout(() => {
      this.input.nativeElement.setSelectionRange(
        maskedText.length,
        maskedText.length
      );
    }, 0);
  }

  ngOnInit() {
    super.ngOnInit();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (
      this.nameSuffix &&
      changes.iinName &&
      changes.iinName.currentValue == '' &&
      changes.iinName.previousValue != ''
    ) {
      this.iinName = '';
    }
  }

  get isShadowHidden() {
    return (this.isValueEmpty() && !this.isFocused) || this.isValueMaxWidth();
  }

  get shadow() {
    const maskedValue = this.maskPipe.transform(
      this.formControl.value,
      this.iinMaskConfig.mask
    );
    return this.maskedService.getShadow(this.maskType, maskedValue);
  }

  private isValueEmpty() {
    return !this.formControl.value || this.formControl.value.length === 0;
  }

  private isValueMaxWidth() {
    return (
      this.formControl.value &&
      this.formControl.value.length === this.maskConfig.shadow.length
    );
  }
}
