import { IMoney } from '@shared/currency/currency.model';
import { PAYMENT_TYPE } from '@shared/models';

export enum PERMISSION {
  READ = 'READ',
  EDIT = 'EDIT',
  CREATE = 'CREATE',
  DELETE = 'DELETE',
  EXPORT = 'EXPORT',
  SIGN = 'SIGN',
  REVOKE = 'REVOKE',
}

export interface IPermission {
  id: string;
  paymentType: PAYMENT_TYPE;
  username: string;
  iin: string;
  bin: string;
  permissions: PERMISSION[];
}

export interface ILimit {
  id?: string;
  paymentType: PAYMENT_TYPE;
  limit: IMoney;
}

export const LIMIT_MAX_AMOUNT = 999999999999;
