import { Injectable } from '@angular/core';
import { ApiService } from '@api/api.service';
import { CompanyService } from '@company/company.service';
import { ILimit } from '@authorities/models';
import { PAYMENT_TYPE } from '@shared/models';
import { CURRENCY } from '@shared/currency/currency.model';

const LIMITS_MOCK: ILimit[] = [
  {
    id: '06392710-7bf1-4eab-8cdc-75d1a3e967e8',
    paymentType: PAYMENT_TYPE.FOREIGN,
    limit: {
      amount: 100.0,
      currency: CURRENCY.KZT,
    },
  },
  {
    id: '06392710-7bf1-4eab-8cdc-75d1a3e967e8',
    paymentType: PAYMENT_TYPE.CONVERSION,
    limit: {
      amount: 100.0,
      currency: CURRENCY.KZT,
    },
  },
  {
    id: '06392710-7bf1-4eab-8cdc-75d1a3e967e8',
    paymentType: PAYMENT_TYPE.IN_BETWEEN,
    limit: {
      amount: 100.0,
      currency: CURRENCY.KZT,
    },
  },
];

@Injectable({
  providedIn: 'root',
})
export class AuthorityLimitsService {
  constructor(
    private companyService: CompanyService,
    private apiService: ApiService
  ) {}

  async getPaymentLimit(
    paymentType: PAYMENT_TYPE,
    authorityId: string = this.companyService.currentAuthority.id
  ) {
    const limits = await this.getLimits(authorityId);
    return limits.find((limit) => limit.paymentType === paymentType) || null;
  }

  getLimits(authorityId: string) {
    const url = this.mapUrl(authorityId);
    return this.apiService.get<ILimit[]>(url);
  }

  createLimit(authorityId: string, limit: ILimit): Promise<ILimit> {
    const url = this.mapUrl(authorityId);
    return this.apiService.post<ILimit>(url, limit);
  }

  updateLimit(authorityId: string, limit: ILimit) {
    const url = `${this.mapUrl(authorityId)}/${limit.id}`;
    return this.apiService.put<ILimit>(url, limit);
  }

  deleteLimit(authorityId: string, limit: ILimit) {
    const url = `${this.mapUrl(authorityId)}/${limit.id}`;
    return this.apiService.delete<ILimit>(url);
  }

  private mapUrl(authorityId: string) {
    return `companies/${this.companyService.currentId}/authorities/${authorityId}/limits`;
  }
}
