import {
  Directive,
  HostListener,
  ElementRef,
  Input,
  AfterContentInit,
  Optional,
  Self,
} from '@angular/core';
import { MaskDirective } from 'ngx-mask';
import { NgModel, NgControl, FormControlDirective } from '@angular/forms';

/**
 * @description Helper directive for fixing pasting data from clipboard with ngx mask prefix directive attached
 * Strip prefixied string and prevent cutting last letters
 */
@Directive({
  selector: '[jReplaceOnPaste]',
})
export class ReplaceOnPasteDirective {
  @Input() jReplaceOnPaste: string;
  @Input() with?: string;
  @HostListener('paste', ['$event']) onPasteEvent(event: any) {
    try {
      let clipboardData, pastedData;
      clipboardData = event.clipboardData || (window as any).clipboardData;
      pastedData = clipboardData.getData('Text');

      this.elem.nativeElement.value = pastedData.replace(
        this.jReplaceOnPaste,
        this.with || ''
      );
    } catch (error) {}
  }
  constructor(private elem: ElementRef<HTMLInputElement>) {}
}
